<template>
    <div class='_top-up'>
        <!-- 充值 - 弹窗 -->
        <el-dialog
            title="充值"
            v-dialogDrag
            :visible.sync="tablesThis.TopupShow"
            :before-close="handleClose">
            <span class='span_body'>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="17%" class="demo-ruleForm">
                    <el-form-item label="当前余额" prop="name">
                        :<el-input v-model="ruleForm.balance" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="充值金额" prop="name">
                        :<el-input v-model="ruleForm.topupAmount" placeholder='请输入充值金额'></el-input>
                    </el-form-item>
                    <el-form-item label="赠送金额" prop="name">
                        :<el-input v-model="ruleForm.givingAmount" placeholder='请输入赠送金额'></el-input>
                    </el-form-item>
                    <el-form-item label="充值时间" prop="name">
                        :<el-date-picker
                            v-model="ruleForm.time"
                            type="date"
                            prefix-icon="el-icon-caret-bottom"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="付款方式" prop="name">
                        :<el-select v-model="value" placeholder="请选择方式">
                            <el-option
                            v-for="item in pay"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="备注" prop="name">
                        :<el-input type="textarea" v-model="ruleForm.note" placeholder='可填写备注说明'></el-input>
                    </el-form-item>
                </el-form>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="tablesThis.TopupShow = false" size='small'>确 定</el-button>
                <el-button @click="tablesThis.TopupShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
  name: 'top_up',
  data () {
    return {
      dialogVisible: true,
      ruleForm: {
        balance: 0, // 余额
        topupAmount: 0, // 充值金额
        givingAmount: 0, // 赠送金额
        time: '',// 充值时间
        pay: [], // 支付方式
        note: '' // 备注
      }
    }
  },
  inject: ['tablesThis']
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
@media screen and (max-width: 1380px){
  ._top-up {
        /deep/.el-dialog__wrapper{
            .el-dialog {
                @include wh(vw1(640),vw1(660));
                border-radius: vw1(8);
                display: flex;
                flex-direction: column;
                .el-dialog__header{
                    @include wh(100%, vw1(60));
                    border-top-right-radius: vw1(8);
                    border-top-left-radius: vw1(8);
                    @include bg-defaul($bg-color);
                    box-sizing: border-box;
                    padding: vw1(22) vw1(30);
                    .el-dialog__title{
                        color: #fff;
                        font-size: vw1(16);
                        line-height: 0;
                    }
                    .el-dialog__headerbtn{
                        top: vw1(20);
                        .el-dialog__close{
                            color: #fff;
                        }
                    }
                }
                .el-dialog__body{
                    flex: 1;
                    .span_body{
                        .el-form{
                            .el-form-item{
                                height: vw1(40);
                                .el-form-item__label{
                                    height: 100%;
                                    text-align: justify;
                                    font-size: vw1(16);
                                    color: #333333;
                                }
                                .el-form-item__label::after{
                                    content: " ";
                                    display: inline-block;
                                    width: 100%;
                                }
                                .el-form-item__content{
                                    display: flex;
                                    .el-select .el-input{
                                        margin-left: 0;
                                    }
                                    .el-input,.el-textarea,.el-select{
                                        margin-left: vw1(8);
                                        .el-textarea__inner{
                                            height: vw1(100);
                                            resize: none;
                                        }
                                        .el-input__inner[disabled=disabled]{
                                            background: #fff;
                                            border:none;
                                        }
                                        .el-input__inner{
                                            height: vw1(40);
                                        }
                                    }
                                    .el-date-editor{
                                        width: 100%;
                                        position: relative;
                                        .el-input__prefix{
                                            // right: vw(10);
                                            left: 93.5%;
                                            font-size: vw1(25);
                                            color: #999999;
                                        }
                                        .el-input__suffix{
                                            display: none;
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
                .el-dialog__footer{
                    @include wh(100%, vw1(80));
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    .dialog-footer {
                        button{
                            @include wh(vw1(120),auto);
                            background-color: #e7eaec;
                            color: #999999;
                            font-size: vw1(16);
                        }
                        button:first-child{
                            @include bg-defaul($bg-color);
                            color: #fff;
                            margin-right: vw1(30);
                        }
                    }
                }
            }
        }
    }  
}
