<template>
    <div class='login_s'>
      <login-com v-show='login_show == 1'></login-com>
      <register v-show='login_show == 2'></register>
      <login-reset v-show='login_show == 3'></login-reset>
      <erp-bingding v-show='login_show == 4'></erp-bingding>
    </div>
</template>
<script>
import loginCom from './login_com/com'
import register from './login_registered/registered'
import loginReset from './login_resetparss/reset'
import erpBingding from './wxnew_binding/binding'
import {getseSsionStorage, nativegetcookie} from '../../cookie/cookie'
import {mapState,mapMutations} from 'vuex'
export default {
    name: 'logins',
    data () {
      return {
      }
    },
    created() {
      let getLoginPage = getseSsionStorage('login_page');
      if (getLoginPage!=null) {
        this.LOGIN_PAGE(getLoginPage)
      }
    },
    provide () {
      return {
        _this: this
      }
    },
    methods: {
      ...mapMutations(['LOGIN_PAGE'])
    },
    computed: {
      ...mapState(['login_show','userLoginInfo'])
    },
    components: {
      loginCom: loginCom,
      register: register,
      loginReset: loginReset,
      erpBingding: erpBingding
    }
}
</script>
<style lang="scss" scoped>
@import '../../style/mixin';
.login_s{
  @include wh(100%, 100%);
  background: url('../../../static/header_back/bg.png')no-repeat;
  background-size: 100% 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  overflow: hidden;
}
</style>

