<template>
      <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1" id="wareBox">
          <div class="modal-header" @mousedown="setStartingPoint">
            {{ that.title4 }}
            <i class="el-icon-close" round @click="hideModal"></i>
          </div>
          <div class="modal-main">
            <el-form ref="form" :model="form" :rules="rules" label-width="16%" class="fromBox">
              <el-form-item prop="name" label="商 品 名 称">
                <el-input v-model.trim="form.name" placeholder="请输入商品名称" maxlength="20" ref="nameInput" class="nameBox" readonly></el-input>
              </el-form-item>
              <el-form-item prop="deposit" label="租 赁 押 金">
                <el-input v-model.trim="form.deposit" placeholder="请输入租赁押金" maxlength="8" ref="depositInput" @keyup.native="change_num(3)"></el-input>
              </el-form-item>              
              <el-form-item prop="payment1" label="计 费 方 式" class="check-item">
                <el-checkbox v-model="form.payment1" @change="paymentFunction(1,$event)">每天</el-checkbox>
                <el-checkbox v-model="form.payment2" @change="paymentFunction(2,$event)">每月</el-checkbox>
                <el-checkbox v-model="form.payment3" @change="paymentFunction(3,$event)">每年</el-checkbox>
              </el-form-item> 
              <el-form-item prop="price" label="计 费 金 额">
                <el-input v-model.trim="form.price" placeholder="请输入计费金额" maxlength="8" ref="priceInput" @keyup.native="change_num(1)" @change="salePrice()"></el-input>
              </el-form-item>                                      
              <el-form-item prop="num" label="租 赁 数 量">
                <el-input v-model.trim="form.num" placeholder="请输入租赁数量" maxlength="3" ref="numInput" @keyup.native="change_num1()" @change="salePrice()"></el-input>
              </el-form-item>
              <el-form-item prop="getMoney" label="预 付 租 金">
                <el-input v-model.trim="form.getMoney" placeholder="请输入预付租金" maxlength="15" ref="get_money_Input" @keyup.native="change_num(2)"></el-input>
              </el-form-item>                            
              <el-form-item prop="rent_obj_id" label="租 赁 对 象">
                <el-select v-model="form.rent_obj_id" placeholder="选择对象" ref="rent_obj_id_Input" @change="change_rent_obj()">
                  <el-option-group key="1" label="学生">
                    <el-option v-for="(item,index) in all_students_list" :value="item.stu_id" :key="item.stu_id" :label="item.stu_name+'----账户余额：'+item.stu_balance">
                      <template>
                        <span>{{item.stu_name}}</span><span style="float: right">账户余额：{{item.stu_balance}}</span>
                      </template>
                    </el-option>
                  </el-option-group>
                  <el-option-group key="0" label="其他">
                    <el-option value="0" key="0" label="其他"></el-option>
                  </el-option-group>
                </el-select>
              </el-form-item>                            
              <el-form-item prop="starTime" label="开 始 时 间" class="birthdayBox">
                <el-date-picker
                  v-model="form.starTime"
                  type="date"
                  format
                  prefix-icon="el-icon-caret-bottom"
                  placeholder="选择时间"
                  :clearable="false"
                  ref="starTimeeInput"
                  popper-class="selectBirthday">
                </el-date-picker>
              </el-form-item>
              <el-form-item prop="endTime" label="结 束 时 间" class="birthdayBox">
                <el-date-picker
                v-model="form.endTime"
                type="date"
                format
                prefix-icon="el-icon-caret-bottom"
                placeholder="选择时间"
                :picker-options="pickerOptions"
                :clearable="false"
                ref="endTimeeInput"
                popper-class="selectBirthday">
              </el-date-picker>
            </el-form-item>              
              <el-form-item prop="pay_id" label="付 款 方 式">
                <el-select v-model="form.pay_id" placeholder="选择付款方式" ref="payInput">
                  <el-option v-for="(item,index) in pay_list" :value="item.pay_id" :key="item.pay_id" :label="item.pay_name" v-if="item.is_only_stu == 0"></el-option>
                  <el-option v-for="(item,index) in pay_list" :value="item.pay_id" :key="item.pay_id" :label="item.pay_name" v-if="item.is_only_stu == 1 && form.rent_obj_id != 0"></el-option>
                </el-select>
              </el-form-item>                                                                        
              <el-form-item prop="resume" label="备 注" style="margin-bottom: 10px">
                <el-input
                type="textarea"
                :autosize="{ minRows: 3, maxRows: 10}"
                placeholder="可填写备注说明"
                maxlength="500"
                v-model="form.resume">
              </el-input>
            </el-form-item>                                                           
          </el-form>                
        </div>
      <div class="modal-footer alertFooter">
        <el-button type="primary" round @click="submit">保存</el-button>
        <el-button round @click="hideModal">取消</el-button> 
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios';
  import {all_students,all_pay_list,goods_rental} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {validateIdNo,validatePhone,checkText} from '../../../../../script/validate.js'
  let that
  export default {
    name: 'lease',
    props: {
        that: {
          type: Object,
          default: false
        },                       
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,  
            all_students_list:[],//学生列表
            pay_list:[],//支付方式列表     
            form: {
              name: this.that.goods_name,
              deposit:this.that.margin_amount,
              payment1:true,//日结
              payment2:false,//月结
              payment3:false,//年结
              pay_id:null,
              goods_id:this.that.editGoodsId,
              getMoney:0,
              rent_obj_type:1,//对象类型学生1，其他2
              num:null,
              price:null,
              starTime:new Date(),
              endTime:new Date(),
              resume:'',
              payment:'',
              rent_obj_id:null//学生为id，其他为0
            },
            rules: {
              getMoney: [
              { required: true, message: '请输入预付金额', trigger: 'blur'}
              ],              
              num: [
              { required: true, message: '请输入出库数量', trigger: 'blur'}
              ],
              price: [
              { required: true, message: '请输入出库单价', trigger: 'blur'} ,
              ],
              pay_id: [
              { required: true}
              ],
              payment: [
              { required: true}
              ],
              starTime: [
              { required: true, message: '请输入开始日期', trigger: 'blur'}
              ],              
              endTime: [
              { required: true, message: '请输入结束日期', trigger: 'blur'}
              ],                            
              rent_obj_id: [
              { required: true}
              ],                                                                
            },
            pickerOptions: {
              disabledDate(time) {
                console.log(11)
                let _now = new Date(that.form.starTime).getTime(),
                    t = 24 * 60 * 60 * 1000,
                    sevenDays = _now - t;
                    return time.getTime() <= sevenDays;
                },
            },
            // pickerOptions1: {
            //   disabledDate(time) {
            //     let _now = Date.now(),
            //     t = 365 * 24 * 60 * 60 * 1000,
            //     sevenDays = _now + t;
            //     return time.getTime() > sevenDays;
            //   },
            // },                
            Imageport          
        }
    },
    beforeCreate: function () {
      that = this;
    },    
    mounted() {
        this.node = document.querySelector('#wareBox')
    },
    created(){
      this.all_students()
      this.all_pay_list()
    },
    watch : {
      
    },
    filters: {
      
    },
    components:{
    },
    methods: {    
        hideModal() {
         this.$emit('hideModal');
        },        
        submit() {
          this.goods_rental()
        },
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },
        paymentFunction(a,b){
          if(a == 1){
            if(b == true){
              this.form.payment2 = false
              this.form.payment3 = false
              this.form.payment1 = b             
            }
            else{
              this.form.payment2 = b
              this.form.payment3 = false
              this.form.payment1 = false             
            }
          }
          else if(a == 2){
            if(b == true){
              this.form.payment1 = false
              this.form.payment3 = false
              this.form.payment2 = b             
            }
            else{
              this.form.payment1 = b
              this.form.payment3 = false
              this.form.payment2 = false             
            }
          }
          else{
            if(b == true){
              this.form.payment1 = false
              this.form.payment2 = false
              this.form.payment3 = b             
            }
            else{
              this.form.payment1 = b
              this.form.payment2 = false
              this.form.payment3 = false             
            }
          }          
        },
        async all_students () {
          let resDate = await all_students(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.all_students_list = resDate.data.data
          }
        },
        async all_pay_list () {
          let resDate = await all_pay_list(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.pay_list = resDate.data.data
          }
        },
        change_rent_obj(){
          if(this.form.rent_obj_id == '0'){
            this.rent_obj_type = 2
          }
          else{
            this.rent_obj_type = 1
          }
        },                                  
        async goods_rental () { 
          if(this.form.payment1 == true){
            this.form.payment = 1
          }
          else if(this.form.payment2 == true){
            this.form.payment = 2
          }          
          else if(this.form.payment3 == true){
            this.form.payment = 3
          }
          else{
            this.form.payment = null
            this.$message.error("请选择计费方式")
            return
          }           
          if(this.form.price){
            this.change_num(1)
          }          
          else{
            this.$message.error('请输入计费金额');
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();    
            return;
          };
          if(this.form.num){
            this.change_num1()
          }         
          else{
            this.$message.error('请输入租赁数量');
            this.$refs.numInput.$el.children[0].focus();
            this.$refs.numInput.$el.children[0].blur();            
            return
          };                    
          if(!this.form.getMoney || this.form.getMoney == 0){
            this.$message.error('请输入预付租金');
            this.$refs.sman_id_Input.$el.children[0].children[0].click();          
            return
          };
          if(!this.form.rent_obj_id){
            this.$message.error('请选择销售对象');
            this.$refs.rent_obj_id_Input.$el.children[0].children[0].click();          
            return
          };
          if(!this.form.starTime){
            this.$message.error('请选择开始日期');
            this.$refs.starTimeeInput.$el.children[0].children[0].click();          
            return
          };
          if(!this.form.endTime){
            this.$message.error('请选择结束日期');
            this.$refs.endTimeeInput.$el.children[0].children[0].click();          
            return
          };                     
          if(!this.form.pay_id){
            this.$message.error('请选择支付方式');
            this.$refs.payInput.$el.children[0].children[0].click();          
            return
          };                                                                                                                     
          let goods_id = this.form.goods_id
          let rent_obj_id = this.form.rent_obj_id        
          let pay_id = this.form.pay_id
          let deposit = this.form.deposit
          let getMoney = this.form.getMoney
          let num = Number(this.form.num)
          let price = this.form.price
          let starTime = parseInt((new Date(this.form.starTime).getTime())/1000)
          let endTime = parseInt((new Date(this.form.endTime).getTime())/1000)
          let remarks = this.form.resume
          let rent_obj_type = this.form.rent_obj_type
          let resDate = await goods_rental(this.that.userLoginInfo.orgid,this.that.userLoginInfo.id,goods_id,num,price,remarks,starTime,endTime,getMoney,pay_id,this.form.payment,rent_obj_id,deposit,rent_obj_type)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });           
            this.$emit('submit')
          }
        },
        salePrice(){
          let allPrice;
          allPrice = (this.form.num*this.form.price).toFixed(2)
          this.form.getMoney = allPrice
        },
        change_num1(){
          var actualPrice = this.form.num;         
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];
            if(actualPrice > 200){
              this.$message.error("最大值为200")
              actualPrice = 200
            }                                     
          }            
          this.form.num = actualPrice
        },
        change_num(val){
          let actualPrice;
          if(val == 1){
            actualPrice = this.form.price;
          }
          else if(val == 2){
            actualPrice = this.form.getMoney
          }
          else{
            actualPrice = this.form.deposit;
          }                                     
          if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
              actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 10000000 && val != 2){
              this.$message.error("最大值为10000000")
              actualPrice = 10000000
            }                                              
          }
          if(val == 1){
            this.form.price = actualPrice
          }
          else if(val == 2){
            this.form.getMoney = actualPrice
          }          
          else{
            this.form.deposit = actualPrice;
          }                              
        },                                                
    }
  }
</script>
