<template>
  <div class="menuBox" :style="{left:x1+'px',top:y1+'px'}" @mousemove="modalMove" @mouseup="cancelMove">
    <div class="moveDiv" @mousedown="setStartingPoint"></div>
    <i class="closeMenu delect_icon" @click="closeMenu" @mouseover="overClose" @mouseout="moveClose" ref="closeBtn"></i>
    <div class="btnBox">
      <el-button class="btn1 postpone">顺延</el-button>
      <el-button class="btn1 leave">请假</el-button>
    </div>
    <div class="btnBox">
      <el-button class="btn2 refresh_icon">调课</el-button>
      <el-button class="btn2 refresh_icon">续排</el-button>
      <el-button class="btn2 batch_icon">课表</el-button>
      <el-button class="btn2 batch_icon">调课记录</el-button>
      <el-button class="btn2 delect_icon">删除本节课</el-button>
      <el-button class="btn2 delect_icon">删除所有课</el-button>
    </div>
    <div class="remarkBox">
      <p><b>系统备注：</b>暂无任何备注</p>
      <div class="remarkUl">
        <el-input
          type="textarea"
          :autosize="{ minRows: 4, maxRows: 4}"
          maxlength="500"
          placeholder="可填写备注说明"
          v-model="textarea1">
        </el-input>
        <el-button>提交备注</el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'menu1',
  props: {
    that: {
      type: Object,
      default: false
    },                       
  },
  data () {
    return {
      x1:this.that.menuX,
      y1:this.that.menuY,
      x:0,
      y:0,
      node: null,
      isCanMove: false,      
      textarea1:''
    }
  },
  mounted() {
    this.node = document.querySelector('.menuBox')
  },  
  created () {
    
  },
  methods: {
    setStartingPoint(e) {
      this.x = e.clientX - this.node.offsetLeft
      this.y = e.clientY - this.node.offsetTop
      this.isCanMove = true
    },

    modalMove(e) {
      if (this.isCanMove) {
        this.node.style.left = e.clientX - this.x + 'px'
        this.node.style.top = e.clientY - this.y + 'px'
      } 
    },
    cancelMove() {
      this.isCanMove = false
    },    
    overClose(){
      $(this.$refs.closeBtn).removeClass("delect_icon")
      $(this.$refs.closeBtn).addClass("delete_icon")
    },
    moveClose(){
      $(this.$refs.closeBtn).removeClass("delete_icon")
      $(this.$refs.closeBtn).addClass("delect_icon")
    },
    closeMenu(){
      this.that.hideMenu()
    },    
  },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin.scss';
.moveDiv{
  width:100%;
  height:20px;
  position: absolute;
  top:0;
  left:0;
  cursor: move;
}
.menuBox:before{
  content: " ";
  width:vw(20);
  height:vw(20);
  border: solid 1px #e7eaec;
  position: absolute;
  left:10%;
  top:-9px;
  border-right: none;
  border-bottom: none;
  display:block;
  background:#fff;
  transform:rotate(45deg);
}
.menuBox i{
  width:vw(20);
  height:vw(20);
  position: absolute;
  right:2%;
  top:2%;
  display:block;
  cursor: pointer;
}
.menuBox{
  position: fixed;
  z-index:999;
  background-color: #ffffff;
  border-radius: 6px;
  border: solid 1px #e7eaec;
  width:vw(500);
  height:auto;
  padding:vw(5) vw(10);
  box-sizing:border-box;
  font-size:vw(14);
  line-height:vw(24);
  padding:0 vw(20);
  box-shadow: 0px 2px 4px 0px rgba(12, 51, 110, 0.12);  
}
.menuBox .btnBox{
  width:100%;
  padding:vw(20) 0 0;
  border-bottom: 1px solid #e7eaec;
}
.menuBox .btnBox .btn1{
  width:vw(80);
  height:vw(40);
  line-height:vw(40);
  display:inline-block;
  vertical-align:top;
  padding:0;
  margin-right:vw(18);
  margin-bottom:vw(18);
  margin-left:0 
}
.menuBox .btnBox .btn2{
  width:vw(140);
  height:vw(40);
  border-radius: 6px;
  border: solid 1px #e7eaec;
  margin-right:vw(18);
  margin-bottom:vw(18);
  margin-left:0;
  box-sizing:border-box;
  padding:0;
  background-size:18% auto;
  background-position: 10% center;
  text-align:left;
  text-indent: 30%
}
.menuBox .btnBox .btn2:nth-child(3n){
  margin-right:0
}
.remarkBox p{
  margin-top:vw(16);
  margin-bottom:vw(16)
}
.remarkBox .remarkUl{
  width:100%;
  overflow:auto;
  margin-bottom:vw(20)
}
.remarkBox .remarkUl .el-textarea{
  width:vw(300);
  height:vw(110);
  float:left;
  border-radius: 6px;
  border: solid 1px #e7eaec;  
}
.remarkBox .remarkUl /deep/ .el-textarea textarea{
  border: none;
  box-sizing:border-box;
}
.remarkBox .remarkUl button{
  width: vw(120);
  height: vw(40);
  line-height:vw(40);
  margin-left:vw(20);
  background-color: #2769fb;
  border-radius: 6px;
  color:#fff;
  padding:0;
  float:left
}
.leave{
  border: solid 1px #ff983d;
  color:#ff983d
}
.postpone{
  border: solid 1px #47b6ff;
  color:#47b6ff
}
@media screen and(max-width: 1381px){
  .menuBox{
    width:vw1(500);
    padding:0 vw1(20);
  }
  .menuBox .btnBox{
    width:100%;
    padding:vw1(20) 0 0;
  }
  .menuBox .btnBox .btn1{
    width:vw1(80);
    height:vw1(40);
    line-height:vw1(40);
    margin-right:vw1(18);
    margin-bottom:vw1(18);    
  }
  .menuBox .btnBox .btn2{
    width:vw1(140);
    height:vw1(40);
    margin-right:vw1(18);
    margin-bottom:vw1(18);
  }
  .remarkBox p{
    margin-top:vw1(16);
    margin-bottom:vw1(16)
  }
  .remarkBox .remarkUl .el-textarea{
    width:vw1(300);
    height:vw1(110);
  }
  .remarkBox .remarkUl{
    margin-bottom:vw1(20)
  }
  .remarkBox .remarkUl button{
    width: vw1(120);
    height: vw1(40);
    line-height:vw1(40);
    margin-left:vw1(20);
  }
  .menuBox:before{
    width:vw1(20);
    height:vw1(20);
  }
  .menuBox i{
    width:vw1(20);
    height:vw1(20);
  }
  
}
@media screen and(min-width: 1381px){
 
}
</style>
