<template>
    <div class='currStudent'>
        <el-dialog
            title="修改学生"
            v-dialogDrag
            :visible.sync="gradeThis.currstudent"
            width="30%">
            <section class='body-conenter'>
                <el-input type='text' suffix-icon="el-icon-search"></el-input>
                <div class='body-conenter-div'>
                    <div class='dis-flex-1'>
                        <label class='checkbox-lable' v-for='item in studentList' :key = 'item.stu_id' @change.stop='labelClick(item.stu_id)'>
                            <input type="checkbox" :checked='item.f'>
                            <span>{{item.truename}}</span>
                        </label>
                    </div>
                </div>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size='small ' @click="saveClick">确 认</el-button>
                <el-button @click="gradeThis.currstudent = false" size='small '>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getaddstudentList, getCorrectCorrec} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'Currststudents',
  data () {
    return {
      studentList: []
    }
  },
  inject: ['gradeThis'],
  props: {
    individualStudentList: String
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created () {
  },
  methods: {
    async getaddstudentList_fun () {
      let resDate = await getaddstudentList(this.userLoginInfo.orgid);
      if (resDate.data.status) {
        let _this = this        
        this.studentList = resDate.data.data
        this.studentList.forEach((data,_index)=>{
          _this.individualStudentList.stulist.forEach((data_is, index)=>{
            if(data.stu_id == data_is.stu_id){
              _this.studentList[_index].f = true
            }
          })
        })
      }
    },
    labelClick(id){
      this.studentList.forEach((data,_index)=>{
        if(data.stu_id == id) {
          if(data.f==false) {
             this.studentList[_index].f = true
          } else {
            this.studentList[_index].f = false
          }
        }
      })
    },
    saveClick () {
      let Arr = [];
      this.studentList.forEach((data)=>{
        if(data.f) {
          Arr.push(data.stu_id)
        }
      })
      getCorrectCorrec(this.individualStudentList.class_id, Arr).then((res)=>{
        if (res.data.status) {
          this.$message({
              message: '修改成功',
              type: 'success',
              duration: 2000,
          });
        }
      })
    }
  },
  watch: {
    individualStudentList(val){
      if(val==""){
        this.studentList = []
      }
      this.getaddstudentList_fun()
    }
  }
}
</script>
