      display: flex;
      flex-direction: column;
      border-radius: vw1(8);
      margin-top: 15vh!important;
     
      .el-dialog__header{
        @include bg-defaul($bg-color);
        border-top-left-radius: vw1(8);
        border-top-right-radius: vw1(8);
        padding: vw1(22) vw1(30);
        height: vw1(60);
        box-sizing: border-box;
        .el-dialog__title{
          font-size: vw1(16);
          color: #fff;
          line-height: vw1(25);
        }
        .el-dialog__headerbtn{
          top: vw(20);
          i{
            color: #fff;
          }
        }
      }
      .el-dialog__body{
