                        margin-bottom: vw1(30);
                        input[type=checkbox]{
                            @include wh(vw1(14),vw1(14));
                            width: 14px;
                            height: 14px;
                            background: #fff;
                            -webkit-appearance:none;
                            border: 1px solid #c9c9c9;
                            border-radius: 2px;
                            outline: none;
                            margin-right: vw1(9);
                        }
                        input[type=checkbox]:checked{
