<template>
    <div class='releasedynamic_conenter'>
        <div class='releasedynamic_conenterTitle'>
            发布最新Banner
        </div>
        <div class='releasedynamic_conenterMain'>
            <div class='releasedynamic_conenterMainLeft'>
                <section class='_conenterMainLeft'>
                    <p class='title'>
                        <strong>APP首页顶部广告图</strong>
                    </p>
                    <p class='imgs'>
                        <img src="../../../../../../static/header_back/20190917111049.png" alt="" v-if='activeDate.banner_pic==""'>
                        <img :src="ImagePz+activeDate.banner_pic" alt="" v-else>
                        <span class='Recommend'>上传图片建议尺寸：750像素 * 355像素</span>
                    </p>
                    <p class='btn'>
                        <input type="file" @change='fileChange($event)'>
                        <el-button size='small'>上传最新Banner图</el-button>
                    </p>
                </section>
            </div>
            <div class='releasedynamic_conenterMainRight'>
                <section class='_conenterMainTitle'>
                    <el-input placeholder="请输入最新Banner标题" size='small' v-model='activeDate.title' maxlength='40'></el-input>
                </section>
                <section class='_conenterMainRichtext'>
                    <el-upload
                        style='display:none;'
                        class="avatar-uploader two-uploader"
                        action="/index/Basess/get_img_update"
                        accept="image/jpeg,image/jpg,image/png"
                        :http-request="handleAvatarSuccessT"
                        :before-upload="beforeAvatarUpload"
                        :show-file-list="false"
                        ref='flieRef'>
                    </el-upload>
                    <quill-editor 
                        v-model="activeDate.content"
                        :options="editorOption"
                        @change='onEditorChange'
                        ref="myQuillEditors">
                    </quill-editor>
                </section>
                <section class='ordio_option'>
                    <span class='ordio_optionTitle'>指定发送给：</span>
                    <label class='_optionbox' v-for='item in form.ordioData' :key='item.id' @change.stop='ordinClick(item.id)'>
                        <input type="checkbox" :checked='item.f'>
                        <span>{{item.name}}</span>
                    </label>
                </section>
            </div>
        </div>
        <div class='_conenterMainBtn'>
            <el-button size='small' @click.stop='releaseClick'>发布</el-button>
            <el-button size='small' @click.stop='insdThis.switch_senior=0'>取消</el-button>
        </div>
    </div>
</template>
<script>
const toolbarOptions = [['bold', 'italic', 'underline', 'strike'],    //加粗，斜体，下划线，删除线
    [{ 'list': 'ordered'}, { 'list': 'bullet' }], //列表
    [{ 'align': [] }],    //对齐方式
    [{ 'indent': '-1'}, { 'indent': '+1' }], // 缩进
    ['image']]
import {UpImage,deleteImg,ReleasedynamicBanner,getedit_banner} from '../../../../../getserver/server'
import ImagePz from '../../../../../config/ImagePz'
export default {
  name: 'releasedynamic',
  data () {
    return {
      form:{
        ordioData: [{id:1,name: '家长',f:false},{id:2,name: '老师',f:false}],
        text: '',
        html:'',
        textStatus: false
      },
      activeDate: {
        title: '',
        content: '',
        banner_pic: '',
        send_obj: ''
      },
      ImagePz,
      editorOption: {
        modules: {
          toolbar:{
            container: toolbarOptions,
            handlers: {
              'image': function (value) {
                if (value) {
                  document.querySelector('.two-uploader .el-upload input').click()
                } else {
                  this.quill.format('image', false);
                }
              }
            }
          }
        }
      }
    }
  },
  inject: ['insdThis'],
  methods: {
    ordinClick(id){
      const _this = this
      let str = [];
      this.form.ordioData.forEach((data,index)=>{
        if (data.id == id) {
          if (data.f==false) {
            data.f = true
          } else {
            data.f = false
          }
        }
        if (data.f==true) {
          str.push(data.id)
        }
      })
      this.activeDate.send_obj = str.join('/')
    },
    fileChange(e){
        // app banner 图上传
        let regulaImg = /(png|jpg|jpeg)/;
        if (!regulaImg.test(e.target.files[0].name)) {
            this.$message.error('文件格式不正确,此功能只能上传png,jpg,jpeg文件,请重新上传!');
            this.$refs.flieRef.value = '';
            return
        }
        let files = e.target.files[0]
        const _this = this
        let from = new FormData();
        from.append('img', files);
        if (this.activeDate.banner_pic!="") {
            // 先删后加
            deleteImg(_this.activeDate.banner_pic).then((res)=>{
                if (res.data.status) {
                    UpImage(from).then((res)=>{
                        _this.activeDate.banner_pic = res.data.sinfo
                    })
                }
            })
        } else {
            UpImage(from).then((res)=>{
                _this.activeDate.banner_pic = res.data.sinfo
            })
        }
    },
    beforeAvatarUpload (file) {
      let regulaImg = /(png|jpg|jpeg)/;
      if (!regulaImg.test(file.name)) {
        this.$message.error('文件格式不正确,此功能只能上传png,jpg,jpeg文件,请重新上传!');
        this.$refs.flieRef.value = '';
        return false
      }
    },
    handleAvatarSuccessT (f) {
      // element-ui 上传组件 上传成功触发
      let from = new FormData();
      from.append('img', f.file);
      // form 传递给图片上传接口
      this.getUpImages(from);
    },
    releaseClick(){
        // 发布
        if (this.activeDate.title.trim().length == 0) {
            this.$message({
                message: 'banner标题不能为空!',
                type: 'error',
                duration: 2000,
            });
        } else if (this.activeDate.content.trim().length == 0) {
            this.$message({
                message: 'banner内容不能为空!',
                type: 'error',
                duration: 2000,
            });
        } else if (this.form.text.trim().length == 0 && this.form.html.indexOf('img') == -1) {
            this.$message({
                message: '输入内容不能为空!请重新输入后进行发布!',
                type: 'error',
                duration: 3000,
            });
            this.activeDate.content = ""
            this.form.text = ""
            this.form.html = ""
            return
        }else if (this.form.text.trim().length > 2000) {
            this.$message({
                message: '输入内容超出2000个字，请进行整理！',
                type: 'error',
                duration: 2000,
            });
        } else if (this.activeDate.banner_pic.trim().length == 0) {
            this.$message({
                message: '请上传App-banner图！',
                type: 'error',
                duration: 2000,
            });
        } else{
            this.ReleasedynamicBanner_fun()
        }
    },
    onEditorChange(event){
        // event.quill.deleteText(2000, 4)
        this.form.html = event.html
        this.form.text = event.text
    },
    onblurChange(val){
      console.log(val)
    },
    async getUpImages (from) {
      // 上传图片
      let resDate = await UpImage(from);
      if (resDate.data.status) {
        // 获取富文本组件实例
        let quill = this.$refs.myQuillEditors.quill
        // 获取光标所在位置
        let length = quill.getSelection().index;
        // 插入图片  res.info为服务器返回的图片地址
        quill.insertEmbed(length, 'image', ImagePz + resDate.data.sinfo)
        // 调整光标到最后
        quill.setSelection(length + 1)
      }
    },
    async ReleasedynamicBanner_fun(){
        if (this.insdThis.bannerData!=null) {
            // 修改
            this.Get_getedit_banner()
        }else {
            // 发布
            let resDate = await ReleasedynamicBanner(this.activeDate)
            if (resDate.data.status) {
                // 发布成功
                this.$message({
                    message: '发布成功!',
                    type: 'success',
                    duration: 2000,
                });
                this.activeDate = {
                    title: '',
                    content: '',
                    banner_pic: '',
                    send_obj: ''
                }
                this.form.textStatus = false
                this.form.text = ""
                this.form.ordioData.forEach((data,i)=>{
                     data.f = false
                })
                this.insdThis.switchnum = 2
                this.insdThis.banner.forEach((data,i)=>{
                    if(i==0) {
                        data.f = false
                    } else {
                        data.f = true
                    }
                })
                this.insdThis.switch_senior = 0
            } 
        }
    },
    async Get_getedit_banner(){
        // 编辑banner
        let resDate = await getedit_banner(this.insdThis.bannerData.b_id,this.activeDate)
        if (resDate.data.status) {
            // 编辑成功
            this.$message({
              message: '编辑成功!',
              type: 'success',
              duration: 2000,
            });
            this.insdThis.switchnum = 2
            this.insdThis.switch_senior = 0
        }
    }
  },
  components: {
  },
  watch: {
    'insdThis.switch_senior'(val){
        if (val == 0) {
          if (this.insdThis.bannerData!=null) {
            this.insdThis.bannerData = null
          }
          this.form.ordioData.forEach((data,i)=>{
            data.f = false
          })
          this.activeDate = {
            title: '',
            content: '',
            banner_pic: '',
            send_obj: ''
          }
        }
    },
    'insdThis.bannerData' (val) {
        const _this = this
        if (val!=null) {
            this.activeDate = {
                title: val.title,
                content: val.content,
                banner_pic: val.banner_pic,
                send_obj: val.send_obj
            }
            let Arr = val.send_obj.split('/')
            Arr.forEach((item)=> {
                _this.form.ordioData.forEach((data)=>{
                    if (item == data.id) {
                        data.f = true
                    }
                })
            })
        }
    },
    'form.text'(val) {
        if (val!=""){
            if (val.trim().length >= 2000 && this.form.textStatus == false){
                this.$message({
                    message: '当前输入达到2000个字，请进行删减，否则无法发布!',
                    type: 'error',
                    duration: 2000,
                });
                this.form.textStatus = true
            }
        }
    } 
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.releasedynamic_conenter {
    display: flex;
    flex-direction: column;
    .releasedynamic_conenterTitle{
        padding: vw(30) 0;
        box-sizing: border-box;
        color: #2769fb;
        font-size: vw(16);
    }
    .releasedynamic_conenterMain{
        display: flex;
        @include wh(100%,vw(660));
        box-shadow: 0px 2px 8px 0px 
		rgba(12, 51, 110, 0.12);
	    border-radius: 8px;
        flex: 1;
        margin-bottom: vw(30);
        .releasedynamic_conenterMainLeft{
            @include wh(vw(360),100%);
            border-right: 1px solid #e7eaec;
            padding: vw(30) vw(40);
            box-sizing: border-box;
            ._conenterMainLeft{
                .title{
                  color: #333333;
                  font-size: vw(16);
                }
                .imgs{
                    @include wh(100%, auto);
                    margin: vw(30) 0;
                    img{
                        @include wh(100%, vw(142));
                    }
                    .Recommend{
                        font-size: vw(14);
                        color: #666666;
                        margin-top: vw(20);
                        display: inline-block;
                    }
                }
                .btn{
                    position: relative;
                    .el-button{
                        @include wh(100%,auto);
                        border-color: #47b6ff;
                        color: #47b6ff;
                        border-radius: vw(6);
                    }
                    input[type=file] {
                        position: absolute;
                        left: 0;
                        top: 0;
                        opacity: 0;
                        @include wh(100%,100%);
                        cursor: pointer;
                    }
                }
            }
        }
        .releasedynamic_conenterMainRight{
            display: flex;
            flex-direction: column;
            padding: vw(30) vw(40);
            box-sizing: border-box;
            overflow: hidden;
            @include wh(100%,100%);
            ._conenterMainRichtext{
                flex: 1;
                margin: vw(30) 0;
                overflow: auto;
                /deep/ .quill-editor{
                    height: 100%;
                    display: flex;
                    flex-direction: column;
                    .ql-container{
                        flex: 1;
                    }
                }
            }
            .ordio_option{
                display: flex;
                font-size: vw(16);
                color: #333333;
                align-items: center;
                .ordio_optionTitle{
                    margin-right: vw(20);
                }
                ._optionbox{
                    display: flex;
                    align-items: center;
                    margin-right: vw(40);
                    cursor: pointer;
                    input[type=checkbox] {
                        cursor: pointer;
                        margin-right: vw(15);
                        @include wh(14px,14px);
                        background: #fff;
                        -webkit-appearance:none;
                        border: 1px solid #c9c9c9;
                        border-radius: 2px;
                        outline: none;
                    }
                    input[type=checkbox]:checked{
                        background-size: 100%;
                        background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                    }
                }
            }
        }
    }
    ._conenterMainBtn{
        .el-button{
            @include wh(vw(120),auto);
            background-color: #e7eaec;
            color: #999999;
            border-color: #e7eaec;
            border-radius: vw(6);
        }
        .el-button:first-child{
            background-color: #2769fb;
            color: #fff;
            margin-right: vw(30);
        }
    }
}  
</style>
