<template>
    <div class='table-gulp'>
        <erp-tabtop></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
                :data="tableData"
                border
                ref='table'
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    prop="cur_id"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="序号">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='15.5%'
                    label="课程名称">
                    <template slot-scope="scope">
                        <span class='hot' @click='layerOpen(scope.row,$event)'>
                            {{scope.row.cur_name}}
                            <i class='hot_icon iconhot-width' v-if='scope.row.popular==1'></i>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="subject.sname"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="科目">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="授课方式">
                    <template slot-scope="scope">
                        <span v-if='scope.row.tmethods==1'>一对一</span>
                        <span v-else-if='scope.row.tmethods==2'>一对多</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="时长">
                    <template slot-scope="scope">
                        <span>{{scope.row.ctime}}分钟</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="状态">
                    <template slot-scope="scope">
                        <span v-if='scope.row.status==1'>开课中</span>
                        <span v-else-if='scope.row.status==1'>已结课</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="address"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="是否上架">
                    <template slot-scope="scope">
                        <span v-if='scope.row.state==1'>已上架</span>
                        <span v-else-if='scope.row.state==2'>未上架</span>
                    </template>
                </el-table-column>
                <el-table-column
                    label="更多操作"
                    min-width='31.1%'
                    show-overflow-tooltip>
                    <template slot-scope="scope" class='erp-call'>
                        <div class="grid-content bg-purple">
                            <label>
                                <el-button @click="repairChange(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
                                    <i class='repair_icon icon-width'></i>
                                    修改
                                </el-button>
                                <el-button type="text" @click="showDelete(scope.row.cur_id)" size="small"  class='erp-button-icon'>
                                    <i class='delete_icon icon-width '></i>
                                    删除
                                </el-button>
                            </label>
                            <label>
                                <el-button type="text" @click="scheduleclick(scope.row, scope.$index)" v-if='scope.row.tmethods==2' size="small"  class='erp-button-icon'>
                                    <i class='classroom_icon icon-width '></i>
                                    课表
                                </el-button>
                            </label>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" v-if='countNum>1'></erp-paging>
        <erp-curriculum :addVisible='addVisible' @close='closefun' v-if='addVisible'></erp-curriculum>
        <erp-hotkc v-if='hotKcshow'></erp-hotkc>
        <transition name='sch'>
            <erp-schedule v-show='scheduleShow'></erp-schedule>            
        </transition>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpCurriculum from '../n_curriculum/curriculum'
import erpPaging from '../../../paging/pagingShort'
import erpTabtop from './table_top/tabTop'
import erpHotkc from './hot_kc/hot_kc'
import erpSchedule from './schedule/schedule'
import {mapState} from 'vuex'
import {getKclist, deleteKc, getKcallList} from '../../../../getserver/server'

export default {
  name: 'table-gulp',
  data () {
    return {
      addVisible: false,
      hotKcshow: false,
      scheduleShow: false,
      tableData: [],
      tableAllDate: [],
      countNum: 0,
      page_num: 1,
      pageSize: 8,
      repairDate: null,
      showDe:false,
      deleteId: null,
      $index: '',
      formseach: {
        value: '',
        states: '',
        kmActive: '',
        searchValue: '', // 搜索的课程
      }
    }
  },
  provide: function () {
    return {
      that: this
    }
  },
  created () {
    this.getKclists(1) // 1=> 默认显示第一页数据
    this.getkcAlllist()
  },
  mounted () {
    this.tableHeight()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    tableHeight () {
        let tableRefs = this.$refs.table.$el; // table 父亲级节点
        let trheight = null
        const _this = this
        setTimeout(()=>{
           trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
           tableRefs.style.height = trheight * (_this.pageSize + 1) + "px"
        },500) 
    },
    layerOpen:function (a,b) {
        // console.log($(b.path[1]).width())
        // console.log(b.currentTarget.getBoundingClientRect())
        // console.log(b)
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    repairChange (data, i) {
      // 课程修改
      this.addVisible = !this.addVisible
      this.repairDate = this.tableData[i]
    },
    showDelete(id){
        this.deleteId = id;
        this.showDe = true
    },
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        this.showDe = false;
        this.delectclassRooms(id)
    },
    delectclassRooms(id){
        this.deletes(id)
    },
    deletes (id) {
        // 删除课程
      const _this = this
       deleteKc(id).then((res)=>{
            if (res.data.status) {
                // 删除成功
                this.$message({
                    message: res.data.sinfo,
                    type: 'success',
                    duration: 2000,
                });
                if (this.tableData.length == 1){
                    if (this.page_num==1) {
                        this.getKclists(1)
                        return;
                    }
                    this.getKclists(this.page_num-1);
                    return;
                }
                this.getKclists(this.page_num)
            }
        }).catch((error)=>{
            console.log(error)
        })
    },
    closefun () {
      this.addVisible = !this.addVisible
    },
    paging_father (n) {
      this.getKclists(n)
      this.page_num = n;
    },
    scheduleclick (data, i) {
        // 课表
        this.scheduleShow = !this.scheduleShow 
    },
    async getKclists (n) {
      let resDate = await getKclist(n,this.formseach.searchValue,this.formseach.kmActive,this.formseach.value,this.formseach.states, this.userLoginInfo.orgid, this.pageSize)
      if (resDate.data.status) {
        this.countNum = resDate.data.data.total // 总页数
        this.tableData = resDate.data.data.data
      }   
    },
    async getkcAlllist () {
        let resDate = await getKcallList('',this.userLoginInfo.orgid)
        if(resDate.data.status) {
            this.tableAllDate = resDate.data.data
        }
    }
  },
  components: {
    erpPaging: erpPaging,
    erpTabtop: erpTabtop,
    erpCurriculum: erpCurriculum,
    erpHotkc: erpHotkc,
    erpSchedule: erpSchedule
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';
.sch-enter,
.sch-leave-to,
.sch-enter-to,
.sch-leave{
  transition: all 0.5s;
}
.sch-enter,
.sch-leave-to{
    opacity: 0;
}
.sch-enter-to,
.sch-leave{
    opacity: 1;
}

.hot{
    display: flex;
    align-items: center;
    justify-content: center;
    .iconhot-width{
        @include wh(32px,16px);
        display: inline-block;
        margin-left: 6px;
    }
}

.div_gulptable{
    @include wh(100%,auto);
}
.div_gulptable /deep/ .el-table {
    height: auto;
    background-color: #ffffff;
    // box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    .el-table__body-wrapper {
        @include bgColor-defaul($bgColor-color);
        height: auto!important;
    }
}

.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: 6px;
    font-size: 16px;
    color: #666666;
}
.grid-content{
    text-align: center;
    display: flex;
    padding: 0 35px;
    box-sizing: border-box;
    label{
      width: 50%;
      display: flex;
      /deep/ .el-button span{
        @include fontColor-defaul($fn-color)
      }
    }
    button:nth-child(2n){
        margin-right: 30px;
        margin-left: 30px;
    }
}
.el-pagination{
    padding-right: 60px;
    margin-top: 30px; 
}
</style>
