<template>
    <!-- 导入表弹窗 -->
    <el-dialog
        class='_upfileConenter'
        title="导入Excel"
        :visible.sync="dialogVisible"
        :before-close="handleClose">
        <label class='_uplabel importWj'>
            <el-input v-model="filesName" placeholder="文件名"></el-input>
            <el-button size='mini'>选择文件</el-button>
            <input type="file" @change='files($event)' ref='flieRef'>
        </label>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size='small' @click="sublieUp">上 传</el-button>
            <el-button @click="handleClose" size='small'>取 消</el-button>
        </span>
    </el-dialog>
</template>
<script>
export default {
  name: 'upFile',
  data () {
    return {
      filesName: '',
      filesbuffer: null
    }
  },
  props: {
    dialogVisible: Boolean
  },
  inject: ['tabTop'],
  methods: {
    files (e) {
      let files = e.target.files[0];
      let regulaImg = /.xls/;
      if (!regulaImg.test(files.name)) {
        this.$message.error('文件格式不正确,此功能只能上传Excle文件,请重新上传');
        this.$refs.flieRef.value = '';
        return
      }
      this.filesName = files.name;
      this.filesbuffer = files;
    },
    handleClose () {
      this.tabTop.upfilemodel.dialogVisible = false
    },
    sublieUp () {
      this.tabTop.importExclefun(this.filesbuffer);
      this.$refs.flieRef.value = '';
      this.filesName = '';
      this.filesbuffer = null;
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';

@media screen and (max-width: 1380px){
._upfileConenter /deep/ .el-dialog{
      margin-top: 30vh!important;
      width: vw1(640);
      height: vw1(360);
      .el-dialog__header{
        height: vw1(60);
        .el-dialog__title{
          font-size: vw1(16);
        }
        .el-dialog__headerbtn{
          top: 5%;
        }
      }
      .el-dialog__body{
        padding: vw1(30);
      }
      ._uplabel{
          .el-input{
             margin-right: vw1(30);
          }
          .el-button{
              @include wh(vw1(160), vw1(40));
              // line-height: vw(40)
          }
      }
      .importWj{
          position: relative;
        .el-input .el-input__inner{
          @include wh(100%,vw1(40));
        }
          input[type=file]{
            @include wh(100%,vw1(40));
          }
      }
      .dialog-footer{
        .el-button{
            @include wh(vw1(120), auto)
        }
      }
  }
}
@media screen and (min-width: 1381px){
  ._upfileConenter /deep/ .el-dialog{
      margin-top: 30vh!important;
      width: vw(640);
      height: vw(360);
      .el-dialog__header{
        height: vw(60);
        .el-dialog__title{
          font-size: vw(16);
        }
        .el-dialog__headerbtn{
          top: 5%;
        }
      }
      .el-dialog__body{
        padding: vw(30);
      }
      ._uplabel{
          .el-input{
             margin-right: vw(30);
          }
          .el-button{
              @include wh(vw(160), vw(40));
              // line-height: vw(40)
          }
      }
      .importWj{
          position: relative;
        .el-input .el-input__inner{
          @include wh(100%,vw(40));
        }
          input[type=file]{
            @include wh(100%,vw(40));
          }
      }
      .dialog-footer{
        .el-button{
            @include wh(vw(120), auto)
        }
      }
  }
}
._upfileConenter /deep/ .el-dialog{
    min-height: 300px;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .el-dialog__header{
      @include bg-defaul($bg-color);
      padding: 0;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      display: flex;
      align-items: center;
      text-indent: 4.6%;
      .el-dialog__title{
        flex: 1;
        color: #fff;
      }
      .el-dialog__headerbtn{
        i{
          color: #fff;
        }
      }
    }
    .el-dialog__body{
      flex:1;
    }
    ._uplabel{
        display: flex;
        .el-input{
            flex: 1;
        }
        .el-button{
            border-radius: 6px;
            border: solid 1px #47b6ff;
            color: #47b6ff;
            // line-height: vw(40)
        }
    }
     .importWj{
        position: relative;
        input[type=file]{
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;
            cursor: pointer;
        }
    }
    .dialog-footer{
      .el-button:nth-child(1){
        @include bg-defaul($bg-color);
        @include borderColor-defaul($bg-color);
      }
    }
}
</style>
