<template>
    <div class='currStudent'>
        <el-dialog
            title="修改课程"
            v-dialogDrag
            :visible.sync="gradeThis.currcoureShow"
            width="30%">
            <section class='right-main-main'>
                 <el-collapse accordion>
                    <el-collapse-item :title="item.sname" :name="item.sid" v-for='item in gradeCurrList' :key ='item.sid'>
                        <div class='collapse-item-div'>
                            <span class='not-data' v-if='item.currlist.length==0'>暂无内容</span>
                            <ul class='collapse-item-ul' v-else>
                                <li :class='{"active": items.f}' v-for='items in item.currlist' :key='items.cur_id' @click.stop='GradeClick(items.cur_id)'>{{items.cur_name}}</li>
                            </ul>
                        </div>
                    </el-collapse-item>
                </el-collapse>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size='small ' @click="gradeClick">确 认</el-button>
                <el-button @click="gradeThis.currcoureShow = false" size='small '>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {GradeList, geteditCurr} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'currcoure',
  data () {
    return {
      gradeCurrList: []
    }
  },
  props: {
    individualGradeInfo: String
  },
  inject: ['gradeThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created () {
  },
  methods: {
    async getGradeList_fun () {
      let resDate = await GradeList(this.userLoginInfo.orgid);
      let _this = this
      if (resDate.data.status) {
        resDate.data.data.forEach((data)=>{
          if (data.currlist.length!=0) {
            data.currlist.forEach((dates,index)=>{
              data.currlist[index]['f'] = false;
            })
          }
        })
        this.gradeCurrList = resDate.data.data
        this.gradeCurrList.forEach((data,_index)=>{
          data.currlist.forEach((info,index)=>{
            if (info.cur_id == _this.individualGradeInfo.currlist.cur_id){
              _this.gradeCurrList[_index].currlist[index].f = true
            }
          })
        })
      }
    },
    GradeClick(id){
      this.gradeCurrList.forEach((data,index)=>{
        data.currlist.forEach((data_is,_index)=>{
          if (data_is.cur_id == id) {
            if (data_is.f==false) {
              this.gradeCurrList[index].currlist[_index].f = true
            }
          } else {
            this.gradeCurrList[index].currlist[_index].f = false
          }
        })
      })
    },
    gradeClick(){
      // 确定
      let ArrString = "";
      let _this = this
      this.gradeCurrList.forEach((data)=>{
        data.currlist.forEach((dats_is)=>{
          if (dats_is.f) {
            ArrString = dats_is.cur_id
          }
        })
      })
      geteditCurr(this.individualGradeInfo.class_id, ArrString).then(res=>{
        if(res.data.status) {
          // 修改成功
          _this.$message({
            message: '修改成功',
            type: 'success',
            duration: 2000,
          });
          this.gradeThis.getGradeList(this.gradeThis.form.gradename,1,10);
        }
      })
    }
  },
  watch: {
    individualGradeInfo (val) {
      if (val!="") {
        this.getGradeList_fun()
      } else {
        this.gradeCurrList = [];
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
.currStudent{
   /deep/ .el-dialog__wrapper .el-dialog{
      @include wh(vw(640)!important,vw(660));
      display: flex;
      flex-direction: column;
      border-radius: vw(8);
      margin-top: 15vh!important;
      .el-dialog__header{
        @include bg-defaul($bg-color);
        border-top-left-radius: vw(8);
        border-top-right-radius: vw(8);
        padding: vw(22) vw(30);
        height: vw(60);
        box-sizing: border-box;
        .el-dialog__title{
          font-size: vw(16);
          color: #fff;
          line-height: vw(25);
        }
        .el-dialog__headerbtn{
          top: vw(20);
          i{
            color: #fff;
          }
        }
      }
      .el-dialog__body{
        flex: 1;
        overflow: auto;
        padding: 0;
        .right-main-main{
            flex: 1;
            overflow: auto;
            .el-collapse{
                .el-collapse-item .el-collapse-item__header{
                    padding: 0 vw(30);
                    .el-icon-arrow-right{
                        transform: rotate(-90deg);
                    }
                    .el-collapse-item__arrow.is-active{
                        transform: rotate(0deg);
                    }
                    .el-icon-arrow-right:before{
                        content: "";
                        display: inline-block;
                        @include wh(vw(20),vw(20));
                        background: url('../../../../../../static/icon/icon_expand.png')no-repeat;
                        background-size: contain;
                    }
                }
                .el-collapse-item__wrap{
                    .el-collapse-item__content .collapse-item-div{
                        padding: 0 vw(30);
                        box-sizing: border-box;
                        .not-data{
                            color: #999999;
                            font-size: vw(16);
                        }
                        .collapse-item-ul{
                            display: flex;
                            flex-wrap: wrap;
                            li{
                                @include wh(auto, vw(36));
                                padding: 0 vw(10);
                                border-radius: vw(6);
                                color: #47b6ff;
                                border: solid 1px #47b6ff;
                                font-size: vw(16);
                                display: flex;
                                justify-content: center;
                                line-height: vw(36);
                                margin-right: vw(20);
                                margin-bottom: vw(20);
                                cursor: pointer;
                                -webkit-touch-callout: none; /* iOS Safari */
                                -webkit-user-select: none; /* Chrome/Safari/Opera */
                                -khtml-user-select: none; /* Konqueror */
                                -moz-user-select: none; /* Firefox */
                                -ms-user-select: none; /* Internet Explorer/Edge */
                                user-select: none;
                            }
                            li.active{
                                background-color: #47b6ff;
                                color: #ffffff;
                            }
                            li:last-child{
                                margin-right: 0;
                            }
                        }
                    }
                }
            }
        }
      }
      .el-dialog__footer{
        .dialog-footer {
          .el-button {
            font-size: vw(16);
            @include wh(vw(120),vw(40));
          }
          .el-button:first-child{
            @include bg-defaul($bg-color);
          }
          .el-button:last-child{
            background-color: #e7eaec;
            border: 1px solid #e7eaec;
            color: #999999;
          }
        }
      }
   }
}
</style>