<template>
    <div class='table_conenter'>
        <topBtn></topBtn>
         <section class='div_gulptable'>
            <el-table
                :data="tableData"
                border
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='6.66%'
                    label="序号">
                    <template slot-scope="scope">
                        <span>
                            {{scope.$index + 1}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='6.66%'
                    label="姓名">
                    <template slot-scope="scope">
                        <span class='color_class' @click='layerOpen(scope.row,$event)'>{{scope.row.stu_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="mobile"
                    show-overflow-tooltip
                    align='center'
                    min-width='11.11%'
                    label="手机">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='11.11%'
                    label="课程">
                    <template >
                        <!-- <span v-if='cur_list.length==0'>
                            {{cur_list[0]}}
                        </span> -->
                        <span class='color_class'>
                            详情
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="t_name"
                    align='center'
                    min-width='6.66%'
                    label="教师">
                </el-table-column>
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="已上|已排">
                    <template slot-scope="scope">
                        <span>
                            {{scope.row.already_done}} | 4
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="date"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="剩余|已购">
                    <template slot-scope="scope">
                        <span>
                            {{scope.row.already_buy}} | 4
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="备注">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)'>
                            {{scope.row.remarks}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    label="更多操作"
                    min-width='31.11%'
                    show-overflow-tooltip>
                    <template  class='erp-call' slot-scope="scope">
                        <div class="grid-content bg-purple">
                            <label>
                                <el-button @click.native='class_tableClick'  type="text" size="small" class='erp-button-icon'>
                                    <i class='blue_three_icon icon-width'></i>
                                    课表
                                </el-button>
                                <el-button type="text"  size="small"  class='erp-button-icon' v-if='scope.row.already_bug>0'>
                                    <i class='book_icon icon-width '></i>
                                    排课
                                </el-button>
                            </label>
                            <label>
                                <el-button type="text"  size="small" @click.native='shopClassClick(scope.row)' class='erp-button-icon'>
                                    <i class='shop_icon icon-width '></i>
                                    购课
                                </el-button>
                                <el-button type="text"  size="small"  class='erp-button-icon' @click.stop='ItemsClick'>
                                    <i class='shop_class_icon icon-width '></i>
                                    购物
                                </el-button>
                            </label>
                            <label>
                                <el-button type="text"  size="small"  class='erp-button-icon' @click.stop='TopupClick'>
                                    <!-- <i class='classroom_icon icon-width '></i> -->
                                    充值
                                </el-button>
                            </label>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <addStudentModel></addStudentModel>
        <transition name='stu'>
            <shoppingClassInfo v-show='shoppclassInfoShow'></shoppingClassInfo> 
        </transition>
        <shoppClass></shoppClass>
        <topUp></topUp>
        <shopItem></shopItem>
        <studentinfo/>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import {studentList} from '../../../../getserver/server'
import {mapState} from 'vuex'
import topBtn from './topBtn/topBtn'
import addStudentModel from './addStudents/addStudent'
import shoppingClassInfo from './shopp_classinfo/shopclass_info'
import shoppClass from './shopp_class/shopp_class'
import topUp from './TopUp/topUp'
import shopItem from './shop_items/shop_items'
import studentinfo from './studentInfor/studentInfor'
export default {
  name: 'tables',
  data () {
    return {
      shoppclassInfoShow: false,
      addStudentShow: false,
      shopClassShos:false,
      TopupShow: false,
      shopitemsShow: false,
      pageSize: 8, // 一页8条数据
      page_num:1, // 默认第一页数据
      tableData: [],
      balance: 0,// 学生余额
      stu_id: '',// 学生id
      seatchForm: {
        seatchS: '',// 筛选 学生
        seatchT: '', // 筛选 教师
        seatchKc: '', // 筛选 课程
        status: 1 // 1在学 2退学状态
      }
    }
  },
  provide: function(){
    return {
      tablesThis: this
    }
  },
  created () {
    this.getStudentlist()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    layerOpen:function (a,b) {
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    class_tableClick () {
      // 课表点击
      this.shoppclassInfoShow = !this.shoppclassInfoShow
    },
    shopClassClick (balances) {
      // 购课点击
      this.shopClassShos = !this.shopClassShos
      this.balance = balances.balance
      this.stu_id = balances.stu_id
    },
    TopupClick () {
        this.TopupShow = !this.TopupShow
    },
    ItemsClick () {
        this.shopitemsShow = !this.shopitemsShow
    },
    async getStudentlist () {
      let resDate = await studentList(this.userLoginInfo.orgid,  this.page_num, this.pageSize, this.seatchForm.seatchS, this.seatchForm.seatchT, this.seatchForm.seatchKc, this.seatchForm.status)
      if (resDate.data.status) {
          this.tableData = resDate.data.data.data
      }
    }
  },
  components:{
    addStudentModel: addStudentModel,
    topBtn: topBtn,
    shoppingClassInfo: shoppingClassInfo,
    shoppClass: shoppClass,
    topUp: topUp,
    shopItem: shopItem,
    studentinfo: studentinfo
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';
.stu-enter,
.stu-leave-to,
.stu-enter-to,
.stu-leave{
    transition: all 0.3s ease;
}
.stu-enter,
.stu-leave-to{
  opacity: 0;
}
.stu-enter-to,
.stu-leave{
    opacity: 1;
}




.color_class{
    color: #2769fb;
}
.div_gulptable{
    @include wh(100%,auto);
}
.div_gulptable /deep/ .el-table {
    height: 540px;
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    .el-table__body-wrapper {
        @include bgColor-defaul($bgColor-color)
    }
}
.grid-content{
    text-align: center;
    display: flex;
    padding: 0 35px;
    box-sizing: border-box;
    label{
      width: 50%;
      display: flex;
      /deep/ .el-button span{
        @include fontColor-defaul($fn-color)
      }
      .icon-width{
            @include wh(20px, 20px);
            display: inline-block;
            margin-right: vw(6);
            font-size: 16px;
            color: #666666;
        }
    }
    button:nth-child(2n){
        margin-right: vw(30);
        margin-left: vw(30);
    }
}
</style>
