<template>
  <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
   <div class="modal-container modal-container1 revisionModal">
    <div class="modal-header" @mousedown="setStartingPoint">
     {{ title }}
     <i class="el-icon-close" round @click="hideModal"></i>
    </div>
    <div class="modal-main">
     <el-collapse v-model="activeNames" @change="handleChange" class="collapseBox">
      <el-collapse-item v-for="(item,index) in currmData" :title="item.sname" :name="item.sid" :key="item.sid">
        <p v-if="item.courses.length == 0">暂无内容</p>
        <el-button v-else v-for="(item1,index1) in item.courses" :key="item1.cur_id" @click="clickActive($event,index,index1)" :class="{'active':item1.active===true}">{{item1.cur_name}}</el-button>
      </el-collapse-item>  
     </el-collapse>
    </div>
    <div class="modal-footer alertFooter">
      <el-button type="primary" round @click="submit()">完成</el-button>
      <el-button round @click="hideModal">取消</el-button> 
    </div>
  </div>
</div>
</template>
<script>
  import axios from 'axios';
  import {teacher_courses,addLesson,delLesson} from '../../../../../getserver/getAxios.js'
  export default {
    name: 'rightModal',
    props: {
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        },
        id: {
          type: Number,
          default: false          
        }             
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            activeNames: [],
            currmData: [],
            cur_list:[]
              
        }
    },
    created(){
       this.all_list_currm()
    },
    mounted() {
        this.node = document.querySelector('.revisionModal')
    },
    methods: {
        handleChange(val) {
          // console.log(val);
        },
        hideModal() {
            this.$emit('hideModal')
        },      
        submit() {
         this.$message({
          message: '修改成功',
          type: 'success'
         });
         this.$emit('submit')
        },        
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },        
        removeArry(val) { 
          var index = this.cur_list.indexOf(val); 
          if (index > -1) { 
            this.cur_list.splice(index, 1); 
          } 
        },
        clickActive(event,index,index1) {
          this.currmData[index].courses[index1].active = !this.currmData[index].courses[index1].active;
          if(this.currmData[index].courses[index1].active == true){
            this.cur_list.push(this.currmData[index].courses[index1].cur_id);
            this.addLesson(this.currmData[index].courses[index1].cur_id)
          }
          else{
            this.removeArry(this.currmData[index].courses[index1].cur_id);
            this.delLesson(this.currmData[index].courses[index1].cur_id)
          }
          // this.$emit('changeCur',this.cur_list)
          console.log(this.cur_list)
          
        },
        async all_list_currm () {
          console.log(this.orgid)
          let _this = this;
          let resDate = await teacher_courses(this.orgid,this.id)
          // console.log(resDate.data.data)
          // console.log(this.cur_list)
          if (resDate.data.status) {
            resDate.data.data.forEach(function(element,index){
              for(let i=0;i<resDate.data.data[index].courses.length;i++){
                resDate.data.data[index].courses[i].active = false
                if(resDate.data.data[index].courses[i].status == 1){
                  resDate.data.data[index].courses[i].active = true
                  _this.activeNames.push(resDate.data.data[index].sid)
                }
              }
            })
            console.log(this.activeNames)
            this.currmData = resDate.data.data
          }
          // console.log(this.currmData)
        },
        async addLesson (cur_id) {
          let _this = this;
          let resDate = await addLesson(cur_id,this.id)
          if (resDate.data.status) {

          }
        },
        async delLesson (cur_id) {
          let _this = this;
          let resDate = await delLesson(cur_id,this.id)
          if (resDate.data.status) {

          }
        },                        
                      
    }
  }
</script>
