<template>
    <div >
        <erp-tabtop @setMealList="setMealList" @searchsetMeal="searchsetMeals" :orgid="orgid" :manager="manager"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
                ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="index"
	                show-overflow-tooltip
	                align='center'
	                min-width='16.667%'
	                label="序号">
                    <template slot-scope="scope"><span>{{scope.$index | serialNumber}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="meal_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='13.333%'
	                label="套餐名称">
	            </el-table-column>
	            <el-table-column
                    show-overflow-tooltip
                    align='center'
	                min-width='13.333%'
	                label="课程名称">
                    <template slot-scope="scope">
                        <span @click="layerOpen($event)">{{scope.row.meals_cur}}</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='13.333%'
	                label="套餐价">
                    <template slot-scope="scope">
                        <span>￥{{scope.row.price}}</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='13.333%'
	                label="状态">
                    <template slot-scope="scope">
                        <span @click="layerOpen($event)" v-if="scope.row.cur_state == 1">启用中</span>
                        <span @click="layerOpen($event)" v-else-if="scope.row.cur_state == 2">已关闭</span>
                    </template>                    
	            </el-table-column>       
	            <el-table-column
	                label="更多操作"
	                min-width='30.001%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
	                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
	                            <i class='repair_icon icon-width'></i>
	                            修改
	                        </el-button>
	                        <el-button type="text" @click="showDelete(scope.row.meal_id)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            删除
	                        </el-button>
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <edit-meal v-if="show" :title="title" :orgid="orgid" :meal_id="meal_id" :manager="manager" @hideModal="hideModal" @submit="submit"></edit-meal>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './set-meal-top/set-meal-top'
import erpPaging from '../../../paging/pagingShort'
import editMeal from '../set-meal/alert-box/editMeal.vue'
import {set_meal_list,searchsetMeal,selectsetMeal,delectMeal} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'table-gulp',
  data () {
    return {
      that: this,
      tableData: [],
      title: '编辑套餐',
      show: false,
      orgid:'',
      manager:'',
      showDe:false,
      deleteId:null,      
      meal_id: Number(),
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数
      pageSize:8,//每页显示条数 
      name:'',//搜索关键字
      status:null,//套餐状态          
    }
  },
  beforeCreate: function () {
   that = this;
  },  
  filters: {//过滤器
    Upper: function (value) {
        return value.substr(1)
    },
    serialNumber ( val ){
        return (that.page_num-1)*that.pageSize+(val + 1)
    }
　　},
  computed:{
    ...mapState(['userLoginInfo']),
  },
  methods: {
    paging_father (n) {
      this.setMealList(n)
    },    
    async setMealList (page) {
        let t = (new Date()).getTime();
        let resDate = await set_meal_list(page,t,this.orgid,this.name,this.status)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total
            this.page_num = page           
        }
    }, 
    async searchsetMeals (name,status,page) {
        let t = (new Date()).getTime();
        let resDate = await searchsetMeal(page,name,status,t,this.orgid)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total
            this.page_num = page           
        }
    },
    async delectMeal (id) {
        let resDate = await delectMeal(id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            if((this.tableData.length -1) == 0){
                this.setMealList (this.page_num-1)
            }
            else{
                this.setMealList (this.page_num)
            }            
        }
    },                
    showDelete(id){
        this.deleteId = id;
        this.showDe = true
    },    
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        this.showDe = false;
        this.delectMeal(id)
    },       
    showAlert(a,b){
        this.meal_id = a.meal_id
        this.show = true
    },
    hideModal() {
        // 取消弹窗回调      
        this.show = false
    },

    submit() {
    // 确认弹窗回调  
    this.last_page = 0
    this.paging_father(this.page_num)
    this.show = false
    }    
  },
  created: function () {
    console.log(this.userLoginInfo)
    this.orgid = this.userLoginInfo.orgid;
    this.manager = this.userLoginInfo.id.toString();
    this.setMealList(1)   
  },
  mounted(){
    let h1 = $(".div_gulptable .el-table .has-gutter th").height()
    $(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")   	
  },
  components: {
    erpTabtop: erpTabtop,
    erpPaging: erpPaging, 
    editMeal:editMeal,   
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: 6px;
    font-size: 16px;
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: 50px;
        margin-left: 50px;
    }
}
.el-pagination{
    padding-right: 60px;
    margin-top: 30px; 
}
.div_gulptable /deep/.erp-button-icon span{
    font-size:16px;
}
</style>
