<template>
    <transition name='_tab'>
        <div class='_currTbale' v-dialogDrag v-show='gradeThis.currTableShow' @click.self='backClick'>
            <transition name='_con'>
                <div class='_currTbale_conenter el-dialog' v-show='conShow' >
                    <header class='_conenter_header el-dialog__header'>
                        <p>
                          <span>钢琴</span>
                          <span>学生人数：4人</span>
                          <span>班主任：呸呸</span>
                        </p>
                        <i class='whitedelete_icon icon-width' @click.stop='closeClick'></i>
                    </header>
                    <main class='_conenter_main'>
                        <div class='_class_infobody_Top'>
                            <div class='_top_left'>
                                <p class='_top_leftTimes'>
                                    <span>日期：</span>
                                    <el-date-picker
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                    <span> 至 </span>
                                    <el-date-picker
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </p>
                                <p class='_top_leftSelect'>
                                    <label class='label_elect selectTwo'>
                                        <span>选择：</span>
                                        <el-select  placeholder="请选择">
                                            <el-option label="全部" value="0"></el-option>
                                            <el-option label="一对一" value="1"></el-option>
                                            <el-option label="一对多" value="2"></el-option>
                                        </el-select>
                                    </label>
                                    <label class='label_elect selectTwo'>
                                        <span>课程：</span>
                                        <el-select  placeholder="请选择">
                                            <el-option label="全部" value="0"></el-option>
                                            <el-option label="一对一" value="1"></el-option>
                                            <el-option label="一对多" value="2"></el-option>
                                        </el-select>
                                    </label>
                                </p>
                            </div>
                            <div class='_top_right'>
                                <Lbutton class='hot_btn' @click.native="exportClick(2, $event)">
                                    <i class='icon-width export_icon'></i>
                                    导出Excel
                                </Lbutton>
                            </div>
                        </div>
                        <div class='_class_infobody_buttom'>
                            <el-table
                                :data="tableData"
                                border
                                stripe
                                resizable=false
                                style="width: 100%">
                                <el-table-column
                                    show-overflow-tooltip
                                    align='center'
                                    min-width='14.7%'
                                    label="日期">
                                </el-table-column>
                                <el-table-column
                                    show-overflow-tooltip
                                    prop="date"
                                    align='center'
                                    min-width='8.8%'
                                    label="时间">
                                </el-table-column>
                                <el-table-column
                                    show-overflow-tooltip
                                    align='center'
                                    min-width='11.76%'
                                    label="课程名称">
                                    <template slot-scope="scope">
                                        <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        </span>
                                    </template>
                                </el-table-column>
                                <el-table-column
                                    show-overflow-tooltip
                                    align='center'
                                    min-width='11.76%'
                                    label="教师">
                                </el-table-column>
                                <el-table-column
                                    show-overflow-tooltip
                                    align='center'
                                    min-width='11.76%'
                                    label="教室">
                                </el-table-column>
                                <el-table-column
                                    show-overflow-tooltip
                                    align='center'
                                    min-width='11.76%'
                                    label="状态">
                                </el-table-column>
                                <el-table-column
                                    label="操作"
                                    min-width='25%'
                                    show-overflow-tooltip>
                                    <template class='erp-call'>
                                        <div class="grid-content bg-purple">
                                            <label>
                                                <el-button type="text"  size="small"  class='erp-button-icon'> 
                                                    删除
                                                </el-button>
                                            </label>
                                        </div>
                                    </template>
                                </el-table-column>
                            </el-table>
                            <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father'></erp-paging>
                        </div>
                    </main>
                </div>
            </transition>
        </div>
    </transition>
</template>
<script>
import Lbutton from '../../../../button/button'
import erpPaging from '../../../../paging/paging'

export default {
  name: 'currtable',
  data () {
    return {
      conShow: false,
      tableData: []
    }
  },
  inject: ['gradeThis'],
  methods: {
    backClick(){
      this.gradeThis.currTableShow = !this.gradeThis.currTableShow
    },
    closeClick(){
      this.backClick()
    },
    paging_father() {

    }
  },
  watch:{
    'gradeThis.currTableShow'(val){
        this.conShow = val
    }
  },
  components: {
    Lbutton: Lbutton,
    erpPaging: erpPaging
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
._tab-enter,
._tab-leave-to,
._tab-enter-to,
._tab-leave,
._con-enter,
._con-leave-to,
._con-enter-to,
._con-leave{
    transition: all 0.45s ease;
}
._tab-enter,
._tab-leave-to{
    opacity: 0;
}
._tab-enter-to,
._tab-leave{
    opacity: 1;
}


._con-enter,
._con-leave-to{
  transform: translateY(-30%);
}

._con-enter-to,
._con-leave{
  transform: translateY(0%);
}
@media screen and (max-width: 1380px){
._currTbale{
    @include wh(100%,100%);
    background: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    ._currTbale_conenter{
        @include wh(vw1(1360),vw1(660));
        background-color: #ffffff;
        border-radius: vw1(8);
        ._conenter_header{
            @include wh(100%, vw1(60));
            @include bg-defaul($bg-color);
            border-top-left-radius: vw1(8);
            border-top-right-radius: vw1(8);
            padding: 0 vw1(30);
            box-sizing: border-box;
            color: #ffffff;
            font-size: vw1(16);
            display: flex;
            align-items: center;
            justify-content: space-between;
            span{
                margin-right: vw1(60);
            }
            .icon-width{
                @include wh(16px,16px);
                display: inline-block;
                cursor: pointer;
            }
        }
        ._conenter_main{
            padding: 0 vw1(30);
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            ._class_infobody_Top{
                display: flex;
                justify-content: space-between;
                padding: vw1(30) 0;
                box-sizing: border-box;
                ._top_left {
                    display: flex;
                    ._top_leftTimes {
                        margin-right: vw1(20);
                        /deep/ .el-input {
                             @include wh(vw1(160), vw1(40));
                            .el-input__inner{
                                @include wh(100%, vw1(40));
                                @include borderColor-defaul($border-color);
                            }
                            .el-input__prefix .el-input__icon {
                                line-height: vw1(40)
                            }
                        }
                    }
                    ._top_leftSelect{
                        .label_elect{
                            margin-right: vw1(20);
                            /deep/ .el-select .el-input{
                                @include wh(vw1(120),vw1(40));
                                .el-input__inner{
                                    @include wh(100%,vw1(40));
                                    @include borderColor-defaul($border-color);
                                }
                            }
                        } 
                    }
                }
                ._top_right .hot_btn {
                    font-size: vw1(16);
                    padding: 0 vw1(16);
                    height: vw1(40);
                    line-height: vw1(40);
                    box-sizing: border-box;
                    @include borderColor-defaul($border-color);
                    .icon-width{
                        @include wh(vw1(20),vw1(20));
                    }
                }
            }
            ._class_infobody_buttom{
                display: flex;
                flex-direction: column;
                flex: 1;
                /deep/ .el-table{
                    height: vw1(450);
                }
            }
        }
    }
}
}

@media screen and (min-width: 1381px){
._currTbale{
    @include wh(100%,100%);
    background: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
