    display: flex;
    align-items: center;
    justify-content: center;
    ._currTbale_conenter{
        display: flex;
        flex-direction: column;
        @include wh(vw(1360),vw(660));
        background-color: #ffffff;
        border-radius: vw(8);
        ._conenter_header{
            @include wh(100%, vw(60));
            @include bg-defaul($bg-color);
            border-top-left-radius: vw(8);
            border-top-right-radius: vw(8);
            padding: 0 vw(30);
            box-sizing: border-box;
            color: #ffffff;
            font-size: vw(16);
            display: flex;
            align-items: center;
            justify-content: space-between;
            span{
                margin-right: vw(60);
            }
            .icon-width{
                @include wh(16px,16px);
                display: inline-block;
                cursor: pointer;
            }
        }
        ._conenter_main{
            padding: 0 vw(30);
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            flex: 1;
            ._class_infobody_Top{
                display: flex;
                justify-content: space-between;
                padding: vw(30) 0;
                box-sizing: border-box;
                ._top_left {
                    display: flex;
                    ._top_leftTimes {
                        margin-right: vw(20);
                        /deep/ .el-input {
                             @include wh(vw(160), vw(40));
                            .el-input__inner{
                                @include wh(100%, vw(40));
                                @include borderColor-defaul($border-color);
                            }
                            .el-input__prefix .el-input__icon {
                                line-height: vw(40)
                            }
                        }
                    }
                    ._top_leftSelect{
                        .label_elect{
                            margin-right: vw(20);
                            /deep/ .el-select .el-input{
                                @include wh(vw(120),vw(40));
                                .el-input__inner{
                                    @include wh(100%,vw(40));
                                    @include borderColor-defaul($border-color);
                                }
                            }
                        } 
                    }
                }
                ._top_right .hot_btn {
                    font-size: vw(16);
                    padding: 0 vw(16);
                    height: vw(40);
                    line-height: vw(40);
                    box-sizing: border-box;
                    @include borderColor-defaul($border-color);
                    .icon-width{
                        @include wh(vw(20),vw(20));
                    }
                }
            }
            ._class_infobody_buttom{
                display: flex;
                flex-direction: column;
                flex: 1;
                /deep/ .el-table{
                    height: vw(450);
                }
            }
        }
    }
}
}


</style>