<template>
    <div>
      <recordTop></recordTop>
      <div class='table-gulp'>
        <section class='div_gulptable'>
            <el-table
                :data="tableData"
                ref='table'
                border
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    prop="goods_name"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="商品名称">
                </el-table-column>
                <el-table-column
                    prop="dep_num"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="出库数量">
                </el-table-column>
                <el-table-column
                    prop="dep_price"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="出库价格(元)">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop='dep_total'
                    align='center'
                    min-width='8.88%'
                    label="出库总额(元)">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    prop='dep_time'
                    min-width='15.55%'
                    label="出库时间">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    prop='dep_code'
                    min-width='15.55%'
                    label="出库单号">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    prop='manager'
                    min-width='8.88%'
                    label="操作员">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop='remark'
                    align='center'
                    min-width='8.88%'
                    label="备注">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='22.22%'
                    label="操作">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
                        <el-button type="text" @click.stop='deletes(scope.row.dep_id)' size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <modifyrecord/>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
        <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" ></erp-paging>
    </div>
    </div>
</template>
<script>
import recordTop from './recordTop/recordTop'
import modifyrecord from './modifyRecord/modifyRecord'
import erpPaging from '../../../paging/pagingShort'
import {getcheckoutList,deletecheckoutList} from '../../../../getserver/server'
export default {
  name: 'statistical',
  data () {
    return {
      showDe:false,
      deleteId: null,
      modifyrecord_Show: false,
      modelOutboundData: null,// 修改记录
      form: {
        goods_name: ''
      },
      tableData: [],
      countNum: 0,
      page_num: 0,
      pageSize: 8
    }
  },
  provide () {
      return {
        "outThis": this
      }
  },
  created(){
    this.get_getcheckoutList(1)
  },
  mounted () {
    this.tableHeight()
  },
  methods: {
    tableHeight () {
      let tableRefs = this.$refs.table.$el; // table 父亲级节点
      let trheight = null
      const _this = this
      setTimeout(()=>{
          trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
          tableRefs.style.height = trheight * ( _this.pageSize + 1 ) + "px"
      },800) 
    },
    paging_father (i) {
      this.page_num = i
      this.get_getcheckoutList(i)
    },
    handleClick(item){
      // 修改点击
      this.modifyrecord_Show = !this.modifyrecord_Show;
      this.modelOutboundData = item
    },
    deletes(id){
        // 删除点击
      this.deleteId = id;
      this.showDe = true
    },
    hideDeleteModal(){
      this.showDe = false
    },
    submitDelete(id){
      this.showDe = false;
      this.delectclassRooms(id)
    },
    delectclassRooms(id){
      this.deletes_fun(id)
    },
    deletes_fun(id){
      // 删除接口
      this.geTdeletecheckoutList(id)
    },
    async geTdeletecheckoutList(id) {
      let resDate = await deletecheckoutList(id)
      if (resDate.data.status) {
        // 删除成功
        this.$message({
          message: "删除成功",
          type: 'success',
          duration: 2000,
        })
        if (this.tableData.length == 1){
          if (this.page_num==1) {
              this.get_getcheckoutList(1)
            return;
          }
          this.get_getcheckoutList(this.page_num-1);
          return;
        } else {
          this.get_getcheckoutList(this.page_num);
        }
      }
    },
    get_getcheckoutList(limit) {
      const _this = this
       getcheckoutList (this.form.goods_name,limit,this.pageSize).then((res)=>{
          if (res.data.status) {
            if (this.form.goods_name!="") {
              this.$message({
                message: "筛选成功",
                type: 'success',
                duration: 2000,
              })
            }
            _this.tableData = res.data.data.data
            _this.countNum = res.data.data.total
          }
       })
    }
  },
  components: {
    recordTop: recordTop,
    modifyrecord: modifyrecord,
    erpPaging: erpPaging
  }
}
</script>
<style lang="scss" scope>
    
</style>