<template>
  <div id="app">
    <!-- <button @click.stop='inse("url1")'>变色</button>
    <button @click.stop='defaultBg'>默认</button> -->
    <router-view v-if="isRouterAlive"></router-view>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
export default {
  name: 'app',
  provide () {
    return {
      reload: this.reload
    }
  },
  data () {
    return {
      isRouterAlive: true
    }
  },
  methods: {
    ...mapMutations(['THIS_VUE']),
    inse(e){
      window.document.documentElement.setAttribute('inse-se', e);
    },
    defaultBg() {
      window.document.documentElement.removeAttribute('inse-se');
    },
    reload () {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    }
  },
  mounted () {
    this.THIS_VUE(this)
  },
  created(){
    
  },
  components: {

  }
}
</script>

<style lang='scss'>
@import '../node_modules/element-ui/lib/theme-chalk/index.css';
@import './style/css_init';
@import './style/global';
@import './style/mixin';
</style>
