<template>
    	<div class="modal-container modal-container2" v-show="show">
    		<div class="modal-header">
    			{{ title }}
    			<i class="el-icon-close" round @click="hideModal"></i>
    		</div>
    		<div class="modal-main">
    			<el-form ref="form" label-width="110px" class="fromBox">
		    		<label class='label_search'>
              <input type="text" placeholder="搜索课程" v-model="searchVal" @change="searchCurrm()">
              <i class='search_icon icon-width'></i>              
            </label>
            <div class="checkList">
              <template v-for="(item,index) in checkedList">
                <el-checkbox v-if="item.checked" v-model="item.checked" :cur_id="item.cur_id" :cur_name="item.cur_name" @change="changeTc($event,item)">
                  <ul>
                    <li>{{item.cur_name}}</li>
                    <li v-if="item.tmethods == 1">一对一</li>
                    <li v-else>一对多</li>
                    <li>{{item.ctime}}分钟</li>
                  </ul>
                </el-checkbox>
                <el-checkbox v-else v-model="item.checked" :cur_id="item.cur_id" :cur_name="item.cur_name" @change="changeTc($event,item)">
                  <ul>
                    <li>{{item.cur_name}}</li>
                    <li v-if="item.tmethods == 1">一对一</li>
                    <li v-else>一对多</li>
                    <li>{{item.ctime}}分钟</li>
                  </ul>
                </el-checkbox>                
              </template>              
              <!-- <el-checkbox v-model="checked">
                <ul>
                  <li>钢琴</li><li>一对多</li><li>60分钟</li>
                </ul>
              </el-checkbox>
              <el-checkbox v-model="checked">
                <ul>
                  <li>钢琴</li><li>一对多</li><li>60分钟</li>
                </ul>
              </el-checkbox> -->
            </div>                                                         
		    	</el-form>
		    </div>
		    <div class="modal-footer alertFooter">
		    	<el-button type="primary" round @click="submit">保存</el-button>
		    	<el-button round @click="hideModal">取消</el-button> 
		    </div>
		</div>
</template>
<script>
  import axios from 'axios';
  import {allCurrm,searchCurrm} from '../../../../../../getserver/getAxios.js'
  export default {
    name: 'rightModal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        }               
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            status:'',
            searchVal:'',
            checkedData:[],
            checkedDataNew:[],
            checkedList:[]   
        }
    },
    created(){
       
    },
    mounted() {
        
    },
    methods: {
        hideModal() {
            this.$emit('hideRightModal')
        },      
        submit() {
         var arr = [];
         var _this = this;
         _this.checkedData.forEach( function(element, index) {
          arr.push(_this.checkedData[index])
         });
         this.checkedDataNew = arr;
         this.$emit('changeTable',this.checkedDataNew)
        }, 
        checkedDataArry(res) {
         this.checkedDataNew = res;
        },              
        async allCurrm (a) {
          let t = (new Date()).getTime();
          let resDate = await allCurrm(t,a)
          if(this.checkedDataNew.length == 0){
            this.checkedList = resDate.data.data;          
          }
          else{
            let len = this.checkedDataNew.length;
            let _this = this;
            let arr = [];
            resDate.data.data.forEach(function(element,index){
              for(var i=0;i<len;i++){
                if(_this.checkedDataNew[i].cur_id == resDate.data.data[index].cur_id){
                  resDate.data.data[index].checked = true;
                  arr[i] = _this.checkedDataNew[i];
                }
                else{
                  //resDate.data.data[index].checked = false;
                }
              }
            });
            this.checkedData = arr;
            this.checkedList = resDate.data.data;
            this.checkedDataNew = resDate.data.data;             
          }
          // console.log(this.checkedData)
          // console.log(this.checkedList)
        }, 
        async searchCurrm () {
          let t = (new Date()).getTime();
          let orgid = this.orgid;
          let resDate = await searchCurrm(t,this.searchVal,orgid)
          this.checkedList = resDate.data.data;
          // console.log(this.checkedList)
        },         
        changeTc(event,item){
          var ta = {};
          ta.cur_name = item.cur_name;
          ta.cur_id = item.cur_id;
          ta.cur_num = '';
          ta.course_model = '2';
          // ta.validity = '';
          ta.cur_value = '';
          ta.actual_price = '';
          var arr = this.checkedData;
          var index1 = '';
          var index2 = '';
          arr.push(ta);
          var len = arr.length;
          var arr1 = arr;
          // console.log(arr4)
          for(var i = 0; i < len; i++) {
            for(var j = i + 1; j < len; j++){
              if (arr1[i].cur_id === arr1[j].cur_id){
                index1 = j;
                index2 = i;
                i++;
                j = i;
              }
            }
          };
          if(index1){
            arr.splice(index1,1)
          };
          if(index1){
            arr.splice(index2,1)
          }         
          this.checkedData = arr;
          // console.log(this.checkedData)
        }                
    }
  }
</script>
