<template>
    <!-- 批量修改金额 -->
    <div class='_salary'>
        <el-dialog
            title="批量设置课程薪酬"
            v-dialogDrag
            :visible.sync="salaryThis.batchsalaryShow"
            width="33.33%">
            <section class='body_section' @scroll="scrollModalMain($event)">
                <!-- <el-form ref="form" :model="form" > -->
                    <label class='_curriculum_labeltwo' @click.stop='kc_rightH'>
                      <span class='label_text'>*课程名称：</span>
                      <div class='kec_inp'>
                        <p>
                            <span v-if='ActiveArr'>
                                {{ActiveArr}}
                            </span>
                            <span v-else>选择课程</span>
                        </p>
                        <i class='Under_icon'></i>
                      </div>
                    </label>
                    <label class='_curriculum_labeltwo'>
                      <span class='label_text'>*赠课计算：</span>
                      <el-select  slot="prepend" v-model='form.chooseWay' placeholder="请选择" class='_main-rightSelect' >
                        <el-option label="计算" value="1"></el-option>
                        <el-option label="不计算" value="2"></el-option>
                      </el-select>
                    </label>
                    <label class='_curriculum_labeltwo'>
                      <span class='label_text'>*计算方式：</span>
                      <el-select  slot="prepend" v-model='form.calculateway' placeholder="请选择" class='_main-rightSelect' >
                        <el-option :label="item.pay_name" :value="item.pay_id_info" v-for='item in Jsway' :key='item.pay_id_info'></el-option>
                        <!-- <el-option label="一对多" value="2"></el-option> -->
                      </el-select>
                    </label>
                    <label class='calculation'>
                        <p>
                            1、按出勤人数: 按所上课程实际出勤学员数计算; 
                        </p>
                        <p>
                            2、按课时消费: 按所有上课学员实际消费金额总和的百分比计算; 
                        </p>
                        <p>
                            3、按授课课时: 根据老师上该课的课时数进行固定金额提成计算。
                        </p>
                    </label>
                    <label class='_curriculum_label _curriculum_time culum_time'>
                      <span>*结算金额：</span>
                      <section class='_curriculum_section'>
                        <el-input v-model='form.settlement'></el-input>
                        <el-button slot="append" v-if='form.calculateway==""'>元/人</el-button>
                        <el-button slot="append" v-else>{{Jsway[form.calculateway-1].cpany}}</el-button>
                      </section>
                    </label>
                    <label class='_curriculum_time textarea_tabel'>
                      <span class='textarea_span'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                      <el-input
                          type="textarea"
                          placeholder="请输入备注说明"
                          show-word-limit
                          v-model='form.note'
                        >
                        </el-input>
                    </label>
                    <label class='_cuiculum_text'>
                     <span>注&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;意：</span>
                     <span class='_cuiculum_sp'>批量设置会将老师单独设置的课程薪酬数据覆盖 您还可以到【日常管理-教师管理-教师详情-薪酬设置】界面，确认每位老师的任教课程或
者单独设置他的薪酬计算方式
                    </span>
                    </label>
                <!-- </el-form> -->
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="Set_upClick">确 定</el-button>
                <el-button @click="salaryThis.batchsalaryShow = false">取 消</el-button>
            </span>
            <transition name='batch'>
                <section class='rightHk' v-show='batchRightHk'>
                    <header class='rightHk_header'>
                        <span>选择课程</span>
                    </header>
                    <main class='rightHk_main'>
                        <section>
                            <label class='label_check' v-for='(item,index) in notKcArr' :key='item.cur_id'>
                                <input type="checkbox" :checked='item.state_in' @click.stop='checkedClick(index)'>
                                <ul>
                                    <li>{{item.cur_name}}</li>
                                    <li>
                                        <span v-if='item.tmethods==1'>一对一</span>
                                        <span v-if='item.tmethods==2'>一对多</span>
                                    </li>
                                    <li>按出勤人数</li>
                                    <li>0元/人</li>
                                </ul>
                            </label>
                            <!-- <label class='label_check'>
                                <input type="checkbox">
                                <ul>
                                    <li>钢琴</li>
                                    <li>一对多</li>
                                    <li>按出勤人数</li>
                                    <li>50元/人</li>
                                </ul>
                            </label> -->
                        </section>
                    </main>
                    <footer class='rightHk_footer'>
                        <span slot="footer" class="dialog-footer">
                            <el-button type="primary" @click='notkcAction'>确 定</el-button>
                            <el-button  @click='cancel'>取 消</el-button>
                        </span>
                    </footer>
                </section>
            </transition>            
        </el-dialog>
    </div>
</template>
<script>
import {kcNotList, JsFsList, addpayinfo} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'batch_salary',
  data () {
    return {
      batchRightHk: false,
      notKcArr: [],
      Jsway: [],
      ActiveArr: '',
      form: {
        active: [], // 选中课程id
        calculateway: '',// 计算方式
        chooseWay: '', // 选择赠课计算
        note: '',// 备注
        settlement: ''// 结算金额
      }
    }
  },
  created (){
    this.getnotKc()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    notkcAction () {
        const _this = this
        let Arr = []
        this.notKcArr.forEach((data)=>{
            if (data.state_in){
              Arr.push(data.cur_name)
            }
        })
        this.ActiveArr = Arr.join('、')
    },
    checkedClick(i){
        this.notKcArr[i].state_in = !this.notKcArr[i].state_in
    },
    kc_rightH(){
      this.batchRightHk = !this.batchRightHk
    },
    cancel (){
      this.ActiveArr = ''
      this.notKcArr.forEach((data)=>{
        if (data.state_in){
            data.state_in = false
        }
      })
    },
    Set_upClick () {
      // 确定
      let jsonArr = [];
      this.notKcArr.forEach((data)=>{
          if (data.state_in) {
            jsonArr.push(data.cur_id)
          }
      })
      this.sumbliefun(jsonArr)
    },
    async getnotKc (){
      let resDate = await kcNotList(this.userLoginInfo.orgid);
      this.notKcArr = resDate.data.data
    },
    async getJsFsfun () {
      // 计算方式
      let resDate = await JsFsList()
      this.Jsway = resDate.data.data
    },
    async sumbliefun (ArrId) {
        let resDate = await addpayinfo(ArrId,this.form.chooseWay, this.form.calculateway, this.form.note, this.form.settlement, this.userLoginInfo.orgid, this.userLoginInfo.id)
        if (resDate.data.status) {
            this.$message({
                message: resDate.data.sinfo,
                type: 'success',
                duration: 1500
            });

            this.salaryThis.listGet('','', this.salaryThis.page_num,10)

            this.form = {
                active: [], // 选中课程id
                calculateway: '',// 计算方式
                chooseWay: '', // 选择赠课计算
                note: '',// 备注
                settlement: ''// 结算金额
            }
            this.ActiveArr = '';
            this.getnotKc()
        }
    }
  },
  inject: ['salaryThis'],
  watch: {
    'form.settlement' (value){
      if (!/^\d+$/g.test(value)){
        this.form.settlement = value.substring(0, value.length-1)
      }
    },
    'salaryThis.batchsalaryShow'(val){
        if (val) {
            // 组件重用，初始化数据。
          this.getnotKc()
          this.getJsFsfun()
          this.ActiveArr = ''
        }
    }
  }
}
</script>
