<template>
    <div class='leaseSection'>
        <leaseTop></leaseTop>
        <div class='div_gulptable'>
            <el-table
                :data="tableData.records"
                stripe
                border
                ref='table'
                style="width: 100%">
                    <el-table-column
                        label="商品名称"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span @click='layerOpen($event)'>
                            {{scope.row.goods_name}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="租赁姓名"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.rent_obj_name}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="租赁数量"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.rent_num}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="开始时间"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.start_time | timeStampfilters}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="结束时间"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.end_time | timeStampfilters}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="计费方式"
                        align='center'
                        show-overflow-tooltip
                        min-width="8.88%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.rent_type_money}}/{{scope.row.rent_type}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="押金(元)"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.rent_margin}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="预付租金(元)"
                        align='center'
                        show-overflow-tooltip
                        min-width="8.88%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.prepaid_rent}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="实收租金(元)"
                        align='center'
                        show-overflow-tooltip
                        min-width="8.88%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.rent_amount}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="状态"
                        align='center'
                        show-overflow-tooltip
                        min-width="6.66%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.status_text}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        label="备注"
                        show-overflow-tooltip
                        align='center'
                        min-width="8.88%">
                        <template slot-scope="scope">
                          <span  @click='layerOpen($event)'>
                            {{scope.row.remarks}}
                          </span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="address"
                        label="操作"
                        align='center'
                        min-width="22.22%">
                        <template slot-scope="scope">
                            <el-button @click="showAlert(scope.row)" type="text" size="small" class='erp-button-icon'>
	                            <i class='repair_icon icon-width'></i>
	                            修改
	                        </el-button>
	                        <el-button type="text" @click="showDelete(scope.row.rent_id)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            删除
	                        </el-button>
                            <el-button type="text" @click="modelClick(1)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            归还
	                        </el-button>
                            <el-button type="text" @click="modelClick(2)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            续租
	                        </el-button>
                            <el-button type="text" @click="modelClick(3)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            记录
	                        </el-button>
                        </template>
                    </el-table-column>
            </el-table>
            <erpPaging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" v-if='countNum>1'/>
        </div>
        <modiftlease/>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
        <returnShop></returnShop>
        <renewal></renewal>
        <leaseModel/>
    </div>
</template>
<script>
import leaseTop from './leaseTop/leaseTop'
import modiftlease from './modify_lease/modify_lease'
import returnShop from './return/return'
import renewal from './Renewal/Renewal'
import leaseModel from './leaseModel/leaseModel'
import {getrecords,deleterecords} from '../../../../getserver/server'
import {mapState} from 'vuex'
import erpPaging from '../../../paging/pagingShort'
export default {
  name: 'lease',
  data () {
    return {
      countNum: 0,
      page_num: 0,
      pageSize: 1,
      showDe:false,
      deleteId: null,
      modiftleaseShow: false,
      returnShow: false,
      renewalShow: false,
      leaseShow: false,
      tableData: [],
      modifyDate: null,
      form: {
        orgid: '',
        key: '',
        start_time: '',
        start_timeStamp: '',
        end_timeStamp: '',
        end_time: '',
        status: ''
      }
    }
  },
  provide(){
    return {
      leaseThis: this
    }
  },
  created() {
    this.get_getrecords(1, this.pageSize)
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  mounted(){
    // this.tableHeight()
  },
  methods: {
    // tableHeight () {
    //     let tableRefs = this.$refs.table.$el; // table 父亲级节点
    //     let trheight = null
    //     const _this = this
    //     setTimeout(()=>{
    //        trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
    //        tableRefs.style.height = trheight * (_this.pageSize + 1) + "px"
    //     },500) 
    // },
    paging_father(i){
      this.page_num = i
      this.get_getrecords(this.page_num,this.pageSize)
    },
    modelClick (i) {
      const _this = this
      switch (parseInt(i)) {
        case 1:
          // 归还
          _this.returnShow = !_this.returnShow
          break;
        case 2:
          // 续租
          _this.renewalShow = !_this.renewalShow
          break;
        case 3:
          // 记录
          _this.leaseShow = !_this.leaseShow
          break;
      }
    },
    showAlert(info){
      this.modiftleaseShow = !this.modiftleaseShow
      this.modifyDate = info
    },
    showDelete(id){
        // 删除点击
      this.deleteId = id;
      this.showDe = true
    },
    hideDeleteModal(){
      this.showDe = false
    },
    submitDelete(id){
      this.showDe = false;
      this.delectclassRooms(id)
    },
    delectclassRooms(id){
      this.deletes_fun(id)
    },
    deletes_fun(id){
      // 删除接口
      this.deleteRecords(id)
    },
    Timeinfo (val) {
      let newDate = new Date(val)
      let yy = newDate.getFullYear();// 年
      let mm = newDate.getMonth() + 1;// 月
      let dd = newDate.getDay();// 日
      if (mm<10){
        mm = '0' + mm
      }
      if (dd < 10) {
        dd = '0' + dd
      }
      let time = yy +'-'+ mm +'-'+ dd
      return time
    },
    async get_getrecords(page,limet) {
      this.form.orgid = this.userLoginInfo.orgid;
      let resDate = await getrecords(this.form,page,limet)
      if (resDate.data.status) {
        // 租凭记录-访问成功
        this.tableData = resDate.data.data.data
        this.page_num = page
        this.countNum = resDate.data.data.total
      }
    },
    async deleteRecords (id) {
      let resDate = await deleterecords(id);
      if (resDate.data.status) {
        // 删除成功
        this.$message({
          message: '删除成功',
          type: 'success',
          duration: 2000,
        });
        if (this.tableData.records.length == 1) {
          // 当前页只剩一条数据 页码--1
          if (this.page_num == 0) {
            this.page_num = 0
          } else {
            this.page_num -- 
          }
          this.get_getrecords(this.page_num, this.pageSize);
        } else {
          this.get_getrecords(this.page_num, this.pageSize);
        }
      }
    }
  },
  components: {
    leaseTop: leaseTop,
    modiftlease: modiftlease,
    returnShop: returnShop,
    renewal: renewal,
    leaseModel: leaseModel,
    erpPaging: erpPaging
  },
  filters: {
    timeStampfilters(val){
      let newDate = new Date(val)
      let yy = newDate.getFullYear();// 年
      let mm = newDate.getMonth() + 1;// 月
      let dd = newDate.getDay();// 日
      if (mm<10){
        mm = '0' + mm
      }
      if (dd < 10) {
        dd = '0' + dd
      }
      let time = yy +'-'+ mm +'-'+ dd
      return time
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.leaseSection{

}
</style>