<template>
    <div>
        <section :class="[{'insShow': this.insdThis.bannertabDate.length==0},'instdynamic-conenter']">
            <ul class='instdynamic-conenter-ul' v-if='this.insdThis.bannertabDate.length!=0'>
                <li class='instdynamic-conenter-li' v-for='item in this.insdThis.bannertabDate' :key='item.b_id'>
                    <div class='banner-img'>
                        <img :src="ImagePz+item.banner_pic" alt="">
                    </div>
                    <div class='conenter-list-left'>
                        <p>
                            <strong>{{item.title}}</strong>
                            <em>{{item.update_time | times}}</em>
                        </p>
                        <p class='pcolor'>
                            <span v-if='item.send_obj==""'>已发：
                              <em>未选择发送对象</em> 
                            </span>
                            <span v-else-if='item.send_obj.indexOf("/")!=-1'>已发：
                                <em v-if='item.send_obj'>{{item.send_obj.split('/')[0]==1?"家长端、":""}}</em> 
                                <em v-if='item.send_obj'>{{item.send_obj.split('/')[1]==2?"老师端、":""}}</em> 
                            </span>
                            <span v-else-if='item.send_obj.indexOf("/")==-1'>已发：
                                <em v-if='item.send_obj'>{{item.send_obj=="2"?"老师端、":"家长端、"}}</em> 
                            </span>
                        </p>
                        <p v-html="item.content"> 
                            {{item.content}}
                        </p>
                    </div>
                    <div class='conenter-list-right'>
                    <el-button type="text" size="small"  @click.stop='bannerModule(item)' class='erp-button-icon'>
                        <i class='repair_icon icon-width '></i>
                        修改
                    </el-button>
                    <el-button type="text" size="small"  @click.stop='stickClick(item.b_id)' class='erp-button-icon'>
                        <i class='placement-icon icon-width '></i>
                        置顶
                    </el-button>
                    <el-button type="text" size="small" @click.stop='out_banner(item.b_id, item.sold_out)'  class='erp-button-icon'>
                        <i class='Lower-shelf-icon icon-width ' v-if='item.sold_out == 0'></i>
                        <i class='placement-icon icon-width ' v-if='item.sold_out == 1'></i>
                        <span v-if='item.sold_out == 1'>上架</span>
                        <span v-if='item.sold_out == 0'>下架</span>
                    </el-button>
                    <el-button type="text" size="small" @click.stop='showDelete(item.b_id)'  class='erp-button-icon'>
                        <i class='delete_icon icon-width '></i>
                        删除
                    </el-button>
                    </div>
                </li>
            </ul>
            <span v-else>
                暂无数据
            </span>
        </section>
        <erpPaging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" v-if='countNum > 0'/>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import {getbannerList, deletebannerList,getstick_banner, getsold_out_banner} from '../../../../../getserver/server'
import erpPaging from '.././../../../paging/pagingShort'
import ImagePz from '../../../../../config/ImagePz'
export default {
  name: 'dynamicbanner',
  data () {
    return {
      countNum: 0,
      page_num: 0,
      pageSize: 4,
      showDe:false,
      deleteId: null,
      ImagePz
    }
  },
  created(){
    this.get_getbannerList(1)
  },
  inject:['insdThis'],
  methods: {
    paging_father(i) {
      // 分页
      this.get_getbannerList(i)
      this.page_num = i
    },
    showDelete(id){
        // 删除警告
        this.deleteId = id;
        this.showDe = true
    },
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        this.showDe = false;
        this.delectclassRooms(id)
    },
    bannerModule (item) {
      // 修改
        this.insdThis.bannerData = item
        this.insdThis.switch_senior = 2
    },
    delectclassRooms(id){
        // 删除-确定 调接口
        this.deletebannerList_fun(id)
    },
    stickClick(id) {
        // 置顶
        this.get_getstick_banner(id)
    },
    out_banner (id,sold_out) {
        // 下架 || 上架
        // let status = 0
        // if (sold_out == 1) {
        //   // 当前状态下架，点击进行上架操作，判断上架数据是否等于5条
        //   this.insdThis.bannertabDate.forEach((data,index)=>{
        //     if(data.sold_out==0) {
        //       status++
        //     }
        //   })
        //   if (status === 5) {
        //     this.$message({
        //       message: 'banner最多置顶5条，请先下架其中一条再进行上架!',
        //       type: 'success',
        //       duration: 3000,
        //     });
        //     return
        //   }
        // }
        this.get_getsold_out_banner(id,sold_out)
    },
    async deletebannerList_fun (id) {
        // 删除banner
        let resDate = await deletebannerList(id)
        if (resDate.data.status) {
            // 删除 成功
            if (this.insdThis.bannertabDate.length == 1) {
            // 最后一个数据被删除
                if (this.page_num==0) {
                    // 最后一页
                    this.get_getbannerList(1)
                } else {
                    this.page_num--
                    this.get_getbannerList(this.page_num)
                }
          }else {
            this.get_getbannerList(this.page_num)
          }
        }
    },
    async get_getbannerList(limit){
        let resDate =await getbannerList(limit,this.pageSize)
        if (resDate.data.status) {
            this.insdThis.bannertabDate = resDate.data.data.data;
            this.countNum = resDate.data.data.total;
        }
    },
    async get_getstick_banner(id){
      let resDate = await getstick_banner(id)
      if (resDate.data.status) {
        // 置顶成功
        this.$message({
            message: 'banner置顶成功!',
            type: 'success',
            duration: 2000,
        });
        this.get_getbannerList(this.page_num)
      }
    },
    async get_getsold_out_banner(id,sold_out) {
      let i = null
      if (sold_out == 0) {
        i = 1
      } else if (sold_out == 1) {
        i = 0
      }
      let resDate = await getsold_out_banner(id,i)
      if (resDate.data.status) {
        // 下架成功
        if (sold_out == 0) {
            this.$message({
              message: 'banner下架成功!',
              type: 'success',
              duration: 2000,
            });
        } else if (sold_out == 1){
            this.$message({
              message: 'banner上架成功!',
              type: 'success',
              duration: 2000,
            });
          }
        this.get_getbannerList(this.page_num)
       }
    }
  },
  components: {
    erpPaging: erpPaging
  },
  filters: {
    times(val){
      let t = val * 1000
      let newDate = new Date(t)
      let yy = newDate.getFullYear();// 年
      let mm = p(newDate.getMonth()+1);// 月
      let dd = p(newDate.getDate());// 日
      function p(s) {
        // 时间补零函数
        return s < 10 ? '0' + s: s;
      }
      return yy +'-'+ mm + '-' +dd
    }
  },
  watch: {
    'insdThis.switch_senior'(val) {
        if (val==0&&this.insdThis.switchnum==2) {
            this.get_getbannerList(this.page_num)
        }
    }
  }
}
</script>
<style lang="scss">
@import '../../../../../style/mixin';
.conenter-list-left{
    p ul li{
       list-style-type: disc!important;
       margin-left: vw(25);
    }
    p ol li{
      list-style: decimal!important;
      margin-left: vw(25);
    }
}
</style>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: 6px;
    font-size: 16px;
    color: #666666;
}

.instdynamic-conenter{
    @include wh(100%, vw(540));
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    border-radius: 8px;
    overflow: auto;
    margin-bottom: vw(30);
    .instdynamic-conenter-ul{
        display: flex;
        flex-direction: column;
    .instdynamic-conenter-li{
        flex: 1;
        height: auto;
        padding: vw(30);
        box-sizing: border-box;
        display: flex;
        align-content: space-between;
        border:1px solid #e7eaec;
        .banner-img{
            @include wh(vw(280),vw(142));
            margin-right: vw(30);
            img{
              @include wh(100%,100%);
            }
        }
        .conenter-list-left{
            flex: 1;
            display: flex;
            flex-direction: column;
            justify-items: center;
            overflow: hidden;
            height: auto;
            p{
                height: 33.33%;
                display: flex;
                align-items: center;
                font-size: vw(16);
                color: #666666;
            }
            p:nth-child(2){
                height: 66.66%;
            }
            p:first-child{
              color: #333333;
              justify-content: space-between;
            }
            p:last-child{
              height: auto;
              width: 100%;
              display: inline-block;
            }
            .pcolor{
              color: #09e197;
            }
        }
        .conenter-list-right{
            display: flex;
            align-items: flex-end;
            .el-button{
                margin-right: vw(44);
                padding: 0;
            }
            .el-button:last-child{
                margin-right: 0;
            }
        }
    }
  }
}
.insShow {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>