<template>
    <el-container class='el-container'>
        <erp-headers></erp-headers>
        <erp-main :num='num'></erp-main>
    </el-container>
</template>
<script>
import erpHeaders from '../../components/header/header'
import erpMain from '../../components/main/main'
import {getseSsionStorage, getlocalStorage, removelocalStorage} from '../../cookie/cookie'
import {mapState, mapMutations} from 'vuex'
export default {
  name: 'home',
  data () {
    return {
      num: '0'
    }
  },
  provide () {
    return {
      indexThis: this
    }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    ...mapMutations(['USER_INFO'])
  },
  created () {
    if (getlocalStorage('userLoginInfo'+this.$route.query.uid) !=null || getlocalStorage('userLoginInfo'+this.$route.query.uid) != ''){
      let thisDate = new Date()
      let jsons = JSON.parse(getlocalStorage('userLoginInfo'+this.$route.query.uid))
      if (thisDate > jsons.expires) {
        // 登录cookie 过期 删除cookie 跳转登陆页面
        removelocalStorage('userLoginInfo'+this.$route.query.uid);
        this.$router.replace({path: '/login'});
        return
      }
      this.USER_INFO(jsons.value)
    }

  },
  components: {
    erpHeaders: erpHeaders,
    erpMain: erpMain
  },
  // beforeRouteEnter (to,from,next) {
  //   next(vm => {
  //     // vm实例
  //     let userinfor = JSON.parse(getlocalStorage('userLoginInfo'+to.query.uid))
  //     vm.USER_INFO(userinfor.value)
  //     console.log(userinfor)
  //   })
  // }
}
</script>
