<template>
	<div class='_headerBox'>
		<div class="leftBox">
			<div class="mechanism">
				<el-dropdown trigger="click" @command="changeOrg">
					<span class="el-dropdown-link">
						{{userLoginInfo.config.name}}<i class="el-icon-arrow-down el-icon--right"></i>
					</span>
					<el-dropdown-menu slot="dropdown" class="orgname">
						<el-dropdown-item v-for="(item,index) in org_list" :command="item.or_id" :key="item.or_id">{{item.or_name}}</el-dropdown-item>
					</el-dropdown-menu>
				</el-dropdown>
			</div>    		
			<ul class="navBox">
				<li v-for="(item,index) in items" :key='index' @click.stop='activeList(item.id)' >
					<a :class="[{'action': item.id == indexthis.num},item.page]"><span>{{ item.message }}</span></a>
				</li>
			</ul>    		
		</div>   	
		<div class="rightBox">
			<el-dropdown @command="handleCommand">
				<span class="el-dropdown-link">
					<!-- <el-avatar class="user_icon"></el-avatar> -->
					{{userLoginInfo.config.contacts}}<i class="el-icon-arrow-down el-icon--right"></i>
				</span>
				<el-dropdown-menu slot="dropdown">
					<el-dropdown-item command='1'>退出登录</el-dropdown-item>
				</el-dropdown-menu>
			</el-dropdown>
			<el-link :underline="false">客服</el-link>
			<el-link :underline="false">帮助</el-link> 
		</div>
	</div>
</template>
<script>
import {mapState,mapMutations} from 'vuex'
import {exitLogin,get_org_list} from '../../../getserver/server'
import {getseSsionStorage, removeSessionStorage, removelocalStorage, setseSsionStorage,getlocalStorage,setlocalStorage} from '../../../cookie/cookie'
export default {
  name: 'headerui',
  data () {
	return {
	items: [],
	orgid:'',
	org_list:[]
	}
  },
  inject: ['indexthis','reload'],
  created () {
	  this.getTabActive();
	  this.get_org_list ()
	  this.rolefun()
  },
  mounted() {
	// console.log(this.userLoginInfo)
  },
  computed: {
	...mapState(['userLoginInfo'])
  },
  methods: {
  ...mapMutations(['USER_INFO']),
	rolefun(){
	  const _this = this
	  this.userLoginInfo.rolelist.forEach((data)=>{
		let pageclass = String;
		switch (data.access_name) {
			case "我的桌面":
				pageclass = 'page1'
			break;
			case "课程表":
				pageclass = 'page2'
			break;
			case "学生":
				pageclass = 'page3'
			break;
			case "考勤管理":
				pageclass = 'page4'
			break;
			case "统计":
				pageclass = 'page6'
			break;
			case "日常管理":
				pageclass = 'page5'
			break;
			case "进销存":
			pageclass = 'page7'
			break;			
		}
		_this.items.push({message: data.access_name, id: data.access_id, page: pageclass})
	  })
	},
	handleCommand (str) {
	  // 退出登录
	  switch (parseInt(str)) {
		case 1:
			// 退出登录
	  		this.exitLigin()
		  break;
	  }
	},
	changeOrg (command){
		var newCookie = JSON.parse(getlocalStorage('userLoginInfo'+this.userLoginInfo.id))
		newCookie.value.orgid = JSON.stringify(command)
		setlocalStorage('userLoginInfo'+this.userLoginInfo.id,newCookie,1)
		this.USER_INFO(newCookie.value)
		this.indexthis.num = '13'
		setseSsionStorage('tabActive', '13')
		this.reload()


	},
	activeList (i) {
		// 点击
		this.indexthis.num = JSON.stringify(i)
		removeSessionStorage('gulpactivenum');
		setseSsionStorage('tabActive', this.indexthis.num)
	},
	getTabActive () {
		let tabactive = getseSsionStorage('tabActive');
		if (tabactive!=null) {
			this.indexthis.num = JSON.parse(tabactive)
		}
	},
	async exitLigin () {
	  let exit = await exitLogin()
	  if (exit.data.status) {
		removelocalStorage('userLoginInfo'+this.userLoginInfo.id)// 删除cookie
		this.$router.replace({path: '/'})
	  }
	},
	async get_org_list () {
		this.orgid = this.userLoginInfo.orgid
		let resDate = await get_org_list(this.orgid)
		if (resDate.data.status) {
			this.org_list = resDate.data.data
		}		
	},
  }
}
</script>
<style lang="scss" scoped>
._headerBox{
	height: 80px;
}
.mechanism{
	display:inline-block;
	line-height:100%;
	vertical-align: middle;
	padding-left:3.125%;
	width: 20.97%;
	font-family: SourceHanSansCN-Bold
}
.mechanism .el-dropdown{
	font-size:20px;
	color:#fff;
	width: 100%;
	display: block;
}
.mechanism /deep/ .el-dropdown span{
	display: inline-block;
	text-overflow: ellipsis;
	white-space: nowrap;
	overflow: hidden;
	position: relative;
	height: 40px;
	line-height: 40px;
	padding-right: 30px;
	max-width: 100%;
}
.mechanism /deep/ .el-dropdown span i{
	position: absolute;
	left: 76%;
	top: 20%;
}
.mechanism .el-dropdown-link .el-icon--right:before{
	content: "\e790";
}
.orgname{
	left:2%!important
}
.leftBox{
	width:69.85%;
	float: left;
	height:100%;
	display: table;
}
.rightBox{
	width:30.15%;
	float: left;
	height:28%;
	margin-top: 28px;
	text-align:right;
	padding-right: 3.125%;
	box-sizing: border-box;
}
.navBox{
	overflow: hidden;
	height:100%;
	display: inline-block;
	vertical-align:middle;
	width: 75.8%;
}
.navBox li{
	// width:14.285%;
	display:block;
	vertical-align: middle;
	text-align: center;
	display: table-cell;
	text-align:center;
	// padding-left: 4%;
	border-left:1.7vw solid transparent;
	box-sizing:border-box;
}
.navBox li a{
	// width:61.54%;
	width:80px;
	display:block;
	height:80px;
	// margin-left: 19.23%;
	cursor: pointer;
	border-radius: 12px;
	position: relative;
	background:url("../../../../static/icon/nav_icon_desktop.png") center 25% no-repeat;
	background-size:30% auto;
	font-size:14px;
}
.navBox li a span{
	width:100%;
	text-align:center;
	display:block;
	position:absolute;
	left:0;
	bottom:15%;
}
.navBox li a.page2{
	background:url(../../../../static/icon/nav_icon_timetable.png) center 25% no-repeat;
	background-size:30% auto;		
}
.navBox li a.page3{
	background:url(../../../../static/icon/nav_icon_student.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a.page4{
	background:url(../../../../static/icon/nav_icon_attendance.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a.page5{
	background:url(../../../../static/icon/nav_icon_daily.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a.page6{
	background:url(../../../../static/icon/nav_icon_statistics.png) center 25% no-repeat;
	background-size:30% auto;		
}	
.navBox li a:hover{
	background-color:rgba(255,255,255,0.3);
}
.action{
	background-color:rgba(255,255,255,0.3)!important;
}
.rightBox .el-dropdown-link {
	cursor: pointer;
	color: #409EFF;
}
.rightBox .el-icon-arrow-down {
	font-size: 12px;
}
.rightBox .user_icon{
	width: 28px;
	height: 28px;
	border-radius:50%;
	border: 2px solid #fff;
	background:url(../../../../static/header_back/LOGO_1024.png) center center no-repeat;
	background-size:100% auto;
	margin-right: 5px;
}
.rightBox .user_icon i {
	display:none
}
.rightBox .el-dropdown-link{
	height: 28px;
	line-height: 28px;
	display: flex;
	color: #ffffff;
}
.el-dropdown-link .el-icon--right{
	line-height: 28px;
}
.rightBox .el-dropdown-link .el-icon--right:before{
	content: "\e790";
}
.rightBox .el-link{
	color:#fff;
	display:inline-block;
	height:28px;
	line-height:28px;
	margin-left:5.263%;
}
.rightBox .el-dropdown{
	vertical-align: top;
}
.el-dropdown-menu{
	padding:0
}
</style>