<template>
    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1">
            <div class="modal-header" @mousedown="setStartingPoint">
                {{ that.title }}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form ref="form" :model="form" :rules="rules" label-width="15%" class="fromBox">
                <el-form-item prop="cate_name" label="教 师" style="margin-top: 3%">
                  <el-select v-model="form.name" placeholder="请选择分类状态" ref="selectRef" class="nameRight"></el-select>
                  <span class="rightSpan">更换</span>
                </el-form-item>
                <el-form-item prop="comeData" label="日 期" class="birthdayBox">
                  <el-date-picker
                    v-model="form.date"
                    type="date"
                    format
                    prefix-icon="el-icon-caret-bottom"
                    placeholder="选择日期"
                    popper-class="selectBirthday"
                    :picker-options="pickerOptions"
                    ref="comeDataInput">
                  </el-date-picker>
                </el-form-item>
                <el-form-item prop="hour" label="上 课 时 间" class="short-item">
                  <el-select v-model="form.hour" placeholder="时">
                    <el-option value="0" label="00"></el-option>
                    <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
                  </el-select>
                  <el-button class="gh gh1" type="text">时</el-button>
                  <el-select v-model="form.minute" placeholder="分" class="minute">
                    <el-option :value="minute" label="00"></el-option>
                    <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
                  </el-select>
                  <el-button class="gh" type="text">分</el-button>          
                </el-form-item>                                              
                <el-form-item prop="name" label="教 室 名 称">
                  <el-select v-model="form.name" placeholder="请选择分类状态" ref="selectRef" class="nameRight"></el-select>
                  <span class="rightSpan">更换</span>
                </el-form-item>
                <el-form-item prop="cur_state" label="批 量 调 课" class="check-item">
                  <el-checkbox v-model="form.status">批量调整当前和排课序列之后的课程</el-checkbox>
                </el-form-item>
                <el-form-item prop="remarks" label="调 课 原 因" >
                  <el-input
                    type="textarea"
                    :autosize="{ minRows: 4, maxRows: 10}"
                    placeholder="可填写原因说明"
                    maxlength="500"
                    v-model="form.remarks">
                  </el-input>
                </el-form-item>                                                                           
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import {cate_list,cate_add} from '../../../../../getserver/getAxios.js'
  import {checkText} from '../../../../../script/validate.js' 
  export default {
    name: 'modal',
    props: {
        that: {
          type: Object,
          default: false
        },                
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            cheackId:null,
            isCanMove: false,
            minute:0,                                    
            form: {
              name: '',
              date:null,
              hours:null,
              minutes:null,
              classRoomName:'',
              status:true,
              remarks:'',
            },
            rules: {
                           
            },
            pickerOptions: {
              disabledDate(time) {
                let _now = Date.now(),
                    t = 365 * 24 * 60 * 60 * 1000,
                    //t = new Date(1950, 1, 1).getTime(),
                    sevenDays = _now - t;
                    //console.log(new Date('1970/01/01 00:00:01').getTime())
                    return time.getTime() > _now;
                    //return time.getTime() > _now || time.getTime() < sevenDays;
                },
            },                               
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    created(){
      
    },
    methods: {
        hideModal() {         
          this.$emit('hideModal')
        },
        submit() {
          
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },
        currentSel() {
          let selVal = this.form.id
        },
        appendzero:function(obj)
        {
          if(obj<10) return "0" +""+ obj;
          else return obj;
        },                        
       async cate_add () {
        let resDate = await cate_add(this.orgid,this.form.name,this.cheackId)
        if (resDate.data.status) {
          this.$message({
            type: 'success',
            message: resDate.data.sinfo
          });
          this.$emit('submit')
        }
        console.log(resDate.data)
      },
      async cate_list () {
        let resDate = await cate_list(this.orgid)
        if (resDate.data.status) {
          this.cateList = resDate.data.data
          // console.log(typeof(this.cateList))
          // console.log(this.cateList)
        }
      },                            
    }
  }
</script>
