<template>
    <div class='addrole'>
         <el-dialog
         v-dialogDrag
        :title="userThis.tabmodel!=null?'修改角色':'新增角色'"
        :visible.sync="userThis.addUserRoleShow"
        width="33.33%"
        >
        <section class='list_fw'>
            <label class='user_name fw_label'>
                <span class='text_span'>*用户名：</span>
                <input type="text" placeholder="请输入用户名" v-model='form.userName' maxlength="16">
            </label>
            <label class='fw_label' style='align-items: start;'>
                <span class='text_span'>*角色描述：</span>
                <el-input
                    type="textarea"
                    placeholder="请输入角色描述"
                    v-model="form.describe"
                    maxlength="500"
                    show-word-limit>
                </el-input>
                <!-- <textarea placeholder="请输入角色描述" v-model='form.describe' maxlength="500"></textarea> -->
            </label>
            <p class='fw_label' style='align-items: start;'>
                <span class='text_span'>*角色授权：</span>
                <section class='permiss_section'>
                    <div  class='tree_div'>
                        <el-tree
                            class='one'
                            :data="data"
                            :default-checked-keys= "defaultKet"
                            ref="rootTree"
                            :show-checkbox="true"
                            default-expand-all
                            node-key="id"
                            :highlight-current="true"
                            :props="defaultProps">
                        </el-tree>
                    </div>
                </section>
            </p>
        </section>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size='mini' @click="subimtAddRole" :loading='saveShow'>保存</el-button>
            <el-button @click="userThis.addUserRoleShow = false" size='mini'>取消</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
import {userRole, addRole, correctRole} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'addrole',
  data () {
    return {
        saveShow: false,
        data: [],
        defaultKet: [],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        addUserShow: true,
        form: {
          userName: '',
          describe: ''
        }
    }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created(){
    this.getuserRole()
  },
  methods: {
    async getuserRole() {
        let resDate = await userRole(this.userLoginInfo.orgid)
        if (resDate.data.status) {
            let infoDate = [];
            resDate.data.data.alist.forEach((data)=>{
                let JsonData = {};
                let ChildernArr = [];
                JsonData['id'] = data.access_id;
                JsonData['label'] = data.access_name;
                if (data.pidlist.length!=0){
                    data.pidlist.forEach((dataitem)=>{
                        let JsonData_two = {};
                        JsonData_two['id'] = dataitem.access_id
                        JsonData_two['label'] = dataitem.access_name
                        ChildernArr.push(JsonData_two);
                        JsonData['children'] = ChildernArr
                    })
                }
                infoDate.push(JsonData)
            })
           this.data = infoDate
        }
    },
    subimtAddRole () {
        // 添加角色
       this.saveShow = true
       const _this = this
       setTimeout(()=>{
          _this.saveShow = false
       }, 1500)
       let resDate = this.getCheckedKeys(this.data, this.$refs.rootTree.getCheckedKeys(), 'id')
       if (this.form.userName == '' || this.form.userName.trim().length == 0) {
          this.$message.error('用户名不能为空!');
          return
       } else if (this.form.describe == '' || this.form.describe.trim().length == 0) {
          this.$message.error('角色描述不能为空!');
          return
       } else if (resDate.length == 0) {
           this.$message.error('角色必须进行授权!');
           return
       }
       if (this.userThis.tabmodel!=null){
           // 修改角色
           correctRole(resDate, this.form.userName, this.userLoginInfo.id, this.userLoginInfo.orgid, this.userThis.tabmodel.role_id, this.form.describe).then((res)=>{
               if (res.data.status) {
                    _this.$message({
                        message: '修改成功',
                        type: 'success',
                        duration: 2000,
                    });
                    // 数据更新
                    _this.userThis.tabmodel.role_name = _this.form.userName
                    _this.userThis.tabmodel.remake = _this.form.describe
                    _this.userThis.tabmodel.aid = resDate.join('1');
                }
           })

       }else {
           // 添加角色
        addRole(resDate, this.form.describe, this.form.userName, this.userLoginInfo.id, this.userLoginInfo.orgid).then((res)=>{
            if (res.data.status) {
              this.$message({
                message: '添加成功',
                type: 'success',
                duration: 2000,
              });
              this.form = {
                userName: '',
                describe: ''
              }
              resDate = ''
            }
        })
       }
    },
    getCheckedKeys(data,keys,key){
        var res = [];
        recursion(data, false);
        return res;

        // arr -> 树形总数据
        // keys -> getCheckedKeys获取到的选中key值
        // isChild -> 用来判断是否是子节点
        function recursion (arr, isChild) {
            var aCheck = [];
            for ( var i = 0; i < arr.length; i++ ) {
                var obj = arr[i];
                aCheck[i] = false;

                if ( obj.children ) {
                    aCheck[i] = recursion(obj.children, true) ? true : aCheck[i];
                    if ( aCheck[i] ) {
                        res.push(obj[key]);
                    }
                }

                for ( var j = 0; j < keys.length; j++ ) {
                    if ( obj[key] == keys[j] ) {
                        aCheck[i] = true;
                        if ( res.indexOf(obj[key]) == -1 ) {
                            res.push(obj[key]);
                        }
                        break;
                    }
                }
            }
            if ( isChild ) {
                return aCheck.indexOf(true) != -1;
            }
        }
    }
  },
  inject: ['userThis'],
  watch:{
    'userThis.tabmodel'(val){
        if (val!=null) {
            const _this = this
            this.form.userName = val.role_name
            this.form.describe = val.remake
            this.defaultKet = val.get_aid.split(',')
        }
    }
  }
}
</script>
