<template>
    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1">
            <div class="modal-header" @mousedown="setStartingPoint">
                {{ title }}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form label-width="18%" class="fromBox">
                <el-form-item label="已有资历：" >
                  <div class="borderBox">
                    <ul>
                      <li v-for="(item,index) in seniority" @click="selectThis(index)" :key="item.s_id" :class="{'on':item.on===true}">{{item.s_name}}</li>
                    </ul>
                  </div>
                  <el-button plain class="funButton red" @click="delSenioritie">移除</el-button>
                </el-form-item>                
                <el-form-item label="新增资历：" >
                  <el-input v-model.trim="s_name" placeholder="请输入资历等级" maxlength="10" ref="nameInput"></el-input>
                  <el-button plain class="funButton" @click="addSenioritie">添加</el-button>
                </el-form-item>                                                             
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import {senioritieList,addSenioritie,delSenioritie} from '../../../../../getserver/getAxios.js'
  import {checkMaxVal} from '../../../../../script/validate.js' 
  export default {
    name: 'modal',
    props: {
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        }        
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            seniority:[],
            id:'',
            s_name:'',         
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    created(){
      this.senioritieList ()
    },
    methods: {
        hideModal() {         
          this.$emit('hideModal')
        },
        submit() {
          this.$emit('hideModal')
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },
        selectThis(index) {
          var _this = this;
          this.id = this.seniority[index].s_id
          this.seniority.forEach(function(element,index1){
            _this.seniority[index1].on = false
          })
          this.seniority[index].on = !this.seniority[index].on
        },
        async senioritieList () {
          let resDate = await senioritieList(this.orgid)
          if (resDate.data.status) {
              console.log(resDate.data)
              resDate.data.data.forEach(function(element,index){
              resDate.data.data[index].on = false
            })
            this.seniority = resDate.data.data
          }
          console.log(this.seniority)
        },
        async addSenioritie () {
          if(this.s_name == ''){
             this.$message.error('请输入资历名称');
             return
          }
          let s_name = this.s_name.toString()
          let resDate = await addSenioritie(s_name,this.orgid)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });           
            this.senioritieList()
            this.s_name = ''
            this.$emit('senioritieList')         
          }
        }, 
        async delSenioritie () {
          let id = this.id
          console.log(this.orgid)
          let resDate = await delSenioritie(id,Number(this.orgid))
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });           
            this.senioritieList()
            this.$emit('senioritieList')            
          }
        },                     
               
    }
  }
</script>
