<template>
    <div class='_modifyRecord'>
        <el-dialog
            title="修改出库信息"
            :visible.sync="outThis.modifyrecord_Show">
            <section class='__body'>
                <label class='label_list'>
                    <span class='lable-title'>商品名称：</span>
                    <el-input v-model='form.goods_name' disabled></el-input>
                </label>
                <label class='label_list'>
                    <span class='lable-title'>*出库数量：</span>
                    <el-input v-model='form.dep_num'></el-input>
                </label>
                <label class='label_list'>
                    <span class='lable-title'>*出库价格：</span>
                    <el-input v-model='form.dep_price'></el-input>
                </label>
                 <label class='label_list not'>
                    <span class='lable-title'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                    <el-input type='textarea' v-model='form.remarks' placeholder='可填写备注信息'></el-input>
                </label>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="modelClock" size='small'>确 定</el-button>
                <el-button @click="outThis.modifyrecord_Show = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {modelcheckoutList} from '../../../../../getserver/server'
export default {
    name: 'modifyRecord',
    data () {
      return {
        form: {
          dep_id: '',
          goods_name: '',
          dep_price: '',
          dep_num: '',
          remarks: ''
        }
      }
    },
    inject: ['outThis'],
    methods: {
      modelClock() {
        // 修改确认
        if (this.form.dep_num == "") {
          this.$message({
            message: "出库数量不能为空！",
            type: 'error',
            duration: 2000,
          })
          return
        } else if (this.form.dep_price == "") {
          this.$message({
            message: "出库价格不能为空！",
            type: 'error',
            duration: 2000,
          })
          return
        }
        this.get_modelcheckoutList()
      },
      async get_modelcheckoutList() {
          let resDate = await modelcheckoutList(this.form)
          if (resDate.data.status) {
              // 修改成功
              this.$message({
                message: "修改成功",
                type: 'success',
                duration: 2000,
              })
              this.outThis.get_getcheckoutList(this.outThis.page_num)
          }
      }
    },
    watch: {
      'outThis.modelOutboundData'(val) {
          if (val!=null) {
            this.form = {
                dep_id: val.dep_id,
                goods_name: val.goods_name,
                dep_price: val.dep_price,
                dep_num: val.dep_num,
                remarks: val.remarks
            }
          }
      },
      'outThis.modifyrecord_Show' (val) {
          if (val == false) {
            this.outThis.modelOutboundData = null
          }
      },
      'form.dep_num' (val) {
        if(typeof(val) != 'number'){
            val = val.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            val = val.split(".")[0];
            if(val > 200){
                this.$message.error("最大值为200")
                val = 200
            }
        }
        this.form.dep_num = val
      },
      'form.dep_price'(val){
        let actualPrice = val;                    
        if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
                actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 10000000){
                this.$message.error("最大值为100万")
                actualPrice = 10000000
            }                                             
        }
        this.form.dep_price = actualPrice
      }
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
._modifyRecord /deep/ .el-dialog__wrapper .el-dialog{
    @include wh(vw(640),vw(520));
    border-radius: vw(8);
    display: flex;
    flex-direction: column;
    .el-dialog__header{
      @include wh(100%, vw(60));
      @include bg-defaul($bg-color);
      border-top-left-radius: vw(8);
      border-top-right-radius: vw(8);
      box-sizing: border-box;
      padding: vw(22) vw(30);
        .el-dialog__title{
            color: #fff;
            font-size: vw(16);
            line-height: vw(20);
        }
        .el-dialog__headerbtn{
            top: vw(20);
            .el-dialog__close{
                color: #fff;
            }
        }
    }
    .el-dialog__body{
        flex: 1;
        overflow: hidden;
        .__body{
            .label_list{
                display: flex;
                align-items: center;
                margin-bottom: vw(30);
                .lable-title{
                    font-size: vw(16);
                    width: 18%;
                    height: vw(40);
                    line-height: vw(40);
                    text-align: justify;
                }
                .lable-title::after{
                    content: " ";
                    display: inline-block;
                    width: 100%;
                }
                .el-input{
                    .el-input__inner{
                        height: vw(40);
                        line-height: vw(40);
                        font-size: vw(16)
                    }
                }
                .el-textarea .el-textarea__inner{
                    height: vw(100);
                    resize: none;
                }
            }
            .not{
                align-items: flex-start;
            }
            .label_list:last-child{
                margin-bottom: 0;
            }
        }
    }
    .el-dialog__footer{
        @include wh(100%, vw(80));
        border-top: 1px solid #e7eaec;
        padding: vw(20) vw(30);
        box-sizing: border-box;
        .dialog-footer{
            .el-button{
                font-size: vw(16);
                @include wh(vw(120),vw(40));
                background-color: #e7eaec;
                color: #999;
                border-color: #e7eaec;
                border-radius: vw(6);
            }
            .el-button:first-child{
                @include bg-defaul($bg-color);
                color: #fff;
                margin-right: vw(30)
            }
        }
        
    }
}
</style>