<template>
    <div class='instdynamic'>
      <div v-show='switch_senior==0'>
        <dynamicTop></dynamicTop>
        <dynamicnews v-show='switchnum == 1'/>
        <dynamicBanner v-show='switchnum==2'/>
        <!-- <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" ></erp-paging> -->
      </div>
      <div v-show='switch_senior==1||switch_senior==2'>
        <releasedynamic v-show='switch_senior==1'/>
        <releasebanner v-show='switch_senior==2'/>
      </div>
    </div>
</template>
<script>
import dynamicTop from './dynamicTop/dynamicTop'
import dynamicnews from './news-dynamic/newsdynamic'
import dynamicBanner from './dynamic-banner/dynamicBanner'
import releasedynamic from './Releasedynamic/releasedunamic'
import releasebanner from './ReleaseBanner/ReleaseBanner'
import {getdynamicList} from '../../../../getserver/server'
export default {
  name: 'instdynamic',
  data () {
    return {
      tabDate:[],// 动态列表数据
      bannertabDate: [],// banner列表数据
      switchnum: "1", // 动态 banner 切换参考
      switch_senior: 0,
      banner: [{id:1,name:'最新动态',f:true},{id:2,name:'Banner',f: false}],
      correctData: null, //动态 - 修改信息
      bannerData: null // banner - 修改信息
    }
  },
  methods: {
  },
  provide(){
    return{
      insdThis: this
    }
  },
  components: {
    dynamicTop: dynamicTop,
    dynamicnews: dynamicnews,
    dynamicBanner: dynamicBanner,
    releasedynamic: releasedynamic,
    releasebanner: releasebanner
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.instdynamic{
  @include wh(100%,100%);
  display: flex;
  flex-direction: column;
}
</style>