<template>
      <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1" id="wareBox">
          <div class="modal-header" @mousedown="setStartingPoint">
            {{ that.title }}
            <i class="el-icon-close" round @click="hideModal"></i>
          </div>
          <div class="modal-main">
            <el-form ref="form" :model="form" :rules="rules" label-width="17.6%" class="fromBox">
              <el-form-item prop="name" label="商 品 名 称">
                <el-input v-model.trim="form.name" placeholder="请输入商品名称" maxlength="16" ref="nameInput" class="nameBox" readonly></el-input>
              </el-form-item>
              <el-form-item prop="sman_type1" label="销 售 员 类 型" class="check-item">
                <el-checkbox v-model="form.sman_type1" @change="sman_type(1,$event)">销售员</el-checkbox>
                <el-checkbox v-model="form.sman_type2" @change="sman_type(2,$event)">教师</el-checkbox>
              </el-form-item>
              <el-form-item prop="sman_id" label="销 售 员">
                <el-select v-model="form.sman_id" placeholder="选择销售员" ref="sman_id_Input" v-if="form.sman_type == 1">
                  <el-option v-for="(item,index) in all_mans_list" :value="item.sm_id" :key="item.sm_id" :label="item.sm_name"></el-option>
                </el-select>
                <el-select v-model="form.sman_id" placeholder="选择销售员" ref="sman_id_Input" v-else>
                  <el-option v-for="(item,index) in teacher_list" :value="item.t_id" :key="item.t_id" :label="item.t_name"></el-option>
                </el-select>                
              </el-form-item>                           
              <el-form-item prop="num" label="销 售 数 量">
                <el-input v-model.trim="form.num" placeholder="请输入数量" ref="numInput" @keyup.native="change_num1()" @change="salePrice()"></el-input>
              </el-form-item>              
              <el-form-item prop="price" label="销 售 单 价">
                <el-input v-model.trim="form.price" placeholder="请输入销售单价" ref="priceInput" @keyup.native="change_num(1)" @change="salePrice()"></el-input>
              </el-form-item>
              <el-form-item prop="sale_obj_id" label="销 售 对 象">  
                <el-select v-model="form.sale_obj_id" placeholder="选择学生" ref="sale_obj_id_Input" @change="changeType">
                  <el-option-group key="1" label="学生">
                    <el-option v-for="(item,index) in all_students_list" :value="item.stu_id" :key="item.stu_id" :label="item.stu_name+'----账户余额：'+item.stu_balance">
                      <template>
                        <span>{{item.stu_name}}</span><span style="float: right">账户余额：{{item.stu_balance}}</span>
                      </template>
                    </el-option>
                  </el-option-group>
                  <el-option-group key="0" label="其他">
                    <el-option value="0" key="0" label="其他"></el-option>
                  </el-option-group>
                </el-select>               
              </el-form-item>                            
              <el-divider></el-divider>
              <el-form-item prop="getMoney" label="实付款">
                <el-input v-model.trim="form.getMoney" ref="sum_payable_Input" class="nameBox" readonly></el-input>
              </el-form-item>  
              <el-form-item prop="time" label="销 售 时 间" class="birthdayBox">
                <el-date-picker
                  v-model="form.time"
                  type="date"
                  format
                  prefix-icon="el-icon-caret-bottom"
                  placeholder="选择时间"
                  :picker-options="pickerOptions"
                  :clearable="false"
                  popper-class="selectBirthday">
                </el-date-picker>
              </el-form-item>                          
              <el-form-item prop="pay_id" label="付 款 方 式">
                <el-select v-model="form.pay_id" placeholder="选择付款方式" ref="payInput">
                  <el-option v-for="(item,index) in pay_list" :value="item.pay_id" :key="item.pay_id" :label="item.pay_name"></el-option>
                </el-select>
              </el-form-item>                                                                         
              <el-form-item prop="resume" label="备 注" style="margin-bottom: 10px">
                <el-input
                type="textarea"
                :autosize="{ minRows: 3, maxRows: 10}"
                placeholder="可填写备注说明"
                maxlength="500"
                v-model="form.resume">
              </el-input>
            </el-form-item>                                                           
          </el-form>                
        </div>
      <div class="modal-footer alertFooter">
        <el-button type="primary" round @click="submit">保存</el-button>
        <el-button round @click="hideModal">取消</el-button> 
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios';
  import {all_mans_index,all_students,all_pay_list,sale_edit,selectedTeacher} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {validateIdNo,validatePhone,checkText} from '../../../../../script/validate.js'
  let that
  export default {
    name: 'modal',
    props: {
        that: {
          type: Object,
          default: false
        },
        goods_details:{
          type: Object,
          default: false          
        }                       
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,  
            all_mans_list:[],//销售员列表
            all_students_list:[],//学生列表
            pay_list:[],//支付方式列表 
            teacher_list:[],//老师列表 
            form: {
              name: this.goods_details.goods_name,
              sman_type1:true,//销售员
              sman_type2:false,//老师
              sale_obj_id:null,
              sman_id:this.goods_details.sman_id,
              pay_id:this.goods_details.pay_id,
              goods_id:this.goods_details.goods_id,
              getMoney:this.goods_details.pay_amount,
              num:this.goods_details.sale_num,
              price:this.goods_details.single_price,
              time:new Date(this.goods_details.sale_time * 1000),
              resume:this.goods_details.remark,
              sman_type:this.goods_details.sman_type,
              sale_obj_type:null
            },
            rules: {
              sman_id: [
              { required: true}
              ],                
              num: [
              { required: true, message: '请输入出库数量', trigger: 'blur'}
              ],
              price: [
              { required: true, message: '请输入出库单价', trigger: 'blur'} ,
              ],
              pay_id: [
              { required: true}
              ],
              sale_obj_id: [
              { required: true}
              ],                                                                             
            },
            pickerOptions: {
              disabledDate(time) {
                let _now = Date.now(),
                    t = 365 * 24 * 60 * 60 * 1000,
                    sevenDays = _now + t;
                    return time.getTime() > sevenDays;
                },
            },        
            Imageport          
        }
    },
    beforeCreate: function () {
      that = this;
    },    
    mounted() {
        this.node = document.querySelector('#wareBox')       
    },
    created(){
      if(this.goods_details.sman_type == 1){
        this.form.sman_type1 = true
        this.form.sman_type2 = false
      }
      else{
        this.form.sman_type1 = false
        this.form.sman_type2 = true      
      }
      if(this.goods_details.sale_obj_type == 2){
        this.form.sale_obj_id = '0'
        this.form.sale_obj_type = 2
      }
      else{
        this.form.sale_obj_id = this.goods_details.sale_obj_id
        this.form.sale_obj_type = 1
      }  
      this.all_mans_index()
      this.selectedTeacher()
      this.all_students()
      this.all_pay_list()
    },
    watch : {
      
    },
    filters: {
      
    },
    components:{
    },
    methods: {    
        hideModal() {
         this.$emit('hideModal');
        },        
        submit() {
          this.sale_edit()
        },
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },
        sman_type(a,b){
          this.$refs.sman_id_Input.$el.children[0].children[0].blur();
          if(a == 1){
            this.form.sman_id = this.all_mans_list[0].sm_id
            if(b == true){
              this.form.sman_type2 = false
              this.form.sman_type1 = b
              this.form.sman_type = 1                       
            }
            else{
              this.form.sman_type2 = true
              this.form.sman_type1 = b
              this.form.sman_type = 2        
            }
          }
          else{
            this.form.sman_id = this.teacher_list[0].t_id
            if(b == true){
              this.form.sman_type1 = false
              this.form.sman_type2 = b
              this.form.sman_type = 2         
            }
            else{
              this.form.sman_type1 = true
              this.form.sman_type2 = b
              this.form.sman_type = 1           
            }
          }
        },
        changeType(){
          if(this.form.sale_obj_id == '0'){
            this.form.sale_obj_type = 2
          }
          else{
            this.form.sale_obj_type = 1
          }
        },
        async all_mans_index () {
          let resDate = await all_mans_index(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.all_mans_list = resDate.data.data
            console.log(this.all_mans_list)
          }
        },
        async all_students () {
          let resDate = await all_students(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.all_students_list = resDate.data.data
          }
        },
        async all_pay_list () {
          let resDate = await all_pay_list(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.pay_list = resDate.data.data
          }
        },
        async selectedTeacher () {
          let resDate = await selectedTeacher(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.teacher_list = resDate.data.data
            console.log(this.teacher_list)
          }
        },                                       
        async sale_edit () { 
          if(!this.form.sman_id){
            this.$message.error('请选择销售员');
            this.$refs.sman_id_Input.$el.children[0].children[0].click();          
            return
          };
          if(!this.form.sale_obj_id){
            this.$message.error('请选择销售对象');
            this.$refs.sale_obj_id_Input.$el.children[0].children[0].click();          
            return
          }; 
          if(!this.form.pay_id){
            this.$message.error('请选择支付方式');
            this.$refs.payInput.$el.children[0].children[0].click();          
            return
          };                                              
          if(this.form.num){
            this.change_num1()
          }         
          else{
            this.$message.error('请输入销售数量');
            this.$refs.numInput.$el.children[0].focus();
            this.$refs.numInput.$el.children[0].blur();            
            return
          };
          if(this.form.price){
            this.change_num(1)
          }          
          else{
            this.$message.error('请输入商品销售单价');
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();    
            return;
          };                                                                        
          let goods_id = this.form.goods_id
          let sale_obj_id = this.form.sale_obj_id
          if(this.form.sman_type1 == true){
            this.form.sman_type = 1
          }
          else{
            this.form.sman_type = 2
          }        
          let sman_id = this.form.sman_id
          let pay_id = this.form.pay_id
          let sale_id = this.goods_details.sale_id
          let getMoney = this.form.getMoney
          let sum_payable = this.goods_details.sum_payable
          let num = this.form.num
          let price = Number(this.form.price).toFixed(2)
          let time = parseInt((new Date(this.form.time).getTime())/1000)
          let remarks = this.form.resume 
          let resDate = await sale_edit(this.that.userLoginInfo.orgid,this.that.userLoginInfo.id,sale_id,num,Number(price),remarks,time,getMoney,sum_payable,pay_id,sman_id,this.form.sale_obj_type,this.form.sman_type,sale_obj_id)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });           
            this.$emit('submit')
          }
        },
        salePrice(){
          let allPrice;
          allPrice = this.form.num*this.form.price
          this.form.getMoney = allPrice
          this.form.getMoney1 = allPrice
        },
        change_num1(){
          var actualPrice = this.form.num;         
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];                          
          }            
          this.form.num = actualPrice
        },
        change_num(val){
          let actualPrice;
          if(val == 1){
            actualPrice = this.form.price;
          }
          else{
            actualPrice = this.form.getMoney1;
          }                                     
          if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
              actualPrice= parseFloat(actualPrice);  
            }                                  
          }
          if(val == 1){
            this.form.price = actualPrice
          }
          else{
            this.form.getMoney1 = actualPrice
          }                   
        },                                                
    }
  }
</script>
