<template>
    <div class='table-gulp'>
    	<erp-tabtop @getclassRoomLists="getclassRoomLists" @searchclassRoom="searchclassRooms" @template="template" @orgid="32"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="index"
	                show-overflow-tooltip
	                align='center'
	                min-width='13.33%'
	                label="序号">
	                <template slot-scope="scope"><span>{{scope.$index | serialNumber}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="name"
	                show-overflow-tooltip
	                align='center'
	                min-width='17.77%'
	                label="教室名称">
	            </el-table-column>
	            <el-table-column
	                prop="total"
	                show-overflow-tooltip
	                align='center'
	                min-width='17.77%'
	                label="容纳量">
	            </el-table-column>
	            <el-table-column            
	            	show-overflow-tooltip
	                align='center'
	                min-width='17.77%'
	                label="教室状态">
	                <template slot-scope="scope">
	                	<span @click="layerOpen($event)" v-if="scope.row.status == 2">不可用</span>
	                	<span @click="layerOpen($event)" v-else-if="scope.row.status == 1">可用</span>
	                </template>
	            </el-table-column>
	            <el-table-column
	                label="更多操作"
	                min-width='33.36%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
	                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
	                            <i class='repair_icon icon-width'></i>
	                            修改
	                        </el-button>
	                        <el-button type="text" @click="showDelete(scope.row.id)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            删除
	                        </el-button>
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
        <edit-class-room v-if="hackReset" :show="show" :title="title" :orgid="userLoginInfo.orgid" :classRoomData="classRoomData" @hideModal="hideModal" @submit="submit"></edit-class-room>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './classroom-top/classroom-top'
import erpPaging from '../../../paging/pagingShort'
import editClassRoom from '../classroom/alert-box/editClassRoom'
import {getclassRoomList,delectclassRoom,searchclassRoom,selectclassRoom,template} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'table-gulp',
  data () {
    return {
      that: this,
      tableData: [],
  	  value: '',
  	  title: '编辑教室',
  	  show: false,
  	  showDe:false,
  	  deleteId:null,
  	  classRoomData:{},
  	  hackReset:true,
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数 
      name:'',//搜索关键字
      status:null,//教室状态
    }
  },
  computed:{
  	...mapState(['userLoginInfo']),
  },
  beforeCreate: function () {
     that = this;
  },  
  filters:{
    serialNumber ( val ){
        return (that.page_num-1)*that.pageSize+(val + 1)
    }   
  },  
  methods: {
    repairChange (data, i) {
      console.log(i)
    },
    deletes (data, i) {
      console.log(i)
    },
    paging_father (n) {
      this.getclassRoomLists(n)
    },    
    async getclassRoomLists (n) {
    	let t = (new Date()).getTime();
    	let resDate = await getclassRoomList(n,this.userLoginInfo.orgid,t,this.name,this.status)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
        this.last_page = resDate.data.data.last_page
        this.counts = resDate.data.data.total 
        this.page_num = n
        console.log(this.tableData)           
    	}
    },
    async searchclassRooms (name,status,page) {
        let t = (new Date()).getTime();
    	let resDate = await getclassRoomList(page,this.userLoginInfo.orgid,t,name,status)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
        this.last_page = resDate.data.data.last_page
        this.counts = resDate.data.data.total
        this.page_num = n            
    	}
    },
    // async selectclassRooms (name,status,page) {
    // 	console.log(status)
    // 	let resDate = await selectclassRoom(name,status,page,this.userLoginInfo.orgid)
    // 	if (resDate.data.status) {
    // 		this.tableData = resDate.data.data.data
    // 	}
    // 	console.log(resDate.data.data)
    // },
    async template (id) {
    	let resDate = await template(id)
    	if (resDate.data.status) {
    	}
    },            
    delectclassRooms (id,) {
		// 删除教室
  		delectclassRoom(id,this.userLoginInfo.orgid).then((resDate)=>{
  			if (resDate.data.status) {
  				this.$message({
  					type: 'success',
  					message: resDate.data.sinfo
  				});
                  if((this.tableData.length -1) == 0){
                      this.getclassRoomLists (this.page_num-1)
                  }
                  else{
                      this.getclassRoomLists (this.page_num)
                  }
  			}			
  		})		
    },        
    showAlert(a,b){
    	this.classRoomData = a;
    	this.show = true
    },   
    hideModal() {
    // 取消弹窗回调
	    this.hackReset = false
	    this.$nextTick(() => {
	    	this.hackReset = true
	    });        
	    this.show = false
    },
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.showDe = false;
    	this.delectclassRooms(id)
    },
    submit() {
    // 确认弹窗回调
	    this.hackReset = false
	    this.$nextTick(() => {
	    	this.hackReset = true
	    });    
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    }    
  }, 
  created: function () {
  	this.getclassRoomLists (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
    $(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editClassRoom: editClassRoom,
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
</style>
