<template>
    <div class='table-gulp'>
    	<erp-tabtop @cate_index="goods_index"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="goods_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="商品名称">
	                <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.goods_name}} </span></template>
              </el-table-column>
              <el-table-column
                 prop="cate_name"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="所属分类">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.cate_name}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="unit_name"
                 show-overflow-tooltip
                 align='center'
                 min-width='5.555%'
                 label="单位">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.unit_name}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sto_avg_money"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="平均成本(元)">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sto_avg_money}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="goods_amount"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="参考售价(元)">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.goods_amount}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="goods_sku"
                 show-overflow-tooltip
                 align='center'
                 min-width='5.555%'
                 label="库存">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.goods_sku}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sto_total_num"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="进货总量">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sto_total_num}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sto_total_money"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="进价总额(元)">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sto_total_money}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="dep_total_num"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="出货总量">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.dep_total_num}} </span></template>
              </el-table-column>              
	            <el-table-column
	            prop="dep_total_money"
	            show-overflow-tooltip
	            align='center'
	            min-width='6.666%'
	            label="出货总额(元)">
              <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.dep_total_money}} </span></template>
	            </el-table-column>	            
	            <el-table-column
	                prop="sale_total_money"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="销售总额(元)">
                  <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sale_total_money}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="remarks"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="备注">
	                <template slot-scope="scope">
	                	<span @click="layerOpen($event)">{{scope.row.remarks}}</span>
	                </template>	                
	            </el-table-column> 
	            <el-table-column
	                label="操作"
	                min-width='22.222%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
                        <el-button  type="text" @click="showAlert(scope.row, scope.$index)" size="small"  class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          编辑
                        </el-button>                        
	                    	<el-button @click="showAlert1(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		入库
	                    	</el-button>
                        <el-button @click="showAlert2(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          出库
                        </el-button>                        
                        <el-button @click="showAlert3(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          销售
                        </el-button>
                        <el-button @click="showAlert4(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          租赁
                        </el-button>                                                	                    	
	                    	<el-button type="text" @click="showDelete(scope.row.goods_id)" size="small"  class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		删除
	                    	</el-button>                    	
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
        <editCommodity v-if="show" :that="that" @hideModal="hideModal" @submit="submit"></editCommodity>
        <warehousing v-if="show1" :that="that" @hideModal="hideModal1" @submit="submit1"></warehousing>
        <outOfstock v-if="show2" :that="that" @hideModal="hideModal2" @submit="submit2"></outOfstock>
        <sales v-if="show3" :that="that" @hideModal="hideModal3" @submit="submit3"></sales>
        <lease v-if="show4" :that="that" @hideModal="hideModal4" @submit="submit4"></lease>
        <importGoods v-if="show5" :that="that" @hideModal="hideModal5" @submit="submit5"></importGoods>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './commodity-top/commodity-top.vue'
import erpPaging from '../../../paging/pagingShort'
import editCommodity from '../commodity/alert-box/editCommodity.vue'
import warehousing from '../commodity/alert-box/warehousing.vue'
import outOfstock from '../commodity/alert-box/outOfstock.vue'
import sales from '../commodity/alert-box/sales.vue'
import lease from '../commodity/alert-box/lease.vue'
import importGoods from '../commodity/alert-box/importGoods.vue'
import {goods_index,goods_del} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'salesman',
  data () {
    return {
      that: this,
      tableData: [],
		  value: '',
		  title: '修改商品',
      title1: '商品入库',
      title2: '商品出库',
      title3: '商品销售',
      title4: '商品租赁',
      title5: '导入商品',
		  show: false,
		  showDe:false,
      show1:false,
      show2:false,
      show3:false,
      show4:false,
      show5:false,
		  deleteId:null,
      goods_name_search:'',//搜索名字
      cate_id:null,//分类id搜索
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数
      delectType:'0',//0是删除，2是顺延,3是取消消课,4是还原,5是离职,6是复职
      editGoodsId:null,
      cate_name:'',//商品所属分类，传给编辑组件
      goods_name:'',//商品名称，传给出入库组件
      margin_amount:null,//商品押金，传入组件

    }
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{
  	serialNumber ( val ){
  		return (that.page_num-1)*8+(val + 1)
  	}
  }, 
  methods: {
    paging_father (n) {
      this.goods_index(n)
    },    
    async goods_index (n) {
    	let t = (new Date()).getTime();
    	let resDate = await goods_index(n,this.userLoginInfo.orgid,t,this.cate_id,this.goods_name_search)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    		this.last_page = resDate.data.data.last_page
    		this.counts = resDate.data.data.total
    		this.page_num = n;   		            
    	}
    },            
    goods_del (id) {
		// 删除商品
		goods_del(id,this.userLoginInfo.orgid).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				if((this.tableData.length -1) == 0){
					this.goods_index (this.page_num-1)
				}
				else{
					this.goods_index (this.page_num)
				}
			}					
    })
		}, 		        
    showAlert(a,b){
    	this.editGoodsId = a.goods_id
      this.cate_name = a.cate_name
    	this.show = true
    },
    showAlert1(a,b){
      this.editGoodsId = a.goods_id
      this.goods_name = a.goods_name
      this.show1 = true
    },
    showAlert2(a,b){
      this.editGoodsId = a.goods_id
      this.goods_name = a.goods_name
      this.show2 = true
    },
    showAlert3(a,b){
      this.editGoodsId = a.goods_id
      this.goods_name = a.goods_name
      this.show3 = true
    },
    showAlert4(a,b){
      this.editGoodsId = a.goods_id
      this.goods_name = a.goods_name
      this.margin_amount = a.margin_amount
      this.show4 = true
    },
    showAlert5(){
      this.show5 = true
    },                       
    hideModal() {
    // 取消弹窗回	        
	    this.show = false
    },
    hideModal1() {
    // 取消弹窗回          
      this.show1 = false
    },
    hideModal2() {
    // 取消弹窗回          
      this.show2 = false
    },
    hideModal3() {
      // 取消弹窗回          
      this.show3 = false
    },
    hideModal4() {
      // 取消弹窗回          
      this.show4 = false
    },
    hideModal5() {
      // 取消弹窗回          
      this.show5 = false
    },                     
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.goods_del(id)       
    	this.showDe = false    	
    },
    submit() {
    // 确认弹窗回调   
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    },
    submit1() {
      // 确认弹窗回调   
      this.last_page = 0
      this.paging_father(this.page_num)
      this.show1 = false
    },
    submit2() {
      // 确认弹窗回调   
      this.last_page = 0
      this.paging_father(this.page_num)
      this.show2 = false
    },
    submit3() {
      // 确认弹窗回调   
      this.last_page = 0
      this.paging_father(this.page_num)
      this.show3 = false
    },
    submit4() {
      // 确认弹窗回调   
      this.last_page = 0
      this.paging_father(this.page_num)
      this.show4 = false
    },
    submit5() {
      // 确认弹窗回调   
      this.show5 = false
    },                        
  }, 
  created: function () {
  	this.goods_index (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")  	
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editCommodity: editCommodity,
    warehousing: warehousing,
    outOfstock:outOfstock,
    sales:sales,
    lease:lease,
    importGoods:importGoods
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
.noIcon{
	margin:0 vw(8)!important;
}
.noIcon .icon-width {
	width:1px
}
.el-table /deep/ th>.cell{
  padding:0;
  font-size:vw(16)
}
@media screen and(max-width: 1381px){
  .el-table /deep/ th>.cell{
    font-size:vw1(16)
  }
  .noIcon{
    margin:0 vw1(8)!important;
  }
}
</style>
