<template>
    <div>
      <seaticTop></seaticTop>
      <div class='table-gulp'>
        <!-- <erp-tabtop></erp-tabtop> -->
        <section class='div_gulptable'>
            <el-table
                :data="tableData.data"
                border
                stripe
                ref='table'
                resizable=false
                style="width: 100%">
                <el-table-column
                    prop="sale_num"
                    show-overflow-tooltip
                    align='center'
                    min-width='13.33%'
                    label="序号">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="商品名称">
                    <template slot-scope="scope">
                        <span class='hot' @click='layerOpen(scope.row,$event)'>
                          {{scope.row.goods_name}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="sto_num"
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="入库量">
                </el-table-column>
                <el-table-column
                    prop="sale_num"
                    show-overflow-tooltip
                    align='center'
                    min-width='12.22%'
                    label="销售量">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="销售额(元)">
                    <template slot-scope="scope">
                        <span>{{scope.row.sale_total}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="avg_storage_pice"
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="平均成本">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="利润">
                    <template slot-scope="scope">
                        <span>{{scope.row.sale_profit}}</span>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts='countNum' :page_num='page_num' :pageSize='pageSize' @pagings='paging_father'></erp-paging>
    </div>
    </div>
</template>
<script>
import seaticTop from './seaticTop/seaticTop'
import erpPaging from '../../../paging/pagingShort'
import {getSaleList, getSalesmanList} from '../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'statistical',
  data () {
    return {
      tableData: [],
      Incumbency: [],// 在职状态的销售员列表
      countNum: 0,
      pageSize: 8,
      page_num: 0,
      form: {
        cate: "",
        smantype: '',
        timetype: '',
        goodsname: '',
        start_time: '',
        end_time: '',
        end_timeStamp: '',
        start_timeStamp: ''
      }
    }
  },
  provide(){
    return {
      statisThis: this
    }
  },
  computed:{
    ...mapState(['userLoginInfo'])
  },
  created(){
    this.getSaleList_fun(1);
    // this.get_getSalesmanList();
  },
  mounted () {
    this.tableHeight()
  },
  methods: {
    tableHeight () {
      let tableRefs = this.$refs.table.$el; // table 父亲级节点
      let trheight = null
      const _this = this
      setTimeout(()=>{
          trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
          tableRefs.style.height = trheight * ( _this.pageSize + 1 ) + "px"
      },300) 
    },
    async getSaleList_fun (limit) {
        let resDate = await getSaleList(this.userLoginInfo.orgid,this.form.cate,this.form.smantype,this.form.timetype,this.form.goodsname,this.form.start_timeStamp,this.form.end_timeStamp, limit,this.pageSize);
        if (resDate.data.status) {
          if (this.form.cate !="" || this.form.cate === 0 || this.form.smantype!="" || this.form.timetype!="" || this.form.timetype === 0 ||this.form.goodsname!="" || this.form.start_timeStamp!="" || this.form.end_timeStamp!=""){
            this.$message({
              message: "筛选成功",
              type: 'success',
              duration: 2000,
            })
          }
          this.tableData = resDate.data.data;
          this.countNum = resDate.data.data.total
        }
    },
    // async get_getSalesmanList () {
    //     let resDate = await getSalesmanList(this.userLoginInfo.orgid)
    //     if (resDate.data.status) {
    //       // 获取在职状态的销售员列表
    //       this.Incumbency = resDate.data.data
    //     }
    // },
    paging_father (i) {
      // 分页
      this.getSaleList_fun(i)
      this.page_num = i
    }
  },
  components: {
    seaticTop: seaticTop,
    erpPaging: erpPaging
  }
}
</script>
<style lang="scss" scope>
    
</style>