<template>
  <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
   <div class="modal-container modal-container1 adju">
    <div class="modal-header" @mousedown="setStartingPoint">
     请假处理
     <i class="el-icon-close" round @click="hideModal"></i>
    </div>
    <div class="modal-main">
      <el-form class="fromBox">
        <ul>
          <li>
            <el-radio v-model="ty" label="1">事假</el-radio> 
          </li>
          <li>
            <el-radio v-model="ty" label="2">病假</el-radio>
          </li>          
          <li>
            <el-radio v-model="ty" label="3">老师请假</el-radio> 
          </li>          
        </ul>
        <el-input
          type="textarea"
          :rows="4"
          placeholder="可填写备注说明"
          v-model="textarea" class="textarea">
        </el-input>                                        
      </el-form>
  </div>
    <div class="modal-footer alertFooter">
      <el-button type="primary" round @click="submit()">完成</el-button>
      <el-button round @click="hideModal">取消</el-button> 
    </div>
  </div>
</div>
