<script>
  import axios from 'axios';
  import {teacher_courses,addLesson,delLesson} from '../../../../../getserver/getAxios.js'
  export default {
    name: 'topModal',
    props: {
        that: {
          type: Object,
          default: false          
        }            
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            ty:'1',
            textarea:'',            
              
        }
    },
    created(){
    },
    mounted() {
        this.node = document.querySelector('.adju')
        this.that.level = Number(this.ty)
        this.that.remarks = this.textarea
    },
    methods: {
        hideModal() {
          this.that.hideLeaveModal()
        },      
        submit() {
          this.that.setLeave(this.that.deleteId)
        },        
        setStartingPoint(e) {
          console.log(this.node)
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },        
        removeArry(val) { 
          var index = this.cur_list.indexOf(val); 
          if (index > -1) { 
            this.cur_list.splice(index, 1); 
          } 
        },
        clickActive(event,index,index1) {
          
          
        },                     
                      
    }
  }
</script>
