<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="13" style="display: flex;">
          <el-button class="el-icon-arrow-left dateBtn prveBtn"></el-button>
          <span class="dateSpan">
            <el-date-picker
              v-model="date"
              prefix-icon="el-icon-caret-bottom"
              type="date"
              :clearable="false"
              placeholder="选择日期">
            </el-date-picker>
          </span>
          <el-button class="el-icon-arrow-right dateBtn nextBtn"></el-button>
          <Lbutton class='hot_btn repair_button'>
            <i class='icon-width repair_icon1'></i>
            记事
          </Lbutton>          
          <Lbutton class='hot_btn'>
            <i class='icon-width batch_icon'></i>
            表格查看
          </Lbutton>
          <Lbutton class='hot_btn'>
            <i class='icon-width batch_icon'></i>
            批量停课
          </Lbutton>          
          <Lbutton class='hot_btn'>
            <i class='icon-width batch_icon'></i>
            批量顺延
          </Lbutton>          
        </el-col>
        <el-col :span="11">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">
            	<label class='label_elect selectTwo' style="margin-left: 0">
                <span>门店： </span>
                <el-select v-model="value" placeholder="全部" popper-class="treeSelect" ref="selectRef">
                  <el-option v-for="(item,index) in storeList" :value="item.id" :key="item.id" :label="item.name">
                  </el-option>
                </el-select>
              </label>                                      	          	
            	<label class='label_elect selectTwo' style="margin-left: 0">
                <span>科目： </span>
                <el-select v-model="value" placeholder="全部" popper-class="treeSelect" ref="selectRef">
                  <el-option :value="1">11
                  </el-option>
                </el-select>
              </label>
              <label class='label_elect selectTwo' style="margin-left: 0">
                <span>课程： </span>
                <el-select v-model="value" placeholder="全部" popper-class="treeSelect" ref="selectRef">
                  <el-option :value="1">11
                  </el-option>
                </el-select>
              </label>              
              <label class='label_elect selectTwo longSelect' style="margin-left: 0;margin-right: 0">
                <span>选择显示： </span>
                <el-select v-model="value" placeholder="学生" popper-class="treeSelect" ref="selectRef">
                  <el-option :value="1">11
                  </el-option>
                </el-select>
              </label>               
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="show" :show="show" :that="that" :title="title" @hideModal="hideModal" @submit="submit" :orgid="userLoginInfo.orgid">
      </dialogComponent>     
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/alertBox'
import Imageport from '../../../../../config/ImagePz.js'
import {mapState} from 'vuex'
export default {
    name: 'tabTops',
    props: {
      that: {
        type: Object,
        default: false
      },      
    },     
    data () {
    	return {
        addVisible: false,// 控制新增课程模态窗
        value: '',
        title: '新增分类',
        show: false,
        showLoad: false,
        inputVal:'',
        date:'',
        storeList:[
        {"id":1,"name":"11"}
        ],
        Imageport    
      }
    },
    created(){
      
    },
    computed:{
      ...mapState(['userLoginInfo']),
    },    
    methods: {
      download(){
        // console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"public/upload/file/classroom.xlsx"
      },
      upload(){
        console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"index/excel/room_ept?orgid=" + this.userLoginInfo.orgid + "&uid=" + this.userLoginInfo.id
      },
      importClassRoom(){
        this.showLoad = true
      },           
      curriculumClose (k) {
        this.addVisible = k
      },
      showAlert(){
      	this.show = true
      },      
      hideModal() {       
        // 取消弹窗回调
        this.show = false
      },
      hideLoadModal() {    
        // 取消弹窗回调
        this.showLoad = false
      },      
      refresh(){
      	//刷新
      	// this.$emit('searchclassRoom', 1)
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.$message({
         type: 'success',
         message: '刷新成功'
       });      		   	
      },      

      submit() {
        // 确认弹窗回调       
        // this.$emit('getclassRoomLists', 1)
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.show = false
      },
      submitLoad() {
        // 确认弹窗回调    
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.showLoad = false
      },      
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:flex;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, vw(40));
  }
  .selectTwo{
    @include wh(vw(220), vw(40));
    margin-right:vw(20)
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        height: 100%;
        /deep/ .el-input {
          height: 100%;
          .el-input__inner{
            height: 100%;
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding:  0 0.5vw;
    .icon-width{
        margin-right: vw(10)
    }
  }
  .selectone1 /deep/ .el-input__inner{
    font-size:vw(16);
    padding:0 vw(10)
  }
  .longSelect{
    width:vw(220);
  }
  .repair_button{
    margin-right:vw(80)
  }
  .dateSpan{
    width:vw(160);
    height:vw(40)
  }
  .dateSpan /deep/ .el-date-editor.el-input{
    width:100%;
  }
  .dateSpan /deep/ .el-icon-caret-bottom:before{
    display:none
  }
  .dateSpan /deep/ input{
    text-align:center;
    height:vw(40)
  }
  .dateBtn{
    width:vw(40);
    height:vw(40);
    border-radius: 6px;
    border: solid 1px #e7eaec;
    padding:0;
    display:inline-block
  }
  .nextBtn{
    margin-left:vw(10);
    margin-right:vw(80)
  } 
  .prveBtn{
    margin-right:vw(10)
  }   
}
@media screen and (max-width: 1440px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      
    }
    .selectone1{
    	//width:40%;
    }
    .el-col-12:last-child{
      margin-top: 0px;
    }
  }
  .selectTwo /deep/ .el-input__inner{
    font-size:vw1(16);
    padding:0 vw1(10)
  }
  ._tabTop{
    // margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
    .label_elect{
      font-size:16px
    }
    .selectTwo{
      @include wh(vw1(170), vw1(40));
      width:vw1(180);
      margin-right:vw1(20)
    }
    .selectTwo.longSelect{
      width:vw1(220)!important;
    }        
  }
  .selectone{
    @include wh(20.52%, vw1(40));
  }
  .selectTwo{
    @include wh(vw1(220), vw1(40));
  }
  .repair_button{
    margin-right:vw1(80)
  }
  .dateSpan /deep/ input{
    text-align:center;
    height:vw1(40)
  }  
  .dateSpan{
    width:vw1(160);
    height:vw1(40)
  }
  .dateBtn{
    width:vw1(40);
    height:vw1(40);
    border-radius: 6px;
    border: solid 1px #e7eaec;
    padding:0;
    display:inline-block
  }
  .nextBtn{
    margin-left:vw1(10);
    margin-right:vw1(80)
  } 
  .prveBtn{
    margin-right:vw1(10)
  }         
}
</style>
