<template>
    <div class='_resetRole'>
        <el-dialog
            title="修改权限"
            v-dialogDrag
            :visible.sync="userThis.resetRoleShow">
            <span class='permiss_section'>
                <el-tree class='one'
                    :data="data"
                    :default-checked-keys= "defaultKet"
                    ref="rootTree"
                    :show-checkbox="true"
                    default-expand-all
                    node-key="id"
                    :highlight-current="true"
                    :props="defaultProps">
                </el-tree>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false" size='small'>确 定</el-button>
                <el-button @click="userThis.resetRoleShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {userRole} from '../../../../../getserver/server'
export default {
  name: 'resetRole',
  data () {
    return {
      dialogVisible: true,
      data: [],
      defaultKet: [],
      defaultProps: {
        children: 'children',
        label: 'label'
      },
    }
  },
  inject: ['userThis'],
  created() {
    this.getuserRole()
  },
  methods: {
    async getuserRole() {
        let resDate = await userRole()
        if (resDate.data.status) {
            let infoDate = [];
            resDate.data.data.alist.forEach((data)=>{
                let JsonData = {};
                let ChildernArr = [];
                JsonData['id'] = data.access_id;
                JsonData['label'] = data.access_name;
                if (data.pidlist.length!=0){
                    data.pidlist.forEach((dataitem)=>{
                        let JsonData_two = {};
                        JsonData_two['id'] = dataitem.access_id
                        JsonData_two['label'] = dataitem.access_name
                        ChildernArr.push(JsonData_two);
                        JsonData['children'] = ChildernArr
                    })
                }
                infoDate.push(JsonData)
            })
           this.data = infoDate
        }
    },
    getCheckedKeys(data,keys,key){
        var res = [];
        recursion(data, false);
        return res;

        // arr -> 树形总数据
        // keys -> getCheckedKeys获取到的选中key值
        // isChild -> 用来判断是否是子节点
        function recursion (arr, isChild) {
            var aCheck = [];
            for ( var i = 0; i < arr.length; i++ ) {
                var obj = arr[i];
                aCheck[i] = false;

                if ( obj.children ) {
                    aCheck[i] = recursion(obj.children, true) ? true : aCheck[i];
                    if ( aCheck[i] ) {
                        res.push(obj[key]);
                    }
                }

                for ( var j = 0; j < keys.length; j++ ) {
                    if ( obj[key] == keys[j] ) {
                        aCheck[i] = true;
                        if ( res.indexOf(obj[key]) == -1 ) {
                            res.push(obj[key]);
                        }
                        break;
                    }
                }
            }
            if ( isChild ) {
                return aCheck.indexOf(true) != -1;
            }
        }
    }
  },
  watch: {
    'userThis.resetRoleShow'(val){
        if (val) {
            if (this.userThis.resetRoleArr!="" && this.userThis.resetRoleArr!=null) {
                this.defaultKet = this.userThis.resetRoleArr.split(',');
            }
        }
    }
  }
}
</script>
