<template>
    <div class='modifylease'>
        <el-dialog
            title="租赁商品"
            :visible.sync="leaseThis.modiftleaseShow"
            v-dialogDrag>
            <span class='formSection'>
                <el-form ref="form" :model="form" label-width="17%">
                    <el-form-item label="商品名称">
                        :<el-input v-model="form.name" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="租凭押金">
                        :<el-input v-model="form.deposit"></el-input>
                    </el-form-item>
                    <el-form-item label="*计费方式">
                        : <section class='el-checkbox'>
                            <label v-for='item in listData' :key='item.id'>
                                <input type="checkbox" :checked='form.statusCheck == item.id?true:false' disabled>
                                <span>{{item.name}}</span>
                            </label>
                        </section>
                    </el-form-item>
                    <el-form-item label="*计费金额">
                        :<el-input v-model="form.money" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*租凭数量">
                        :<el-input v-model="form.ShowNumber" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="预售租金">
                        :<el-input v-model="form.rent"></el-input>
                    </el-form-item>
                    <el-form-item label="*租凭对象">
                        :<el-input v-model="form.object" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*开始时间">
                        :<el-input v-model="form.StarTime" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*结束时间">
                        :<el-date-picker
                            v-model="form.endtime"
                            type="date"
                            :editable='false'
                            placeholder="选择日期"
                            :picker-options="pickerOptions">
                            </el-date-picker>
                    </el-form-item>
                    <el-form-item label="备注" class='remarks'>
                        :<el-input type='textarea' v-model="form.remarks"></el-input>
                    </el-form-item>
                </el-form>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitClick" size='small'>确 定</el-button>
                <el-button @click="leaseThis.modiftleaseShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getmodifyDate} from '../../../../../getserver/server'
export default {
  name: 'modify_lease',
  data () {
    const that = this
    return {
      pickerOptions: {
        disabledDate(time) {
            let times = new Date(that.form.StarTime).getTime();
            let n = 24 * 60 * 60 * 1000
            let saveDate = times - n
            return time.getTime() <= saveDate
        }
      },
      listData:[
        {id: 1, name: '每日', f: false},
        {id: 2, name: '每月', f: false},
        {id: 3, name: '每年', f: false}
      ],
      form: {
        name: '',// 商品名称
        deposit: '',// 押金
        statusCheck: '',// 计费方式
        money: '', // 计费金额
        ShowNumber: '', // 计费数量
        rent: '',// 预付租金
        object: '',// 租凭对象
        StarTime: '',// 开始时间
        endtime: '',// 结束时间
        remarks: ''// 备注
      }
    }
  },
  inject: ['leaseThis'],
  methods: {
    submitClick(){
      // 确定保存
      if (this.form.endtime=="") {
          this.$message({
          message: '结束时间不能为空值！',
          type: 'success',
          duration: 2000,
        });
        return
      }
      this.getmodifyDate()
    },
    async getmodifyDate () {
        let formJson = {}
        let date = (new Date(this.form.endtime).getTime()) / 1000
        formJson['rent_id'] = this.leaseThis.modifyDate.rent_id
        formJson['rent_margin'] = this.form.deposit
        formJson['prepaid_rent'] = this.form.rent
        formJson['end_time'] = date
        formJson['remark'] = this.form.remarks
        let resDate = await getmodifyDate(formJson);
        
        if (resDate.data.status) {
            // 修改成功
            this.$message({
                message: '修改成功',
                type: 'success',
                duration: 2000,
            });
            this.leaseThis.get_getrecords(this.leaseThis.page_num ,this.leaseThis.pageSize)
        }
    }
  },
  watch: {
    'leaseThis.modifyDate'(val){
        if (val!=null) {
            // 数据产生变化 - 做数据初始化
            this.form.name = val.goods_name
            this.form.deposit = val.rent_margin
            switch (val.rent_type) {
                case "日":
                    this.form.statusCheck = 1
                  break;
                case "月":
                    this.form.statusCheck = 2
                  break;
                case "年":
                    this.form.statusCheck = 3
                  break;
            }
            this.form.money = val.rent_type_money
            this.form.ShowNumber = val.rent_num
            this.form.rent = val.prepaid_rent
            this.form.object = val.rent_obj_name
            this.form.StarTime = val.start_time
            this.form.endtime = val.end_time
            this.form.remarks = val.remarks
        }
    },
    'leaseThis.modiftleaseShow' (val) {
        if (val==false) {
          this.leaseThis.modifyDate = null
        }
    },
    'form.StarTime' (val) {
        if (typeof(val) != "string" && val!='') {
           let time = this.leaseThis.Timeinfo(val)
           this.form.StarTime = time
        }
    },
    'form.endtime' (val) {
        if (typeof(val) != "string" && val!='') {
           let time = this.leaseThis.Timeinfo(val)
           this.form.endtime = time
        }
    },
    'form.deposit' (val) {
        if (!/\d+$/g.test(val)){
            // 不是数字 剔除掉非数字 并保留一个小数点
            this.form.deposit = val.replace(/[^0-9X.,]*/g,"");
            this.form.deposit = this.form.deposit.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            this.form.deposit = this.form.deposit.replace(".","$#$").replace(/\./g,"").replace("$#$","."); //只保留第一个. 清除多余的  
        } else {
            // 是数字的话 判断只能有一个小数点
            this.form.deposit = val.replace(/[^0-9X.,]*/g,"");
            this.form.deposit = this.form.deposit.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
        }
    },
    'form.rent' (val) {
        if (!/\d+$/g.test(val)){
            // 不是数字 剔除掉非数字 并保留一个小数点
            this.form.rent = val.replace(/[^0-9X.,]*/g,"");
            this.form.rent= this.form.rent.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            this.form.rent = this.form.rent.replace(".","$#$").replace(/\./g,"").replace("$#$","."); ; //只保留第一个. 清除多余的  
        } else {
            // 是数字的话 判断只能有一个小数点
            this.form.rent = val.replace(/[^0-9X.,]*/g,"");
            this.form.rent = this.form.rent.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
        }
    }
  }
}
</script>
