<template>
  <div>
    <section :class="[{'insShow': insdThis.tabDate.length==0},'instdynamic-conenter']">
        <ul class='instdynamic-conenter-ul' v-if='insdThis.tabDate.length!=0'>
          <li class='instdynamic-conenter-li' v-for='item in insdThis.tabDate' :key='item.ds_id'>
              <div class='conenter-list-left'>
              <p>
                  <strong>{{item.title}}</strong>
                  <em>{{item.create_time | times}}</em>
              </p>
              <p v-html="item.content"> 
                  {{item.content}}
              </p>
              </div>
              <div class='conenter-list-right'>
              <el-button type="text" size="small" @click.stop='correctClick(item)' class='erp-button-icon'>
                  <i class='repair_icon icon-width '></i>
                  修改
              </el-button>
              <el-button type="text" size="small" @click.stop='showDelete(item.ds_id)'  class='erp-button-icon'>
                  <i class='delete_icon icon-width '></i>
                  删除
              </el-button>
              </div>
          </li>
        </ul>
        <span v-else>暂无数据</span>
    </section>
    <erpPaging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" v-if='countNum > 1'/>
    <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
  </div>
</template>
<script>
import {getdynamicList,deleteDynamic} from '../../../../../getserver/server'
import erpPaging from '.././../../../paging/pagingShort'

export default {
  name: 'dynamicnews',
  data () {
    return {
      countNum: 0,
      page_num: 0,
      pageSize: 4,
      showDe:false,
      deleteId: null
    }
  },
  created () {
    this.getdynamicList_fun(1)
  },
  inject:['insdThis'],
  methods: {
    async getdynamicList_fun (limit) {
        let resDate = await getdynamicList(limit, this.pageSize);
        if (resDate.data.status) {
            // 列表获取成功
            this.insdThis.tabDate = resDate.data.data.data
            this.countNum = resDate.data.data.total
        }
    },
    async deletedynamic(id){
      let resDate = await deleteDynamic(id)
      if (resDate.data.status) {
          // 删除成功
          if (this.insdThis.tabDate.length == 1) {
              // 最后一个数据被删除
              if (this.page_num==0) {
                  // 最后一页
                this.getdynamicList_fun(1)
              } else {
                this.page_num--
                this.getdynamicList_fun(this.page_num)
              }
          } else {
            this.getdynamicList_fun(this.page_num)
        }
      }
    },
    correctClick(item){
     // 修改
     this.insdThis.switch_senior = 1
     this.insdThis.correctData = item
    },
    paging_father(i){
      // 分页
      this.getdynamicList_fun(i)
      this.page_num = i;
    },
    showDelete(id){
        // 删除警告
        this.deleteId = id;
        this.showDe = true
    },
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        this.showDe = false;
        this.delectclassRooms(id)
    },
    delectclassRooms(id){
        // 删除-确定
        this.deletedynamic(id)
    },
  },
  filters: {
    times(val){
      let t = val * 1000
      let newDate = new Date(t)
      let yy = newDate.getFullYear();// 年
      let mm = p(newDate.getMonth()+1);// 月
      let dd = p(newDate.getDate());// 日
      let hh = p(newDate.getHours());// 时
      let ff = p(newDate.getMinutes());// 分
      let ss = p(newDate.getSeconds());// 秒
      function p(s) {
        // 时间补零函数
        return s < 10 ? '0' + s: s;
      }
      return yy +'-'+ mm + '-' +dd+ ' ' +hh+ ':' +ff+ ':' +ss
    }
  },
  components: {
    erpPaging: erpPaging
  },
  watch: {
    'insdThis.switch_senior' (val) {
        if (val==0) {
            this.getdynamicList_fun(1)
        }
    }
  }
}
</script>
<style lang="scss">
@import '../../../../../style/mixin';
.conenter-list-left{
    p ul li{
       list-style-type: disc!important;
       margin-left: vw(20);
    }
    p ol li{
      list-style: decimal!important;
      margin-left: vw(20);
    }
}
</style>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: 6px;
    font-size: 16px;
    color: #666666;
}
.instdynamic-conenter{
    @include wh(100%, vw(540));
    box-shadow: 0px 2px 8px 0px 
rgba(12, 51, 110, 0.12);
    border-radius: 8px;
    overflow: auto;
    margin-bottom: vw(30);
    .instdynamic-conenter-ul{
        display: flex;
        flex-direction: column;
    .instdynamic-conenter-li{
        flex: 1;
        height: auto;
        padding: vw(30);
        box-sizing: border-box;
        display: flex;
        align-content: space-between;
        border:1px solid #e7eaec;
        .conenter-list-left{
          flex: 1;
          height: 100%;
          overflow: hidden;
            p{
              font-size: vw(16);
              color: #666666;
            }
            p:first-child{
              display: flex;
              color: #333333;
              justify-content: space-between;
              margin-bottom: vw(20);
            }
            p:last-child{
              height: auto;
              overflow: auto;
              // min-height: 33.33%;
            }
        }
        .conenter-list-right{
            display: flex;
            align-items: flex-end;
            .el-button{
                padding: 0;
            }
            .el-button:first-child{
                margin-right: vw(44);
                margin-left: vw(155);
            }
        }
      }
    }
}
.insShow {
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>