<template>
    <div class='_tabTop'>
        <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索" v-model="outThis.form.goods_name">
                    <i class='search_icon icon-width' @click='searchKec'></i>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <Lbutton class='hot_btn' @click.native="exportClick">
              <i class='icon-width export_icon'></i>
              导出Excel
            </Lbutton>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import Imageport from '../../../../../config/ImagePz'
import {mapState} from 'vuex'
export default {
  name: 'seaticTop',
  inject:['outThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    searchKec () {
      // 搜索
      this.outThis.get_getcheckoutList(1)
    },
    exportClick () {
       window.location.href = Imageport + "/index/excel/dep_record_ept?goods_name="+ this.outThis.form.goods_name + "&orgid=" + this.userLoginInfo.orgid;
    }
  },
  components: {
    Lbutton: Lbutton
  }
}
</script>
