@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .tabConenter{
    line-height: vw(40);
    padding-left: vw(50);
    box-sizing: border-box;
    font-size: vw(16);
    color: #333333;
    text-align: left;
    span{
        margin-right: vw(40);
    }
  }
