<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索用户" v-model="userThis.searchValue">
                    <i class='search_icon icon-width' @click.stop='search'></i>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                <Lbutton class='hot_btn' @click.native='toviewClick'>
                   <i class='icon-width To_view_icon'></i>
                    查看角色
                </Lbutton>
                <Lbutton class='hot_btn' @click.native='addUserRole'>
                    <i class='icon-width jia_icon'></i>
                    新增角色
                </Lbutton>
                <Lbutton class='hot_btn ' @click.native='addUser'>
                    <i class='icon-width jia_icon'></i>
                    <!-- <input type="file" ref='files'> -->
                    新增账号
                </Lbutton>
                <Lbutton class='hot_btn'  @click.native='refresh'>
                    <i class='icon-width refresh_icon'></i>
                    刷新数据
                </Lbutton>
            </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
export default {
  name: 'userTop',
  data () {
    return {
      searchValue: ''
    }
  },
  inject: ['userThis'],
  methods:{
    toviewClick(){
      this.userThis.toviewShow = !this.userThis.toviewShow
    },
    addUser () {
      this.userThis.addUserShow = !this.userThis.addUserShow
    },
    addUserRole () {
      this.userThis.addUserRoleShow = !this.userThis.addUserRoleShow
    },
    refresh () {
      const _this = this
      this.userThis.getuserList()
      setTimeout(()=>{
        _this.$message({
          message: '刷新成功',
          type: 'success',
          duration: 2000,
        });
      }, 200)
    },
    search () {
      this.userThis.getuserList(this.userThis.searchValue, 1, 1, 10)
    }
  },
  components: {
    Lbutton: Lbutton
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .icon-width{
    display: inline-table;
    @include wh(vw(24), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: vw(12);
    padding-right: vw(16);
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(80%, 100%);
        // flex: 1;
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    @include wh(vw(128), vw(40));
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
}
</style>
