<template>
    <div class='releasedynamic_conenter'>
        <div class='releasedynamic_conenterTitle'>
            发布最新动态
        </div>
        <div class='releasedynamic_conenterMain'>
            <section class='_conenterMainTitle'>
                <el-input placeholder="请输入最新动态标题" size='small' v-model='activeData.title'  maxlength='40'></el-input>
            </section>
            <section class='_conenterMainRichtext'>
                <el-upload
                    style='display:none;'
                    class="avatar-uploader"
                    ref='flieRef'
                    action="/index/Basess/get_img_update"
                    accept="image/jpeg,image/jpg,image/png"
                    :http-request="handleAvatarSuccess"
                    :show-file-list="false"
                    :before-upload="beforeAvatarUpload">
                </el-upload>
                <quill-editor 
                v-model="activeData.content"
                :options="editorOption"
                @change='onEditorChange'
                ref="myQuillEditor"></quill-editor>
            </section>
            <section class='ordio_option'>
                <span class='ordio_optionTitle'>指定发送给：</span>
                <label class='_optionbox' v-for='item in form.ordioData' :key='item.id' @change.stop='ordinClick(item.id)'>
                    <input type="checkbox" :checked='item.f'>
                    <span>{{item.name}}</span>
                </label>
            </section>
        </div>
        <div class='_conenterMainBtn'>
            <el-button size='small' @click.stop='submitClick' :loading='loadingStatus'>发布</el-button>
            <el-button size='small' @click.stop='insdThis.switch_senior=0'>取消</el-button>
        </div>
    </div>
</template>
<script>
import {Releasedynamic,correctDataDynamic,UpImage} from '../../../../../getserver/server'

import ImagePz from '../../../../../config/ImagePz'
const toolbarOptions = [['bold', 'italic', 'underline', 'strike'],    //加粗，斜体，下划线，删除线
    [{ 'list': 'ordered'}, { 'list': 'bullet' }], //列表
    [{ 'align': [] }],    //对齐方式
    [{ 'indent': '-1'}, { 'indent': '+1' }], // 缩进
    ['image']]
export default {
  name: 'releasedynamic',
  data () {
    return {
      loadingStatus: false,
      form:{
        ordioData: [{id:1,name: '家长',f:false},{id:2,name: '老师',f:false}],
        text: '',
        html: '',
        textStatus: false
      },
      activeData: {
        title: '',
        content: '',
        send_obj: '',
      },
      editorOption: {
        modules: {
          toolbar: {
            container: toolbarOptions,
            handlers: {
              'image': function (value) {
                if (value) {
                  document.querySelector('.el-upload input').click()
                } else {
                  this.quill.format('image', false);
                }
              }
            }
          }
        }
      }
    }
  },
  inject: ['insdThis'],
  methods: {
    beforeAvatarUpload (file) {
      let regulaImg = /(png|jpg|jpeg)/;
      if (!regulaImg.test(file.name)) {
        this.$message.error('文件格式不正确,此功能只能上传png,jpg,jpeg文件,请重新上传!');
        this.$refs.flieRef.value = '';
        return false
      }
    },
    handleAvatarSuccess (f) {
      // element-ui 上传组件 上传成功触发
      let from = new FormData();
      from.append('img', f.file);
      this.getUpImage(from)
    },
    ordinClick(id){
      const _this = this
      let str = [];
      this.form.ordioData.forEach((data,index)=>{
        if (data.id == id) {
          if (data.f==false) {
            data.f = true
          } else {
            data.f = false
          }
        }
        if (data.f==true) {
          str.push(data.id)
        }
      })
      this.activeData.send_obj = str.join('/')
    },
    submitClick () {
      // 发布
      if (this.activeData.title.trim().length==0) {
        this.$message({
          message: '动态标题不能为空!',
          type: 'error',
          duration: 2000,
        });
        return
      }else if (this.activeData.content.trim().length==0) {
        this.$message({
          message: '内容不能为空!请编辑后进行确定发布操作',
          type: 'error',
          duration: 2000,
        });
        return
      } else if (this.form.text.trim().length == 0 && this.form.html.indexOf('img') == -1) {
        this.$message({
          message: '输入内容不能为空!请重新输入后进行发布!',
          type: 'error',
          duration: 3000,
        });
        this.activeData.content = ""
        this.form.html = ""
        this.form.text = ""
        return
      }else if (this.form.text.trim().length > 2000) {
        this.$message({
          message: '输入内容超出2000个字，请进行整理！',
          type: 'error',
          duration: 3000,
        });
        return
      }
      this.getReleasedynamic()
    },
    onEditorChange(event){
      // event.quill.deleteText(2000, 4)
      this.form.text = event.text
      this.form.html = event.html
    },
    async getUpImage (from) {
      // 上传图片
      let resDate = await UpImage(from);
      if (resDate.data.status) {
        // 获取富文本组件实例
        let quill = this.$refs.myQuillEditor.quill
        // 获取光标所在位置
        let length = quill.getSelection().index;
        // 插入图片  res.info为服务器返回的图片地址
        quill.insertEmbed(length, 'image', ImagePz + resDate.data.sinfo)
        // 调整光标到最后
        quill.setSelection(length + 1)
      }
    },
    async getReleasedynamic (){
        // 发布
        this.loadingStatus = true
        if (this.insdThis.correctData!=null) {
          // 修改操作
          let resDate = await correctDataDynamic(this.insdThis.correctData.ds_id, this.activeData)
          if (resDate.data.status) {
            // 编辑成功
             this.loadingStatus = false
             this.$message({
                message: '编辑成功!',
                type: 'success',
                duration: 2000,
             });
          } else {
            this.loadingStatus = false
          }
        } else {
          // 新增操作
          let resDate = await Releasedynamic(this.activeData)
          if (resDate.data.status) {
              // 发布成功
              this.loadingStatus = false
              this.$message({
                  message: '发布成功!',
                  type: 'success',
                  duration: 2000,
              });
              this.form.textStatus = false
              this.form.text = ""
              this.form.ordioData.forEach((data,i)=>{
                data.f = false
              })
              this.activeData.title = ''
              this.activeData.content = ''
              this.activeData.send_obj = ''
          } else {
              // 失败
              this.loadingStatus = false
          }
        }
        this.insdThis.switchnum = 1
        this.insdThis.banner.forEach((data,i)=>{
            if(i==1) {
                data.f = false
            } else {
                data.f = true
            }
        })
        this.insdThis.switch_senior = 0
    }
  },
  components: {
  },
  watch: {
    'insdThis.switch_senior'(val){
        if (val == 0) {
          if (this.insdThis.correctData!=null) {
            this.insdThis.correctData = null
          }
          this.form.ordioData.forEach((data,i)=>{
              data.f = false
          })
          this.activeData.title = ''
          this.activeData.content = ''
          this.activeData.send_obj = ''
        }
    },
    'insdThis.correctData' (val) {
      if (val!=null) {
        const _this = this
        this.activeData.title = val.title
        this.activeData.content = val.content
        this.activeData.send_obj = val.send_obj
        let Arr = val.send_obj.split('/')
        Arr.forEach((item)=> {
          _this.form.ordioData.forEach((data)=>{
            if (item == data.id) {
              data.f = true
            }
          })
        })
      }
    },
    'form.text'(val) {
      if (val!=""){
          if (val.trim().length >= 2000 && this.form.textStatus == false){
              this.$message({
                  message: '当前输入达到2000个字，请进行删减，否则无法发布!',
                  type: 'error',
                  duration: 3000,
              });
              this.form.textStatus = true
          }
      }
    } 
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.releasedynamic_conenter {
    display: flex;
    flex-direction: column;
    .releasedynamic_conenterTitle{
        padding: vw(30) 0;
        box-sizing: border-box;
        color: #2769fb;
        font-size: vw(16);
    }
    .releasedynamic_conenterMain{
        display: flex;
        flex-direction: column;
        @include wh(100%,vw(660));
        box-shadow: 0px 2px 8px 0px 
		rgba(12, 51, 110, 0.12);
	    border-radius: 8px;
        padding: vw(30) vw(40);
        box-sizing: border-box;
        flex: 1;
        margin-bottom: vw(30);
        ._conenterMainRichtext{
            flex: 1;
            margin: vw(30) 0;
            overflow: auto;
            /deep/ .quill-editor{
                height: 100%;
                display: flex;
                flex-direction: column;
                .ql-container{
                    flex: 1;
                }
            }
        }
        .ordio_option{
            display: flex;
            font-size: vw(16);
            color: #333333;
            align-items: center;
            .ordio_optionTitle{
              margin-right: vw(20);
            }
            ._optionbox{
                display: flex;
                align-items: center;
                margin-right: vw(40);
                cursor: pointer;
                input[type=checkbox] {
                    cursor: pointer;
                    margin-right: vw(15);
                    @include wh(14px,14px);
                    background: #fff;
                    -webkit-appearance:none;
                    border: 1px solid #c9c9c9;
                    border-radius: 2px;
                    outline: none;
                }
                input[type=checkbox]:checked{
                    background-size: 100%;
                    background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                }
            }
        }
    }
    ._conenterMainBtn{
        .el-button{
            @include wh(vw(120),auto);
            background-color: #e7eaec;
            color: #999999;
            border-color: #e7eaec;
            border-radius: vw(6);
        }
        .el-button:first-child{
            background-color: #2769fb;
            color: #fff;
            margin-right: vw(30);
        }
    }
}  
</style>
