import axios from 'axios';
import store from '../state/state'
import Qs from 'qs'
// 添加响应拦截器
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // return config;
　　//这里经常搭配token使用，将token值配置到tokenkey中，将tokenkey放在请求头中
console.log(store.state.userLoginInfo)
    if (config.url && store.state.userLoginInfo != null){
      config.headers['X-uid'] = store.state.userLoginInfo.id || store.state.userLoginInfo.uid
      config.headers['X-Token'] = store.state.userLoginInfo.token
      config.headers['orgid'] = store.state.userLoginInfo.orgid
    }
    return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});
// 添加响应拦截器
axios.interceptors.response.use(function (response) {
　　// 对响应数据做点什么
	// console.log(response.data.status)
  if(!response.data.status){
    if(response.data.error_code == '10005'){
      store.state.__this.$notify.error({
        title: "登录信息过期，请重新登录",
        message: '这是一条错误的提示消息'
      });
    	setTimeout(function(){store.state.__this.$router.replace({path: '/'})},2000)
    } else if(response.data.error_code == '20007') {
      // 用户不存在
      store.state.__this.$notify.error({
        title: response.data.error_msg,
        message: '这是一条错误的提示消息'
      });
      return response
    }
    else if(response.data.error_code == '60000') {
      // 用户账号密码错误
      store.state.__this.$notify.error({
        title: response.data.error_msg,
        message: '这是一条错误的提示消息'
      });
      return response
    }
    else{
      store.state.__this.$message.error(response.data.error_msg);
    }
  }
  else{
    return response
  }
}, function (error) {
　　// 对响应错误做点什么
  if (error && error.response) {
  	console.log(error.response.status)
        switch (error.response.status) {
            case 400: store.state.__this.$message.error('请求错误(400)') ; break;
            case 401: store.state.__this.$message.error('未授权，请重新登录(401)'); break;
            case 403: store.state.__this.$message.error('拒绝访问(403)'); break;
            case 404: store.state.__this.$message.error('请求出错(404)'); break;
            case 408: store.state.__this.$message.error('请求超时(408)'); break;
            case 500: store.state.__this.$message.error('服务器错误(500)'); break;
            case 501: store.state.__this.$message.error('服务未实现(501)'); break;
            case 502: store.state.__this.$message.error('网络错误(502)'); break;
            case 503: store.state.__this.$message.error('服务不可用(503)'); break;
            case 504: store.state.__this.$message.error('网络超时(504)'); break;
            case 505: store.state.__this.$message.error('HTTP版本不受支持(505)'); break;
            default: store.state.__this.$message.error('连接出错(${err.response.status})!');
        }
    }else{
        store.state.__this.$message.error('连接服务器失败!')
    }
    //store.state.__this.$message.error(error.message);
　　return Promise.reject(error)
});
