<template>
    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1">
            <div class="modal-header" @mousedown="setStartingPoint">
                {{ title }}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form ref="form" :model="form" :rules="rules" label-width="18%" class="fromBox">
                <el-form-item prop="cate_name" label="所属分类：" >
                  <el-select v-model="form.cate_name" placeholder="请选择分类状态" popper-class="treeSelect" @focus="showSelect()" ref="selectRef">
                    <el-option :value="valueModel" :label="labelModel">
                      <el-tree :data="cateList" node-key="id" :check-strictly="true" check-on-click-node auto-expand-parent default-expand-all :default-checked-keys= "[cheackId]" :props="defaultProps" @node-click="handleNodeClick" @check-change="handleClick" ref="treeForm"></el-tree>                          
                    </el-option>          
                  </el-select>
                </el-form-item>                
                <el-form-item prop="name" label="分类名称：" >
                  <el-input v-model.trim="form.name" placeholder="请输入分类名称" maxlength="20" ref="nameInput"></el-input>
                </el-form-item>                                             
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import {cate_list,cate_add} from '../../../../../getserver/getAxios.js'
  import {checkText} from '../../../../../script/validate.js' 
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        },
        that: {
          type: Object,
          default: false
        },                
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            cheackId:null,
            isCanMove: false,
            cateList:[],
            valueModel:'',
            labelModel:'',                        
            form: {
              name: '',
              id:null,
              cate_name:''
            },
            rules: {
              name: [
              { required: true, message: '请输入分类名称', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              ],              
              cate_name: [
              { required: true, message: '请选择分类状态', trigger: 'change' }
              ],              
            },
            defaultProps: {
              children: 'sub',
              label: 'cate_name',
              id: 'id'
            }                      
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    created(){
      this.cate_list()
    },
    methods: {
      showSelect(){
        let _this = this
        if(this.cheackId){
          let node1 = document.querySelector('.treeSelect .is-current');
          setTimeout(function(){
            _this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop = node1.children[0].offsetTop-50           
          },200)
        }
      },
      handleNodeClick(data) {
        this.form.pid = data.cate_id
        this.cheackId = data.cate_id
        this.form.cate_name = data.cate_name
        this.$refs.treeForm.setCheckedNodes([data]);
        this.$refs.selectRef.blur()
      }, 
      handleClick(data, checked, node){
       if(checked == true){
         this.cheackId = data.id;
         this.$refs.treeForm.setCheckedNodes([data]);
         this.$refs.selectRef.blur()
       }
      },   
        hideModal() {         
          this.$emit('hideModal')
        },
        submit() {
          var a = this.form.name;
          var c = this.status;
          if(!this.cheackId && this.cheackId != 0){
            this.$message.error('请选择商品分类');
            this.$refs.selectRef.$el.children[0].children[0].click();   
            return;
          };         
          if(a == ''){
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return
          }
          else if(a.length < 2){
            this.$message.error('分类名称长度不正确');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          };           
          this.cate_add ()
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },
        currentSel() {
          let selVal = this.form.id
          // let obj = {}; 
          // obj = this.cateList.find((item)=>{//这里的this.cateList就是上面遍历的数据源 
          //   return item.cate_id === selVal;//筛选出匹配数据 
          // }); 
          // this.form.pid = obj.cate_pid;
        },                
       async cate_add () {
        let resDate = await cate_add(this.orgid,this.form.name,this.cheackId)
        if (resDate.data.status) {
          this.$message({
            type: 'success',
            message: resDate.data.sinfo
          });
          this.$emit('submit')
        }
        console.log(resDate.data)
      },
      async cate_list () {
        let resDate = await cate_list(this.orgid)
        if (resDate.data.status) {
          this.cateList = resDate.data.data
          // console.log(typeof(this.cateList))
          // console.log(this.cateList)
        }
      },                            
    }
  }
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@media screen and(max-width: 1380px){
  .modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10;
  }
  .modal-container {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate3d(-50%,-50%,0);
    width:vw1(640);
    height:vw1(420);
  }
  .modal-header {
    height: vw1(60);
    @include bg-defaul($bg-color);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: left;
    cursor: move;
    box-sizing:border-box;
    padding:0 vw1(30);
    font-size:vw1(16);
  }
  .el-icon-close{
    position: absolute;
    right: vw1(30);
    cursor: pointer;
    top:vw1(24)
  }
  .modal-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: vw1(80);
    border-top: 1px solid #ddd;
  }
  .modal-footer button {
    margin-right:vw1(30);
  }
  .modal-main {
    padding: vw1(15) vw1(40);
    height:vw1(280);
    box-sizing:border-box;
  }
  .el-button.is-round{
    border-radius:6px;
    //padding: vw1(12) vw1(23);
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
    line-height:vw1(40)
  }
  .modal-container1 /deep/ .el-button--primary{
    color:#fff!important;
  }
  .el-button{
    font-size:vw1(16);
  }
  .fromBox /deep/ .el-form-item__label:before{
    color: #333 !important;
    display: inline-block;
    float: left;
    margin-right: 0px !important;
  }
  .fromBox /deep/ .el-form-item__label{
    height:vw1(40);
    line-height: vw1(40);
    text-align: justify;
    font-size: vw1(16);
    padding:0;
    color: #333333;
    vertical-align: top;
  }
  .fromBox /deep/ .el-form-item__label:after {
    content: " ";
    display: inline-block;
    width: 100%;
  }
  .fromBox /deep/ .el-input__inner{
    height:vw1(40);
    line-height:vw1(40)
  }
  .fromBox /deep/ .el-form-item{
    margin-top: vw1(30);
    font-size:vw1(16);
    margin-bottom:0
  }
  .fromBox /deep/ .el-form-item__content{
    line-height:vw1(40)
  }
  .fromBox /deep/ .el-select{
    width:100%
  }
}
