<template>
    	<div class="modal-container modal-container2" v-show="show">
    		<div class="modal-header">
    			{{ title }}
    			<i class="el-icon-close" round @click="hideModal"></i>
    		</div>
    		<div class="modal-main">
    			<el-collapse v-model="activeNames" @change="handleChange" class="collapseBox">
              <el-collapse-item v-for="(item,index) in currmData" :title="item.sname" :name="item.sid" :key="item.sid">
                <p v-if="item.courses.length == 0">暂无内容</p>
                <el-button v-else v-for="(item1,index1) in item.courses" :key="item1.cur_id" @click="clickActive($event,index,index1)" :class="{'active':item1.active===true}">{{item1.cur_name}}</el-button>
              </el-collapse-item>  
            </el-collapse>
		    </div>
		    <div class="modal-footer alertFooter">
		    	<el-button type="primary" round @click="hideModal">完成</el-button>
		    </div>
		</div>
</template>
<script>
  import axios from 'axios';
  import {all_list_currm} from '../../../../../../getserver/getAxios.js'
  export default {
    name: 'rightModal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        },
        cur_list: {
          type: Array,
          default: ''
        }              
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            activeNames: [],
            currmData: [],
              
        }
    },
    created(){
       this.all_list_currm()
    },
    mounted() {
        
    },
    methods: {
        handleChange(val) {
          // console.log(val);
        },
        hideModal() {
            this.$emit('hideRightModal')
        },      
        submit() {
         this.$emit('changeTable',this.checkedDataNew)
        },
        removeArry(val) { 
          var index = this.cur_list.indexOf(val); 
          if (index > -1) { 
            this.cur_list.splice(index, 1); 
          } 
        },
        clickActive(event,index,index1) {
          this.currmData[index].courses[index1].active = !this.currmData[index].courses[index1].active;
          if(this.currmData[index].courses[index1].active == true){
            this.cur_list.push(this.currmData[index].courses[index1].cur_id)
          }
          else{
            this.removeArry(this.currmData[index].courses[index1].cur_id)
          }
          // this.$emit('changeCur',this.cur_list)
          console.log(this.cur_list)
          
        },
        async all_list_currm () {
          console.log(this.orgid)
          let _this = this;
          let resDate = await all_list_currm(this.orgid)
          // console.log(resDate.data.data)
          // console.log(this.cur_list)
          if (resDate.data.status) {
            resDate.data.data.forEach(function(element,index){
              for(let i=0;i<resDate.data.data[index].courses.length;i++){
                resDate.data.data[index].courses[i].active = false
                if(_this.cur_list.length != 0){
                  for(let j=0;j<_this.cur_list.length;j++){
                    if(resDate.data.data[index].courses[i].cur_id == _this.cur_list[j]){
                      resDate.data.data[index].courses[i].active = true
                      _this.activeNames.push(resDate.data.data[index].sid)
                    }
                  }                  
                }
              }
            })
            console.log(this.activeNames)
            this.currmData = resDate.data.data
          }
          // console.log(this.currmData)
        },         
                      
    }
  }
</script>
