<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="18">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_elect selectTwo day'>
                  <span>日期： </span>
                  <el-date-picker
                  v-model="startTime"
                  type="date"
                  format
                  prefix-icon="el-icon-caret-bottom"
                  placeholder="选择日期"
                  popper-class="selectBirthday"
                  @change="changest()">
                </el-date-picker>
                </label>
                <label class='label_elect selectTwo day'>
                  <span>日期： </span>
                  <el-date-picker
                    v-model="endTime"
                    type="date"
                    format
                    prefix-icon="el-icon-caret-bottom"
                    placeholder="选择日期"
                    :picker-options="pickerOptions"
                    popper-class="selectBirthday"
                    @change="changeed()">
                  </el-date-picker>
                </label>                               
                <label class='label_elect selectTwo'>
                    <span>选择： </span>
                    <el-select v-model="that.type" placeholder="全部" @change="selectFn1($event)">
                    	<el-option value="1" label="全部"></el-option>
                      <el-option value="2" label="本年"></el-option>
                      <el-option value="3" label="本月"></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectTwo'>
                  <span>课程： </span>
                  <el-select v-model="that.courseId" placeholder="全部" @change="selectFn2($event)">
                    <el-option value="" label="全部"></el-option>
                    <el-option-group
                    v-for="group in courseList"
                    :key="group.sid"
                    :label="group.sname">
                    <el-option v-for="(item,index) in group.courses" :value="item.cur_id" :label="item.cur_name" :key="item.cur_id"></el-option>
                    </el-option-group>
                    <!-- <el-option value="1" label="在职中"></el-option>
                    <el-option value="2" label="已离职"></el-option> -->
                  </el-select>
                </label>                
            </div>
        </el-col>
        <el-col :span="6">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">
            	<Lbutton class='hot_btn' @click.native="upload()">
            		<i class='icon-width export_icon'></i>
            		导出Excel
            	</Lbutton>              
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="show" :show="show" :title="title" :orgid="orgid" :manager="manager" @hideModal="hideModal" @submit="submit">	
      </dialogComponent>
      <modal-seniority v-if="showSeniority" :title="titleSeniority" :orgid="orgid" :manager="manager" @hideModal="hideModalSeniority" @submit="submitSeniority"> 
      </modal-seniority>
      <importTeacher v-if="showLoad" @hideModal="hideLoadModal" @submit="submitLoad">
      </importTeacher>           
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/addTeacher'
import modalSeniority from '../alert-box/seniority'
import importTeacher from '../alert-box/importTeacher'
import Imageport from '../../../../../config/ImagePz.js'
import {all_list_currm} from '../../../../../getserver/getAxios.js'
import {mapState} from 'vuex'
let _this
export default {
    name: 'tabTops',
	props: {
        orgid: {
          type: String,
          default: false
        },
        that: {
          type: Object,
          default: false          
        }                                 
    },
    beforeCreate: function () {
      _this = this
    },       
    data () {
    	return {
        courseList:[],//资历
        show: false,
        inputVal:'',
        showLoad: false,
        showSeniority:false,
        startTime:'',
        endTime:'',
        pickerOptions: {
          disabledDate(time) {
            let _now = Date.now(),
            t = 365 * 24 * 60 * 60 * 1000,
                    //t = new Date(1950, 1, 1).getTime(),
            sevenDays = _now - t;
            if(_this.that.startTime){
              return time.getTime() < _this.that.startTime.getTime();
            }
                //console.log(new Date('1970/01/01 00:00:01').getTime())
            //return time.getTime() > sevenDays;
                //return time.getTime() > _now || time.getTime() < sevenDays;
          },
        },        
        Imageport
      }
    },
    computed:{
    	...mapState(['userLoginInfo']),
    },
    created(){
      this.all_list_currm()
    },    
    methods: {
      changest(){
        this.$emit('changeStartTime',this.startTime)
      },
      changeed(){
        this.$emit('changeEndTime',this.endTime)
      },      
      upload(){
        window.location.href = this.Imageport+"index/excel/teacher_ept?orgid=" + this.userLoginInfo.orgid + "&uid=" + this.userLoginInfo.id + "&meal_name=" + this.inputVal + "&cur_state=" + this.status
      },                                           
      async all_list_currm () {
        let resDate = await all_list_currm(this.orgid)
        if (resDate.data.status) {
          this.courseList = resDate.data.data
        }
        console.log(resDate.data)
        console.log(this.seniority)
      },       
      submit() {
        // 确认弹窗回调
        // this.$emit('getclassRoomLists', 1)      
        this.show = false;
        this.$emit('teacherList',1)
      },
      submitSeniority() {     
        this.showSeniority = false;
      },      
      change() {
        console.log(this.inputVal)
      	let name = this.inputVal
        // console.log(this.$emit())
      	this.$emit('searchsetMeal',name,1)
      },
      selectFn1(e) {
        console.log(this.that)
        this.$emit('scheduleList',1)
      },
      selectFn2(e) {
        this.$emit('scheduleList',1)
      }         
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
      modalSeniority:modalSeniority,
      importTeacher:importTeacher
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
    cursor: pointer
  }
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:block;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: vw(6);
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, 40px);
  }
  .selectTwo{
    @include wh(198px, 40px);
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: vw(8);
        height: 100%;
        .el-icon-arrow-up{
          display: flex;
          align-items: center;
        }
        /deep/ .el-input{
          height: 100%;
           .el-input__inner {
             height: 100%;
           }
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding: 0 0.5vw;
    .icon-width{
        margin-right: vw(10);
    }
  }
  .selectTwo /deep/ .el-input__inner{
  	font-size:vw(16);
  	padding:0 vw(10)
  }
  .day{
    width:vw(220)!important;
    margin-left:0
  }
  .day /deep/ .el-date-editor.el-input{
    width:vw(140)
  }
  .day /deep/ .el-input__prefix{
    right:0;
    left:inherit;
  }
  .day /deep/ .el-input__prefix .el-input__icon{
    line-height:vw(46)
  }
  .day /deep/ .el-input__suffix{
    display:none
  }
}
@media screen and (max-width: 1380px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      width: 40%!important;
    }
    .selectone1{
    	width:40%;
    }
    .el-col-12:last-child{
      width: 60%!important;
      margin-top: 0px;
    }
  }
  .selectTwo /deep/ .el-input__inner{
  	font-size:vw1(16);
  	padding:0 vw1(10)
  }
  ._tabTop{
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
  }
  .day /deep/ .el-date-editor.el-input{
    width:vw1(140)
  }
  .day{
    width:vw1(220)!important
  }
  .day /deep/ .el-input__prefix .el-input__icon{
    line-height:vw1(46)
  }      
}
</style>
