<template>
    <!-- 修改金额 -->
    <div class='_salary'>
        <el-dialog
            title="修改课程薪酬"
            v-dialogDrag
            :visible.sync="salaryThis.salaryShow"
            width="">
            <section @scroll="scrollModalMain($event)">
                <el-form ref="form"  >
                    <label class='_curriculum_labeltwo'>
                      <span class='label_text'>*授课方式：</span>
                      <el-select  slot="prepend" v-model='form.tmethods' placeholder="请选择" class='_main-rightSelect' disabled>
                        <el-option label="一对一" :value="1"></el-option>
                        <el-option label="一对多" :value="2"></el-option>
                      </el-select>
                    </label>
                    <!-- <el-form-item label="*课程名称：">
                        <el-input placeholder='请输入课程名称' maxlength='12'></el-input>
                    </el-form-item> -->
                    <label class='_curriculum_labeltwo'>
                      <span class='label_text'>*计算方式：</span>
                      <el-select  slot="prepend" v-model='form.pay_id_info' placeholder="请选择" class='_main-rightSelect' >
                        <el-option :label="item.pay_name" :value="item.pay_id_info" v-for='item in alculate' :key='item.pay_id_info'></el-option>
                      </el-select>
                    </label>
                    <label class='calculation'>
                        <p>
                            1、按出勤人数: 按所上课程实际出勤学员数计算; 
                        </p>
                        <p>
                            2、按课时消费: 按所有上课学员实际消费金额总和的百分比计算; 
                        </p>
                        <p>
                            3、按授课课时: 根据老师上该课的课时数进行固定金额提成计算。
                        </p>
                    </label>
                    <label class='_curriculum_label _curriculum_time culum_time'>
                      <span class='label_text'>*结算金额：</span>
                      <el-input v-model='form.bay_paich'></el-input>
                      <el-button slot="append" v-if='alculate[form.pay_id_info-1]'>{{alculate[form.pay_id_info-1].cpany}}</el-button>
                    </label>
                    <label class='_curriculum_time textarea_tabel'>
                      <span class='textarea_span'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                      <el-input
                          type="textarea"
                          placeholder="请输入备注说明"
                          show-word-limit
                          maxlength='500'
                          v-model='form.remake'
                        >
                        </el-input>
                    </label>
                    <label class='_cuiculum_text'>
                      注意：批量设置会将老师单独设置的课程薪酬数据覆盖
                    </label>
                </el-form>
                <!-- <span>这是一段信息</span> -->
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size='mini' @click="confirmClick()" :loading="configShow">确 定</el-button>
                <el-button @click="salaryThis.salaryShow = false" size='mini'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {JsFsList, KcxcModify} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'mt_salary',
  data () {
    return {
      configShow: false, // 确认状态
      form: {
        tmethods: '',// 授课方式
        pay_id_info: '',// 计算方式
        bay_paich: '', // 结算金额
        remake: ''  // 备注
      },
      alculate: [], // 计算方式
    }
  },
  created () {
    this._Js_Fs();
  },
  props: {
    repairSalary: Object
  },
  inject: ['salaryThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    Shut_down(){
      this.salaryThis.salaryShow = false
    },
    confirmClick () {
      // 确 认 修 改
      const _this = this
      this.configShow = true
      setTimeout(()=>{
        _this.configShow = false
      },1500)
      this._kcModiyFun()
    },
    async _Js_Fs(){
      let resDate = await JsFsList()
      this.alculate = resDate.data.data
    },
    async _kcModiyFun () {
      const _this = this
      if (this.form.pay_id_info == "") {
         this.$message({
          message:"计算方式不能为空值",
          type: 'error',
          duration: 1500
        });
        return
      } else if(this.form.bay_paich == "") {
        this.$message({
          message:"结算金额不能为空",
          type: 'error',
          duration: 1500
        });
        return
      }
      let resDate = await KcxcModify(this.repairSalary.pay_id_list, this.repairSalary.cur_id, this.form.pay_id_info, this.form.remake, this.form.bay_paich, this.repairSalary.orgid, this.userLoginInfo.id)
      if (resDate.data.status) {
        // 修改成功
        this.$message({
          message: resDate.data.sinfo,
          type: 'success',
          duration: 1500
        });
        this.salaryThis.listGet(this.salaryThis.KmId, this.salaryThis.KmName,this.salaryThis.page_num,this.salaryThis.pageSize)
        // this.salaryThis.tableData.data.forEach((data)=>{
        //    if (data.pay_id_list == _this.repairSalary.pay_id_list && data.cur_id ==  _this.repairSalary.cur_id ) {
        //       data.pay_info.pay_id_info = _this.form.pay_id_info
        //       data.bay_paich = _this.form.bay_paich
        //       data.remake = _this.form.remake
        //    }
        // })
      }
    }
  },
  watch: {
    'form.bay_paich'(value){
      if (!/^\d+$/g.test(value)){
        this.form.bay_paich = value.substring(0, value.length-1)
      }
    },
    'salaryThis.salaryShow'(val){
      if (val==false){
        this.form = {
          tmethods: '', // 授课方式
          pay_id_info: '',// 计算方式
          bay_paich: '', // 结算金额
          remake: ''  // 备注
        }
      } else {
        this.form.tmethods = this.repairSalary.curriculums.tmethods
        this.form.pay_id_info = this.repairSalary.pay_info.pay_id_info
        this.form.bay_paich = this.repairSalary.bay_paich
        this.form.remake = this.repairSalary.remake
      }
    }
  }
}
</script>
