<template>
    <div class='schedule' @click.self='close' v-dialogDrag>
        <transition name='seche'>
            <div class='schedule_conenter el-dialog' v-show='that.scheduleShow'>
                <header class='schedule_header el-dialog__header'>
                    <section>
                        <span>课程：围棋</span>
                        <span>时长：45</span>
                        <span>课次：0</span>
                    </section>
                    <i class='whitedelete_icon' @click.stop='close'></i>
                </header>
                <main class='schedule_main'>
                    <section class='schedule_mainTop'>
                        <div class='Timeschedule'>
                            <span>日期：
                                <el-date-picker
                                v-model="time_one"
                                type="date"
                                placeholder="选择日期">
                                </el-date-picker>
                            </span>
                            <em>至</em>
                            <span>
                                <el-date-picker
                                v-model="time_two"
                                type="date"
                                placeholder="选择日期">
                                </el-date-picker>
                            </span>
                        </div>
                        <div>
                            <Lbutton class='erp-lbutton'> 
                                <i class='icon-width import_icon'></i>
                                导出Excel
                            </Lbutton>
                        </div>
                    </section>
                    <section class='schedule_mainBottom'>
                        <el-table
                        :data="tableData"
                        style="width: 100%"
                        resizable=false>
                            <el-table-column
                                align='center'
                                label="上课日期"
                                width="180">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.date}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                align='center'
                                label="上课时间"
                                width="180">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.name}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                align='center'
                                label="教师">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.address}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                align='center'
                                label="教师">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.address}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                align='center'
                                label="教室">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.address}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                align='center'
                                label="状态">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.address}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                align='center'
                                label="操作">
                                <template slot-scope="scope">
                                    <el-button
                                        @click.native.prevent="deleteRow(scope.$index, tableData)"
                                        type="text"
                                        size="small">
                                        删除
                                        </el-button>
                                </template>
                            </el-table-column>
                        </el-table>
                    </section>
                </main>
                <footer class='schedule_footer'>
                    <erp-paging></erp-paging>
                </footer>
            </div>
        </transition>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import erpPaging from '../../../../paging/paging'
export default {
    name: 'schedule',
    data () {
      return {
        time_one: '', // 时间1
        time_two: '', // 时间2
        tableData: [{
          date: '2016-05-03',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }, {
          date: '2016-05-02',
          name: '王小虎',
          address: '上海市普陀区金沙江路 1518 弄'
        }]
      }
    },
    inject: ['that'],
    methods: {
      close(){
        this.that.scheduleShow = !this.that.scheduleShow
      },
      layerOpen:function (a,b) {
        // console.log($(b.path[1]).width())
        // console.log(b.currentTarget.getBoundingClientRect())
        // console.log(b)
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }
      }
    },
    components: {
      Lbutton: Lbutton,
      erpPaging: erpPaging
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
.seche-enter,
.seche-leave-to{
    opacity: 0.5;
    transform: translateY(-50%);
    transition-delay: 1s;
    transition: all  0.3s;
    
}
.seche-enter-to,
.seche-leave{
    opacity: 1;
    transform: translateY(0%);
    transition-delay:1s;
    transition: all 0.3s;
}

.erp-lbutton{
    @include wh(128px, 40px);
    .icon-width{
        @include wh(20px, 20px);
        display: inline-block;
    }
}

.schedule{
   @include wh(100%, 100%);
   position: fixed;
   top: 0;
   left: 0;
   background: rgba($color: #000000, $alpha: 0.25);
   z-index: 2;
   display: flex;
   justify-content: center;
   align-items: center;
   .schedule_conenter{
      @include wh(70%, vw(660));
    //   min-height: 76%;
      background: #fff;
      border-radius: 8px;
      display: flex;
      flex-direction: column;

      .schedule_header{
        @include wh(100%, 8.3%);
        @include bg-defaul($bg-color);
        color: #fff;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        display: flex;
        align-items: center;
        padding:0 2.2%;
        box-sizing: border-box;
        font-size: 16px;
        section{
            flex: 1;
            span{
                margin-right: 4.4%;
            }
        }
        i{
            display: inline-block;
            @include wh(20px,20px);
            cursor: pointer;
        }
      }

      .schedule_main {
          flex: 1;
          padding: 2.2%;
          box-sizing: border-box;
          display: flex;
          flex-direction: column;

          .schedule_mainTop{
              display: flex;
              justify-content: space-between;
              margin-bottom: 3%;
              .Timeschedule{
                font-size: 16px;
                span,em{
                    margin-right: 8px;
                }
              }
              .Timeschedule /deep/ .el-input{
                @include wh(23%, 40px);
                .el-input__icon{
                  display: none;
                }
              }
          }
          .schedule_mainBottom{
            flex: 1;
            box-shadow: 0 0 1px 0 #999;
            overflow: auto;

          }
          .schedule_mainBottom /deep/ .el-table{
              margin-bottom: 0;
              .el-table__body-wrapper .el-table__body tbody .el-table__row td .cell .el-button{
                  @include fontColor-defaul($fn-color)
              }
              .el-table__body-wrapper .el-table__body tbody .el-table__row td .cell span{
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;
              }
          }
      }

      .schedule_footer{
          padding:  0 2.2%;
          box-sizing: border-box;
          @include wh(100%, 11.11%);
      }
   }
}
</style>
