<template>
    <!-- 课程薪酬 -->
    <section class='salary_'>
        <div class='_tabTop'>
            <el-row class='meadio'>
                <el-col :span="12">
                    <div class="grid-content bg-purple-dark _tabTopconent">
                        <label class='label_elect'>
                            <span>课程薪酬设置（课消百分比按原价计算）：</span>
                            <el-switch
                                v-model='switchState'
                                active-color="#2769fb"
                                inactive-color="#999999"
                                @change='changeSwitch'>
                            </el-switch>
                        </label>
                        <label class='label_elect selectone'>
                            <span>科目: </span>
                            <el-select  placeholder="请选择" v-model="KmId" @change='choose'>
                                <el-option label="全部" value='0'></el-option>
                                <el-option v-for='item in KmList' :key='item.sid' :label="item.sname" :value="item.sid"></el-option>
                            </el-select>
                        </label>
                        <label class='label_search label_elect'>
                            <input type="text" placeholder="搜索课程" v-model='KmName'>
                            <i class='search_icon icon-width' @click.stop='searchClick'></i>
                        </label>
                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                        <Lbutton class='hot_btn' @click.native='batchSalaryClick'>
                            <i class='icon-width batch_icon'></i>
                            批量设置
                        </Lbutton>
                        <Lbutton class='hot_btn' @click.native='exportClick'>
                            <i class='icon-width export_icon'></i>
                            导出Excel
                        </Lbutton>
                    </div>
                </el-col>
            </el-row>
        </div>
        <div class='div_gulptable'>
            <el-table
                :data="tableData.data"
                ref='table'
                border
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    label="序号"
                    align='center'
                    show-overflow-tooltip
                    min-width="8.8%">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)'>
                            {{scope.row.pay_id_list}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    label="课程名称"
                    align='center'
                    min-width="15.5%">
                     <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)'>
                            {{scope.row.cur_name}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="科目"
                    min-width="8.8%">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)'>
                            {{scope.row.subjectsall.sname}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="授课方式"
                     min-width="11.11%">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)' v-if='scope.row.curriculums.tmethods==1'>
                            一对一
                        </span>
                        <span @click='layerOpen(scope.row,$event)' v-if='scope.row.curriculums.tmethods==2'>
                            一对多
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="计算方式"
                     min-width="10.27%">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)' v-if='scope.row.pay_info.pay_id_info == 1'>
                            按出勤人数
                        </span>
                        <span @click='layerOpen(scope.row,$event)' v-if='scope.row.pay_info.pay_id_info == 2'>
                            按课时消费百分比
                        </span>
                        <span @click='layerOpen(scope.row,$event)' v-if='scope.row.pay_info.pay_id_info == 3'>
                            按授课课时
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="结算金额"
                    min-width="11.11%">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row, $event)'>
                            {{scope.row.bay_paich}}{{scope.row.pay_info.cpany}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="备注"
                     min-width="11.11%">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)'>
                            {{scope.row.remake}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    align='center'
                    label="更多操作"
                     min-width="20.16%">
                    <template slot-scope="scope">
                        <el-button
                            @click.native.prevent="repair_g(scope.$index, tableData.data)"
                            type="text"
                            size="small">
                            <i class='repair_icon icon-width'></i>
                            修改
                        </el-button>
                        <el-button
                            @click.native.prevent="showDelete(tableData.data[scope.$index].pay_id_list)"
                            type="text"
                            size="small">
                            <i class='delete_icon icon-width'></i>
                            删除设置
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div class='salary_footer' v-if='tableData.countarr>1'>
            <erp-paging :page_num='page_num' :counts='tableData.countarr' :pageSize='pageSize'  @pagings='pagings'></erp-paging>
        </div>
        <erp-mtsalary :repairSalary='repairSalary' ></erp-mtsalary>
        <erp-basalary ></erp-basalary>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </section>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import Lbutton from '../../../button/button';
import erpPaging from '../../../paging/pagingShort';
import erpMtsalary from './mt-salary/mt-salary'
import erpBasalary from './batch-salary/batch_salary'
import {KcList, getkmList, switchInfo, deleteKcxc} from '../../../../getserver/server'
import {getseSsionStorage,setseSsionStorage, setlocalStorage, getlocalStorage} from '../../../../cookie/cookie'
import {mapState} from 'vuex'
import Imageport from '../../../../config/ImagePz'
export default {
    name: 'salary',
    data () {
      return {
        salaryShow: false,
        batchsalaryShow: false,
        KmId: '',
        KmName: '',
        switchState: null,
        page_num: 0,
        pageSize: 8,
        kmActive: '',
        KmList: [],
        tableData: {},
        repairSalary: {},
        showDe:false,
	    deleteId:null,
      }
    },
    computed: {
     ...mapState(['userLoginInfo'])
    },
    created () {
        let userinfo = JSON.parse(getlocalStorage('userLoginInfo'+this.userLoginInfo.id))
        userinfo.value.config.pay_state==2?this.switchState=false:this.switchState=true
        this.listGet('','', 1,this.pageSize)
        this.GetKM()
    },
    provide:function(){
        return {
          salaryThis: this
        }
    },
    mounted () {
      this.tableHeight()
    },
    methods: {
        tableHeight () {
            let tableRefs = this.$refs.table.$el; // table 父亲级节点
            let trheight = null
            const _this = this
            setTimeout(()=>{
                trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
                tableRefs.style.height = trheight * ( _this.pageSize + 1 ) + "px"
            },500) 
        },
        layerOpen:function (a,b) {
            var pt = $(b.path[1]).width();
            var t = b.currentTarget.getBoundingClientRect();
            if (pt < t.width) {
                var that = b.currentTarget;
                var text = b.path[0].innerText;
                var l = t.x-10+'px';
                var t = t.y-10+'px';
                layer.open({
                    type: 1,
                    fixed: false,
                    icon: 2, 
                    offset: [t,l],   //相对定位
                    shade: false,
                    title: false, //不显示标题
                    area: [pt, 'auto'],
                    content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                    success: function(layero,index){
                        layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                    }
                });
            }      	
        },
        batchSalaryClick () {
          this.batchsalaryShow = !this.batchsalaryShow
        },
        repair_g(index, data){
          this.salaryShow = !this.salaryShow
          this.repairSalary = data[index]
        },
        choose (){
          this.listGet(this.KmId, this.KmName)
        },
        searchClick () {
          this.listGet(this.KmId, this.KmName)
        },
        exportClick () {
            // 导出
            window.location.href = Imageport + "index/index/pay_export?orgid="+this.userLoginInfo.orgid+"&uid="+this.userLoginInfo.id+"&cur_name="+this.KmName+"&subject="+this.KmId
        },
        pagings (i) {
            this.listGet(this.KmId,this.KmName,i,this.pageSize);
            this.page_num = i
        },
        showDelete(id){
            this.deleteId = id;
            this.showDe = true
        },
        hideDeleteModal(){
            this.showDe = false
        },
        submitDelete(id){
            this.showDe = false;
            this.delectclassRooms(id)
        },
        delectclassRooms(id){
            this.deleDataKe(id)
        },
        changeSwitch () {
            let i = Number;
            const _this = this;
            let userinfo = JSON.parse(getlocalStorage('userLoginInfo'))
            this.switchState==true? i=1 : i=2;
            userinfo.value.config.pay_state = i
            switchInfo(i, this.userLoginInfo.orgid).then((res)=>{
                if (res.data.status) {
                    _this.$message({
                        message: res.data.sinfo,
                        type: 'success',
                        duration: 1500
                    });
                    setlocalStorage('userLoginInfo', userinfo, 'z')
                } else {
                    this.switchState = false
                }
            })
        },
        async listGet (subjectId, curname, page, limt) {
          // 课程薪酬 - 列表请求
          let resData = await KcList(subjectId, curname, this.userLoginInfo.orgid, this.userLoginInfo.id, page, limt);
          if (resData.data.status) {
            if(subjectId != '' && curname!='') {
                this.$message({
                    message: '筛选成功',
                    type: 'success',
                    duration: 2000,
                });
            }
            this.tableData = resData.data.data
          }
        },
        async GetKM () {
            let resDate = await getkmList ()
            this.KmList = resDate.data.data
        },
        async deleDataKe (id) {
            // 删除课程
            let resDate = await deleteKcxc(id, this.userLoginInfo.orgid, this.userLoginInfo.id)
            if (resDate.data.status) {
                this.$message({
                    message: '删除成功',
                    type: 'success',
                    duration: 1500
                });
                const _this = this
                if (this.tableData.data.length == 1) {
                    if (this.page_num==1) {
                        this.listGet(this.KmId,this.KmName,1,this.pageSize);
                    }else {
                        this.listGet(this.KmId,this.KmName,this.page_num-1,this.pageSize);
                    } 
                    return
                }
                 this.listGet(this.KmId,this.KmName,this.page_num,this.pageSize);
            }
        }
    },
    components: {
        Lbutton: Lbutton,
        erpPaging: erpPaging,
        erpMtsalary: erpMtsalary,
        erpBasalary: erpBasalary
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';
.icon-width{
    @include wh(20px,20px);
    display: inline-block;
}
.salary_{
  @include wh(100%, auto);
  display: flex;
  flex-direction: column;

input,button{
    
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: 12px;
    padding-right: 12px;
    box-sizing: border-box;
    border-radius: 6px;
    input {
        border: 0;
        @include wh(80%, 100%);
        // flex: 1;
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    @include wh(vw(128), vw(40));
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
}
}
</style>
