<template>
    <div >
        <erp-tabtop @teacherList="teacherList" @searchsetMeal="searchTeacher" @selectTeacher1="selectTeacher1" @selectTeacher2="selectTeacher2" :orgid="orgid" :manager="manager"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
                ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='8.888%'
	                label="序号">
                    <template slot-scope="scope"><span>{{scope.$index | serialNumber}} </span></template>
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='8.888%'
	                label="姓名">
                    <template slot-scope="scope">
                        <span>{{scope.row.name}}</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
                    show-overflow-tooltip
                    align='center'
	                min-width='8.888%'
	                label="性别">
                    <template slot-scope="scope">
                        <span v-if="scope.row.sex == 1">男</span>
                        <span v-else>女</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='11.111%'
	                label="手机">
                    <template slot-scope="scope">
                        <span>{{scope.row.cellphone}}</span>
                    </template>                    
	            </el-table-column>
	            <el-table-column
	                show-overflow-tooltip
	                align='center'
	                min-width='8.888%'
	                label="资历">
                    <template slot-scope="scope">
                        <span>{{scope.row.seniority_name}}</span>
                    </template>                                       
	            </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='11.111%'
                    label="入职日期">
                    <template slot-scope="scope">
                        <span>{{scope.row.entry_time | date}}</span>
                    </template>                    
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.888%'
                    label="状态">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 1">在职中</span>
                        <span v-else>已离职</span>                        
                    </template>                                       
                </el-table-column>                       
	            <el-table-column
	                label="更多操作"
	                min-width='30.001%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
                            <div class="leftButton" style="width:70%;float: left">
                                <el-button @click="showck(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
                                    <i class='repair_icon icon-width'></i>
                                    修改课程
                                </el-button>
                                <el-button type="text" @click="showDelete(scope.row.id)" size="small"  class='erp-button-icon'>
                                    <i class='delete_icon icon-width '></i>
                                    删除
                                </el-button>
                                <el-button type="text" @click="showAlert(scope.row, scope.$index)" size="small"  class='erp-button-icon'>
                                    <i class='detail_icon icon-width '></i>
                                    详情
                                </el-button>                            
                                <el-button type="text" @click="showtimeTable(scope.row, scope.$index)" size="small"  class='erp-button-icon'>
                                    <i class='classroom_icon icon-width '></i>
                                    课表
                                </el-button>                                
                            </div>
                            <div class="rightButton" style="width:30%;float: left">
                                <el-button type="text" @click="showSche(scope.row.id)" size="small"  class='erp-button-icon'>
                                    <i class='icon-width '></i>
                                    调度
                                </el-button>
                                <el-button v-if="scope.row.status == 1" type="text" @click="showJobstatus1(scope.row.id)" size="small"  class='erp-button-icon'>
                                    <i class='icon-width '></i>
                                    离职
                                </el-button>
                                <el-button v-else type="text" @click="showJobstatus2(scope.row.id)" size="small"  class='erp-button-icon'>
                                    <i class='icon-width '></i>
                                    复职
                                </el-button>                                                                
                            </div>                                                                                     
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <edit-teacher v-if="show" :title="title" :orgid="orgid" :id="id" :manager="manager" @hideModal="hideModal" @submit="submit"></edit-teacher>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
        <revision-courses v-if="show1" :title="title2" :orgid="orgid" :id="id" :manager="manager" @hideModal="hideRevision" @submit="submitRevision"></revision-courses>
        <time-table v-if="showtb" :title="title3" :orgid="orgid" :id="id" @hideModal="hidetimeTable" @submittimeTable="submittimeTable"></time-table>
        <scheduling v-if="showsche" :that="data1"></scheduling >
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './gulp-teacher-top/gulp-teacher-top'
import erpPaging from '../../../paging/pagingShort'
import editTeacher from './/alert-box/editTeacher.vue'
import timeTable from './/alert-box/timeTable.vue'
import scheduling from './/alert-box/scheduling.vue'
import revisionCourses from './/alert-box/revision.vue'
import {teacher_list,searchTeacher,selectTeacher1,selectTeacher2,delTeacher,dispatch,jobstatus1,jobstatus2} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
// import tableLayer from '../../../../script/tableLayer.js'
let that
export default {
	// ...tableLayer,
  name: 'table-gulp',
  data () {
    return {
      that: this,
      tableData: [],
      title: '修改老师资料',
      title2: '修改课程',
      title3: '',
      show: false,
      show1:false,
      showtb:false,
      orgid:'',
      manager:'',
      showDe:false,
      deleteId:null,
      showsche:false,
      data1:{}, 
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数
      id: Number(),
      delectType:'5',//0是删除，2是顺延,3是取消消课,4是还原,5是离职,6是复职
      name:'',//搜索关键字
      se_id:'',//资历状态
      status:'',//教室状态
    }
  },
  beforeCreate: function () {
   that = this;
  },
  filters: {//过滤器
　　 date: function (value) {   
     var d = new Date(value * 1000);    //根据时间戳生成的时间对象
     var date = that.appendzero(d.getFullYear()) + "-" + 
     that.appendzero(d.getMonth() + 1) + "-" +
     that.appendzero(d.getDate());           
     return date
    },
    serialNumber ( val ){
        return (that.page_num-1)*that.pageSize+(val + 1)
    }    
　　},
  computed:{
    ...mapState(['userLoginInfo']),
  },
  methods: {
    appendzero:function(obj)
    {
        if(obj<10) return "0" +""+ obj;
        else return obj;
    },
    paging_father (n) {
      this.teacherList(n)
    },      
    async teacherList (page) {
        console.log(this.userLoginInfo.orgid)
        let t = (new Date()).getTime();
        let resDate = await teacher_list(page,t,this.orgid,this.name,this.se_id,this.status)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total
            this.page_num = page
        }
        console.log(resDate.data.data.data)
    }, 
    async searchTeacher (name,page) {
        let t = (new Date()).getTime();
        let resDate = await searchTeacher(page,t,this.orgid,name)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total
            this.page_num = page;            
        }
        console.log(resDate.data.data.data)
    },
    async selectTeacher1 (se_id,page) {
        let t = (new Date()).getTime();
        let resDate = await selectTeacher1(page,t,this.orgid,se_id)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total
            this.page_num = page            
        }
        console.log(resDate.data.data.data)
    },
    async selectTeacher2 (status,page) {
        let t = (new Date()).getTime();
        let resDate = await selectTeacher2(page,t,this.orgid,status)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total
            this.page_num = page            
        }
        console.log(resDate.data.data.data)
    }, 
    async dispatch (id) {//调度
        let resDate = await dispatch(this.orgid,id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.teacherList (1)
        }
    },
    async jobstatus1 (id) {//离职
        let resDate = await jobstatus1(id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.teacherList (this.page_num)
        }
    },
    async jobstatus2 (id) {//复职
        let resDate = await jobstatus2(id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.teacherList (this.page_num)
        }
    },                  
    delectTeacher (id) {
        // 删除教师
        delTeacher(this.orgid,id).then((resDate)=>{
            if (resDate.data.status) {
                this.$message({
                    type: 'success',
                    message: resDate.data.sinfo
                });
                if((this.tableData.length -1) == 0){
                    this.teacherList (this.page_num-1)
                }
                else{
                    this.teacherList (this.page_num)
                }                
            }           
        })      
    },             
    showDelete(id){
        this.delectType = "0"
        this.deleteId = id
        this.showDe = true
    },
    showJobstatus1(id){
        this.deleteId = id
        this.delectType = "5"
        this.showDe = true        
    },    
    showJobstatus2(id){
        this.deleteId = id
        this.delectType = "6"
        this.showDe = true        
    },   
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        if(this.delectType == "5"){
            this.jobstatus1(id)
        }
        else if(this.delectType == "6"){
            this.jobstatus2(id)
        }        
        else{
            this.delectTeacher(id)
        }        
        this.showDe = false
    },       
    showAlert(a,b){
        console.log(a,b)
        this.id = a.id
        this.show = true
    },
    showtimeTable(a,b){
        console.log(a,b)
        this.id = a.id
        this.title3 = a.name
        this.showtb = true
    },    
    showck(a,b){
      this.id = a.id
      this.show1 = true
    },
    showSche(a){
        this.id = a.id
        this.showsche = true
    },    
    hideModal() {
        // 取消弹窗回调      
        this.show = false
    },
    hideRevision() {
        // 取消弹窗回调      
        this.show1 = false
    },
    hidetimeTable() {
        this.showtb = false
    },    

    submit() {
    // 确认弹窗回调  
    this.last_page = 0
    this.paging_father(this.page_num)    
    this.show = false
    },
    submitRevision() { 
        this.show1 = false
    },
    submittimeTable() {
        this.showtb = false
    },
    hideLeaveModal(){
        this.showsche = false
    },                
  },
  created: function () {
    console.log(this.userLoginInfo)
    this.orgid = this.userLoginInfo.orgid;
    this.manager = this.userLoginInfo.id.toString();
    this.teacherList(1)   
  },
  mounted(){
    this.data1 = this 	
    let h1 = $(".div_gulptable .el-table .has-gutter th").height()
    $(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")  
  },
  components: {
    erpTabtop: erpTabtop,
    erpPaging: erpPaging, 
    editTeacher:editTeacher, 
    revisionCourses:revisionCourses,
    timeTable:timeTable,
    scheduling:scheduling,  
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(vw(20),vw(20));
    display: inline-block;
    margin-right: 6px;
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button{
        margin-right: vw(15);
        float: left;
        margin-left:0
    }
    .rightButton button{
        margin-right:0;
        margin-left:vw(20)
    }
    .rightButton button .icon-width {
        width:0
    }
    button:last-child{
        margin-right: 0
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(30); 
}
.div_gulptable /deep/.erp-button-icon span{
    font-size:vw(14);
}
@media screen and (max-width: 1380px) {
   .icon-width{
    @include wh(vw1(20),vw1(20));
    font-size: vw1(16);
}
.grid-content{
    button{
        margin-right: vw1(15);
        float: left;
        margin-left:0
    }
    button:last-child{
        margin-right: 0
    }    
}
.el-pagination{
    padding-right: vw1(60);
    margin-top: vw1(30); 
}
.div_gulptable /deep/.erp-button-icon span{
    font-size:vw1(14);
} 
}
</style>
