<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <ul class='labelBanner'>
                        <li :class='{"active": item.f}' v-for='item in insdThis.banner' :key='item.id' @click.stop='switchClick(item.id)'>{{item.name}}</li>
                    </ul>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                <Lbutton class='hot_btn' @click.native='ReleaseDynamic(1)'>
                    <i class='icon-width jia_icon'></i>
                    发布新动态
                </Lbutton>
                <Lbutton class='hot_btn'  @click.native='ReleaseDynamic(2)'>
                    <i class='icon-width jia_icon'></i>
                    发布新Banner
                </Lbutton>
            </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
export default {
    name: 'dynamicTop',
    data () {
      return {
      }
    },
    created () {
    },
    inject: ['insdThis'],
    methods: {
      switchClick (id) {
        if (id==1) {
          this.insdThis.switchnum = 1
        } else if (id == 2) {
          this.insdThis.switchnum = 2
        }
        this.insdThis.banner.forEach((data)=> {
            if (data.id == id) {
                data.f = true
            } else {
                data.f = false
            }
        })
      },
      ReleaseDynamic (i) {
        this.insdThis.switch_senior = i
      }
    },
    watch: {
      
    },
    components: {
      Lbutton: Lbutton,
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(240), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    border-color: #2769fb;
    box-sizing: border-box;
    border-radius: vw(20);
    position: relative;
    .labelBanner{
        @include wh(100%,100%);
        display: flex;
        align-items: center;
        color: #2769fb;
        li{
            text-align: center;
            flex: 1;
            height: 100%;
            line-height: vw(40);
            cursor: pointer;
            padding-left: 12px;
            padding-right: 12px;
            font-size: vw(16);
        }
        li.active{
            background: #2769fb;
            color: #fff;
            border-radius: vw(20);
        }
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(35);
    .el-select{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    // height:vw(40);
    @include wh(auto, vw(40));
    white-space: nowrap;
    padding: 0 vw(8);
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
}
@media screen and (max-width: 1380px) {
  ._tabTop{
    margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
  }
}
</style>
