<template>
    <!-- 登录 -->
    <div class='logins_conters'>
        <header class='logins_header'>
            <section class='_headersection'>
                <span class='login_head'>登录免费琴行管理</span>
                <span class='go_registered' @click.stop='registeredClick'>去注册</span>
            </section>
        </header>
        <main class='logins_main' v-show='true'>
          <section class='login_input'>
              <label class='user input_label'>
                  <span class='input_label_user spacing'>账&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;号：</span>
                  <el-input v-model="form.user" placeholder="请输入账号" autofocus autocomplete='off'></el-input>
              </label>
              <label class='password input_label'>
                  <span class='input_label_user spacing' >密&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;码：</span>
                  <el-input v-model="form.password" placeholder="请输入密码" :type='passwordText' maxlength='18' autocomplete='off' @focus='burlInput(comThis)' @keyup.enter.native='login_change'></el-input>
              </label>
              <label class='login_btn'>
                  <el-button :loading="login_static" @click.stop='login_change' >立即登录</el-button>
              </label>
              <section class='Setupthe_section'>
                  <label class='Setupthe_one'>
                      <input type="checkbox" @click.stop='checkedClick($event)' :checked='form.checkedStatus'>
                      <span>记住密码</span>
                  </label>
                  <label class='Setupthe_one' @click.stop='forget_password'>
                      <span>忘记密码</span>
                  </label>
              </section>
          </section>
          <section class='wx_login_contener' >
              <label class='wx_login_label'>
                  <img src="../../../../static/icon/icon_wechat.png" alt="">
                  <span>扫描<em>二维码</em>登录</span>
              </label>
          </section>
        </main>
        <section class='wxConenter' v-show='false'>
          <erp-syards v-show='false'></erp-syards>
          <erp-binding v-show='false'></erp-binding>
        </section>
    </div>
</template>
<script>
import axios from 'axios'
import erpSyards from './WeChat_syards/erpSyards'
import erpBinding from './WeChat_binding/binding'
import {userlogin} from '../../../getserver/server'
import {setseSsionStorage, nativegetcookie, setlocalStorage} from '../../../cookie/cookie'
import {setbase64} from '../../../cookie/decoding'
import {mapMutations} from 'vuex'
export default {
  name: 'login_com',
  data () {
    return {
      comThis: this,
      login_static: false,
      passwordText: 'text',
      form: {
        user: '', // 用户名
        password: '', // 密码
        checked: 0, // 是否记住密码 0否 1是
        checkedStatus: false
      }
    }
  },
  inject: ['_this'],
  created () {
    var logincookie = nativegetcookie('ZXJwMnVzZXJpbmZv');
    if (logincookie != '') {
      this.Inputblur(logincookie)
    }
  },
  methods: {
    ...mapMutations(['LOGIN_PAGE']),
    Inputblur (string) {
      // 账号输入失去焦点时=>验证该账号是否上次登录记住过密码
      let decoding = JSON.parse(unescape(string).replace('=think:',''));
      let account = setbase64(unescape(decoding.account)).replace('erp2', ''); // 账号
      let pwd = setbase64(unescape(decoding.pwd)).replace('erp2', ''); // 密码
      this.form.user = account;
      this.form.password = pwd;
      this.form.checkedStatus = 'checked';
      this.form.checked = 1
    },
    login_change () {
      this.login_static = true
      if (this.form.user == '') {
        this.$notify.error({
          title: '账号不为空',
          message: '这是一条错误消息',
          duration: 1500
        });
        this.login_static = false
        return
        // 备用  - >  用户名不能是汉字限制
        // else if (!/.*[\\u4e00-\\u9fa5]+.*$/.test(this.form.user)) {
        //   this.$notify.error({
        //     title: '用户名不能存在汉字',
        //     message: '这是一条错误消息',
        //     duration: 1500
        //   });
        //   this.login_static = false
        //   return
        // }
      } else if(this.form.password == ''){
        this.$notify.error({
          title: '密码不能为空',
          message: '这是一条错误消息',
          duration: 1500
        });
        this.login_static = false
        return
      } else if (this.form.password.length < 6) {
        this.$notify.error({
          title: '密码不能少于6位',
          message: '这是一条错误消息',
          duration: 1500
        });
        this.login_static = false
        return
      }
      this.userpass_login()
    },
    checkedClick (e) {
      e.toElement.checked?this.form.checked=1:this.form.checked=0
    },
    userpass_login () {
      const that = this
      userlogin(this.form.user, this.form.password, this.form.checked).then((res)=>{
        if (res.data.status && res.data.error_code === 0) {
          // 登录成功
          // 用户信息存入临时存储 之后执行跳转
          setlocalStorage('userLoginInfo'+res.data.data.id, res.data.data)
          if (res.data.data.orgid == "") {
            // 登录用户未进行认证
            res.data.sinfo = '登录成功,请进行机构的认证'
            that.$router.replace({path: '/certification',query:{uid: res.data.data.id}})
            return
          } else {
            res.data.sinfo = '登录成功'
            that.$router.replace({path: '/index',query:{uid: res.data.data.id}});
          }
          that.$notify({
            title: res.data.sinfo,
            message: '这是一条成功的提示消息',
            type: 'success',
            duration: 1500
          });
          return;
        }
        that.login_static = false
      })
    },
    registeredClick () {
      this.tabLoginfun(2)
    },
    forget_password () {
      // 忘记密码
      this.tabLoginfun(3)
    },
    tabLoginfun (n) {
      setseSsionStorage('login_page', n)
      this.LOGIN_PAGE(n);
    }
  },
  components: {
    erpSyards: erpSyards,
    erpBinding: erpBinding
  },
  mounted () {
  },
  watch:{
    'form.password'(val){
      if (val!='' && this.passwordText != 'password'){
        this.passwordText = 'password'
      } else {
        this.passwordCode(this, val)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
.logins_conters {
    @include wh(26%, 63%);
    min-height: 465px;
    overflow: hidden;
    background-color: #ffffff;
    margin-right: 15%;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .logins_header{
      @include wh(100%, 13%);
      background: url("../../../../static/header_back/login_top_bg.png")no-repeat;
      background-size: 100% 100%;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      padding: 0 7.8%;
      box-sizing: border-box;
      ._headersection {
        @include wh(100%,100%);
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .login_head{
          font-size: 16px;
        }
        .go_registered{
          font-size: 13px;
          display: flex;
          align-items: center;
          cursor: pointer;
          -moz-user-select:none; /*火狐*/
            -webkit-user-select:none; /*webkit浏览器*/
                -ms-user-select:none; /*IE10*/
                    -khtml-user-select:none; /*早期浏览器*/
                        user-select:none;
        }
        .go_registered::after{
          content: '';
          display: inline-block;
          @include wh(15px,15px);
          background: url("../../../../static/login_icon/icon_switch.png")no-repeat;
          background-size: 100%;
          margin-left: 8px;
        }
      }
    }
    .logins_main {
      flex: 1;
      padding: 7.7% 6.1%;
      box-sizing: border-box;
      .login_input {
        .password {
          margin-bottom: 6.6%;
        }
        .input_label:first-child{
            margin-bottom: 5%;
        }
        .input_label{
          display: flex;
          align-items: center;
          font-size: 13px;
          color: #333333;
          .input_label_user{
            margin-right: 3.4%;
          }
          .spacing{
            letter-spacing: 2px
          }
        }
        .Setupthe_section{
            display: flex;
            justify-content: space-between;
            margin-top: 2%;
            color: #999999;
            .Setupthe_one{
                display: flex;
                align-items: center;
                cursor: pointer;
                 -moz-user-select:none; /*火狐*/
                  -webkit-user-select:none; /*webkit浏览器*/
                      -ms-user-select:none; /*IE10*/
                          -khtml-user-select:none; /*早期浏览器*/
                              user-select:none;
            }
            input[type='checkbox']{
                width: 14px;
                height: 14px;
                background: #fff;
                -webkit-appearance:none;
                border: 1px solid #999999;
                border-radius: 2px;
                outline: none;
                margin-right: 9px;
                cursor: pointer;
            }
            input[type=checkbox]:checked{
                background-size: 100%;
                background: url("../../../../static/icon/icon_unselected.png")no-repeat center;
            }
        }
      }
      .wx_login_contener{
          @include wh(100%, auto);
          margin-top: 19.23%;
          .wx_login_label{
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              cursor: pointer;
              img{
                  @include wh(60px,60px);
                  border-radius: 100%;
                  margin-bottom: 5.8%;
              }
              span{
                 font-size: 16px;
                 color: #999999;
                 em{
                     color: #2769fb;
                 }
              }
          }
      }
    }
    .wxConenter{
      display: flex;
      @include wh(100%, 100%);
    }
  }
</style>
<style scoped>
.input_label /deep/ .el-input{
    flex: 1;
}
.login_btn /deep/ .el-button {
    width: 100%;
    height: 9.2%;
    background: #2769fb;
    color: #fff;
}
</style>