<template>
    <div class='_tabTop'>
        <el-row class='meadio'>
        <el-col :span="14">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_elect selectone'>
                    <span>分类: </span>
                    <el-select v-model="form.cate_name" placeholder="请选择" popper-class="treeSelect" @focus="showSelect()" ref="selectRef">
                      <el-option :value="valueModel" :label="labelModel">
                        <el-tree :data="data" node-key="id" :check-strictly="true" check-on-click-node auto-expand-parent default-expand-all :default-checked-keys= "[cheackId]" :props="defaultProps" @node-click="handleNodeClick" @check-change="handleClick" ref="treeForm"></el-tree>                          
                      </el-option>          
                    </el-select>
                </label>
                <label class='label_elect selectTwo'>
                    <span>销售员: </span>
                    <el-select placeholder="请选择" v-model='statisThis.form.smantype'>
                        <el-option :label="item.name" :value="item.id" v-for='item in form.Saletype' :key='item.sm_id'></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectone'>
                    <span>选择: </span>
                    <el-select placeholder="请选择" v-model='statisThis.form.timetype'>
                        <el-option :label="item.name" :value="item.id" v-for='item in form.smantypeArr' :key='item.id'></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectone'>
                    <span>日期: </span>
                    <el-date-picker
                        v-model='statisThis.form.start_time'
                        type="date"
                        :clearable='false'
                        placeholder="选择日期">
                    </el-date-picker>
                </label>
                <label class='label_elect selectone' style='margin-left: 8px'>
                    <span>至</span>
                    <el-date-picker
                        v-model='statisThis.form.end_time'
                        type="date"
                        :clearable='false'
                        placeholder="选择日期"
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </label>
                <label class='label_search'>
                    <input type="text" placeholder="搜索课程" v-model='statisThis.form.goodsname'>
                    <i class='search_icon icon-width' @click='searchKec'></i>
                </label>
            </div>
        </el-col>
        <el-col :span="5">
            <section class='tabConenter'>
                <span>总金额：{{statisThis.tableData.total_amount}}元</span>
                <span>总利润：{{statisThis.tableData.total_profit}}元</span>
            </section>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                <Lbutton class='hot_btn' @click.native='exportClick'>
                    <i class='icon-width export_icon'></i>
                    导出Excel
                </Lbutton>
                 <Lbutton class='hot_btn' @click.native='RefreshClick' >
                    <i class='icon-width refresh_icon'></i>
                    刷新数据
                </Lbutton>
            </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import Imageport from '../../../../../config/ImagePz'
import {cate_list} from '../../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
  name: 'seaticTop',
  data () {
    const that = this
    return {
        valueModel:'',
        labelModel:'',
        cheackId:null,
        data: [],
        form: {
            name: '',
            id:null,
            cate_name:'',
            smantypeArr: [{id:0,name:"全部"},{id:1,name:"日"},{id:2,name:"月"},{id:3,name:'年'}],
            Saletype: [{id:1,name: '销售员'},{id:2,name: '老师'}]
        },
        pickerOptions: {
          disabledDate(time) {
            let times = new Date(that.statisThis.form.start_time).getTime();
            let n = 24 * 60 * 60 * 1000
            let saveDate = times - n
            return time.getTime() <= saveDate
          }
        },
        defaultProps: {
          children: 'sub',
          label: 'cate_name'
        } 
    }
  },
  inject: ['statisThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created(){
    this.getcate_list()
  },
  methods: {
    async getcate_list () {
        let resDate = await cate_list(this.userLoginInfo.orgid)
        if (resDate.data.status){
            this.data = resDate.data.data
        }
    },
    searchKec(){
      //搜索 名称
      this.statisThis.getSaleList_fun(1)
    },
    exportClick () {
       window.location.href = Imageport + "/index/excel/sale_census_ept?orgid="+ this.userLoginInfo.orgid + "&cate_id=" + this.statisThis.form.cate + "&sman_type=" +this.statisThis.form.smantype+ "&time_type=" +this.statisThis.form.timetype+ "&goods_name=" +this.statisThis.form.goodsname+ "&start_time=" + this.statisThis.form.start_timeStamp + "&end_time=" + this.statisThis.form.end_timeStamp;
    },
    RefreshClick () {
      // 刷新数据
      this.statisThis.getSaleList_fun(this.statisThis.page_num)
      const _this =this
      setTimeout(()=>{
        _this.$message({
          message: "刷新成功",
          type: 'success',
          duration: 2000,
        })
      },500)
    },
    handleNodeClick(data) {
      this.form.pid = data.cate_id
      this.cheackId = data.cate_id
      this.form.cate_name = data.cate_name
      this.$refs.treeForm.setCheckedNodes([data]);
      this.$refs.selectRef.blur()
    },
    handleClick(data, checked, node){
      if(checked == true){
        this.cheackId = data.id;
        this.$refs.treeForm.setCheckedNodes([data]);
        this.$refs.selectRef.blur()
      }
      
    },
    showSelect(){
      let _this = this
      if(this.cheackId){
        let node1 = document.querySelector('.treeSelect .is-current');
        setTimeout(function(){
          _this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop = node1.children[0].offsetTop-50
        },200)
      }
    },
    timeStamp (val) {
      let Stamp = (new Date(val)).getTime()
      return Stamp
    }
  },
  components: {
    Lbutton: Lbutton
  },
  watch: {
    cheackId(val) {
      if (val!=null) {
        // 选中分类
        this.statisThis.form.cate = val
        this.statisThis.getSaleList_fun(1)
      }
    },
    'statisThis.form.smantype'(val){
      if (val!="") {
        this.statisThis.getSaleList_fun(1)
      }
    },
    'statisThis.form.timetype' (val) {
      if (val!="" || val === 0) {
        this.statisThis.getSaleList_fun(1)
      }
    },
    'statisThis.form.start_time'(val) {
      // 开始时间 监听
      if (val!=null || val!="") {
        let Stamp = this.timeStamp(val)/1000
        this.statisThis.form.start_timeStamp = Stamp
        this.statisThis.form.end_time = ""
        this.statisThis.form.end_timeStamp = ""
      }
    },
    'statisThis.form.end_time'(val) {
      // 结束时间 监听
      if (val!=null || val!="") {
        let Stamp = this.timeStamp(val)/1000
        this.statisThis.form.end_timeStamp = Stamp
        if (this.statisThis.form.start_timeStamp == "") {
          this.$message({
            message: '请先选择开始时间!',
            type: 'warning',
            duration: 2000,
          });
          this.statisThis.form.end_timeStamp = ""
          this.statisThis.form.end_time = ""
        } else if (this.statisThis.form.start_time!="" && this.statisThis.form.end_time!=""){
          // 请求接口
          this.statisThis.getSaleList_fun(1)
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .tabConenter{
    line-height: vw(40);
    padding-left: vw(50);
    box-sizing: border-box;
    font-size: vw(16);
    color: #333333;
    span:last-child{
        margin-left: vw(40);
    }
  }
  .icon-width{
    display: inline-table;
    @include wh(vw(24), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: vw(12);
    padding-right: vw(16);
    box-sizing: border-box;
    border-radius: 6px;
    margin-left: vw(20);
    input {
        @include wh(80%, 100%);
        // flex: 1;
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
    display: inline-block;
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #333333;
    margin-left: vw(20);
    .el-select,.el-date-editor{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
    /deep/.el-date-editor{
        .el-input__inner{
            height: vw(40);
            font-size: vw(16)
        }
        .el-input__prefix{
            display: none;
        }
    }

  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    @include wh(vw(128), vw(40));
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
} 
.treeSelect .el-scrollbar .el-select-dropdown__item{
  height:auto;
  padding:0
}
.treeSelect .el-scrollbar .el-select-dropdown__item .el-tree /deep/ .el-tree-node.is-checked>.el-tree-node__content{
  @include bg-defaul($bg-color);
  @include fontColor-defaul($bgColor-color)
}
.treeSelect .el-scrollbar .el-select-dropdown__item.selected{
  font-weight: 500
}
</style>