var scrollModalMain = (e) => {
    // console.log(e)
    // console.log($(e.path[0]).find(".el-select").find(".el-input--suffix"))
    // console.log($(e.path[0]).find(".el-select").find(".el-input--suffix").length)
    let selectobj = $(e.path[0]).find(".el-select").find(".el-input--suffix")
    for(var i=0;i<selectobj.length;i++){
    	// console.log($(selectobj[i]))
    	if($(selectobj[i])[0].className.indexOf("is-focus") != -1){
    		$(selectobj[i])[0].children[0].click()
    	}
    } 
}
export {
	scrollModalMain
}
