<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索班级" v-model='gradeThis.form.gradename'>
                    <i class='search_icon icon-width' @click='searchKec'></i>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                <Lbutton class='hot_btn' @click.native="funClick">
                    <i class='icon-width jia_icon'></i>
                    新增班级
                </Lbutton>
                <Lbutton class='hot_btn '  @click.native="exportClick(0, $event)">
                    <i class='icon-width import_icon'></i>
                    <!-- <input type="file" ref='files'> -->
                    导入Excel
                </Lbutton>
                <Lbutton class='hot_btn ' @click.native="exportClick(2, $event)">
                    <i class='icon-width download_icon'></i>
                    <!-- <input type="file" ref='files'> -->
                    下载模板
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="exportClick(1, $event)">
                    <i class='icon-width export_icon'></i>
                    导出Excel
                </Lbutton>
                 <Lbutton class='hot_btn' @click.native="refreshData">
                    <i class='icon-width refresh_icon'></i>
                    刷新数据
                </Lbutton>
            </div>
        </el-col>
      </el-row>
      <erp-upfile :dialogVisible='upfilemodel.dialogVisible'></erp-upfile>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import Imageport from '../../../../../config/ImagePz'
import erpUpfile from './up_filemodel/upfile'
import {mapState} from 'vuex'
import {getkmList, getKclist, GradeimportExcel} from '../../../../../getserver/server'
/**
 * getkmList 獲取科目接口
 * getKclist 课程列表接口
 **/
export default {
    name: 'tabTops',
    data () {
      return {
        upfilemodel:{
          dialogVisible: false
        }
      }
    },
    inject: ['gradeThis'],
    provide () {
      return {
        tabTop: this
      }
    },
    created () {
      const _this = this
      getkmList().then((res)=>{
        if(res.status) {
          _this.kmLisy = res.data.data
        }
      })
    },
    computed: {
      ...mapState(['userLoginInfo'])
    },
    methods: {
      funClick (i) {
        this.gradeThis.addGradeShow = !this.gradeThis.addGradeShow
      },
      refreshData () {
        this.gradeThis.getGradeList(this.gradeThis.form.gradename,1,this.gradeThis.pageSize)
        this.$message({
          message: "刷新成功",
          type: 'success',
          duration: 2000,
        });
      },
      searchKec () {
        // 搜索课程
        this.gradeThis.getGradeList(this.gradeThis.form.gradename,1,this.gradeThis.pageSize)
        this.$message({
          message: "筛选成功",
          type: 'success',
          duration: 2000,
        });
      },
      exportClick (n, e) {
        // 导出excel
        switch (n) {
          case 0:
            // 导入
            this.upfilemodel.dialogVisible = !this.upfilemodel.dialogVisible
            // window.location.href = Imageport + "./index/index/daoru_banji"
            break;
          case 1:
            // 导出
            window.location.href = Imageport + "./index/index/dchu_banji?orgid="+this.userLoginInfo.orgid+"&cls_name="+this.gradeThis.form.gradename
            break;
          case 2:
            window.location.href = Imageport + "public/upload/file/classes.xls"
            break;
        }
      },
      async importExclefun (files) {
        // 导入模板
        let from = new FormData()
        from.append('excel', files)
        from.append('orgid', this.userLoginInfo.orgid)
        let resDate = await GradeimportExcel(from)
        if (resDate.data.status) {
          this.$message({
            message: resDate.data.data,
            type: 'success',
            duration: 2000,
          });
          this.gradeThis.getGradeList(this.gradeThis.form.gradename,this.gradeThis.page_num,this.gradeThis.pageSize)
          // this.that.getKclists(1)
          return
        }
        this.$message.error(resDate.data.sinfo);
      },
      async getcmList (kc, kmid, fs, state) {
        // kc 课程名称
        // kmid 科目id
        // fs 授课方式
        // state 状态
        // 筛选功能
        let resData = await getKclist(1, kc, kmid, fs, state, this.userLoginInfo.orgid)
        if (resData.status) {
          this.$message({
            message: '筛选成功',
            type: 'success',
            duration: 2000,
          });
        }
        this.that.tableData = resData.data.data.data
      },
      setUphot () {
        this.that.hotKcshow = !this.that.hotKcshow
      }
    },
    watch: {
      kmActive:function(val1){
        this.searchKec()
      },
      value: function(val1){
        this.searchKec()
      },
      states: function(val1){
        this.searchKec()
      }
    },
    components: {
      Lbutton: Lbutton,
      erpUpfile: erpUpfile
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(160), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: 12px;
    padding-right: 12px;
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(35);
    .el-select{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    // height:vw(40);
    @include wh(vw(128), vw(40));
    white-space: nowrap;
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
}
@media screen and (max-width: 1380px) {
  ._tabTop{
    margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
  }
}
</style>
