<template>
    <section class='_shopClass'>
        <el-dialog
            title="购课"
            v-dialogDrag
            :visible.sync="tablesThis.shopClassShos">
            <section class='body-section'>
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="普通购课" name="0">
                        <section class='el-tab-section el-tab-section-left'>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*科&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;目：</span>
                                <el-select slot="prepend" placeholder="请选择" class='_main-rightSelect' v-model='form.activekemuList'>
                                    <el-option label="请选择" value="-1" ></el-option>
                                    <el-option :label="item.sname" :value="item.sid" v-for='item in initDate.kemuList' :key='item.sid'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*课&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;程：</span>
                                <el-select slot="prepend" placeholder="请选择" class='_main-rightSelect' v-model='form.activekcmuList' >
                                    <el-option label="请选择" value="-1" ></el-option>
                                    <el-option :label="item.cur_name + '(' +(item.tmethods==2?'一对多':'一对一') +')'" :value="item.cur_id" v-for='item in initDate.kcmuList' :key='item.cur_id'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*教师资历：</span>
                                <el-select slot="prepend" placeholder="请选择" class='_main-rightSelect' v-model='form.activejsSenList'>
                                    <el-option label="请选择" value="-1" ></el-option>
                                    <el-option :label="item.s_name" :value="item.s_id" v-for='item in initDate.jsSenList' :key='item.s_id'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title xin_class'>教&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;师：</span>
                                <el-select slot="prepend" placeholder="请选择" class='_main-rightSelect' v-model='form.activeTeacher'>
                                    <el-option label="请选择" value="-1" ></el-option>
                                    <el-option :label="item.name" :value="item.id" v-for='item in initDate.Teacher' :key='item.id'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*收费标准：</span>
                                <el-input placeholder="填数字" v-model="form.chargeStandard"  @paste.native.capture.prevent="handlePaste">
                                    <template slot="append">元</template>
                                </el-input>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*购买课时：</span>
                                <el-input placeholder="填数字" v-model="form.Class_hour" maxlength='5' @paste.native.capture.prevent="handlePaste">
                                    <template slot="append">节</template>
                                </el-input>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title xin_class'>有 效 期 ：</span>
                                <el-date-picker
                                    v-model="form.Termvalidity"
                                    type="date"
                                    @change="DATE_Termva"
                                    :editable='false'
                                    format="yyyy-MM-dd"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*购课时间：</span>
                                <el-date-picker
                                    v-model="form.thisDate"
                                    type="date"
                                    @change="DATE_change"
                                    :editable='false'
                                    :picker-options="pickerOptions"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </label>
                            <label class='label-drop-down label-dis-flex'>
                                <span class='label-drop-down-title xin_class'>课程备注：</span>
                                <el-input disabled placeholder="该课程暂无备注"></el-input>
                            </label>
                            <label class='label-drop-down label-dis-flex' style='align-items: end;'>
                                <span class='label-drop-down-title xin_class'>购课备注：</span>
                                <el-input
                                    v-model='form.shopClass'
                                    type="textarea"
                                    placeholder="可填写备注说明"
                                    show-word-limit
                                    maxlength='500'>
                                </el-input>
                            </label>
                        </section>
                        <section class='el-tab-section el-tab-section-right'>
                            <div class='el-tab-section-label'>
                                <span class='el-tab-section-label-title'>*优惠方案：</span>
                                <div class='radio_div'>
                                    <label class='radio_section' v-for='item in DiscountList' :key='item.id' @click.stop='discountChange(item.id)'>
                                        <input type="radio" :checked='item.f'>
                                        <span>{{item.name}}</span>
                                        <section class='_input_append' v-if='item.id!=1'>
                                            <el-input placeholder="填数字" v-if='item.id==2' @paste.native.capture.prevent="handlePaste" v-model="form.DiscountActive2" :disabled='item.f==false'>
                                                <template slot="append">%</template>
                                            </el-input>
                                            <el-input placeholder="填数字" v-if='item.id==3' @paste.native.capture.prevent="handlePaste" v-model="form.DiscountActive3" :disabled='item.f==false'>
                                                <template slot="append">元</template>
                                            </el-input>
                                        </section>
                                    </label>
                                    <p class='radio_section' v-for='items in DiscountKs' :key='items.id'>
                                        <input type="checkbox" :checked='items.f'  @click.stop='DiscountKsChange(items.id)'>
                                        <span>{{items.name}}</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" :disabled="items.f==false" v-model="form.FreeClassHours" @paste.native.capture.prevent="handlePaste">
                                                <template slot="append">节</template>
                                            </el-input>
                                        </section>
                                    </p>
                                </div>
                            </div>
                            <label class='el-tab-section-label el-tab-margin-left'>
                                <span class='personnel'>录 入 人 ：<em>{{userLoginInfo.config.contacts}}</em></span>
                                <span class='personnel'>跟 进 人 ：<em>{{userLoginInfo.config.contacts}}</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left' >
                                <span class='el-tab-section-title'>实 付 款 ：</span>
                                <el-input v-model="form.Actual_payment" :readonly='true'>
                                    <template slot="append">元</template>
                                </el-input>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left'>
                                <span class='personnel'>账户余额：<em>{{tablesThis.balance}}元</em></span>
                                <span class='personnel'><em>原价: {{form.original_price}}元；优惠：{{form.Discount_mony}}元</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append' >
                                <span class='el-tab-section-title'>*付款方式：</span>
                                <el-select slot="prepend" placeholder="请选择" class='_main-rightSelect' v-model='form.Activemethod'>
                                    <el-option label="请选择" value="-1" ></el-option>
                                    <el-option :label="item.pay_name" :value="item.pay_id" v-for='item in initDate.methodList' :key = 'item.pay_id'></el-option>
                                </el-select>
                            </label>
                        </section>
                    </el-tab-pane>
                    <el-tab-pane label="套餐购课" name="1">
                        <section class='el-tab-section el-tab-section-left'>
                            <label class='class-shopClass'>
                                <span class='label-drop-down-title'>*套&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;餐：</span>
                                <el-select slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                            <div class='div_gulptable'>
                                <el-table
                                    :data="tableData"
                                    stripe
                                    border
                                    style="width: 100%">
                                    <el-table-column
                                    prop="name"
                                    label="课程名称"
                                    align='center'
                                    min-width="33.33%">
                                    </el-table-column>
                                    <el-table-column
                                    prop="nums"
                                    align='center'
                                    label="课程节数"
                                    min-width="33.33%">
                                    </el-table-column>
                                    <el-table-column
                                    prop="jiage"
                                    align='center'
                                    label="课程价格"
                                    min-width="33.33%">
                                    </el-table-column>
                                </el-table>
                            </div>
                            <div class='time_div'>
                                <label class='label-drop-down class-margin-right'>
                                    <span class='label-drop-down-title'>*收费总价：</span>
                                    <el-input placeholder="填数字" v-model="input2">
                                        <template slot="append">元</template>
                                    </el-input>
                                </label>
                                <label class='label-drop-down '>
                                    <span class='label-drop-down-title'>*购课时间：</span>
                                    <el-date-picker
                                        v-model="value1"
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </label>
                            </div>
                            <div class='time_div'>
                                <label class='label-drop-down class-margin-right'>
                                    <span class='label-drop-down-title'>*有 效 期 ：</span>
                                    <el-date-picker
                                        v-model="value1"
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </label>
                                <label class='label-drop-down '>
                                    <span class='label-drop-down-title'>*上课时间：</span>
                                    <el-date-picker
                                        v-model="value1"
                                        type="date"
                                        placeholder="选择日期">
                                    </el-date-picker>
                                </label>
                            </div>
                            <label class='class-shopClass'>
                                <span class='label-drop-down-title'>*套餐备注：</span>
                                <el-input type='text' placeholder="该套餐暂无备注" disabled></el-input>
                            </label>
                            <label class='class-shopClass textarea-class'>
                                <span class='label-drop-down-title'>*购课备注：</span>
                                <el-input type='textarea' placeholder="可填写购课备注"></el-input>
                            </label>
                        </section>
                        <section class='el-tab-section el-tab-section-right'>
                            <label class='el-tab-section-label'>
                                <span class='el-tab-section-label-title'>*优惠方案：</span>
                                <div class='radio_div'>
                                    <label class='radio_section'>
                                        <input type="radio">
                                        <span>暂无优惠</span>
                                    </label>
                                    <label class='radio_section'>
                                        <input type="radio">
                                        <span>折扣优惠</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" v-model="input2">
                                                <template slot="append">%</template>
                                            </el-input>
                                        </section>
                                    </label>
                                    <label class='radio_section'>
                                        <input type="radio">
                                        <span>金额优惠</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" v-model="input2">
                                                <template slot="append">%</template>
                                            </el-input>
                                        </section>
                                    </label>
                                    <label class='radio_section'>
                                        <input type="checkbox">
                                        <span>赠课课时</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="填数字" v-model="input2">
                                                <template slot="append">%</template>
                                            </el-input>
                                        </section>
                                    </label>
                                </div>
                            </label>
                            <label class='el-tab-section-label el-tab-margin-left'>
                                <span class='personnel'>录 入 人 ：<em>BEE</em></span>
                                <span class='personnel'>跟 进 人 ：<em>BEE</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left' >
                                <span class='el-tab-section-title'>实 付 款 ：</span>
                                <el-input v-model="input2">
                                    <template slot="append">元</template>
                                </el-input>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left'>
                                <span class='personnel'>账户余额：<em>{{tablesThis.balance}}元</em></span>
                                <span class='personnel'><em>原价: 0.00元；优惠：0.00元</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append' >
                                <span class='el-tab-section-title'>*付款方式：</span>
                                <el-select   slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                    <el-option label="男" value="1" ></el-option>
                                    <el-option label="女" value="2" ></el-option>
                                </el-select>
                            </label>
                        </section>
                    </el-tab-pane>
                </el-tabs>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="shopClassSave"  size='mini'>确 定</el-button>
                <el-button @click="tablesThis.shopClassShos = false" size='mini'>取 消</el-button>
            </span>
        </el-dialog>
    </section>
</template>
<script>
import {getkmList, getKclist,kcOneByone,CorrespondingT,studentShoopClass} from '../../../../../getserver/server'
import {senioritieList,all_pay_list} from '../../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
    name: 'Shoppclass',
    data () {
        return {
            pickerOptions:{
                disabledDate(time) {
                    return time.getTime() > Date.now();
                }
            },
            activeName: "0",
            DiscountKs: [{id:4,name: '赠课课时',f:false}],
            DiscountList: [{id:1,name:'暂不优惠', f:true},{id:2,name:'折扣优惠',f:false},{id:3,name: '金额优惠',f:false}],
            initDate: {
              // 初 始 数 据
              kemuList: [],// 科目 列表
              jsSenList: [], // 教师 资历 列表
              kcmuList: [],// 课程列表
              Teacher: [], // 课程对应的教师
              methodList: [] // 支付方式
            },
            form: {
                stu_id: '',// 学生id
                classify: 1,// 1普通 或 2套餐
                userid: "",
                activekemuList: "-1",// 选中的科目
                activejsSenList: "-1", // 选中的教师资历
                activekcmuList: "-1", // 选中的课程
                activeTeacher: '-1',// 选中的教师 id
                chargeStandard: '', // 收费标准
                type:'1',// 优惠方式 // 默认 不优惠
                type_num: '',
                Class_hour: '',// 购买课时
                Termvalidity: '', // 有效期
                thisDate: '', // 购买时间
                shopClass:'',// 购课备注
                DiscountActive2: '', // 优惠折扣
                DiscountActive3: '', // 优惠金额
                FreeClassHours: 0, // 赠课课时
                Actual_payment: '', // 实付款
                Activemethod: '-1',// 选中的付款方式
                original_price: '0.00', // 原价
                Discount_mony: '0.00' // 优惠金额
            },
            tableData: [
                {id: 1, name: '芭蕾', nums: '3节', jiage: '300'},
                {id: 2, name: '芭蕾', nums: '3节', jiage: '500'}
            ],
            dialogVisible: true
        }
    },
    inject: ['tablesThis'],
    created(){
      this.getkmList_fun();
      this.all_pay_list_fun();
      this.initNewDAte()
      this.form.userid = this.userLoginInfo.id
    },
    computed:{
      ...mapState(['userLoginInfo'])
    },
    methods: {
        discountChange (id) {
            const _this = this
            this.DiscountList.forEach((data)=>{
                if (data.id == id) {
                    data.f = true
                    _this.form.type = data.id
                } else {
                    data.f = false
                    _this.form.DiscountActive3 = ""
                    _this.form.DiscountActive2 = ""
                }
            })
        },
        DiscountKsChange (id) {
            const _this = this
            this.DiscountKs[0].f = !this.DiscountKs[0].f
            if (this.DiscountKs[0].f == false) {
                _this.form.FreeClassHours = ""
            }
        },
        handlePaste(){
          // 禁止文本框粘贴
          return false
        },
        initNewDAte () {
            // 初始化购课时间
            let date = new Date();
            let YYYY = date.getFullYear(); // 年
            let MM = date.getMonth()+1; // 月
            let DD = date.getDate(); // 日
            if (MM < 10) {
                MM = "0"+ MM
            }
            if (DD < 10) {
                DD = "0" + DD
            }
            this.form.thisDate = YYYY +"-"+ MM + "-" + DD 
        },
        DATE_change (val) {
            // 购课时间
            let date = new Date(val);
            let YYYY = date.getFullYear(); // 年
            let MM = date.getMonth()+1; // 月
            let DD = date.getDate(); // 日
            if (MM < 10) {
                MM = "0"+ MM
            }
            if (DD < 10) {
                DD = "0" + DD
            }
            this.form.thisDate = YYYY +"-"+ MM + "-" + DD
        },
        DATE_Termva (val) {
            // 有效期 格式化
            let date = new Date(val);
            let YYYY = date.getFullYear(); // 年
            let MM = date.getMonth()+1; // 月
            let DD = date.getDate(); // 日
            if (MM < 10) {
                MM = "0"+ MM
            }
            if (DD < 10) {
                DD = "0" + DD
            }
            this.form.Termvalidity = YYYY +"-"+ MM + "-" + DD
        },
        handleClick(tab, event){
            this.activeName = tab.index
        },
        shopClassSave(){
            // 购课
            const _this = this
            if (this.form.activekemuList == '-1'){
                this.$message({
                    message: '科目为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if ( this.form.activekcmuList == '-1') {
                this.$message({
                    message: '课程为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if ( this.form.activejsSenList == '-1') {
                this.$message({
                    message: '教师资历为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if ( this.form.chargeStandard == '') {
                this.$message({
                    message: '收费标准为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if ( this.form.Class_hour == '') {
                this.$message({
                    message: '购买课时为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if ( this.form.thisDate == '') {
                this.$message({
                    message: '购课时间为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if (this.DiscountList[1].f ==true && this.form.DiscountActive2 == "" ) {
                this.$message({
                    message: '折扣优惠为选中状态，是必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });  
            } else if (this.DiscountList[2].f ==true && this.form.DiscountActive3 == "" ) {
                this.$message({
                    message: '金额优惠为选中状态，是必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });  
            } else if (this.DiscountKs[0].f == true && this.form.FreeClassHours == "") {
                this.$message({
                    message: '赠送课时为选中状态，是必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else if (this.form.Activemethod == "-1") {
                this.$message({
                    message: '付款方式为必填数据，不能为空！',
                    type: 'error',
                    duration: 2000,
                });
            } else {
                if (this.DiscountList[1].f == true) {
                    this.form.type_num = this.form.DiscountActive2
                } else if (this.DiscountList[2].f == true) {
                    this.form.type_num = this.form.DiscountActive3
                } else if (this.DiscountList[0].f == true) {
                    this.form.type_num = ""
                }
                switch (parseInt(_this.activeName)) {
                    case 0:
                        // 普通购课
                        _this.form.classify = 1
                    break;
                    case 1:
                        // 套餐购课
                        _this.form.classify = 2
                    break;
                }
                this.get_studentShoopClass()
            } 
        },
        async all_pay_list_fun () {
            // 获取全部付款方式列表
            let resDate = await all_pay_list(this.userLoginInfo.orgid);
            if (resDate.data.status) {
                // 获取成功
                this.initDate.methodList = resDate.data.data
            }
        },
        async getkmList_fun () {
            // 科目列表
            let resDate = await getkmList();
            if (resDate.data.status) {
                this.initDate.kemuList = resDate.data.data;
            }
        },
        async getKcallList(kmid) {
            // 课程 列表
            let resDate = await getKclist("","", kmid, "", "", "");
            if (resDate.data.status) {
                this.initDate.kcmuList = resDate.data.data.data;
            }
        },
        async getkcOneByone (curid) {
            let resDate = await kcOneByone(curid)
            if (resDate.data.status) {
              // 获取指定课程数据
              this.senioritieList_fun(resDate.data.data.t_senioritys.split('/'), resDate.data.data.tqualific.split('/'))
            }
        },
        async senioritieList_fun (a,b){
            // 教师 资历
            // a 教师资历id 数组
            // b 教师资历价格 数组
            let resDate = await senioritieList(this.userLoginInfo.orgid);
            const _this = this
            if (resDate.data.status) {
                let moneyArr = [];
                a.forEach((data,index)=>{
                    b.forEach((datas,i)=>{
                        if (index == i) {
                            moneyArr.push({id: data, money: datas})
                        }
                    })
                })
                moneyArr.forEach((data)=>{
                    resDate.data.data.forEach((item)=>{
                        if (data.id == item.s_id) {
                            item['money'] = data.money
                            _this.initDate.jsSenList.push(item)
                        }
                    })
                })
            }
        },
        async get_CorrespondingT (orgid, curid) {
            // 获取 对应课程的教师列表
            let resDate = await CorrespondingT(orgid,curid)
            if (resDate.data.status) {
                this.initDate.Teacher = resDate.data.data.data;
            }
        },
        async get_studentShoopClass() {
            let resDate = await studentShoopClass(this.form)
            if (resDate.data.status) {
                // 购课成功
                this.$message({
                    message: resDate.data.sinfo,
                    type: 'success',
                    duration: 2000,
                 });  
                this.DiscountKs = [{id:4,name: '赠课课时',f:false}],
                this.DiscountList = [{id:1,name:'暂不优惠', f:true},{id:2,name:'折扣优惠',f:false},{id:3,name: '金额优惠',f:false}]
                this.form={
                    stu_id: '',// 学生id
                    classify: 1,// 1普通 或 2套餐
                    userid: "",
                    activekemuList: "-1",// 选中的科目
                    activejsSenList: "-1", // 选中的教师资历
                    activekcmuList: "-1", // 选中的课程
                    activeTeacher: '-1',// 选中的教师 id
                    chargeStandard: '', // 收费标准
                    type:'1',// 优惠方式 // 默认 不优惠
                    type_num: '',
                    Class_hour: '',// 购买课时
                    Termvalidity: '', // 有效期
                    thisDate: '', // 购买时间
                    shopClass:'',// 购课备注
                    DiscountActive2: '', // 优惠折扣
                    DiscountActive3: '', // 优惠金额
                    FreeClassHours: 0, // 赠课课时
                    Actual_payment: '', // 实付款
                    Activemethod: '-1',// 选中的付款方式
                    original_price: '0.00', // 原价
                    Discount_mony: '0.00' // 优惠金额
                }
            }
        },
        toFixed_fun (val) {
            let values = val.toFixed(2)
            return values
        }
    },
    watch: {
      'tablesThis.stu_id'(val){
          if (val !="" || val !=null ) {
            this.form.stu_id = val
            this.initNewDAte()
          }
      },
      "form.activekemuList"(val){
        if (val!="-1") {
            // 科目被选中
            this.initDate.kcmuList = []
            this.form.activekcmuList = "-1"
            this.getKcallList(val)
        } else {
            // 科目不选中
            this.initDate.kcmuList = []
            this.form.activekcmuList = "-1"
        }
      },
      'form.activekcmuList'(val) {
          // 课程是否选中、
          if (val!="-1") {
              // 选择
              this.initDate.jsSenList = []
              this.getkcOneByone(val)
              this.get_CorrespondingT(this.userLoginInfo.orgid, val)
          } else {
              // 不选择
              this.initDate.jsSenList = [] // 清空资历 数据
              this.form.activejsSenList = "-1"
          }
      },
      'form.activejsSenList' (val) {
          // 教师资历 数据监听
          const _this = this
          if (val!="-1") {
              // != -1的情况
              this.initDate.jsSenList.forEach((data)=>{
                  if(data.s_id == val) {
                      if (data.money != ""){
                        _this.form.chargeStandard = data.money
                      } else {
                        _this.form.chargeStandard = 0
                      }
                  }
              })
          } else {
              _this.form.chargeStandard = ""
          }
      },
      'form.Class_hour' (value) {
          // 需求 1-10000 购课课时
        if (!/^[1-9]\d{0,5}$/g.test(value)){
            if (value=="") {
                // 当购课课时等于空。无法计算原价，原价恢复成默认值
                this.form.original_price = '0.00'
                this.form.Actual_payment = ""
                this.form.Class_hour = value
                return
            }
            this.form.Class_hour = value.substring(0, value.length-1)
        } else {
            if (this.form.Class_hour > 10000) {
                this.$message({
                    message: '最高不能超过1万',
                    type: 'error',
                    duration: 2000,
                });
                this.form.Class_hour = '10000'
                return
            } else {
                if (this.form.chargeStandard != "") {
                    // 收费标准不等于 空 计算原价
                    this.form.original_price = value * this.form.chargeStandard
                    this.form.Actual_payment = this.form.original_price
                    if (this.form.original_price==0) {
                        this.form.original_price = '0.00'
                        this.form.Actual_payment = ""
                    }
                    return
                }
            }
            this.form.Class_hour = value
        }
      },
      'form.chargeStandard' (value) {
        if (!/\d$/g.test(value)){
            if (value=="") {
                // 当收费标准等于空。无法计算原价，原价恢复成默认值
                this.form.original_price = '0.00'
                this.form.Actual_payment = ""
                this.form.chargeStandard = value
                return
            }
            this.form.chargeStandard = value.substring(0, value.length-1)
        } else {
            // 收费标准
            if (this.form.Class_hour!=''){
                // 购买课时不为空时计算  原价
                this.form.original_price = value * this.form.Class_hour
                this.form.Actual_payment = this.form.original_price
                if (this.form.original_price==0) {
                    this.form.original_price = '0.00'
                    this.form.Actual_payment = ""
                }
                return
            }
            this.form.chargeStandard = value
        }
      },
      'form.DiscountActive2' (val) {
          // 折扣优惠 计算优惠价  计算实付款
          if (this.form.original_price == "0.00" && this.DiscountList[0].f == false) {
              // 原价为空，并 没有选择暂不优惠的情况 无法计算， 提示信息
            this.$message({
                message: '请先设置收费标准并设置购买课时计算原价!',
                type: 'error',
                duration: 2000,
            });
            this.form.DiscountActive2 = ""
            return
          } else {
              // 有原价
            if (!/^\d*\.{0,1}\d{0,2}$/.test(val)) {
                if (val == "") {
                    // 折扣金额优惠 为空时 实付金额 与 优惠价回到初始化价位
                    this.form.Actual_payment = this.form.original_price   // 实付款 = 原价
                    this.form.Discount_mony = '0.00'// 初始化
                    return
                }
                this.form.DiscountActive2 = val.substring(0, val.length-1);
                // 计算实际支付金额 原价*折扣=实付款
                // 计算优惠金额 原价-实付款 = 优惠金额
                this.form.Actual_payment = this.toFixed_fun(this.form.original_price * (val/100)); // 实付款
                this.form.Discount_mony = this.form.original_price - this.form.Actual_payment // 优惠金额
            } else {
                if (val > 100) {
                    this.$message({
                        message: '不能超过100%',
                        type: 'error',
                        duration: 2000,
                    });
                    this.form.DiscountActive2 = '100'
                    // 计算优惠价格 以及 实付款
                    if (this.form.FreeClassHours !="") {
                        // 赠课课时不等空 
                        this.form.Actual_payment = "0.00"; // 实付款
                        return
                    } else {
                        this.form.Actual_payment = this.toFixed_fun(this.form.original_price * (val/100)); // 实付款
                        this.form.Discount_mony = this.form.original_price  // 优惠 为100% 优惠金额 等于 原价金额
                        return
                    }
                } else if (val == 100){
                    if (this.form.FreeClassHours !="") {
                        // 赠课课时不等空 
                        this.form.Actual_payment = "0.00"; // 实付款
                    } else {
                        this.form.Actual_payment = this.toFixed_fun(this.form.original_price * (val/100)); // 实付款
                        this.form.Discount_mony = this.form.original_price  // 优惠 为100% 优惠金额 等于 原价金额
                        return
                    }
                }
                if (val == "") {
                    if (this.form.FreeClassHours !="") {
                        // 赠课课时不为空的情况 计算实付款 和 优惠价
                        this.form.DiscountActive2 = ""
                        this.form.Actual_payment = this.toFixed_fun(this.form.chargeStandard * this.form.Class_hour); // 实付款
                        this.form.Discount_mony = this.toFixed_fun(this.form.FreeClassHours *  this.form.chargeStandard);// 优惠价
                    }else {
                        // 折扣为空 折扣恢复默认""  实付金额 = 原价 优惠金额 = 0.00
                        // 赠课课时为空的情况
                        this.form.DiscountActive2 = ""
                        this.form.Actual_payment = this.form.original_price
                        this.form.Discount_mony = "0.00"
                    }
                    return
                }
                if(this.form.FreeClassHours !="") {
                    // 当 赠课不为空的情况 
                    let  a_mony = this.toFixed_fun(this.form.chargeStandard * this.form.Class_hour) * (val/100);// 得出原优惠价
                    let b_mony = this.toFixed_fun(this.form.FreeClassHours * this.form.chargeStandard); // 得出赠课优惠价
                    this.form.Discount_mony = this.toFixed_fun(parseFloat(a_mony) + parseFloat(b_mony));
                    this.form.Actual_payment = this.toFixed_fun(this.toFixed_fun(this.form.chargeStandard * this.form.Class_hour)  - a_mony); // 实付款
                }else {
                    // 赠课为空的情况
                    this.form.Actual_payment = this.toFixed_fun(this.form.original_price * (val/100)); // 实付款
                    this.form.Discount_mony = this.form.original_price - this.form.Actual_payment // 优惠金额
                }
                 this.form.DiscountActive2 = val
              }
          }
      },
      'form.DiscountActive3' (value) {
          // 范围为0-原价值 仅允许填入数字和小数点 计算实付款 原价-优惠金额=实付款
         if (this.form.original_price == "0.00" && this.DiscountList[0].f == false){
              // 原价为空，无法计算， 提示信息
            this.$message({
                message: '请先设置收费标准并设置购买课时计算原价!',
                type: 'error',
                duration: 3000,
            });
            this.form.DiscountActive3 = ""
         } else {
             // 原价不为空的情况
             if (!/\d$/.test(value)) {
                 // 输入 非字符型 剔除最后一位，计算实际金额 和优惠金额
                if(value == "") {
                    if (this.form.FreeClassHours != "") {
                        // 赠送课时 存在的情况
                        this.form.Actual_payment = this.toFixed_fun(parseFloat(this.form.chargeStandard) * parseFloat(this.form.Class_hour))   // 实付款
                        let sj_mony = this.toFixed_fun( (parseFloat(this.form.FreeClassHours) + parseFloat(this.form.Class_hour))* parseFloat(this.form.chargeStandard)) // 原价
                        let yh_mony = this.toFixed_fun( parseFloat(sj_mony) - parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard) )// 没有算金额优惠的 实付价格
                        this.form.Discount_mony = this.toFixed_fun(parseFloat(sj_mony) - parseFloat(yh_mony)) // 优惠金额
                        return
                    }
                    this.form.Actual_payment = this.form.original_price   // 实付款 = 原价
                    this.form.Discount_mony = '0.00'// 初始化
                    this.form.DiscountActive3 = value
                  return
                }
                this.form.DiscountActive3 = value.substring(0, value.length-1);
                this.form.Actual_payment = this.toFixed_fun(this.form.original_price - value);// 实付款
                this.form.Discount_mony = this.form.original_price - this.form.Actual_payment // 优惠金额
             } else {
                //
                if (parseFloat( value ) > parseFloat( this.form.original_price)) {
                    // 大于原价
                    if (this.form.FreeClassHours != "") {
                            // 赠送课时 存在的情况
                            // 尚未做完
                             this.$message({
                                message: '优惠金额不能大于除赠课优惠的原价!',
                                type: 'error',
                                duration: 2000,
                            });
                            this.form.DiscountActive3 = this.toFixed_fun(this.form.original_price - this.toFixed_fun((this.form.FreeClassHours * this.form.chargeStandard)))
                            this.form.Discount_mony = this.toFixed_fun(this.form.FreeClassHours * this.form.chargeStandard + parseFloat(this.form.DiscountActive3)) // 优惠金额
                        return
                    }
                    this.$message({
                        message: '优惠金额不能大于原价!',
                        type: 'error',
                        duration: 2000,
                    });
                    this.form.Actual_payment = this.toFixed_fun(this.form.original_price - this.form.DiscountActive3);// 实付款
                    this.form.DiscountActive3 = this.form.original_price
                    this.form.Discount_mony = this.form.original_price// 优惠金额
                    return
                } else {
                    if (value == "") {
                        // 折扣为空 折扣恢复默认""  实付金额 = 原价 优惠金额 = 0.00
                        this.form.Actual_payment = this.form.original_price   // 实付款 = 原价
                        this.form.Discount_mony = '0.00'// 初始化
                        this.form.DiscountActive3 = value
                        if (this.form.FreeClassHours != "") {
                              // 赠送课时 存在的情况
                              this.form.Discount_mony = this.toFixed_fun(this.form.FreeClassHours * this.form.chargeStandard) // 优惠金额
                            return
                        }
                        return
                    } else if (value == this.form.original_price) {
                        // 输入值 等于 原价时
                        this.form.DiscountActive3 = this.form.original_price
                        this.form.Actual_payment = this.toFixed_fun(this.form.original_price - this.form.DiscountActive3);// 实付款
                        if (this.form.FreeClassHours != "") {
                                // 赠送课时 存在的情况
                            this.form.Discount_mony = this.toFixed_fun(this.form.FreeClassHours * this.form.chargeStandard  + parseFloat(value))// 优惠金额
                            return
                        }
                        this.form.Discount_mony = this.form.original_price// 优惠金额
                      return
                    }
                    
                    this.form.DiscountActive3 = value
                    if (this.form.FreeClassHours != "") {
                        // 赠课课时不为空的情况 
                        let sj_mony = this.toFixed_fun( (parseFloat(this.form.FreeClassHours) + parseFloat(this.form.Class_hour))* parseFloat(this.form.chargeStandard)) // 原价
                        let yh_mony = this.toFixed_fun( parseFloat(sj_mony) - parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard) )// 没有算金额优惠的 实付价格
                        this.form.Actual_payment = this.toFixed_fun( parseFloat(yh_mony) - parseFloat(this.form.DiscountActive3)); // 实付款
                        this.form.Discount_mony = this.toFixed_fun(this.form.FreeClassHours * this.form.chargeStandard  + parseFloat(value))// 优惠金额
                        return
                    }
                    this.form.Actual_payment = this.toFixed_fun(this.form.original_price - this.form.DiscountActive3); // 实付款
                    this.form.Discount_mony = this.form.original_price - this.form.Actual_payment // 优惠金额
                }
             }
         }
      },
      'form.FreeClassHours' (value) {
          // 赠送课时计算 （赠送课时 是不计价，只计优惠价）
          if (this.form.chargeStandard == "") {
            // 收费标准 为空 提示 
            this.$message({
                message: '请输入收费标准!',
                type: 'error',
                duration: 2000
            });
            this.form.FreeClassHours = ""
            return
          } else {
            // 收费标准不为空  判断 购买课时是否为空
            if (!/^[1-9]\d{0,5}$/g.test(value)){
                // 非数字
                if (value == '') {
                    // 赠课课时为空时
                    if (this.form.DiscountActive2 == "" && this.form.DiscountActive3 == "") {
                        // 优惠方案 暂不优惠的情况 直接计算优惠价 和 原价 （实付价格不用计算）
                        this.form.Discount_mony = '0.00'// 优惠价
                        this.form.original_price = this.toFixed_fun( parseInt(this.form.Class_hour) * parseInt(this.form.chargeStandard))// 原价
                    } else{
                        if (this.form.DiscountActive2 != "") {
                            // 存在折扣优惠的情况 计算原价 以及 优惠价

                            this.form.Actual_payment = this.toFixed_fun((parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard)) * (this.form.DiscountActive2/100)); // 实付款

                            this.form.Discount_mony = this.toFixed_fun((parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard)) - parseFloat(this.form.Actual_payment)) // 优惠金额
                            this.form.original_price= this.toFixed_fun(parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard))// 原价

                        } else if(this.form.DiscountActive3 != "") {
                            // 存在金额优惠的情况
                            this.form.Actual_payment = this.toFixed_fun(parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard) - parseFloat(this.form.DiscountActive3)); // 实付款
                            this.form.Discount_mony = parseFloat(this.form.DiscountActive3 + ".00" )// 优惠金额
                            this.form.original_price= this.toFixed_fun( parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard))// 原价
                        }
                    }
                }
                this.form.FreeClassHours = value.substring(0, value.length-1);
            } else {
                // 真
                if (this.form.DiscountActive2 == "" && this.form.DiscountActive3 == "") {
                    // 优惠方案 暂不优惠的情况 直接计算优惠价 和 原价 （实付价格不用计算）
                    this.form.Discount_mony = this.toFixed_fun(value * this.form.chargeStandard) // 优惠价
                    this.form.original_price = this.toFixed_fun( ( parseFloat(value) + parseFloat(this.form.Class_hour)) * this.form.chargeStandard) // 原价
                } else {
                    // 判断 - 优惠方案
                    if (this.form.DiscountActive2 != "") {
                        // 存在折扣优惠的情况 计算原价 以及 优惠价
                        let sj_mony = this.toFixed_fun((parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard)) * (this.form.DiscountActive2/100)) // 实际支付价格
                        let yh = this.toFixed_fun((parseFloat(this.form.Class_hour) * parseFloat(this.form.chargeStandard)) - sj_mony) // 优惠价
                        this.form.Discount_mony = this.toFixed_fun(parseFloat(yh) + value * this.form.chargeStandard ) // 实际优惠价
                        this.form.original_price = this.toFixed_fun( ( parseFloat(value) + parseFloat(this.form.Class_hour)) * this.form.chargeStandard) // 原价
                    } else if (this.form.DiscountActive3 != "") {
                        // 存在金额优惠的情况
                        this.form.Discount_mony = this.toFixed_fun(parseFloat(this.form.DiscountActive3) + parseFloat(value) * parseFloat(this.form.chargeStandard)) // 实际优惠价
                        this.form.original_price = this.toFixed_fun( ( parseFloat(value) + parseFloat(this.form.Class_hour)) * this.form.chargeStandard) // 原价
                    }
                }
            }
         }
      }
   }
}
</script>
