<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="8">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索学生" v-model='tablesThis.seatchForm.seatchS'>
                    <i class='search_icon icon-width' ></i>
                </label>
                <label class='label_search'>
                    <input type="text" placeholder="搜索教师"  v-model='tablesThis.seatchForm.seatchT'>
                    <i class='search_icon icon-width' ></i>
                </label>
                <label class='label_search'>
                    <input type="text" placeholder="搜索课程" v-model='tablesThis.seatchForm.seatchKc'>
                    <i class='search_icon icon-width' ></i>
                </label>
                <label class='out_of_school'>
                    <input type="checkbox" :checked='tablesThis.seatchForm.status==1?false:true'>
                    <span>已退学</span>
                </label>
            </div>
        </el-col>
        <el-col :span="16">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                 <Lbutton class='hot_btn' @click.native="addStudentClick">
                    <i class='icon-width jia_icon'></i>
                    添加学生
                </Lbutton>
                <Lbutton class='hot_btn '  @click.native="exportClick(0, $event)">
                    <i class='icon-width import_icon'></i>
                    <!-- <input type="file" ref='files'> -->
                    导入学生
                </Lbutton>
                <Lbutton class='hot_btn '  @click.native="exportClick(0, $event)">
                    <i class='icon-width import_icon'></i>
                    <!-- <input type="file" ref='files'> -->
                    导入购课
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="exportClick(1, $event)">
                    <i class='icon-width download_icon'></i>
                    课程列表
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="exportClick(1, $event)">
                    <i class='icon-width download_icon'></i>
                    学生信息
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="exportClick(1, $event)">
                    <i class='icon-width download_icon'></i>
                    充值记录
                </Lbutton>
                 <Lbutton class='hot_btn' @click.native="refreshData">
                    <i class='icon-width refresh_icon'></i>
                    刷新数据
                </Lbutton>
            </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
export default {
  name: 'topBtn',
  methods: {
    addStudentClick () {
      this.tablesThis.addStudentShow = !this.tablesThis.addStudentShow
    }
  },
  inject: ['tablesThis'],
  components: {
    Lbutton: Lbutton
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(140), vw(40));
    margin-right: vw(20);
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: 12px;
    padding-right: 12px;
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .out_of_school{
      display: flex;
      align-items: center;
      color: #333333;
      font-size: vw(16);
      input[type=checkbox]{
            @include wh(vw(14),vw(14));
            width: 14px;
            height: 14px;
            background: #fff;
            -webkit-appearance:none;
            border: 1px solid #c9c9c9;
            border-radius: 2px;
            outline: none;
            margin-right: vw(9);
        }
        input[type=checkbox]:checked{
            background-size: 100%;
            background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
        }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    @include wh(vw(120), vw(40));
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
}
</style>
