<template>
  <el-tabs v-model="activeName" @tab-click="handleClick" v-cloak class="elTabPane">
    <el-tab-pane v-for="item in tabList" :label="item.name" :name="item.id" :key='item.id'>
      <classification :name="'tab'+item.id" v-if='item.value==1 && activeName==item.id'></classification>
      <salesman :name="'tab'+item.id" v-if='item.value==2 && activeName==item.id'></salesman>
      <commodity :name="'tab'+item.id" v-if='item.value==3 && activeName==item.id'></commodity>
      <leaserecord :name="'tab'+item.id" v-if='item.value==5 && activeName==item.id'></leaserecord>
      <salesRecords :name="'tab'+item.id" v-if='item.value==4 && activeName==item.id'></salesRecords>
      <warehousing :name="'tab'+item.id" v-if='item.value==6 && activeName==item.id'></warehousing>
      <outbound :name="'tab'+item.id" v-if='item.value==7 && activeName==item.id'></outbound>
      <statiscal :name="'tab'+item.id" v-if='item.value==8 && activeName==item.id'></statiscal>
    </el-tab-pane>
  </el-tabs>
</template>
<script>
import classification from './classification/classification.vue'
import salesman from './salesman/salesman.vue'
import commodity from './commodity/commodity.vue'
import salesRecords from './salesRecords/sales_records.vue'
import statiscal from './sales_statistical/statistical'
import outbound from './outbound_record/outbound_record'
import warehousing from './Warehousing_record/Warehousing_record'
import leaserecord  from './lease_record/lease_record'
import {mapState} from 'vuex'
import {setseSsionStorage,getseSsionStorage} from '../../../cookie/cookie'
export default {
  name: 'inventory',
  data() {
    return {
      activeName: '',
      tabList:[]
    }
  },
  inject: ['mainThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created () {
    this.getGulpactivenum();
    this.rolefun()
  },
  components: {
    classification: classification,
    salesman:salesman,
    commodity:commodity,
    statiscal: statiscal,
    outbound: outbound,
    leaserecord : leaserecord ,
    salesRecords:salesRecords,
    warehousing: warehousing
  },
  methods: {
    rolefun () {
      const _this = this
      _this.userLoginInfo.rolelist.forEach((data)=>{
          if(data.access_id == this.mainThis.num){
            // 日常管理权限下 - 第二级分类权限遍历
            _this.tworolefun(data.pidinfo)
          }
      })
    },
    tworolefun (arr){
      // 第二级分类权限遍历
      const _this = this
      if(getseSsionStorage('gulpactivenum')==null){
        this.activeName = JSON.stringify(arr[0].access_id)
      }
      arr.forEach((data)=>{
        let roldactive = ""
        switch(data.access_name){
          case "分类管理":
            roldactive = '1'
            break;
          case "销售员管理":
            roldactive = '2'
            break;
          case "商品管理":
            roldactive = '3'
            break;
          case "租凭记录":
            roldactive = '5'
            break;
          case "销售记录":
            roldactive = '4'
            break;
          case "入库记录":
            roldactive = '6'
            break;
          case "出库记录":
            roldactive = '7'
            break;
          case "销售统计":
            roldactive = '8'
            break;                      
          default:{
            roldactive = ""
          }
        }
        _this.tabList.push({name:data.access_name,id: JSON.stringify(data.access_id), value: roldactive})
      })
    },    
    handleClick(tab, event) {
      this.activeName = JSON.stringify(parseInt(tab.$vnode.key));
      setseSsionStorage('gulpactivenum', parseInt(tab.$vnode.key));
    },
    getGulpactivenum(){
      let cookie = getseSsionStorage('gulpactivenum');
      if(cookie!=null) {
        this.activeName =  cookie
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
@import '../../../style/media';
@import '../../../style/var';

.erp_div{
  @include wh(100%, 100%);
  // background-color: #ffffff;
  // box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.elTabPane /deep/ .el-tabs__content{
  padding:0 3.125%;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__nav{
  width:100%;
  overflow:hidden;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__item{
  padding:0;
  padding:0 1.6%;
  height:60px;
  line-height:60px;
  font-size:16px;
  color: #333333;
  font-family: SourceHanSansCN-Bold;
}
.elTabPane /deep/ .el-tabs__item.is-active{
  color: #2769fb;
  @include fontColor-defaul($fn-color)
  
}
.elTabPane /deep/ .el-tabs__active-bar{
  height:3px;
  width:20px!important;
  margin:0 1.56%;
  margin-left:1.66%;
  box-sizing:border-box;
  @include bg-defaul($bg-color)
}
.elTabPane /deep/ .el-tabs__nav-scroll{
  margin-left:3.125%;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__content{
  
}
.elTabPane /deep/ .el-tabs__content .el-tab-pane{
  height:auto;
}
.elTabPane  /deep/ .el-tabs__content{

  .el-tab-pane .table-gulp .div_gulptable .el-table .el-table__header-wrapper table .has-gutter tr{
    @include bgColor-defaul($bgColor-color);
    th{
      @include bgColor-defaul($bgColor-color);
      @include borderColor-defaul($border-color)
    }
  }

  .el-tab-pane .table-gulp .div_gulptable .el-table .el-table__body-wrapper .el-table__body tbody tr{
    @include bgColor-defaul($bgColor-color);
    td {
      @include borderColor-defaul($border-color)
    }
  }

  @include bgColor-defaul($bgColor-color);
}
.elTabPane /deep/ .el-tabs__header{
  margin:0;
  @include bgColor-defaul($bgColor-color);
}
</style>

