<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="8">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索教师" v-model="inputVal" @change="change">
                    <i class='search_icon icon-width'></i>
                </label>
                <label class='label_elect selectTwo'>
                    <span>资历： </span>
                    <el-select v-model="seniority1" placeholder="全部" @change="change()">
                    	<el-option value="" label="全部"></el-option>
                        <el-option v-for="item in seniority" :value="item.s_id" :key='item.s_id' :label="item.s_name"></el-option>
                        <!-- <el-option value="1" label="普通"></el-option>
                        <el-option value="2" label="高级"></el-option>
                        <el-option value="3" label="专家"></el-option> -->
                    </el-select>
                </label>
                <label class='label_elect selectTwo'>
                  <span>状态： </span>
                  <el-select v-model="status" placeholder="全部" @change="change()">
                    <el-option value="" label="全部"></el-option>
                    <el-option value="1" label="在职中"></el-option>
                    <el-option value="2" label="已离职"></el-option>
                  </el-select>
                </label>                
            </div>
        </el-col>
        <el-col :span="16">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">
            	<Lbutton class='hot_btn' @click.native='refresh()'>
            		<i class='icon-width refresh_icon'></i>
            		刷新数据
            	</Lbutton>
            	<Lbutton class='hot_btn' @click.native="upload()">
            		<i class='icon-width export_icon'></i>
            		导出Excel
            	</Lbutton>
              <Lbutton class='hot_btn' @click.native="download()">
                <i class='icon-width download_icon'></i>
                下载模板
              </Lbutton> 
              <Lbutton class='hot_btn' @click.native="importTeacher()">
                <i class='icon-width import_icon'></i>
                导入Excel
              </Lbutton>                                                                   	          	
            	<Lbutton class='hot_btn' @click.native="showAddBox()">
            		<i class='icon-width jia_icon'></i>
            		新增教师
            	</Lbutton>
              <Lbutton class='hot_btn' @click.native="showSen()">
                <i class='icon-width icon_manage'></i>
                资历管理
              </Lbutton>               
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="showAdd" :show="showAdd" :title="title" :orgid="orgid" :manager="manager" @hideModal="hideModal" @submit="submit">	
      </dialogComponent>
      <modal-seniority v-if="showSeniority" :title="titleSeniority" :orgid="orgid" @senioritieList="senioritieList" :manager="manager" @hideModal="hideModalSeniority" @submit="submitSeniority"> 
      </modal-seniority>
      <importTeacher v-if="showLoad" @hideModal="hideLoadModal" @submit="submitLoad">
      </importTeacher>        
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/addTeacher'
import modalSeniority from '../alert-box/seniority'
import importTeacher from '../alert-box/importTeacher'
import Imageport from '../../../../../config/ImagePz.js'
import {senioritieList} from '../../../../../getserver/getAxios.js'
import {mapState} from 'vuex'
export default {
    name: 'tabTops',
	props: {
        orgid: {
            type: String,
            default: false
        },
        manager: {
        	type: String,
        	default: false
        },        
    },    
    data () {
    	return {
        value: '',
        seniority1:'',
        seniority:[],//资历
        title: '新增教师',
        titleSeniority:'资历管理',
        showAdd: false,
        inputVal:'',
        status:'',
        showLoad: false,
        showSeniority:false,
        Imageport
      }
    },
    computed:{
    	...mapState(['userLoginInfo']),
    },
    created(){
      this.senioritieList()
    },    
    methods: {
      download(){
        window.location.href = this.Imageport+"public/upload/file/teacher.xlsx"
      },
      upload(){
        window.location.href = this.Imageport+"index/excel/teacher_ept?orgid=" + this.userLoginInfo.orgid + "&se_id=" + this.seniority1 + "&status=" + this.value
      },
      importTeacher(){
        this.showLoad = true
      },            
      showAddBox(){
      	this.showAdd = true
      },
      showSen(){
        this.showSeniority = true
      },                 
      hideModal() {
        // 取消弹窗回调
        this.$emit('hideModal');
        this.showAdd = false;
      },
      hideLoadModal() {      
        // 取消弹窗回调
        this.showLoad = false
      },       
      hideModalSeniority() {
        // 取消弹窗回调
        this.showSeniority = false;
      }, 
      submitLoad() {
        // 确认弹窗回调   
        this.$emit('teacherList',1)
        this.showLoad = false
      },            
      refresh(){
      	//刷新
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.$message({
         type: 'success',
         message: '刷新成功'
       });      		   	
      },      
      async senioritieList () {
        let resDate = await senioritieList(this.orgid)
        if (resDate.data.status) {
          this.seniority = resDate.data.data
        }
      },       
      submit() {
        // 确认弹窗回调
        // this.$emit('getclassRoomLists', 1)      
        this.showAdd = false;
        this.$parent.last_page = 0
        this.$parent.paging_father(1)        
        // this.$emit('teacherList',1)
      },
      submitSeniority() {     
        this.showSeniority = false;
      },      
      change() {
        // console.log(this.$emit())
        this.$parent.name = this.inputVal
        this.$parent.se_id = this.seniority1
        this.$parent.status = this.status        
        this.$parent.last_page = 0
        this.$parent.paging_father(1)        
      	//this.$emit('searchsetMeal',name,1)
      },
      selectFn1(e) {
        this.$emit('selectTeacher1',e,1)
      },
      selectFn2(e) {
        this.$emit('selectTeacher2',e,1)
      }         
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
      modalSeniority:modalSeniority,
      importTeacher:importTeacher,
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(26), vw(26));
    cursor: pointer
  }   
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:block;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: vw(6);
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, vw(40));
  }
  .selectTwo{
    @include wh(vw(220), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: vw(8);
        height: 100%;
        .el-icon-arrow-up{
          display: flex;
          align-items: center;
        }
        /deep/ .el-input{
          height: 100%;
           .el-input__inner {
             height: 100%;
             border-radius:6px
           }
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding: 0 0.5vw;
    .icon-width{
        margin-right: vw(10);
    }
  }
  .selectTwo /deep/ .el-input__inner{
  	font-size:vw(16);
  	padding:0 vw(10)
  }
}
@media screen and (max-width: 1380px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      width: 40%!important;
    }
    .selectone1{
    	width:40%;
    }
    .el-col-12:last-child{
      width: 60%!important;
      margin-top: 0px;
    }
  }
  .selectTwo /deep/ .el-input__inner{
  	font-size:vw1(16);
  	padding:0 vw1(10)
  }
  ._tabTop{
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }    
  }
  .selectone{
    @include wh(20.52%, vw1(40));
  }
  .selectTwo{
    @include wh(vw1(220), vw1(40));
  }     
}
</style>
