<template>

  <el-amap ref="map" :vid="vid" :center="center" :zoom="zoom" class="amap-demo"  v-if='vid!="completegps"'>
    <el-amap-info-window :position="window.position" :content="window.content" ></el-amap-info-window>
  </el-amap>

  <el-amap ref="map" :vid="vid" :center="center" :zoom="zoom" class="amap-demo" :plugin="plugin" v-else>
    <el-amap-info-window :position="window.position" :content="window.content" ></el-amap-info-window>
  </el-amap>
  
</template>
<script>
import Vue from 'vue'
import VueAMap from 'vue-amap'
Vue.use(VueAMap)
VueAMap.initAMapApiLoader({
  key: '7d99a804cc5cb82a41c5c2947a558a58',
  plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType' , 'AMap.Geolocation', 'AMap.Geocoder','OverView'],
  uiVersion: '1.0', // ui库版本，不配置不加载,
  v: '1.4.4'
})
export default {
  name: 'insGps',
  data () {
    return {
      zoom: 12,
      plugin: [{
        pName: 'OverView',
        isOpen: true,
        events: {
          init(instance) {
            console.log(instance);
          }
        }
      },{
        pName: 'ToolBar',
        events: {
          init(instance) {
            console.log(instance);
          }
        }
      }]
    }
  },
  props: {
    vid: String,
    center: Array,
    window: Object
  }
}
</script>
