<template>
    <div class="modal-bg" v-if="showWhich" v-show="show" v-dialogDrag>
      <!-- @mousemove="modalMove" @mouseup="cancelMove" -->
        <div class="modal-container el-dialog" id="delectModal">
            <div class="modal-header el-dialog__header" @mousedown="setStartingPoint">
                {{topTitle}}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <p v-if="showWord"><i :class="iconClass"></i>{{title}}</p>
              <el-input type="password" v-if="showPassword" v-model="password" min="6" placeholder="" autofocus></el-input>
            </div>
            <div class="modal-footer alertFooter">
                <el-button round type="primary" @click="submit">确认</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  export default {
    name: 'modal',
    props: {
        title_props: String,
        show: {
          type: Boolean,
          default: false
        },        
        id:{
          type: Number,
          default: false
        }       
    },
    computed:{
      showWhich: function(){
        // console.log(this.$parent.delectType)
        if(this.$parent.delectType && this.$parent.delectType == '2'){
          this.showWord = true
          this.showPassword = false
          this.title = "你确认要顺延排课吗？"
          this.topTitle = "警告"
          this.iconClass = "el-icon-question"
        }
        else if(this.$parent.delectType && this.$parent.delectType == '3'){
          this.showPassword = true
          this.showWord = false
          this.$parent.password = this.password
          this.topTitle = "请输入登录密码取消消课"
        }
        else if(this.$parent.delectType && this.$parent.delectType == '4'){
          this.showWord = true
          this.showPassword = false
          this.title = "确定还原吗？"
          this.topTitle = "警告"
          this.iconClass = "el-icon-question"
        }
        else if(this.$parent.delectType && this.$parent.delectType == '5'){
          this.showWord = true
          this.showPassword = false
          this.title = "您确定要处理为离职吗？"
          this.topTitle = "警告"
        }
        else if(this.$parent.delectType && this.$parent.delectType == '6'){
          this.showWord = true
          this.showPassword = false
          this.title = "您确定要处理为复职吗？"
          this.topTitle = "警告"
        }                                
        else{
          this.showWord = true
          this.showPassword = false
          this.title = "执行删除后将无法恢复，确定继续吗？"
          this.topTitle = "警告"
          this.iconClass = "el-icon-warning"        
        }
        return true        
      }
    },
    data() {
        return {
            topTitle:'警告',
            title: '执行删除后将无法恢复，确定继续吗？',
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            iconClass:'el-icon-warning',
            password:'',
            showPassword:false,
            showWord:true        
        }
    },
    created(){
      
    },
    mounted() {
        this.node = document.querySelector('#delectModal')
    },
    methods: {
        hideModal() {
          this.password = ''
          this.$parent.show = false
          this.node.style.left = "0"
          this.node.style.top = "0"
          this.$emit('hideDeleteModal')
        },
        submit(){
          // this.$parent.show = false
          this.$emit('submitDelete',this.id)
        },
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        }, 
    },
    watch: {
      "title_props"(val) {
        if (val != null && val != "") {
          this.title = val
        } else {
          this.title = '执行删除后将无法恢复，确定继续吗？'
        }
      },  
    },
  }
</script>
<style lang="scss" scoped>
@import '../../style/mixin.scss';
@media screen and(min-width: 1380px){
  .modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10000;
    display: flex;
    align-items: center;
  }
  .modal-container {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    // position: fixed;
    // top: 50%;
    // left: 50%;
    // transform: translate(-50%,-50%);
    width:vw(520);
    height:vw(320);
  }
  .modal-header {
    height: vw(60);
    // background: #2769fb;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: left;
    cursor: move;
    box-sizing:border-box;
    padding:0 vw(30);
    font-size:vw(20);
  }
  .el-icon-close{
    position: absolute;
    right: vw(30);
    cursor: pointer;
    top:vw(22);
    font-size: vw(20);
  }  
  .modal-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: vw(80);
    border-top: 1px solid #ddd;
  }
  .modal-footer button {
    width: vw(100);
    margin-right:vw(30);
  }
  .modal-main {
    padding: vw(15) vw(30);
    height:vw(180);
    box-sizing:border-box;
    display:flex;
    align-items: center;
    justify-content: center;    
  }
  .modal-main p{
    font-size: vw(20);
    color:#333;
    display:flex;
    align-items: center;
  }
  .el-button.is-round{
    border-radius:6px;
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
    line-height:vw(40)
  }
  .el-button--primary{
    // background-color: #2769fb!important;
    color:#fff!important;
  }
  .el-button{
    font-size:vw(16);
  }
  .el-icon-warning{
    font-size:vw(40);
    color:#ff5d47;
    margin-right:vw(10)
  }
  .el-icon-question{
    font-size:vw(40);
    @include fontColor-defaul($fontColor);
    margin-right:vw(10)
  }  
}
