<template>
    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1">
            <div class="modal-header" @mousedown="setStartingPoint">
                {{ title }}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form ref="form" :model="form" :rules="rules" label-width="16%" class="fromBox">              
                <el-form-item prop="name" label="姓 名" >
                  <el-input v-model.trim="form.name" placeholder="请输入销售员姓名" maxlength="20" ref="nameInput"></el-input>
                </el-form-item>
                <el-form-item prop="phone" label="手 机 号" >
                  <el-input v-model.trim="form.phone" placeholder="请输入手机号" maxlength="11" ref="phoneInput" @keyup.native="change_phone()"></el-input>
                </el-form-item>                                                              
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import {mans_add} from '../../../../../getserver/getAxios.js'
  import {validatePhone,checkText} from '../../../../../script/validate.js' 
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        }        
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            cateList:[],
            form: {
              name: '',
              phone:'',
            },
            rules: {
              name: [
              { required: true, message: '请输入销售员姓名', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              ],              
              phone: [
              { required: true, message: '请输入手机号码', trigger: 'blur'} ,
              { validator:validatePhone, trigger: 'blur'}
              ],              
            },          
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    created(){
    },
    methods: {
        hideModal() {         
          this.$emit('hideModal')
        },
        submit() {
          var a = this.form.name;
          var c = this.status;
          if(a == ''){
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return
          }
          else if(a.length < 2){
            this.$message.error('销售员姓名长度不正确');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          }; 
          let reg =/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
          if(!this.form.phone){
            this.$message.error('请输入手机号码');
            this.$refs.phoneInput.$el.children[0].focus();
            this.$refs.phoneInput.$el.children[0].blur();
            return;
          }
          else if((!reg.test(this.form.phone)) && this.form.phone != ''){
            this.$message.error('请输入正确的电话号码')           
            return                  
          };                    
          this.mans_add ()
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },             
       async mans_add () {
        let resDate = await mans_add(this.orgid,this.form.name,this.form.phone,1)
        if (resDate.data.status) {
          this.$message({
            type: 'success',
            message: resDate.data.sinfo
          });
          this.$emit('submit')
        }
        console.log(resDate.data)
      },
      change_phone(){
        var actualPrice = this.form.phone;          
        if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];                          
          }            
          this.form.phone = actualPrice
        },                                
    }
  }
</script>
