<template>
    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container">
            <div class="modal-header" @mousedown="setStartingPoint">
                导入Excel
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form ref="form" :model="form" :rules="rules" label-width="110px" class="fromBox">
                <!-- :http-request="upLoad" -->
              	<el-upload
	              	class="upload-demo"
	              	ref="upload"
	              	action=""
	              	:on-change="handleChange"
	              	:before-upload="beforeAvatarUpload"
	              	:limit="1"
	              	:on-exceed="handleExceed"
	              	:auto-upload="false">
	              	<el-input readonly v-model="name" placeholder="文件名" class="upLoadBox"></el-input>
	              	<el-button size="small" class="changeFile">选取文件</el-button>
                </el-upload>                                           
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="upLoad">上传</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import axios from 'axios';
  import {room_ipt} from '../../../../../getserver/getAxios.js' 
  import {mapState} from 'vuex'
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },       
    },
    computed:{
      ...mapState(['userLoginInfo']),
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            form: {
              excel: '',
              uid: '',
              orgid:'',
            },
            rules: {
              excel: [
              { required: true, message: '请选择文件', trigger: 'blur' }
              ],              
            },
            name:'',
            file:''           
        }
    },
    created(){
      
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    methods: {
        hideModal() {
            this.$emit('hideModal')
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },
		//导入execl
        async upLoad () {
	         let param = new FormData(); //创建form对象
	         param.append('excel',this.file);//通过append向form对象添加数据
	         param.append('uid',this.userLoginInfo.id);//添加form表单中其他数据
	         param.append('orgid',this.userLoginInfo.orgid);//添加form表单中其他数据
	         let config = {
	         	headers:{'Content-Type':'multipart/form-data'}	         	
	         };  //添加请求头
	         //axios.post(f.action,param,config)//上传
           let response = await room_ipt(param,config)
           if (response.data.status) {
            this.$message({
              type: 'success',
              message: response.data.sinfo
            });
            this.submitUpload()
           }           
	         // .then(response=>{
          //   console.log(response)
	         // 	this.$message({
          //     type: 'success',
          //     message: response.data.sinfo
          //   });
          //   this.submitUpload()
	         // }) 
	  	},
	  	handleChange(file){
	  		this.name = file.name;
        this.file = file.raw;
	  		console.log(file.raw)
	  	},        
	  	submitUpload() {
        this.$emit('submit')
	  		// this.$refs.upload.submit();
	  	},
	  	beforeAvatarUpload(file) {
	  		console.log(file.type)
	  		const isXls = file.type === 'application/vnd.ms-excel';
	  		const isXls1 = file.type === 'application/wps-office.xls';
	  		const isLt2M = file.size / 1024 / 1024 < 2;

	  		if (!isXls && !isXls1) {
	  			this.$message.error('上传文件只能是 excel 文件!');
	  		}	  		
	  		if (!isLt2M) {
	  			this.$message.error('上传文件大小不能超过 2MB!');
	  		}
	  		return isXls && isLt2M;
	  	},
	  	handleExceed(files, fileList) {
	  		fileList = files
	  		this.name = fileList[0].name;
	  		console.log(fileList)
	  		//this.$message.warning('当前只能上传 1 个文件');
	  	},     
    }
  }
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@media screen and(min-width: 1380px){
  .modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10;
  }
  .modal-container {
    background: #fff;
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    width:vw(640);
    height:vw(420);
  }
  .modal-header {
    height: vw(60);
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: left;
    cursor: move;
    box-sizing:border-box;
    padding:0 vw(30);
    font-size:vw(16);
  }
  .el-icon-close{
    position: absolute;
    right: vw(30);
    cursor: pointer;
    top:vw(24)
  }  
  .modal-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: vw(80);
    border-top: 1px solid #ddd;
  }
  .modal-footer button {
    margin-right:vw(30);
  }
  .modal-main {
    padding: vw(15) vw(30);
    height:vw(280);
    box-sizing:border-box;
  }
  .el-button.is-round{
    border-radius:6px;
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
  }
  .el-button--primary{
    color:#fff!important;
  }
  .el-button{
    font-size:vw(16);
  }
  .upLoadBox{
  	width:vw(390);
  	height:vw(40);
  	border-radius: 6px;
	border: solid 1px #e7eaec;
	box-sizing:border-box
  }
  .modal-container /deep/ .el-upload-list{
  	display:none
  }
  .upLoadBox /deep/ .el-input__inner{
  	width:vw(390);
  	height:vw(40);  	
  }
  .changeFile{
  	width: vw(160);
	height: vw(40);
	background-color: #ffffff;
	border-radius: 6px;
	border: solid 1px #47b6ff;
	color:#47b6ff;
	font-size: vw(16);
	margin-left:vw(30);
	box-sizing:border-box
  } 
}
