<template>
    <!-- 热门课程 -->
    <section class='_hotKc' v-dialogDrag>
        <div class='_hotContenter el-dialog'>
            <header class='_hotHerader el-dialog__header'>
                <span>设置热门课程</span>
                <i class='whitedelete_icon icon_width' @click.stop='close'></i>
            </header>
            <main class='_hotMain'>
                <label class='check_list' v-for='item in activedefuleArr' :key='item.cur_id' @change='chenckedClick(item.cur_id)'>
                    <input type="checkbox" :checked='item.active==1?true:false' >
                    <span>{{item.name}}</span>
                </label>
            </main>
            <footer class='_hotFooter'>
                <button class='_hotSave' @click.stop='seaveClick'>保存</button>
                <button class='_hotQx' @click.stop='close'>取消</button>
            </footer>
        </div>
    </section>
</template>
<script>
import  {savehotkc} from '../../../../../getserver/server'
export default {
  name: 'hotkc',
  data () {
    return {
      activedefuleArr: []
    }
  },
  created(){
    this.that.tableAllDate.forEach((data)=>{
      this.activedefuleArr.push({cur_id: data.cur_id, active: data.popular, name: data.cur_name})
    })
  },
  mounted () {
  },
  inject: ['that'],
  methods: {
    close () {
      this.that.hotKcshow = !this.that.hotKcshow
    },
    seaveClick(){
        if (this.activedefuleArr.length == 0) {
          this.$message({
            message: "您暂时没有存在课程，请先新增课程！！",
            type: 'error',
            duration: 2000,
          });
          return
        }
        const _this = this
        let activeArr = []
        this.activedefuleArr.forEach((item)=>{
          if(item.active==1){
            activeArr.push(item.cur_id)
          }
        })
        if (activeArr.length==0) {
          this.$message({
            message: "您没有选择课程，请先选择课程！！",
            type: 'error',
            duration: 2000,
          });
          return
        }
        savehotkc(activeArr).then((res)=>{
            if (res.data.status) {
                // 设置成功
                _this.$message({
                    message: res.data.sinfo,
                    type: 'success',
                    duration: 2000,
                });
                _this.that.getKclists()
                _this.that.getkcAlllist()
              this.close()
            } else {
              _this.$message.error(res.data.error_msg);
            }
        })
    },
    chenckedClick (id) {
      this.activedefuleArr.forEach((data,i)=>{
        if (data.cur_id == id) {
          data.active==1?data.active=2:data.active=1
          return
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
._hotKc{
    position: fixed;
    @include wh(100%,100%);
    background: rgba(0,0,0,0.3);
    top: 0;
    left: 0;
    z-index: 100;
    display: flex;
    justify-content: center;
    align-items: center;
    ._hotContenter{
        @include wh(33.33%, vw(660));
        background: #fff;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        font-size: 16px;
        ._hotHerader{
            @include wh(100%, 8.3%);

            @include bg-defaul($bg-color);

            border-top-right-radius: 8px;
            border-top-left-radius: 8px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 0 4.6%;
            box-sizing: border-box;
            color: #fff;
            .icon_width{
                @include wh(20px,20px);
                display: inline-block;
                cursor: pointer;
            }
        }
        ._hotMain{
          flex: 1;
          padding: 3.1% 4.6%;
          box-sizing: border-box;
          overflow: auto;
          .check_list{
            @include wh(100%, 6.9%);
            display: flex;
            font-size: 16px;
            align-items: center;
            -webkit-user-select:none; 
            -moz-user-select:none; 
            -ms-user-select:none; 
            user-select:none;
            margin-bottom: 2.5%;
            input[type=checkbox]{
              margin-right: 2.3%;
              @include wh(14px,14px);
              background: #fff;
              -webkit-appearance:none;
              border: 1px solid #c9c9c9;
              border-radius: 2px;
              outline: none;
              margin-right: 9px;
            }
            input[type=checkbox]:checked{
                background-size: 100%;
                background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
            }
          }
        }
        ._hotFooter{
          @include wh(100%, 11.11%);
          display: flex;
          align-items: center;
          justify-content: flex-end;
          padding: 0 4.6%;
          box-sizing: border-box;
          button{
            @include wh(18%, 50%);
            border-radius: 6px;
          }
          ._hotSave{
            @include bg-defaul($bg-color);
            // background:#2769fb;
            color: #fff;
            border: 1px solid;
            @include  borderColor-defaul($bg-color);
            margin-right: 4.6%;
          }
          ._hotQx{
            background:#e7eaec;
            color: #e7eaec;
            border: solid 1px #e7eaec;
            color: #999999;
          }
        }
    }
}
</style>
