<template>
    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
      <div class="modal-container modal-container1 timeModal">
        <div class="modal-header" @mousedown="setStartingPoint">
          {{ title }}
          <i class="el-icon-close" round @click="hideModal"></i>
        </div>
        <div class="modal-main">
            <erp-tabtop @scheduleList="scheduleList" @changeStartTime="changeStartTime" @changeEndTime="changeEndTime" :orgid="orgid" :that='data1'></erp-tabtop>
            <section class='div_gulptable'>
                <el-table
                    ref="table"
                    :data="tableData"
                    border
                    stripe
                    resizable=false
                    style="margin-bottom: 1%">
                    <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='13.846%'
                    label="上课日期">
                    <template slot-scope="scope"><span>{{scope.row.cur_time | date}}</span></template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='10.769%'
                    label="上课时间">
                    <template slot-scope="scope">
                        <span>{{scope.row.cur_time | time}}</span>
                    </template>                    
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='15.385%'
                    label="课程名称">
                    <template slot-scope="scope">
                        <span>{{scope.row.cur_name}}</span>
                    </template>                    
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='9.23%'
                    label="学生">
                    <template slot-scope="scope">
                        <span>{{scope.row.stu_name}}</span>
                    </template>                    
                    </el-table-column>
                    <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='10.769%'
                    label="教室">
                    <template slot-scope="scope">
                        <span>{{scope.row.room_name}}</span>
                    </template>                                       
                    </el-table-column>
                    <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='9.23%'
                    label="状态">
                    <template slot-scope="scope">
                        <span v-if="scope.row.status == 1">未消课</span>
                        <span v-else-if="scope.row.status == 2">已消课</span>
                        <span v-else>请假</span>
                    </template>                    
                    </el-table-column>                     
                    <el-table-column
                    label="更多操作"
                    min-width='30.769%'>
                    <template slot-scope="scope" class='erp-call'>
                        <div class="grid-content bg-purple">
                            <el-button type="text" size="small" class='erp-button-icon' v-if="scope.row.status != 2" @click="showcl(scope.row, scope.$index)">
                                调课
                            </el-button>
                            <el-button type="text" size="small" class='erp-button-icon' v-if="scope.row.status != 2" @click="showPostpone(scope.row.sc_id)">
                                顺延
                            </el-button>                            
                            <el-button type="text" size="small" class='erp-button-icon' v-if="scope.row.status == 1 && scope.row.cur_type == 1" @click="showLeaveOn(scope.row.sc_id)">
                                请假
                            </el-button>                            
                            <el-button type="text" size="small" class='erp-button-icon' v-if="scope.row.status != 2" @click="showDelete(scope.row.sc_id)">
                                删除
                            </el-button>
                            <el-button type="text" size="small" class='erp-button-icon' v-if="scope.row.status == 3" @click="showReduction(scope.row.sc_id)">
                                还原
                            </el-button>                            
                            <el-button type="text" size="small" class='erp-button-icon' v-if="scope.row.status == 2" @click="showCancel(scope.row.sc_id)">
                                取消消课
                            </el-button>                                                                                                                                                                        
                        </div>
                    </template>
                    </el-table-column>
                </el-table>
                <erp-paging :counts="counts" :page_num="page_num" :page_size="page_size" @pagings='paging_father' v-if='last_page>1'></erp-paging>
            </section>        
        </div>
        <div class="modal-footer alertFooter">
            <el-button type="primary" round @click="submit()">完成</el-button>
            <el-button round @click="hideModal">取消</el-button> 
        </div>
      </div>
      <class-adjustment v-if="showclass" :that='data1'></class-adjustment>
      <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
      <leave v-if="showLeave" :that="data1"></leave>
    </div>
</template> 
<script>
import $ from '../../../../../script/jquery-vendor.js'
import erpTabtop from '../gulp-teacher-top/timeTable-top.vue'
import classAdjustment from '../alert-box/adjustment.vue'
import leave from '../alert-box/leave.vue'
import erpPaging from '../../../../paging/pagingShort.vue'
// import editTeacher from './/alert-box/editTeacher.vue'
// import revisionCourses from './/alert-box/revision.vue'
import {schedule,delectClasses,schedulePostpone,scheduleRecover,setLeave,reduction} from '../../../../../getserver/getAxios' 
import {mapState} from 'vuex'
// import tableLayer from '../../../../script/tableLayer.js'
let that
export default {
	// ...tableLayer,
  name: 'modal',
  props: {
    id:{
        type: Number,
        default: false        
    },
    title:{
        type: String,
        default: false        
    }    
  },
  data () {
    return {
      x: 0,
      y: 0,
      node: null,
      isCanMove: false,
      tableData: [],
      show: false,
      showclass:false,
      showLeave:false,
      orgid:'',     
      t_id:'',
      page:'1',
      type:'1',
      courseId:'',
      startTime:'',
      endTime:'',
      c_id:'',//当前点击的课程id
      room_id:'',//当前点击的教室id
      kcName:'',//当前点击的课程名称
      sc_id:'',//当前点击的课表id
      cur_time:'',//当前点击的课表上课时间
      data1:{},
      password:'',
      deleteId:null,
      showDe:false,
      remarks:'',
      level:1,
      delectType:'0',//0是删除，2是顺延,3是取消消课,4是还原
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数
      page_size:'6'    
    }
  },
  beforeCreate: function () {
   that = this;
  },  
  filters: {//过滤器
　　 date: function (value) {   
     var d = new Date(value * 1000);    //根据时间戳生成的时间对象
     var date = that.appendzero(d.getFullYear()) + "-" + 
     that.appendzero(d.getMonth() + 1) + "-" +
     that.appendzero(d.getDate());           
     return date
   },
   time: function (value) {   
     var d = new Date(value * 1000);    //根据时间戳生成的时间对象
     var date = that.appendzero(d.getHours()) + ":" + 
     that.appendzero(d.getMinutes()) + ":" +
     that.appendzero(d.getSeconds());           
     return date
   },   
　　},
  computed:{
    ...mapState(['userLoginInfo']),
  },
  created: function () {
    this.orgid = this.userLoginInfo.orgid;
    this.scheduleList(1)   
  },  
  mounted() {
    this.node = document.querySelector('.timeModal');
    this.data1 = this
  },
  methods: {
    paging_father (n) {
      this.scheduleList(n)
      this.page_num = n;
    },    
    setStartingPoint(e) {
        this.x = e.clientX - this.node.offsetLeft
        this.y = e.clientY - this.node.offsetTop
        this.isCanMove = true
    },       

    modalMove(e) {
        if (this.isCanMove) {
            this.node.style.left = e.clientX - this.x + 'px'
            this.node.style.top = e.clientY - this.y + 'px'
        } 
    },        
    cancelMove() {
        this.isCanMove = false
    },
    changeStartTime(e) {
        this.startTime = e;
        this.scheduleList(1)
    },
    changeEndTime(e) {
        this.endTime = e;
        this.scheduleList(1)
    },              
    appendzero:function(obj)
    {
        if(obj<10) return "0" +""+ obj;
        else return obj;
    },       
    async scheduleList (page) {
        let t = (new Date()).getTime();
        let st
        let et
        if(this.startTime){
            st = this.startTime.getTime()/1000
        }
        else{
            st = this.startTime
        }
        if(this.endTime){
            et = this.endTime.getTime()/1000
        }
        else{
            et = this.endTime
        }        
        let resDate = await schedule(this.orgid,this.id,page,this.type,this.courseId,st,et)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data
            this.last_page = resDate.data.data.last_page
            this.counts = resDate.data.data.total          
        }
        console.log(resDate.data.data)
    },
    async delectClasses (id) {
        let resDate = await delectClasses(id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.scheduleList (1)
        }
    },
    async schedulePostpone (id) {
        let resDate = await schedulePostpone(id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.scheduleList (1)
        }
    },
    async scheduleRecover (id) {
        let resDate = await scheduleRecover(id,this.password,this.userLoginInfo.id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.scheduleList (1)
        }
    },
    async setLeave (id) {
        let resDate = await setLeave(id,this.level,this.remarks)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.scheduleList (1)
        }
    },
    async reduction (id) {
        let resDate = await reduction(id)
        if (resDate.data.status) {
            this.$message({
                type: 'success',
                message: resDate.data.sinfo
            });
            this.scheduleList (1)
        }
    },                                  
    showDelete(id){
        this.delectType = '0'
        this.deleteId = id;
        this.showDe = true
    },
    showPostpone(id){
        this.delectType = '2'
        this.deleteId = id
        this.showDe = true
    },
    showCancel(id){
        this.delectType = '3'
        this.deleteId = id
        this.showDe = true
    },
    showReduction(id){
        this.delectType = '4'
        this.deleteId = id
        this.showDe = true
    },    
    showLeaveOn(id){
        this.deleteId = id
        this.showLeave = true
    },                  
    hideDeleteModal(){
        this.showDe = false
    },
    hideLeaveModal(){
        this.showLeave = false
    },
    submitLeave(id){
        this.showLeave = false
        this.setLeave(id)
    },   
    submitDelete(id){
        if(this.delectType == "2"){
            this.schedulePostpone(id)
        }
        else if(this.delectType == "3"){
            if(this.password == '' || this.password.length < 6){
                this.$message.error('请输入正确的登录密码')
                console.log(this.password)
                return
            }
            this.scheduleRecover(id)
            this.password = ''
        }
        else if(this.delectType == "4"){
            this.reduction(id)
        }        
        else{
            this.delectClasses(id)
        }
        this.showDe = false;
    },       
    showAlert(a,b){
        console.log(a,b)
        this.id = a.id
        this.show = true
    },
    showcl(a,b){
      this.c_id = a.cur_id
      this.room_id = a.room_id
      this.kcName = a.cur_name
      this.sc_id = a.sc_id
      this.cur_time = a.cur_time
      this.showclass = true
    },
    hideModal() {
        // 取消弹窗回调   
        this.$emit('hideModal')
    },
    hideRe() {
        // 取消弹窗回调      
        this.showclass = false
    },    

    submit() {
    // 确认弹窗回调  
    this.scheduleList(1)
    this.show = false
    },
    submitRevision() { 
        this.show1 = false
    }        
  },
  components: {
    erpTabtop: erpTabtop,
    classAdjustment:classAdjustment,
    leave:leave,
    erpPaging: erpPaging, 
    // editTeacher:editTeacher, 
    // revisionCourses:revisionCourses,  
    'itemp':{
    	render: function(h){
    	  return h(
    	  	'p',
    	  	{
    	  	  'class': {
    	  	  	foo: true
    	  	  },
    	  	  on: {
    	  	  	click: function(event){
    	  	  		// console.log($('.foo').width())
    	  	  		console.log(event.currentTarget)
    	  	  		console.log(event.currentTarget.getBoundingClientRect())
    	  	  		console.log($(event))
    	  	  		if (event.offsetWidth < event.scrollWidth) {
    	  	  			var that = event.currentTarget;
    	  	  			var text = that.innerText;
    	  	  			var l = event.currentTarget.getBoundingClientRect().x+'px';
    	  	  			var t = event.currentTarget.getBoundingClientRect().y+'px';
    	  	  			var w = that.offsetWidth;
    	  	  			window.layer.open({
    	  	  				type: 1,
    	  	  				fixed: false,
    	  	  				icon: 2, 
				                offset: [t,l],   //相对定位
				                shade: false,
				                title: false, //不显示标题
				                area: ['150px', 'auto'],
				                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
				                success: function(layero,index){
				                console.log(layero);
				                layero.css("min-width",w).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
				            }
				        });
    	  	  		}  
    	  	  	}
    	  	  }
    	  	},
    	  	this.$slots.default
    	  )
    	},
    	props: {
    		that: Object
    	}
    }
  }
}
</script>
