    background-color: #aaa;
  }
  .modal-main form{
    overflow:hidden;
    overflow-y:auto;
    padding: 0 vw(30);
    width:100%;
    height:100%;
    box-sizing:border-box;
  }
  .modal-main form::-webkit-scrollbar{ 
    display:block; 
    width:vw(6); 
    margin:0 auto; 
    border-radius: 10px; 
    background:#ccc;
  }
  .el-button.is-round{
    border-radius:6px;
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
  }
  .el-button--primary{
    color:#fff!important;
  }
  .modal-main /deep/ .el-input__inner{
    height:vw(40);
    line-height:vw(40);
    padding:0 vw(15);
    font-size: vw(16);
  }
  .el-button{
    font-size:vw(16);
    height:vw(40);
    line-height:vw(40);
    padding:0;
  }
  .modal-bg {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    z-index: 10;
    transform:translate3d(0,0,0)
  }
  .div_gulptable /deep/ .el-table{
    height:vw(420)
  }
  ._paging /deep/ input{
    height:28px!important
  }
  ._tabTop /deep/ .selectTwo{
    width: vw(198);
    height: vw(40);
}   
