import Vue from 'vue'
import App from './App.vue'
import './element/ui'
import './script/drag'
import router from './router/index.js'
import store from './state/state'
import deleteModal from './components/public/delete.vue'
import VueAMap from 'vue-amap'
import VueQuillEditor from 'vue-quill-editor'// 富文本编辑器插件
// start 富文本编辑器插件 css
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
// end 富文本编辑器插件css

import '../node_modules/layui-layer/layer.js'
import '../src/script/clickBocument.js'
import layerOpen from '../src/script/tableLayer.js'

import {scrollModalMain} from '../src/script/scollForModel.js'
// alert(document.documentElement.clientWidth)

Vue.config.productionTip = false
Vue.use(VueAMap)
Vue.use(VueQuillEditor)

Vue.component('deleteModal', deleteModal)  //组册公用组件
Vue.prototype.burlInput = (e) => {
  // 鼠标移入改编文本框type - password 防止浏览器记住密码，对password属性的输入框进行私密信息填充
  e.passwordText = 'password'
}
Vue.prototype.layerOpen = (e) => {
  layerOpen.layerOpen(e)
}
Vue.prototype.scrollModalMain = (e) => {
  scrollModalMain(e)
}
Vue.prototype.passwordCode = (e,val) => {
  // 密码正则验证验证
  let Repregular = /^\w+$/g
  if(!Repregular.test(val)) {
    e.form.password = val.substring(0,val.length-1)
  }
}
VueAMap.initAMapApiLoader({
  key: '7d99a804cc5cb82a41c5c2947a558a58',
  plugin: ['AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType' , 'AMap.Geolocation', 'AMap.Geocoder','OverView'],
  uiVersion: '1.0', // ui库版本，不配置不加载,
  v: '1.4.4'
})

new Vue({
  router,
  store,
  render: h => h(App),
}).$mount('#app')

