<template>
    <!-- // 机构认证 -->
    <section class='ification'>
        <main class='_ificationMain'>
            <div class='_ificationMain_header'>
                <span>为了让您更好地使用免费琴行管理服务，请完善以下信息：</span>
            </div>
            <div class='_ificationMain_amin'>
              <div class='_ificationMain_aminLeft'>
                <label class='_ificationMain_aminLeft_onefun'>
                  <p>
                    <img src="../../../static/header_back/default_bg1.png" alt="" v-if='ruleForm.logo == ""'>
                     <img :src="Imageport + ruleForm.logo" alt="" v-else>
                  </p>
                  <button>上传LOGO</button>
                  <input type="file" style='opacity: 0;' @change="filesChange">
                </label>
              </div>
              <div class='_ificationMain_aminRight'>
                <section class='_ificationMain_aminRight_Left'>
                  <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="*机构名称" prop="name">
                      <el-input v-model="ruleForm.name" placeholder='请输入机构名称' maxlength="20" show-word-limit></el-input>
                    </el-form-item>
                    <el-form-item label="*机构简介" prop="name" class='formdesc'>
                      <el-input type="textarea" v-model="ruleForm.Introduction" placeholder='请输入机构简介' maxlength='200' show-word-limit></el-input>
                    </el-form-item>
                    <el-form-item label="*机构地址" prop="name" class='gps_diz'>
                      <el-input type="textarea" v-model="ruleForm.address"  placeholder='请输入机构地址-最长50个字' @blur="Inputblur($event)" @keyup.native="Inputblur($event)" maxlength="50" ></el-input>
                      <button style="opacity: 0;" @keyup.enter="Inputblur"> 假 </button>
                    </el-form-item>
                    <label class='coverDz'>
                      <el-amap vid="amap" :plugin="plugin" :events='events' class="amap-demo" :zoom='zoom' :center="center">
                        <el-amap-info-window :position="window.position" :content="window.content"></el-amap-info-window>
                      </el-amap>
                    </label>
                  </el-form>
                </section>
                <section class='_ificationMain_aminRight_Right'>
                  <el-form :model="ruleForm" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="*联 系 人" prop="name" class='label_kz'>
                      <el-input v-model="ruleForm.contact" placeholder='请输入联系人' maxlength="12" show-word-limit></el-input>
                    </el-form-item>
                    <el-form-item label="*联系方式" prop="name">
                      <el-input v-model="ruleForm.contaceophen" placeholder='请输入联系方式'></el-input>
                    </el-form-item>
                    <el-form-item label="*微  信  号" prop="name" class='label_kz'>
                      <el-input v-model="ruleForm.wx_phone" placeholder='请输入微信号' maxlength='20' show-word-limit></el-input>
                    </el-form-item>
                    <el-form-item label=" 备 注" prop="name"  class='label_bz'>
                      <el-input type="textarea" v-model="ruleForm.descbz" placeholder='请输入备注-最长500个字' maxlength='500' ></el-input>
                    </el-form-item>
                  </el-form>
                </section>
              </div>
            </div>
            <div class='_ificationMain_footer'>
              <button @click.stop='saveInput'>保存，进入系统</button>
            </div>
        </main>
    </section>
</template>
<script>
import {UpImage, deleteImg, getSavecert} from '../../getserver/server'
import {setlocalStorage, getlocalStorage} from '../../cookie/cookie'
import {mapState, mapMutations} from 'vuex'
import Imageport from '../../config/ImagePz'
export default {
  name: 'certifications',
  data () {
    var _this = this
    return {
      Imageport,
      center:[0, 0],
      zoom: 12,
      lng: '',
      lat: '',
      window: {
        position: [121.59996, 31.197646],
        content: ''
      },
      events:{
        'click': (e) => {
          _this.lng = e.lnglat.lng
          _this.lat = e.lnglat.lat
          _this.window.position = [_this.lng, _this.lat]
          _this.center = [_this.lng, _this.lat]
          var geocoder = new AMap.Geocoder({
            radius: 1000,
            extensions: "all"
          }); 
          geocoder.getAddress([_this.lng ,_this.lat], function(status, result) {
            if (status === 'complete' && result.info === 'OK') {
              if (result && result.regeocode) {
                _this.window.content = result.regeocode.formattedAddress;
                _this.ruleForm.address = result.regeocode.formattedAddress;
                _this.ruleForm.lng = result.regeocode.crosses[0].location.lng
                _this.ruleForm.lat = result.regeocode.crosses[0].location.lat
              }
            }
          });   
        }
      },
      plugin:[{
        pName: 'Geolocation',
        buttonPosition: 'RB',
        timeout: 10000,
        enableHighAccuracy: true,//是否使用高精度定位，默认:true
        events: {
          init(o) {
            // o 是高德地图定位插件实例
            // o.getCurrentPosition((status, result) => {
            //   if (result && result.position) {
            //     _this.center = [result.position.lng, result.position.lat];
            //     _this.ruleForm.address = result.formattedAddress;
            //     _this.ruleForm.lng = result.position.lng
            //     _this.ruleForm.lat = result.position.lat
            //   } else {
            //     // 获取地理位置失败
            //     _this.center = [113.280637,23.125178];
            //     _this.$message.error('获取地理位置失败! 请手动输入');
            //   }
            // });
            o.getCityInfo((status, result) => {
              if (result && result.info == "SUCCESS") {
                // 定位成功
                _this.center = result.center
                _this.ruleForm.address = result.province + result.city;
                _this.ruleForm.lng = result.center[0];
                _this.ruleForm.lat = result.center[1];
                _this.Inputblur()
              }
            })
          }
        }
      },{
        pName: 'ToolBar',
        events: {
          init(instance) {
            // console.log(instance);
          }
        }
      }],
      ruleForm: {
        logo: "",// logos
        name: '',// 机构名称
        Introduction: '',// 机构简介
        address: '', // 机构地址
        contact: '',// 联系人
        contaceophen: '', // 联系人电话
        wx_phone: '',// 微信号
        descbz: '', // 备注
        lng: '',// 经度
        lat: ''// 纬度
      }
    }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    ...mapMutations(['USER_INFO']),
    Inputblur (e) {
      const _this = this;
      let geocoder = ''; 
      let marker = '';
      var map = new AMap.Map("container", {
          resizeEnable: true
      });
      if(!geocoder){
          geocoder = new AMap.Geocoder({
            city: "", //默认：“全国”
          });
      }
      if (this.ruleForm.address.trim().length!=0){

        geocoder.getLocation(this.ruleForm.address, function(status, result) {
            if (status === 'complete'&&result.geocodes.length) {
                var lnglat = result.geocodes[0].location
                _this.center = [lnglat.lng, lnglat.lat]
                _this.window.position = [lnglat.lng, lnglat.lat]
                _this.window.content = _this.ruleForm.address;
                _this.ruleForm.lng = lnglat.lng
                _this.ruleForm.lat = lnglat.lat
            }else{
              if (e!=undefined && e.type == "blur") {
                _this.$message.error('根据地址查询位置失败');
              }
            }
        });
      }
    },
    saveInput () {
      // 保存
      this.savsrJG()
    },
    filesChange (e) {
      const _this = this
      let files = e.target.files[0];
      let regulaImg = /(png|jpg|jpeg)/;
      if (!regulaImg.test(files.name)) {
        this.$message.error('文件格式不正确,此功能只能上传png,jpg,jpeg文件,请重新上传!');
        this.$refs.flieRef.value = '';
        return
      }
      if (_this.ruleForm.logo=='') {
        // 第一次上传
        _this.upImg(e.target.files[0])
      } else {
        // 第n次上传
        deleteImg(_this.ruleForm.logo).then((res)=>{
          if (res.data.status) {
            // 删除成功
            _this.upImg(e.target.files[0])
          }
        })
      }
    },
    upImg (files) {
      // 上传方法
      let from = new FormData();
      from.append('img', files);
      const _this = this
      UpImage(from).then((res)=>{
        if (res.data.status) {
          _this.ruleForm.logo = res.data.sinfo
        }
      }).catch((error)=>{
        console.log(error)
      })
    },
    async savsrJG () {
      if (this.userLoginInfo.id != '' && this.ruleForm.name != '' &&  this.ruleForm.contact !='' && this.ruleForm.contact.trim().length !=0 && this.ruleForm.contaceophen !='' && this.ruleForm.wx_phone != '' && this.ruleForm.wx_phone.trim().length > 5 && this.ruleForm.Introduction != '' && this.ruleForm.Introduction.trim().length != 0 && this.ruleForm.address !='' && this.ruleForm.address.trim().length!=0 && this.ruleForm.lng != "" && this.ruleForm.lat !="") {
        // 必填项全部填写
        if (this.ruleForm.name.trim().length == 0) {
          this.$message.error('机构名称不能为空!');
          return
        } else if (!/^1[3589674]\d{9}/.test( this.ruleForm.contaceophen)){
          // 手机号码不符合常规
          this.$message.error('手机号码不符合规定! 请检查');
          return
        } else if (!/^([a-zA-ZA-z]|\d|[a-zA-ZA-z]|_|-){5,20}$/.test(this.ruleForm.wx_phone)){
          // 微信号填写不规范
          this.$message.error('微信号填写不规范! 6-20位，字母、数字、下划线和减号');
          return
        }
        let resData = await getSavecert(this.userLoginInfo.value.id, this.ruleForm.name, this.ruleForm.logo, this.ruleForm.contact, this.ruleForm.contaceophen, this.ruleForm.wx_phone, this.ruleForm.Introduction, this.ruleForm.address, this.ruleForm.descbz, this.ruleForm.lng, this.ruleForm.lat)
        if (resData.data.status) {
          // 认证成功
          this.$notify({
            title: '认证通过!',
            message: '认证成功提示消息',
            type: 'success'
          });
          let dataRes = resData.data.sinfo
          let jsons = JSON.parse(getlocalStorage('userLoginInfo'+this.$route.query.uid))
          dataRes['config'] = JSON.parse(resData.config.data)
          dataRes['token'] = jsons.value.token
          setlocalStorage('userLoginInfo'+this.$route.query.uid,  dataRes);
          this.$router.replace({path: '/index',query:{uid: this.$route.query.uid}});
        } else {
          this.$message.error(resData.data.error_msg);
        }
      } else if (this.ruleForm.wx_phone.trim().length < 6){
        this.$message.error("微信号不能少于6位，请检查!");
      }else {
        this.$message.error('必填字段不能为空!');
      }
    }
  },
  created () {
    if (getlocalStorage('userLoginInfo'+this.$route.query.uid) !=null || getlocalStorage('userLoginInfo'+this.$route.query.uid) != ''){
      let jsons = JSON.parse(getlocalStorage('userLoginInfo'+this.$route.query.uid))
      this.USER_INFO(jsons)
      this.ruleForm.contaceophen =  jsons.value.user_aco
      this.ruleForm.wx_phone =  jsons.value.user_aco
    }
  },
  mounted () {

  },
  watch: {
  }
}
</script>
<style lang="scss" scoped>
@import '../../style/mixin';
.ification{
  @include wh(100%, 100%);
  overflow: hidden;
  background-color: #e8eced;
  display: flex;
  justify-content: center;
  align-items: center;
  ._ificationMain {
      @include wh(81%, 88%);
      min-height: 500px;
      background-color: #ffffff;
      border-radius: 8px;
      box-shadow: 0 0 1px 0px #ccc;
      display: flex;
      flex-direction: column;
      ._ificationMain_header{
        @include wh(100%, 9.5%);
        border-bottom: 1px solid #e7eaec;
        display: flex;
        align-items: center;
        text-indent: 40px;
        font-size: 16px;
        color: #333333;
      }
      ._ificationMain_amin{
        display: flex;
        flex: 1;
        border-bottom: 1px solid #e7eaec;
        ._ificationMain_aminLeft {
          @include wh(17.8%, 100%);
          border-right: 1px solid #e7eaec;
          padding-left: 40px;
          padding-top: 40px;
          padding-right: 40px;
          box-sizing: border-box;
          ._ificationMain_aminLeft_onefun{
            position: relative;
            display: inline-block;
            @include wh(100%, auto);
            input[type='file']{
              position: absolute;
              @include wh(100%, 35px);
              left: 0;
              bottom: 0;
              cursor: pointer;
            }
            p{
              img{
                @include wh(100%, auto);
              }
            }
            
            button{
              @include wh(100%, 35px);
              border-radius: 6px;
              border: 1px solid #47b6ff;
              color: #47b6ff;
              background-color: #ffffff;
              margin-top:2.9%;
              cursor: pointer;
            }
          }
        }
        ._ificationMain_aminRight {
          flex: 1;
          display: flex;
          ._ificationMain_aminRight_Left{
            padding: 40px;
            box-sizing: border-box;
            @include wh(50%,100%);
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .coverDz{
            // padding: 0 40px;
            flex: 1;
            display: inline-block;
          }
           ._ificationMain_aminRight_Left /deep/ .el-form {
             height: 100%;
             display: flex;
             flex-direction: column;
           }
          ._ificationMain_aminRight_Left /deep/ .el-form .gps_diz .el-form-item__content {
            height: 100%;
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .gps_diz .el-form-item__content .search-box{
            height: 100%;
            width: 100%;
            box-shadow: none;
            .search-box-wrapper{
              border: 1px solid #e7eaec;
              .search-btn{
                display: none;
              }
              input{
                height: 100%;
              }
            }
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .el-form-item,._ificationMain_aminRight_Right /deep/ .el-form .el-form-item {
            margin-bottom: 6%;
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .el-form-item .el-form-item__content .el-textarea{
            line-height: normal;
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .el-form-item .el-form-item__content .el-textarea textarea, ._ificationMain_aminRight_Right /deep/ .el-form .el-form-item .el-form-item__content .el-textarea textarea{
            resize: none;
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .formdesc,._ificationMain_aminRight_Left /deep/ .el-form .gps_diz {
            height: 14.7%;
          }
          ._ificationMain_aminRight_Left /deep/ .el-form .formdesc .el-form-item__content, ._ificationMain_aminRight_Left /deep/ .el-form .el-form-item .el-form-item__content .el-textarea, ._ificationMain_aminRight_Left /deep/ .el-form .formdesc .el-form-item__content textarea{
            height: 100%;
          }
          ._ificationMain_aminRight_Right{
            flex: 1;
            padding: 40px;
            box-sizing: border-box;
          }
          ._ificationMain_aminRight_Right /deep/ .el-form .label_kz .el-form-item__label {
            letter-spacing: 1px;
          }
          ._ificationMain_aminRight_Right /deep/ .el-form .label_bz .el-form-item__label {
            letter-spacing: 12px;
            padding-right: 0px;
          }
        }
      }
      ._ificationMain_footer{
        @include wh(100%, 9.5%);
        display: flex;
        justify-content: flex-end;
        align-items: center;
        padding-right: 40px;
        box-sizing: border-box;
        button{
          @include wh(11.5%, 50%);
          color: #fff;
          background-color: #2769fb;
          border-radius: 6px;
          border: 1px solid #2769fb;
        }
      }
  }
}
</style>
