<template>
  <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
   <div class="modal-container modal-container1 adju">
    <div class="modal-header" @mousedown="setStartingPoint">
     调课：
     <i class="el-icon-close" round @click="hideModal"></i>
    </div>
    <div class="modal-main">
      <el-form ref="form" :model="form" :rules="rules" class="fromBox" label-width="20%">
        <el-form-item prop="name" label="课 程">
          <el-input v-model="form.name" placeholder="请输入课程" maxlength="4" disabled="disabled"></el-input>
        </el-form-item>
        <el-form-item prop="teacher" label="教 师" class="selectLong">
          <el-select v-model="form.teacher" placeholder="选择" :disabled="key1==='1'?true:false">
            <el-option v-for="(item,index) in currmData" :value="item.t_id" :key="item.t_id" :label="item.t_name"></el-option>
          </el-select>
          <el-button class="gh" @click="jie">更换</el-button>
        </el-form-item>
        <el-form-item prop="date" label="日 期">
          <el-date-picker
            v-model="form.date"
            type="date"
            format
            prefix-icon="el-icon-caret-bottom"
            placeholder="选择日期"
            :picker-options="pickerOptions"
            popper-class="selectBirthday">
          </el-date-picker>
        </el-form-item>
        <el-form-item prop="hour" label="上 课 时 间" class="short-item">
          <el-select v-model="form.hour" placeholder="时">
            <el-option value="0" label="00"></el-option>
            <el-option v-for="i in 24" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh gh1" type="text">时</el-button>
          <el-select v-model="form.minute" placeholder="分" class="minute">
            <el-option :value="minute" label="00"></el-option>
            <el-option v-for="i in 60" :value="i" :label="appendzero(i)" :key="i"></el-option>
          </el-select>
          <el-button class="gh" type="text">分</el-button>          
      </el-form-item>
      <el-form-item prop="classRoom" label="教 室 名 称" class="selectLong">
        <el-select v-model="form.classRoom" placeholder="选择教室" :disabled="key2==='1'?true:false">
          <el-option v-for="(item,index) in cur_list" :value="item.room_id" :key="item.room_id" :label="item.room_name"></el-option>
        </el-select>
        <el-button class="gh"" @click="jie1">更换</el-button>
      </el-form-item>
      <el-form-item prop="pl" label="批 量 调 课">
        <el-checkbox v-model="form.pl"></el-checkbox>
        <span class="minText">批量调整当前和排课序列之后的课程</span>
      </el-form-item>
      <el-form-item prop="reason" label="调 课 原 因" style="margin-top: 3%;">
        <el-input
          type="textarea"
          :rows="4"
          placeholder="可填写原因说明"
          maxlength="500"
          v-model="form.reason" class="textarea">
        </el-input>
      </el-form-item>                                               
    </el-form>
  </div>
    <div class="modal-footer alertFooter">
      <el-button type="primary" round @click="submit()">完成</el-button>
      <el-button round @click="hideModal">取消</el-button> 
    </div>
  </div>
</div>
</template>
<script>
  import axios from 'axios';
  import {selectedTeacher,selectedRoom,changeSchedule} from '../../../../../getserver/getAxios.js'
  export default {
    name: 'topModal',
    props: {
        that: {
          type: Object,
          default: false          
        }            
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            activeNames: [],
            currmData: [],
            cur_list:[],
            key1:'1',
            key2:'1',
            minute:0,
            form:{
              name:'',
              teacher:'',
              date:'',
              hour:'0',
              minute:'0',
              classRoom:'',
              reason:'',
              pl:false
            },
            rules:{
                                       
            },
            pickerOptions: {
              disabledDate(time) {
                let _now = Date.now(),
                t = 1 * 24 * 60 * 60 * 1000,
                sevenDays = _now - t;
                return time.getTime() < sevenDays;
              },
            },             
              
        }
    },
    created(){
       this.selectedTeacher()
       this.selectedRoom()
       this.form.name = this.that.kcName
       let d = new Date(this.that.cur_time * 1000)
       this.form.date = d      
       this.form.hour = d.getHours()
       this.form.minute = d.getMinutes()
    },
    mounted() {
        this.node = document.querySelector('.adju')
    },
    methods: {
        hideModal() {
            this.that.hideRe()
        },      
        submit() {
          this.changeSchedule ()
          this.that.hideRe()
        },        
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },        
        appendzero:function(obj)
        {
          if(obj<10) return "0" +""+ obj;
          else return obj;
        },
        jie(){
          this.key1 = '2'
        },
        jie1(){
          this.key2 = '2'
        },        
        async selectedTeacher () {
          let resDate = await selectedTeacher(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.currmData = resDate.data.data
            this.form.teacher = this.that.id
          }          
        },
        async selectedRoom () {
          let resDate = await selectedRoom(this.that.userLoginInfo.orgid)
          if (resDate.data.status) {
            this.cur_list = resDate.data.data
            this.form.classRoom = this.that.room_id
          }          
        },
        async changeSchedule () {
          let t_id = this.that.id
          let sc_id = this.that.sc_id
          let cur_id = this.that.c_id
          console.log(this.form.date)
          let y = this.form.date.getFullYear()
          let m = this.form.date.getMonth() + 1
          let d = this.form.date.getDate()
          let h = this.appendzero(this.form.hour)
          let mo = this.appendzero(this.form.minute)
          let s = '00'
          let time= y+"/"+m+"/"+d+" "+h+":"+mo+":"+s
          let cur_time = (new Date(time).getTime())/1000
          let room_id = this.that.room_id
          let remark = this.form.reason
          let orgid = Number(this.that.userLoginInfo.orgid)
          let resDate = await changeSchedule(t_id,sc_id,cur_id,cur_time,room_id,remark,orgid)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });
            this.that.scheduleList(1)           
          }          
        },                
        async addLesson (cur_id) {
          
        },
        async delLesson (cur_id) {
          
        },                        
                      
    }
  }
</script>
