<template>
    <div class='instInfo'>
        <!-- 机构信息 -->
        <section class='instInfo-section' v-if='typeof(taback) != "string"'>
            <div class='logo_img'>
                <img :src="ImagePz + taback.logo" alt="logo" title='logo'>
            </div>
            <div class='text_lable'>
                <em>机构简介：</em><span>&nbsp;{{taback.describe}}</span>
            </div>
            <div class='text_lable'>
                <em>地&nbsp;&nbsp;&nbsp;&nbsp;址：</em><span>&nbsp;{{taback.address}}</span>
            </div>
            <div class='MapDiv'>
                <div class='Map_gps'>
                    <inGps :vid='vid' :center='center' :window='window'/>
                    <Lbutton class='hot_btn' @click.native='consultamap'>
                      <i class='icon-width whit_view_icon'></i>
                      查看地图
                    </Lbutton>
                </div>
                <div class='Map_list'>
                    <div>
                        <span>联系人电话：</span>
                        <span>{{taback.telephone}}</span>
                    </div>
                    <div>
                        <span>微信号：</span>
                        <span>{{taback.wechat}}</span>
                    </div>
                    <div>
                        <span>联系人：</span>
                        <span>{{taback.contact_man}}</span>
                    </div>
                </div>
            </div>
            <Lbutton class='hot_btn' @click.native='modelInfoClick'>
              <i class='icon-width icon_correct'></i>
              修改信息
            </Lbutton>
        </section>
        <modifuInfo />
        <completeGps/>
    </div>
</template>
<script>
import {getmechanismInfo} from '../../../../getserver/server'
import Lbutton from '../../../button/button'
import ImagePz from '../../../../config/ImagePz'
import modifuInfo from './modifyInfo/modifyInfo'
import inGps from './insGps/insGps'
import completeGps from './completegps/completegps'
export default {
  name: 'inst_info',
  data () {
    return {
      taback: "",
      vid: 'instInfo',
      modifuInfoShow: false,
      completegpsShow: false,
      modifyInfo: "",
      ImagePz,
      center: [116.404, 39.915],
      window: {
        position: [116.404, 39.915],
        content: 'Nihao'
      }
    }
  },
  provide(){
    return {
      InstThis: this
    }
  },
  created(){
    this.mechanisminfo()
  },
  methods: {
    consultamap () {
      this.completegpsShow = !this.completegpsShow
    },
    modelInfoClick () {
      this.modifuInfoShow = !this.modifuInfoShow
      this.modifyInfo = this.taback
    },
    async mechanisminfo () {
      let resDate = await getmechanismInfo()
      if (resDate.data.status) {
        this.taback = resDate.data.data
        this.center = [resDate.data.data.lng,resDate.data.data.lat]
        this.window = {
          position: this.center,
          content: this.taback.address
        }
      }
    }
  },
  components: {
    Lbutton: Lbutton,
    modifuInfo: modifuInfo,
    completeGps: completeGps,
    inGps: inGps
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.hot_btn{
    @include wh(vw(128), vw(40));
    white-space: nowrap;
    margin-left: vw(20);
    position: absolute;
    top: vw(30);
    right:vw(40);
    .icon-width{
      display: inline-block;
      @include wh(vw(20),vw(20));
      margin-right: vw(5);
    }
  }
.instInfo{
  @include wh(100%, 100%);
  margin-top: vw(30);
  margin-bottom: vw(30);
  box-sizing: border-box;
  background-color: #ffffff;
  box-shadow: 0 0 1px #000;
  border-radius: 8px;
  .instInfo-section {
    position: relative;
    padding: vw(30) vw(40);
    box-sizing: border-box;
    .logo_img{
        @include wh(vw(88),vw(88));
        border: solid 1px #e7eaec;
        border-radius: vw(8);
        padding: vw(10) vw(11);
        box-sizing: border-box;
        img{
          @include wh(100%,100%);
          border-radius: 100%;
        } 
    }
    .text_lable{
      margin: vw(30) 0 vw(30) 0;
      font-size: vw(16);
      display: flex;
      align-items: flex-start;
      em{
        color: #262626;
        width: 5%;
        display: inline-block;
        height: vw(20);
        text-align: justify;
        line-height: vw(20);
      }
      em::after{
          content: "";
          display: inline-block;
          width: 100%;
      }
      span{
        color: #666666;
        flex: 1;
      }
    }
    .MapDiv{
        @include wh(vw(680),auto);
        .Map_gps{
          @include wh(100%,vw(300));
          position: relative;
          .hot_btn{
            position: absolute;
            z-index: 111;
            background: #2769fb;
            color: #fff;
            @include wh(vw(108),vw(40));
            font-size: vw(16);
            bottom: vw(16);
            right: vw(25);
            top: auto;
            border: none;
          }
        }
        .Map_list{
            display: flex;
            justify-content: space-between;
            font-size: vw(16);
            margin-top: vw(16);
            div {
                span{
                  color: #262626;
                }
                span:last-child{
                  color: #666666;
                }
            }
        }
    }
  }
} 
</style>