<template>
    <div class='_gradeConenter'>
        <!-- 班级管理 -->
        <topbtn></topbtn>
        <section class='div_gulptable'>
             <el-table
                :data="tableData"
                ref='table'
                border
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    prop="class_id"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="序号">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="班级名称">
                    <template slot-scope="scope">
                        <span class='hot' @click='layerOpen(scope.row,$event)'>
                            {{scope.row.class_name}}
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="班级课程">
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row,$event)'>
                            <em v-if='scope.row.currlist!=null'>{{scope.row.currlist.cur_name}}</em>
                            <em v-else></em>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="班级容量">
                    <template slot-scope="scope" >
                        <span>{{scope.row.class_count}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="当前人数">
                    <template slot-scope="scope">
                        <span>{{scope.row.cheadmaster}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="班主任">
                    <template slot-scope="scope">
                        <span v-if='scope.row.headmasterinfo!=null'>{{scope.row.headmasterinfo.t_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="address"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="备注">
                    <template slot-scope="scope" >
                        <span  @click='layerOpen(scope.row,$event)'>{{scope.row.remarks}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    label="更多操作"
                    min-width='37.77%'
                    show-overflow-tooltip>
                    <template slot-scope="scope" class='erp-call'>
                        <div class="grid-content bg-purple">
                            <el-button type="text" @click="currTableClick(scope.row, scope.$index)"  size="small" class='erp-button-icon'>
                                <i class='repair_icon icon-width'></i>
                                课表
                            </el-button>
                            <el-button @click="repairChange(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
                                <i class='repair_icon icon-width'></i>
                                修改资料
                            </el-button>
                            <el-button @click="currStudents(scope.row.class_id)" type="text" size="small" class='erp-button-icon'>
                                <i class='repair_icon icon-width'></i>
                                修改学生
                            </el-button>
                            <!-- <el-button type="text" v-if='scope.row.cheadmaster>0' size="small"  class='erp-button-icon'>
                                <i class='delete_icon icon-width '></i>
                                排课
                            </el-button> -->
                            <el-button type="text" @click="deletes(scope.row.class_id)" size="small"  class='erp-button-icon'>
                                <i class='delete_icon icon-width '></i>
                                删除
                            </el-button>
                            <el-button type="text" @click="showDelete(scope.row)" size="small"  class='erp-button-icon'>
                                <i class='delete_icon icon-width '></i>
                                修改课程
                            </el-button>
                        </div>
                    </template>
                </el-table-column>
            </el-table>
            <addGrade></addGrade>
            <currDate :individualGradeinfo='individualGradeinfo'></currDate>
            <currstudent :individualStudentList='individualStudentList'></currstudent>
            <currcourse :individualGradeInfo='individualGradeInfo'></currcourse>
            <currTable></currTable>
            <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize"></erp-paging>
            <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
        </section>
    </div>
</template>
<script>
import topbtn from './topbtn/topbtn'
import erpPaging from '../../../paging/pagingShort'
import addGrade from './addgrade/addgrade'
import currDate from './correction/corr_data'
import currstudent from './curr_students/currstudents'
import currcourse from './curr_course/course'
import currTable from './curr_table/curr_table'
import $ from '../../../../script/jquery-vendor.js'
import {gradeList, getGradeDelete, getCorrectinfo, getStudentinve} from '../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'grade',
  data (){
    return {
      countNum: 0,
      page_num: 0,
      tableData: [],
      form: {
        gradename: '',// 搜索课程名称
        page: 1 // 页码数
      },
      pageSize: 8,
      individualGradeinfo: "",
      individualStudentList: "",
      individualGradeInfo: '',
      addGradeShow: false,
      currDateShow: false,
      currstudent: false,
      currcoureShow: false,
      currTableShow: false,
      showDe:false,
	  deleteId: null
    }
  },
  provide (){
    return {
      gradeThis: this
    }
  },
  computed :{
    ...mapState(['userLoginInfo'])
  },
  created(){
    this.getGradeList(this.form.gradename,1,this.pageSize)
  },
  mounted () {
      this.tableHeight()
    // this.$refs.table.$el.children[2].children[0].children[1].children[0]
  },
  methods:{
      layerOpen:function (a,b) {
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    tableHeight () {
        let tableRefs = this.$refs.table.$el; // table 父亲级节点
        let trheight = null
        const _this = this
        setTimeout(()=>{
           trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
           tableRefs.style.height = trheight * ( _this.pageSize + 1 ) + "px"
        },500) 
    },
    async getGradeList (a,b,c){
        let resDate = await gradeList(a,b,c,this.userLoginInfo.orgid)
        if (resDate.data.status) {
            this.tableData = resDate.data.data.data;
            this.countNum = resDate.data.data.countarr
        }
    },
    paging_father(n){
      // 分页 - 请求接口
      this.getGradeList(this.form.gradename,n,this.pageSize)
      this.page_num = n;
    },
    repairChange(data, index){
        this.currDateShow = !this.currDateShow
        this.individualGradeinfo = data
    },
    currStudents (id) {
        this.currstudent = !this.currstudent
        let _this = this
        getStudentinve(id).then((res)=>{
            if (res.data.status) {
                _this.individualStudentList = res.data.data
            }
        })
    },
    showDelete (info) {
        this.currcoureShow = !this.currcoureShow
        this.individualGradeInfo = info
    },
    currTableClick () {
        this.currTableShow = !this.currTableShow
    },
    deletes(id){
        this.deleteId = id;
        this.showDe = true
    },
    hideDeleteModal(){
        this.showDe = false
    },
    submitDelete(id){
        this.showDe = false;
        this.delectclassRooms(id)
    },
    delectclassRooms(id){
        this.deletes_fun(id)
    },
    deletes_fun (id) {
      // 删除
      let _this = this
        getGradeDelete(id).then((res)=>{
            if (res.data.status) {
                // 删除成功
                _this.$message({
                    message: '删除成功',
                    type: 'success',
                    duration: 2000,
                });
                if (_this.tableData.length == 1) {
                    if (_this.page_num == 1){
                        _this.getGradeList(this.form.gradename,1,this.pageSize)
                    } else {
                        _this.getGradeList(this.form.gradename,_this.page_num-1,this.pageSize)
                    }
                    return
                }
                _this.getGradeList(this.form.gradename,_this.page_num,this.pageSize)
            }
        }).catch(error=>{
            console.log(error)
        })
    }
  },
  components: {
    topbtn: topbtn,
    erpPaging: erpPaging,
    addGrade: addGrade,
    currDate: currDate,
    currstudent: currstudent,
    currcourse: currcourse,
    currTable: currTable
  },
  watch:{
    currDateShow(val){
      if(val==false) {
        this.individualGradeinfo = ""
      }
    },
    currstudent (val) {
      if (val == false) {
        this.individualStudentList = ""
      }
    },
    currcoureShow (val) {
    if (val == false) {
        this.individualGradeInfo = ""
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';

.div_gulptable{
    @include wh(100%,auto);
}
.div_gulptable /deep/ .el-table {
    height: 540px;
    background-color: #ffffff;
    // box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    .el-table__body-wrapper {
        @include bgColor-defaul($bgColor-color)
    }
}
.grid-content{
    text-align: center;
}
</style>