
/**
 * h5获取cookie的方法
 * */

// sessionStorage
// 种植cookie
export const setseSsionStorage = (name, value) => {
    window.sessionStorage.setItem(name, JSON.stringify(value))
}

// 获取cookie
export const getseSsionStorage = (name) =>{
    return window.sessionStorage.getItem(name)
}

// 删除cookie
export const removeSessionStorage = (name) => {
    window.sessionStorage.removeItem(name)
}

// localStorage 
// 种植 cookie
export const setlocalStorage= (name, value, code) => {
    if(code==null||code==undefined) {
        let Days = 1 //有效时间默认1天
        let exp = new Date();
        let expires  = exp.getTime() + Days * 24 * 60 * 60 * 1000
        window.localStorage.setItem(name, JSON.stringify({value,expires}))
    } else {
        window.localStorage.setItem(name, JSON.stringify(value))
    }
}
// 获取cookie 
export const getlocalStorage = (name) => {
    return window.localStorage.getItem(name)
}
// 删除cookie 
export const removelocalStorage= (name) => {
     window.localStorage.removeItem(name)
}
/**
 * 原生获取cookie的方法
 **/ 

// 获取
export const nativegetcookie = (name) => {
    let cookie = document.cookie; // 获取全部cookie
    let ca = cookie.split(';'); // 凡是;号都切割开，成为数组
    for (var i = 0; i < ca.length; i++) { // 循环
        var c = ca[i];
        while (c.charAt(0) == ' ') c = c.substring(1);
        if (c.indexOf(name) != -1){
            return c.substring(name.length, c.length);
        }
    }
    return "";
}

