<template>
    <div class="Shop_items">
        <el-dialog
            title="购物"
            v-dialogDrag
            :visible.sync="tablesThis.shopitemsShow"
            width="30%"
            :before-close="handleClose">
            <section class='_shop_items'>
                 <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="21%" class="demo-ruleForm">
                    <el-form-item label="学生姓名" prop="name">
                        :<el-input v-model="ruleForm.name" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*销售员类型" prop="name">
                        : <section class='radio_class'>
                            <label class='radio-label'>
                                <input type="checkbox">
                                <span>销售员</span>
                            </label>
                            <label class='radio-label'>
                                <input type="checkbox">
                                <span>教师</span>
                            </label>
                        </section>
                    </el-form-item>
                    <el-form-item label="*销  售  员" prop="name">
                        :<el-select v-model="ruleForm.personnel" placeholder="请选择方式">
                            <el-option
                            v-for="item in form.personnel_form"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="*销 售 商 品" prop="name">
                        :<el-select v-model="ruleForm.personnel_way"
                                multiple
                                filterable
                                allow-create
                                default-first-option
                                placeholder="请选择方式">
                            <el-option
                            v-for="item in form.personnel_way_form"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <section class='div_gulptable'>
                        <el-table
                            :data="ruleForm.table_personnel_way"
                            height="auto"
                            border
                            style="width: 100%">
                            <el-table-column
                            align='center'
                            label="商品名称"
                            min-width="25.5%">
                            <template slot-scope="scope">
                                {{scope.row.name}}
                            </template>
                            </el-table-column>
                            <el-table-column
                            align='center'
                            label="单价(元)"
                            min-width="22%">
                            <template slot-scope="scope">
                                <el-input size='small' v-model="scope.row.onjia"  placeholder="请输单价"></el-input>
                            </template>
                            </el-table-column>
                            <el-table-column
                            align='center'
                            label="数量(台)"
                            min-width="22%">
                            <template slot-scope="scope">
                                <el-input size='small' v-model="scope.row.shul"  placeholder="请输数量"></el-input>
                            </template>
                            </el-table-column>
                            <el-table-column
                            align='center'
                            label="总价(元)"
                            min-width="22%">
                            <template slot-scope="scope">
                                {{scope.row.count}}
                            </template>
                            </el-table-column>
                        </el-table>
                    </section>
                    <el-form-item label="实付款" prop="name">
                        :<el-input v-model="ruleForm.payment" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="*付 款 方 式" prop="name">
                        :<el-select v-model="form.payway"
                                multiple
                                filterable
                                allow-create
                                default-first-option
                                placeholder="选择方式">
                            <el-option
                            v-for="item in ruleForm.payway"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="销售时间" prop="name">
                        :<el-date-picker
                            v-model="ruleForm.time"
                            type="date"
                            prefix-icon="el-icon-caret-bottom"
                            placeholder="选择时间">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="备注" prop="name">
                        :<el-input type='textarea' placeholder='可填写备注信息' v-model="ruleForm.payment" maxlength="500" show-word-limit></el-input>
                    </el-form-item>
                 </el-form>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="tablesThis.shopitemsShow = false" size='small'>确 定</el-button>
                <el-button @click="tablesThis.shopitemsShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
  name: 'shop_items',
  data () {
    return {
      dialogVisible: true,
      form: {
        personnel_way_form: [],
        personnel_form: [],
        payway: [] // 付款方式
      },
      ruleForm: {
        name: '傻逼',
        personnel: '',
        payment: '',
        personnel_way: [],
        table_personnel_way: [{
            id:'1',
            name: 'BD230',
            onjia: '100',
            shul: '10',
            count: '198000'
        },{
            id:'2',
            name: 'PD240',
            onjia: '100',
            shul: '10',
            count: '198000'
        }],
        payway: '',// 选中的付款方式
        time: '' // 销售时间
      }
    }
  },
  inject: ['tablesThis']
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
@media screen and (max-width: 1380px){
    .Shop_items{
    /deep/ .el-dialog__wrapper{
        .el-dialog{
            @include wh(vw1(640),vw1(660));
            display: flex;
            flex-direction: column;
            border-radius: vw1(8);
            .el-dialog__header{
                @include wh(100%, vw1(60));
                @include bg-defaul($bg-color);
                box-sizing: border-box;
                padding: vw1(22) vw1(30);
                border-top-left-radius: vw1(8);
                border-top-right-radius: vw1(8);
                .el-dialog__title{
                    font-size: vw1(16);
                    color: #fff;
                    line-height: normal;
                }
                .el-dialog__headerbtn{
                    top: vw1(20);
                    .el-dialog__close{
                    color: #ffff;
                    }
                }
            }
            .el-dialog__body{
                flex: 1;
                overflow-y:auto;
                ._shop_items{
                        .el-form{
                            .el-form-item{
                                height: vw1(40);
                                .el-form-item__label{
                                    height: 100%;
                                    text-align: justify;
                                    font-size: vw1(16);
                                    color: #333333;
                                }
                                .el-form-item__label::after{
                                    content: " ";
                                    display: inline-block;
                                    width: 100%;
                                }
                                .el-form-item__content{
                                    display: flex;
                                    .radio_class{
                                        flex: 1;
                                        margin-left: vw1(8);
                                        display: flex;
                                        .radio-label{
                                            font-size: vw1(16);
                                            display: flex;
                                            align-items: center;
                                            color: #999999;
                                            margin-right: vw1(14);
                                            input[type=checkbox]{
                                                background: #fff;
                                                -webkit-appearance:none;
                                                border: 2px solid #999999;
                                                border-radius: 2px;
                                                outline: none;
                                                @include wh(14px,14px);
                                                margin-right: vw1(9);
                                            }
                                            input[type=checkbox]:checked{
                                                background-size: 100%;
                                                background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                                            }
                                        }
                                        .radio-label:last-child{
                                            margin-right: 0;
                                        }
                                    }
                                    .el-select .el-input{
                                        margin-left: 0;
                                    }
                                    .el-input,.el-textarea,.el-select{
                                        margin-left: vw1(8);
                                        .el-textarea__inner{
                                            height: vw1(100);
                                            resize: none;
                                        }
                                        .el-input__count{
                                            line-height: normal;
                                        }
                                        .el-input__inner[disabled=disabled]{
                                            background: #fff;
                                            // border:none;
                                        }
                                        .el-input__inner{
                                            height: vw1(40);
                                        }
                                    }
                                    .el-date-editor{
                                        width: 100%;
                                        position: relative;
                                        .el-input__prefix{
                                            // right: vw(10);
                                            left: 91%;
                                            font-size: vw1(25);
                                            color: #999999;
                                        }
                                        .el-input__suffix{
                                            display: none;
                                        }
                                    }
                                }
                            }
                            .div_gulptable{
                                @include wh(100%,auto);
                            }
                            .div_gulptable /deep/ .el-table {
                                height: 540px;
                                background-color: #ffffff;
                                box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
                                .el-table__body-wrapper {
                                    @include bgColor-defaul($bgColor-color)
                                }
                            }
                        }
                    }
            }
            .el-dialog__footer{
                @include wh(100%, vw1(80));
                .dialog-footer{
                    button{
                        @include wh(vw1(120), auto);
                        font-size: vw1(16);
                        background-color: #e7eaec;
                        color: #999999;
                    }
                    button:first-child{
                        @include bg-defaul($bg-color);
                        color: #fff;
                    }
                }
            }
        }
    }
  }
}
