<template>
    <!-- 查看用户弹窗 -->
    <div class='_toview'>
        <el-dialog
        title="查看角色"
        v-dialogDrag
        :visible.sync="userThis.toviewShow"
        width="70%"
        >
            <section class='div_gulptable'>
                <el-table
                    :data="tableData"
                    stripe
                    border
                    style="width: 100%"
                    resizable=false
                    height="100%">
                    <el-table-column
                    prop="role_name"
                    label="角色名称"
                    min-width="14.7%"
                    align='center'>
                    </el-table-column>
                    <el-table-column
                    label="状态"
                    min-width="14.7%"
                    align='center'>
                    <template slot-scope="scope">
                        <span v-if='scope.row.is_del == 0'>启用</span>
                        <span v-else-if='scope.row.is_del == 1'>禁用</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="类型"
                    align='center'
                    min-width="14.7%">
                    <template slot-scope="scope">
                        <span v-if='scope.row.deflau == 1'>系统默认</span>
                        <span v-else-if='scope.row.deflau == 2'>自行创建</span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="权限描述"
                    align='center'
                    min-width="30.8%"
                    show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span @click='layerOpen(scope.row, $event)' class='color'>
                            {{scope.row.remake}}
                        </span>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="操作"
                    align='center'
                    min-width="20.5%">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            size="small"
                            @click.native='modifyStop(scope.row)'>
                            <i class='repair_icon icon-width'></i>
                            修改
                        </el-button>
                        <el-button
                            type="text"
                            size="small"
                            @click.stop="showDelete(scope.row.role_id)">
                            <i class='delete_icon icon-width'></i>
                            删除    
                        </el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </section>
            <span slot="footer" class="dialog-footer">
                <!-- <erp-paging ></erp-paging> -->
                <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="7"></erp-paging>
                 <!-- v-if='countNum>1' -->
            </span>
        </el-dialog>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import erpPaging from '../../../../paging/pagingShort';
import {RoleList, deleteRole} from '../../../../../getserver/server'
export default {
  name: 'toView',
  data () {
    return {
      tableData: [],
      countNum: 0,
      showDe:false,
      page_num: 0,
	  deleteId: null
    }
  },
  inject:['userThis'],
  created(){
    this.getRoleList(1,7)
  },
  methods: {
    hideDeleteModal(){
        this.showDe = false
    },
    showDelete(id){
        this.deleteId = id;
        this.showDe = true
    },
    submitDelete(id){
      this.showDe = false;
      this.delectclassRooms(id)
    },
    modifyStop(info){
        this.userThis.addUserRoleShow = !this.userThis.addUserRoleShow
        this.userThis.tabmodel = null;
        this.userThis.tabmodel = info
    },
    delectclassRooms(id){
        const _this = this
        deleteRole(id).then((res)=>{
            if(res.data.status) {
                _this.$message({
                    message: '操作成功',
                    type: 'success',
                    duration: 2000,
                });
                if (this.tableData.length == 1) {
                    if (this.page_num == 1) {
                        this.getRoleList(1, 7)
                    } else {
                        this.getRoleList(this.page_num-1, 7)
                    }
                    return
                }
                this.getRoleList(this.page_num, 7)
            }
        })
    },
    layerOpen:function (a,b) {
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    async getRoleList (i,limt) {
      let resDate = await RoleList(i,limt)
      if (resDate.data.status) {
        this.countNum = resDate.data.sinfo.countarr
        this.tableData = resDate.data.sinfo.data
      }
      this.userThis.getuserList('',0)
    },
    paging_father(n){
      this.getRoleList(n,7);
      this.page_num = n;
    }
  },
  components: {
    erpPaging: erpPaging
  },
  watch: {
    'userThis.toviewShow'(){
        this.getRoleList(1,7)
    },
    'userThis.tabmodel.role_name'(){
        this.getRoleList(1,7)
    },
    'userThis.tabmodel.remake'(){
        this.getRoleList(1,7)
    },
    'userThis.tabmodel.aid'(){
        this.getRoleList(1,7)
    },
    deep: true
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';

@media screen and (max-width: 1380px){
  // vw1
  ._toview /deep/ .el-dialog__wrapper .el-dialog{
      @include wh(auto, vw1(660));
      .el-dialog__header{
          @include wh(100%, vw1(60));
          padding-left: vw1(30);
          span{
            font-size: vw1(16);
          }
          .el-dialog__headerbtn{
              top: vw1(20);
          }
      }
      .el-dialog__body{
          padding: vw1(30);
      }
      .el-dialog__footer{
          padding: vw1(20);
      }
  }
}
@media screen and (min-width: 1380px){
  // vw
  ._toview /deep/ .el-dialog__wrapper .el-dialog{
      @include wh(auto, vw(660));
      .el-dialog__header{
          @include wh(100%, vw(60));
          padding-left: vw(30);
          span{
            font-size: vw(16);
          }
          .el-dialog__headerbtn{
              top: vw(20);
          }
      }
      .el-dialog__body{
          padding: vw(30);
      }
      .el-dialog__footer{
          padding: vw(20);
      }
  }
}
.color{
  @include fontColor-defaul($fn-color)
}
._toview /deep/ .el-dialog__wrapper .el-dialog{
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .el-dialog__header{
        padding-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        @include bg-defaul($bg-color);
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
        span{
          color: #ffffff;
        }
        .el-dialog__headerbtn{
            .el-dialog__close{
                color: #ffffff;
            }
        }
    }
    .el-dialog__body{
        flex: 1;
        overflow: auto;
        box-sizing: border-box;
        overflow: hidden;
        .div_gulptable{
            height: 100%;
        }
    }
    .el-dialog__footer{
        border-top: 1px solid;
        @include  borderColor-defaul($border-color);
    }
}
</style>
