<template>
    <div class='table-gulp'>
    	<erp-tabtop @cate_index="mans_index" @orgid="32"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="index"
	                show-overflow-tooltip
	                align='center'
	                min-width='16.666%'
	                label="序号">
	                <template slot-scope="scope"><span>{{scope.$index | serialNumber}} </span></template>
	            </el-table-column>
	            <el-table-column
	            prop="sm_name"
	            show-overflow-tooltip
	            align='center'
	            min-width='16.666%'
	            label="姓名">
	            </el-table-column>	            
	            <el-table-column
	                prop="sm_mobile"
	                show-overflow-tooltip
	                align='center'
	                min-width='16.666%'
	                label="手机号">
	            </el-table-column>
	            <el-table-column
	                prop="status"
	                show-overflow-tooltip
	                align='center'
	                min-width='16.666%'
	                label="状态">
	                <template slot-scope="scope">
	                	<span v-if="scope.row.status == 1">在职</span>
	                	<span v-else>离职</span>
	                </template>	                
	            </el-table-column> 
	            <el-table-column
	                label="操作"
	                min-width='33.333%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
	                    	<el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
	                    		<i class='repair_icon icon-width'></i>
	                    		修改销售员信息
	                    	</el-button>	                    	
	                    	<el-button type="text" @click="showDelete(scope.row.sm_id)" size="small"  class='erp-button-icon'>
	                    		<i class='delete_icon icon-width '></i>
	                    		删除
	                    	</el-button>
	                    	<el-button v-if="scope.row.status == 1" type="text" @click="showJobstatus1(scope.row.sm_id)" size="small"  class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		离职
	                    	</el-button>
	                    	<el-button v-else type="text" @click="showJobstatus2(scope.row.sm_id)" size="small"  class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		复职
	                    	</el-button>	                    	
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
        <editSalesman v-if="show" :that="that" @hideModal="hideModal" @submit="submit"></editSalesman>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './salesman-top/salesman-top.vue'
import erpPaging from '../../../paging/pagingShort'
import editSalesman from '../salesman/alert-box/editSalesman.vue'
import {mans_index,mans_del,mans_jobstatus1,mans_jobstatus2} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'salesman',
  data () {
    return {
      that: this,
      tableData: [],
		  value: '',
		  title: '修改销售员',
		  show: false,
		  showDe:false,
		  deleteId:null,
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数
      name:'',//修改的销售员名字
      id:'',//修改的销售员id
      phone:'',//修改的电话号码
      status:1,
      delectType:'5',//0是删除，2是顺延,3是取消消课,4是还原,5是离职,6是复职

    }
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{
  	serialNumber ( val ){
  		return (that.page_num-1)*8+(val + 1)
  	}
  }, 
  methods: {
    paging_father (n) {
      this.mans_index(n)
    },    
    async mans_index (n) {
    	let t = (new Date()).getTime();
    	let resDate = await mans_index(n,this.userLoginInfo.orgid,t)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    		this.last_page = resDate.data.data.last_page
    		this.counts = resDate.data.data.total
    		this.page_num = n;   		            
    	}
    },            
    mans_del (id) {
		// 删除教室
		mans_del(id,this.userLoginInfo.orgid).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				if((this.tableData.length -1) == 0){
					this.mans_index (this.page_num-1)
				}
				else{
					this.mans_index (this.page_num)
				}
			}					
    })
		},
		async jobstatus1 (id) {//离职
			let resDate = await mans_jobstatus1(id)
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				this.mans_index (this.page_num)
			}
		},
    async jobstatus2 (id) {//复职
    	let resDate = await mans_jobstatus2(id)
    	if (resDate.data.status) {
    		this.$message({
    			type: 'success',
    			message: resDate.data.sinfo
    		});
    		this.mans_index (this.page_num)
    	}
    }, 		        
    showAlert(a,b){
    	this.phone = a.sm_mobile
    	this.id = a.sm_id
      this.name = a.sm_name
      this.status = a.status
    	this.show = true
    },   
    hideModal() {
    // 取消弹窗回	        
	    this.show = false
    },
    showDelete(id){
    	this.delectType = "0"
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	if(this.delectType == "5"){
    		this.jobstatus1(id)
    	}
    	else if(this.delectType == "6"){
    		this.jobstatus2(id)
    	}        
    	else{
    		this.mans_del(id)
    	}        
    	this.showDe = false    	
    },
    submit() {
    // 确认弹窗回调   
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    },
    showJobstatus1(id){
    	console.log(id)
    	this.deleteId = id
    	this.delectType = "5"
    	this.showDe = true        
    },    
    showJobstatus2(id){
    	this.deleteId = id
    	this.delectType = "6"
    	this.showDe = true        
    },        
  }, 
  created: function () {
  	this.mans_index (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")  	
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editSalesman: editSalesman,
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
.noIcon{
	margin-left:0
}
.noIcon .icon-width {
	width:1px
}
</style>
