<template>
    <div>
      <wareTop></wareTop>
      <div class='table-gulp'>
        <section class='div_gulptable'>
            <el-table
                :data="tableData.data"
                border
                stripe
                resizable=false
                ref='table'
                style="width: 100%">
                <el-table-column
                    prop="goods_name"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="商品名称">
                </el-table-column>
                <el-table-column
                    prop="sto_num"
                    show-overflow-tooltip
                    align='center'
                    min-width='8.88%'
                    label="入库数量">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="sto_single_price"
                    align='center'
                    min-width='8.88%'
                    label="入库价格(元)">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop="sto_total_money"
                    align='center'
                    min-width='8.88%'
                    label="入库总额(元)">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='15.55%'
                    label="入库时间">
                    <template slot-scope="scope">
                        <span>{{scope.row.entry_time | times}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    prop='sto_code'
                    min-width='15.55%'
                    label="入库单号">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    prop='manager'
                    min-width='8.88%'
                    label="操作员">
                    <template slot-scope="scope">
                        <span v-if='scope.row.manager==0'></span>
                        <span v-else>{{scope.row.manager}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    prop='remark'
                    align='center'
                    min-width='8.88%'
                    label="备注">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='22.22%'
                    label="操作">
                    <template slot-scope="scope">
                        <el-button @click="handleClick(scope.row)" type="text" size="small">修改</el-button>
                        <el-button type="text" @click.stop='deletes(scope.row.sto_id)' size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <modifyRecord/>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
        <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father' :pageSize="pageSize" v-if='countNum>1'></erp-paging>
    </div>
    </div>
</template>
<script>
import wareTop from './wareTop/wareTop'
import modifyRecord from './modifyWare_recard/modify_recard'
import {getstoragelist, deletestoragelist} from '../../../../getserver/server'
import {mapState} from 'vuex'
import erpPaging from '../../../paging/pagingShort'
export default {
  name: 'statistical',
  data () {
    return {
      showDe:false,
      deleteId: null,
      modifyrecord_Show: false,
      modifyData: null,// 修改参数
      form: {
        goods_name: ''
      },
      tableData: [],
      countNum: 0,
      page_num: 0,
      pageSize: 8
    }
  },
  provide () {
      return {
        "outThis": this
      }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created () {
    this.getstoragelist_fun(1)
  },
  mounted(){
    this.tableHeight()
  },
  methods: {
    tableHeight () {
      let tableRefs = this.$refs.table.$el; // table 父亲级节点
      let trheight = null
      const _this = this
      setTimeout(()=>{
          trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
          tableRefs.style.height = trheight * ( _this.pageSize + 1 ) + "px"
      },500) 
    },
    handleClick(item){
      // 修改点击
      this.modifyrecord_Show = !this.modifyrecord_Show;
      this.modifyData = item
    },
    deletes(id){
        // 删除点击
      this.deleteId = id;
      this.showDe = true
    },
    hideDeleteModal(){
      this.showDe = false
    },
    submitDelete(id){
      this.showDe = false;
      this.delectclassRooms(id)
    },
    delectclassRooms(id){
      this.deletes_fun(id)
    },
    deletes_fun(id){
      // 删除接口
      this.delete_deletestoragelist(id)
      // alert("删除接口调用");
    },
    paging_father(i){
      // 分页
      this.page_num = i
      this.getstoragelist_fun(i)
    },
    async delete_deletestoragelist (stuid) {
      // 删除接口
      let resDate = await deletestoragelist(stuid)
      if (resDate.data.status) {
        // 删除成功
        this.$message({
          message: "删除成功",
          type: 'success',
          duration: 2000,
        })
        if (this.tableData.data.length == 1){
          if (this.page_num==1) {
              this.getstoragelist_fun(1)
            return;
          }
          this.getstoragelist_fun(this.page_num-1);
          return;
        } else {
          this.getstoragelist_fun(this.page_num);
        }
      }
    },
    async getstoragelist_fun (limit) {
      let resDate = await getstoragelist(this.userLoginInfo.orgid,this.form.goods_name, limit, this.pageSize)
      if (resDate.data.status) {
        // 列表访问成功
        if (this.form.goods_name.trim().length != 0) {
          this.$message({
            message: "筛选成功!",
            type: 'success',
            duration: 2000,
          })
        }
        this.tableData = resDate.data.data
        this.countNum = resDate.data.data.total
      }
    }
  },
  components: {
    wareTop: wareTop,
    modifyRecord: modifyRecord,
    erpPaging: erpPaging
  },
  filters: {
     times(val){
      let t = val * 1000
      let newDate = new Date(t)
      let yy = newDate.getFullYear();// 年
      let mm = p(newDate.getMonth()+1);// 月
      let dd = p(newDate.getDate());// 日
      function p(s) {
        // 时间补零函数
        return s < 10 ? '0' + s: s;
      }
      return yy +'-'+ mm + '-' +dd
    }
  }
}
</script>
<style lang="scss" scope>
    
</style>