<template>
  <el-tabs v-model="activeName" @tab-click="handleClick" v-cloak class="elTabPane">
    <el-tab-pane v-for="item in tabList" :label="item.name" :name="item.id" :key='item.id'>

      <erp-gulp-table :name="'tab'+item.id" v-if='item.value==1 && activeName==item.id'></erp-gulp-table>
      <set-gulp-meal :name="'tab'+item.id" v-if='item.value==2 && activeName==item.id'></set-gulp-meal> 
      <gulp-classroom :name="'tab'+item.id" v-if='item.value==3 && activeName==item.id'></gulp-classroom>
      <gulpGrade :name="'tab'+item.id" v-if='item.value==4 && activeName==item.id'></gulpGrade>
      <gulp-teacher :name="'tab'+item.id" v-if='item.value==5 && activeName==item.id'></gulp-teacher>
      <instInfo :name="'tab'+item.id" v-if='item.value==6 && activeName==item.id'></instInfo>
      <instdynamic :name="'tab'+item.id" v-if='item.value==7 && activeName==item.id'></instdynamic>
      <gulp-user-ment :name="'tab'+item.id" v-if='item.value==8 && activeName==item.id'></gulp-user-ment>
      <gulp-salary :name="'tab'+item.id" v-if='item.value==10 && activeName==item.id'></gulp-salary>

    </el-tab-pane>
  </el-tabs>
</template>
<script>
import erpGulpTable from './gulp-table/gulp-table'
import setGulpMeal from './set-meal/gulp-set-meal'
import gulpClassroom from './classroom/gulp-classroom'
import gulpTeacher from './teacher-management/gulp-teacher'
import gulpSalary from './salary/salary'
import gulpUserMent from './user_mment/user_mment'
import gulpGrade from './grade/grade'
import instInfo from './Inst_info/Inst_info'
import instdynamic from './instdynamic/instdynamic'
import {mapState} from 'vuex'
import {setseSsionStorage,getseSsionStorage} from '../../../cookie/cookie'
export default {
  name: 'gulp',
  data() {
    return {
      activeName: '',
      tabList:[]
    }
  },
  inject: ['mainThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created () {
    this.getGulpactivenum();
    this.rolefun()
  },
  components: {
    erpGulpTable: erpGulpTable,
    setGulpMeal: setGulpMeal,
    gulpClassroom:gulpClassroom,
    gulpSalary: gulpSalary,
    gulpUserMent: gulpUserMent,
    gulpTeacher: gulpTeacher,
    gulpGrade: gulpGrade,
    instInfo: instInfo,
    instdynamic: instdynamic
  },
  methods: {
    rolefun () {
      const _this = this
      _this.userLoginInfo.rolelist.forEach((data)=>{
          if(data.access_id == this.mainThis.num){
            // 日常管理权限下 - 第二级分类权限遍历
            _this.tworolefun(data.pidinfo)
          }
      })
    },
    tworolefun (arr){
      // 第二级分类权限遍历
      const _this = this
      if(getseSsionStorage('gulpactivenum')==null){
        this.activeName = JSON.stringify(arr[0].access_id)
      }
      arr.forEach((data)=>{
        let roldactive = ""
        switch(data.access_name){
          case "课程管理":
              roldactive = '1'
            break;
          case "套餐管理":
              roldactive = '2'
            break;
          case "教室管理":
              roldactive = '3'
            break;
          case "班级管理":
              roldactive = '4'
            break;
          case "教师管理":
              roldactive = '5'
            break;
          case "机构信息":
              roldactive = '6'
            break;
          case "机构动态":
              roldactive = '7'
            break;
          case "用户管理":
              roldactive = '8'
            break;
          case "操作记录":
              roldactive = '9'
            break;
          case "课程薪酬":
              roldactive = '10'
            break;
          default:{
              roldactive = ""
          }
        }
        _this.tabList.push({name:data.access_name,id: JSON.stringify(data.access_id), value: roldactive})
      })
    },
    handleClick(tab, event) {
      this.activeName = JSON.stringify(parseInt(tab.$vnode.key));
      setseSsionStorage('gulpactivenum', parseInt(tab.$vnode.key));
    },
    getGulpactivenum(){
      let cookie = getseSsionStorage('gulpactivenum');
      if(cookie!=null) {
        this.activeName =  cookie
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
@import '../../../style/media';
@import '../../../style/var';

.erp_div{
  @include wh(100%, 100%);
  // background-color: #ffffff;
  // box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.elTabPane /deep/ .el-tabs__content{
  padding:0 3.125%;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__nav{
  width:100%;
  overflow:hidden;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__item{
  padding:0;
  padding:0 1.6%;
  height:60px;
  line-height:60px;
  font-size:16px;
  color: #333333;
  font-family: SourceHanSansCN-Bold;
}
.elTabPane /deep/ .el-tabs__item.is-active{
  color: #2769fb;
  @include fontColor-defaul($fn-color)
  
}
.elTabPane /deep/ .el-tabs__active-bar{
  height:3px;
  width:20px!important;
  margin:0 1.56%;
  margin-left:1.66%;
  box-sizing:border-box;
  @include bg-defaul($bg-color)
}
.elTabPane /deep/ .el-tabs__nav-scroll{
  margin-left:3.125%;
  box-sizing:border-box;
}
.elTabPane /deep/ .el-tabs__content{
  
}
.elTabPane /deep/ .el-tabs__content .el-tab-pane{
  height:auto;
}
.elTabPane  /deep/ .el-tabs__content{

  .el-tab-pane .table-gulp .div_gulptable .el-table .el-table__header-wrapper table .has-gutter tr{
    @include bgColor-defaul($bgColor-color);
    th{
      @include bgColor-defaul($bgColor-color);
      @include borderColor-defaul($border-color)
    }
  }

  .el-tab-pane .table-gulp .div_gulptable .el-table .el-table__body-wrapper .el-table__body tbody tr{
    @include bgColor-defaul($bgColor-color);
    td {
      @include borderColor-defaul($border-color)
    }
  }

  @include bgColor-defaul($bgColor-color);
}
.elTabPane /deep/ .el-tabs__header{
  margin:0;
  @include bgColor-defaul($bgColor-color);
}
</style>

