<template>
    <!-- 重置密码 -->
    <div class='logins_conters'>
        <header class='logins_header'>
            <section class='_headersection'>
                <span class='login_head'>忘记密码</span>
                <span class='go_registered' @click.stop='ge_login'>去登录</span>
            </section>
        </header>
         <main class='logins_main'>
             <section class='login_input'>
                <label class='user input_label'>
                    <span class='input_label_user spacing'>
                        <em>手</em><em>机</em><em>号：</em>
                    </span>
                    <el-input v-model="form.phone" autocomplete='off' placeholder="请输入账号"></el-input>
                </label>
                <label class='input_label verification_code'>
                    <span class='input_label_user spacing'>
                      <em>验</em><em>证</em><em>码：</em>
                    </span>
                    <el-input v-model="form.phoneCode" autocomplete='off' placeholder="请输入验证码"></el-input>
                    <el-button @click.stop='get_phoneCode' v-show='Time_s==0'>获取验证码</el-button>
                    <el-button @click.stop='get_phoneCode' v-show='Time_s > 0'>{{Time_s}}s重新获取</el-button>
                </label>
                <label class='input_label'>
                    <span class='input_label_user spacing'>
                     <em>新</em><em>密</em><em style='text-align: right;'>码：</em>
                    </span>
                    <el-input v-model="form.password" autocomplete='off' placeholder="请输入密码" :type='passwordText' @focus='burlInput(comThis)' maxlength='18'></el-input>
                </label>
                <label class='input_label'>
                     <span class='input_label_user spacing'>
                        <em>确</em>
                        <em>认</em>
                        <em>密</em>
                        <em>码：</em>
                    </span>
                    <el-input v-model="form.once_passwords" autocomplete='off' placeholder="请重新确认密码" :type='passwordText' @focus='burlInput(comThis)' maxlength='18'></el-input>
                </label>
                <label class='button_label'>
                  <el-button :loading="reginState" @click.stop='resetClick'>确定</el-button>
                </label>
             </section>
         </main>
    </div>
</template>
<script>
import {getuserincode, resetPassword} from '../../../getserver/server'
import {setseSsionStorage} from '../../../cookie/cookie'
import {mapMutations} from 'vuex'
export default {
  name: 'resets',
  data () {
    return {
      Time_s: 0,
      reginState: false,
      comThis: this,
      passwordText: 'text',
      form : {
        phone: '',
        phoneCode: '', // 验证码
        password: '', // 新密码
        once_passwords: '' // 重置密码
      }
    }
  },
  methods: {
    ...mapMutations(['LOGIN_PAGE']),
    resetClick () {
      if (this.form.phone.trim().length == 0){
        this.$message.error('手机号不能为空')
      }else if (this.form.phoneCode == '') {
        this.$message.error('错误!验证码不能为空')
      } else if (this.form.password.trim().length == 0 || this.form.once_passwords.trim().length == 0){
        this.$message.error('密码不能为空')
      }else if (this.form.password.length < 6 || this.form.once_passwords.length < 6) {
        this.$message.error('密码不能少于6位')
      } else if (this.form.password != this.form.once_passwords) {
        this.$message.error('错误!两次密码必须相同')
      } else  {
        this.userReset()
        return
      }
      this.reginState = false
    },
    async userReset(){
      this.reginState = true
      let reset = await resetPassword(this.form.phone, this.form.password, this.form.phoneCode, this.form.once_passwords)
      setTimeout(()=>{
        this.reginState = false
      }, 1000)
      if (reset.data.status) {
        // 重置成功成功
        this.$notify({
          title: reset.data.sinfo,
          message: '可以前往登录',
          type: 'success',
          duration: 1500
        });
      }
      this.form = {
        phone: '',
        phoneCode: '', // 验证码
        passwords: '', // 新密码
        once_passwords: '' // 重置密码
      }
    },
    get_phoneCode () {
      // 获取手机验证码
      const phone_validation = /^1[4679385]\d{9}$/
      if (phone_validation.test(this.form.phone)){
        // 手机号码验证通过
        const that = this
        getuserincode(that.form.phone,0).then((res)=>{
          if (res.status) {
            // 发送成功
            that.Time_s = 60;// 设置验证码按钮假死时间 60s
            that.$message({
              message: '发送成功,请注意查收',
              type: 'success'
            });
            setTimeout(()=>{
              alert('您的验证码为:'+ res.data.sinfo)
            }, 1500);
            that.TimeServer()
          } else {
            that.$message.error('发送失败')
          }
        }).catch((error)=>{
          console.log(error);
        })
        return
      }
      // 手机号码验证失败
      this.$message.error('请输入正确的手机号码!');
    },
    ge_login () {
      // 去登录
      this.tabLoginfun(1)
    },
    tabLoginfun (n) {
      setseSsionStorage('login_page', n)
      this.LOGIN_PAGE(n);
    },
    TimeServer () {
      // 控制验证码重新获取时间
      const that = this
      let times =function(){
        setTimeout(()=>{
          that.Time_s--
          if (that.Time_s == 0 || that.Time_s == '0') {
            // 60s 已过，关闭定时器
            clearTimeout(times)
            return;
          }
          times()
        }, 1000)
      }
      times()
    }
  },
  watch: {
    'form.password'(val){
      this.passwordCode(this, val)
    },
    'form.once_passwords'(value){
      let Repregular = /^\w+$/g
      if(!Repregular.test(value)) {
        this.form.once_passwords = value.substring(0,value.length-1)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../style/mixin';
.logins_conters {
    @include wh(26%, 63%);
    min-height: 430px;
    background-color: #ffffff;
    margin-right: 15%;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .logins_header{
      @include wh(100%, 13%);
      background: url("../../../../static/header_back/login_top_bg.png")no-repeat;
      background-size: 100% 100%;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;
      padding: 0 7.8%;
      box-sizing: border-box;
      ._headersection {
        @include wh(100%,100%);
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .login_head{
          font-size: 16px;
        }
      }
      .go_registered{
          font-size: 13px;
          display: flex;
          align-items: center;
          cursor: pointer;
          -moz-user-select:none; /*火狐*/
            -webkit-user-select:none; /*webkit浏览器*/
                -ms-user-select:none; /*IE10*/
                    -khtml-user-select:none; /*早期浏览器*/
                        user-select:none;
        }
        .go_registered::after{
          content: '';
          display: inline-block;
          @include wh(15px,15px);
          background: url("../../../../static/login_icon/icon_switch.png")no-repeat;
          background-size: 100%;
          margin-left: 8px;
        }
    }
    .logins_main {
      flex: 1;
      padding: 7.7% 6.1%;
      box-sizing: border-box;
       .login_input {
          .input_label{
            margin-bottom: 5%;
          }
        .input_label{
          display: flex;
          align-items: center;
          font-size: 13px;
          color: #333333;
          .input_label_user{
            margin-right: 3.4%;
            @include wh(20%,auto);
            display: flex;
            justify-content: space-between;
            em{
              display: inline-block;
              @include wh(100%,100%);
              // letter-spacing: 10px;
            }
          }
          
        }
        .button_label{
            margin-top: 7.6%;
            display: inline-block;
            @include wh(100%, 100%);
        }
      }
    }
}
</style>
<style scoped>
.verification_code /deep/ .el-input{
    width: 40%;
    margin-right: 4%;  
}
.button_label /deep/ .el-button{
  width: 100%;

  height: 9.2%;
  background: #2769fb;
  color: #ffffff;
  font-size: 13px;
  border-radius: 8px;
}
.verification_code /deep/ .el-button{
  border: 1px solid #2769fb;
  color: #2769fb;
  padding-left: 10px;
  padding-right: 10px;
}
.input_label /deep/ .el-input{
    flex: 1;
}
</style>