<template>
    <div class='adduser'>
        <el-dialog
        title="修改用户信息"
        v-dialogDrag
        :visible.sync="userThis.currectShow"
        width="33.33%"
        >
        <section class='list_fw' @scroll="scrollModalMain($event)">
            <label class='user_name fw_label'>
                <span class='text_span'>*用户名：</span>
                <input type="text" placeholder="请输入用户名" v-model='form.userName' maxlength="18">
            </label>
            <label class='user_name fw_label'>
                <span class='text_span'>*手机号：</span>
                <input type="text" placeholder="请输入联系方式-手机号" v-model='form.contact' maxlength="11">
            </label>
            <label class='_curriculum_labeltwo fw_label'>
                <span>*性 别：</span>
                <el-select slot="prepend" v-model='form.sex' placeholder="请选择" class='_main-rightSelect'>
                    <el-option label="男" value="1"></el-option>
                    <el-option label="女" value="2"></el-option>
                </el-select>
            </label>
            <div class='fw_label _curriculum_labeltwo checkLabel'>
                <span class='text_span'>*角 色：</span>
                <section class='section_contener'>
                    <p v-for='item in roleform.auth' :key='item.role_id'>
                        <label class='checkbox_label' @change.stop='RoleChecked(item.role_id)' >
                            <input type="checkbox" :checked='item.f==1?false:true'>
                            <span>{{item.role_name}}</span>
                        </label>
                    </p>
                </section>
            </div>
            <label class='fw_label checkLabel'>
                <span class='text_span'>*所属门店：</span>
                <section class='section_contener'>
                    <p>
                        <label class='checkbox_label' @change.stop='intuClick(item.or_id)' v-for='item in roleform.orglist' :key='item.or_id'>
                            <input type="checkbox" :checked='item.f=="1"?false:true'>
                            <span>{{item.or_name}}</span>
                        </label>
                    </p>
                </section>
            </label>
        </section>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size='mini' @click="save" :loading="saveShow">保存</el-button>
            <el-button @click="userThis.currectShow = false" size='mini'>取消</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
import {userRole, correctUser} from '../../../../../getserver/server'
/**
 * userRole - 添加账号 角色授权信息以及所属门店信息
 * */
import {mapState} from 'vuex'
export default {
  name: 'adduser',
  data () {
    return {
      dialogVisible: true,
      checkedBoxShow: false,
      roleform: {
          orglist:{
            or_name: ''
          }
      },
      saveShow: false,
      form: {
        userName: '',
        contact: '',
        sex: '',
        roldId: '',
        orgId: ''
      }
    }
  },
  inject: ['userThis'],
  created(){
    this.getuserRole()
  },
  computed: {
      ...mapState(['userLoginInfo'])
  },
  methods: {
    intuClick (oid){
        const _this = this
        this.roleform.orglist.forEach((items,index)=>{
            if (items.or_id == oid) {
                if (items.f == "2") {
                  _this.roleform.orglist[index].f = "1"
                } else {
                   _this.roleform.orglist[index].f = "2"
                }
            } else {
                _this.roleform.orglist[index].f == "1"
            }
        })
    },
    RoleChecked (id) {
      this.roleform.auth.forEach((data)=>{
          if (data.role_id == id){
              if(data.f == 1) {
                data.f = 2
              } else {
                data.f = 1
              }
          }
      })
    },
    save(){
      // 保存
      const _this = this
      let rolsAccArr = []
      this.saveShow = true
      setTimeout(()=>{
        _this.saveShow = false
      }, 1500)
      if (this.form.userName.trim().length < 6) {
        this.$message.error('用户名不能少于6个字符!');
        return
      } else if (!/^[a-zA-zA-Z]/.test(this.form.userName)){
        this.$message.error('用户名必须以字母开头');
        return
      } else if(!/^1(3|8|4|9|5|6|7)\d{9}/.test(this.form.contact)){
        this.$message.error('手机号码格式不正确!请检查');
        return
      } else if(this.form.password < 6 || this.form.repeatpassword < 6){
        this.$message.error('密码只能以为数字、字母、或下划线组成，且必须为6到18位');
        return
      } else if(this.form.password!=this.form.repeatpassword){
        this.$message.error('两次密码必须相同!请检查');
        return
      } else if(!/\w/.test(this.form.password)){
        this.$message.error('密码只能以为数字、字母、或下划线组成，且必须为6到18位');
        return
      }
      this.roleform.auth.forEach((data)=>{
        if(data.f == 2) {
            rolsAccArr.push(data.role_id)
        }
      })
      let inum = 0 
      this.roleform.orglist.forEach((data)=>{
        if (data.f == "2") {
            inum++
            _this.checkedBoxShow = true
            _this.form.orgId = data.or_id
        }
      })
      if (inum == 0) {
        this.checkedBoxShow = false
        this.form.orgId = ""
      }
      if (rolsAccArr.length == 0) {
          this.$message({
            message: '请选择角色!',
            type: 'error'
          });
          return
      } else if (!this.checkedBoxShow) {
          this.$message({
            message: '请选择所属机构',
            type: 'error'
          });
          return
      }
      this.getAddaccount(rolsAccArr)
    },
   async getuserRole () {
       // 获取 角色授权信息 以及 机构信息
       let resDate = await userRole(this.userLoginInfo.orgid)
       if (resDate.data.status) {
        this.roleform = resDate.data.data
       }
    },
    getAddaccount (rolsAccArr) {
        // 修改用户
        const _this = this;
        // if(this.form.orgId == ""){
        //     this.$message({
        //         message: '请选择角色!',
        //         type: 'error'
        //     });
        //     return
        // } else if(this.form.orgId == ""){
        //     this.$message({
        //         message: '请选择角色!',
        //         type: 'error'
        //     });
        //     return
        // }
        correctUser(this.userThis.currectinfo.uid, this.form.userName, this.form.contact, this.form.orgId, this.form.sex, rolsAccArr).then((res)=>{
            if (res.data.status){
                _this.$message({
                    message: res.data.sinfo,
                    type: 'success'
                });
                _this.userThis.getuserList('', 0, 1, 10);
            }
        })
    }
  },
  watch: {
    'userThis.currectinfo'(val){
        this.form.userName = val.nickname
        this.form.contact = val.cellphone
        this.form.sex = JSON.stringify(val.sex)
        // this.form.roldId = val.rid
        let Ridarrs = val.rid.split(',')
        const _this = this
        this.roleform.auth.forEach((data)=>{
          Ridarrs.forEach((id)=>{
            if (data.role_id == id){
                if(data.f == 1) {
                    data.f = 2
                } else {
                    data.f = 1
                }
             }
          })
        })
        this.roleform.orglist.forEach((orgdata)=>{
            if(orgdata.or_id == val.orginfo.or_id) {
                orgdata.f = "2"
            } else {
                orgdata.f = "1"
            }
        })
        this.form.orgId = val.orginfo.or_id
        this.checkedBoxShow = true
    },
    'userThis.currectShow'(val){
        if (val==false) {
            this.getuserRole()
        }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';

@media screen and (max-width: 1380px){
    // vw1
    .adduser /deep/ .el-dialog__wrapper .el-dialog{
