                    border-radius: 6px; 
                }
            }
            }
        }
        .el-dialog__footer{
            padding: vw1(20);
            .dialog-footer .el-button{
                @include wh(vw1(120),auto);
                font-size: vw1(16);
            }
            .dialog-footer .el-button:nth-child(1){
                margin-right: vw1(30);
            }
        }
    }
}
@media screen and (min-width: 1380px){
    // vw
    .adduser /deep/ .el-dialog__wrapper .el-dialog{
        @include wh(auto, vw(660));
        .el-dialog__header{
            @include wh(100%, vw(60));
            padding-left: vw(30);
            span{
                font-size: vw(16);
            }
            .el-dialog__headerbtn{
                top: vw(20);
            }
        }
        .el-dialog__body{
            padding: vw(30);
            .list_fw {
            .user_name span{
                letter-spacing: vw(4) 
            }
            .user_password span{
