            }
            .fw_label {
                margin-bottom: vw(30);
                .section_contener{
                    p{
                        margin-right: vw(32);
                        margin-bottom: vw(24);
                        .checkbox_label{
                            font-size: vw(16);
                            input[type=checkbox]{
                                @include wh(14px,14px);
                                margin-right: vw(9);
                            }
                            input[type=checkbox]:checked{
                                background-size: 100%;
                                background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                            }
                        }
                    }
                }
                .text_span{
                    @include wh(vw(100), auto);
