.adduser /deep/ .el-dialog__wrapper .el-dialog{
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .el-dialog__header{
        padding-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        @include bg-defaul($bg-color);
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
        span{
          color: #ffffff;
        }
        .el-dialog__headerbtn{
            .el-dialog__close{
                color: #ffffff;
            }
        }
    }
    .el-dialog__body{
        flex: 1;
        overflow: hidden;
        box-sizing: border-box;
        .list_fw {
            height: 100%;
            overflow: auto;
          .user_name span{
          }
          .user_password span{
          }
          ._curriculum_labeltwo span:nth-child(1){
              @include wh(vw(100), auto);
              color: #333333;
          }
          .fw_label {
            display: flex;
            align-items: center;
            .section_contener{
                flex:1;
                display: flex;
                flex-wrap: wrap;
                p{
                    -webkit-touch-callout: none; /* iOS Safari */
                    -webkit-user-select: none; /* Chrome/Safari/Opera */
                    -khtml-user-select: none; /* Konqueror */
                    -moz-user-select: none; /* Firefox */
                    -ms-user-select: none; /* Internet Explorer/Edge */
                    user-select: none; /* Non-prefixed version, currently not supported by any browser */
                    .checkbox_label{
                        display: flex;
                        align-items: center;
                        color: #999999;
                        input[type=checkbox]{
                            background: #fff;
                            -webkit-appearance:none;
                            border: 2px solid #999999;
                            border-radius: 2px;
                            outline: none;
                        }
                        input[type=checkbox]:checked{
                            background-size: 100%;
                            background: url("../../../../../../static/icon/icon_unselected.png")no-repeat center;
                        }
                    }
                }
                // .checkbox_label:last-child{
                //     margin-bottom: 0;
                // }
            }
            .text_span{
                color: #333333;
            }
            input[type=text],input[type=password] {
                flex: 1;
                border: 1px solid #e7eaec;;
                border-radius: 6px; 
            }
          }
          .fw_label:last-child{
            margin-bottom: 0;
            -webkit-touch-callout: none; /* iOS Safari */
            -webkit-user-select: none; /* Chrome/Safari/Opera */
            -khtml-user-select: none; /* Konqueror */
            -moz-user-select: none; /* Firefox */
            -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently not supported by any browser */
          }
          .checkLabel{
            align-items: end;
          }
        }
    }
    .el-dialog__footer{
        border-top: 1px solid;
        @include  borderColor-defaul($border-color);
        .dialog-footer .el-button{
            // line-height: 0; 
            background-color: #e7eaec;
            color: #999999;
            text-align: center;
        }
        .dialog-footer .el-button:nth-child(1){
            @include bg-defaul($bg-color);
            color: #fff;
        }
    }
}

</style>
