<template>
    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
        <div class="modal-container modal-container1">
            <div class="modal-header" @mousedown="setStartingPoint">
                {{ title }}
                <i class="el-icon-close" @click="hideModal"></i>
            </div>
            <div class="modal-main">
              <el-form ref="form" :model="form" :rules="rules" label-width="18%" class="fromBox">
                <el-form-item prop="name" label="教室名称：" >
                  <el-input v-model.trim="form.name" placeholder="请输入教室名称" maxlength="20" ref="nameInput"></el-input>
                </el-form-item>
                <el-form-item prop="capacity" label="容纳量：" >
                  <el-input v-model.trim="form.capacity" placeholder="请输入容纳量" @focus="focusPrice" @keyup.native="change_actual_price()" maxlength="3" ref="capacityInput"></el-input>
                </el-form-item>
                <el-form-item prop="classRoom" label="教室状态：" >
                  <el-select v-model="form.classRoom" placeholder="请选择教室状态" @change="currentSel">
                    <el-option label="可用" value="1"></el-option>
                    <el-option label="不可用" value="2"></el-option>             
                  </el-select>
                </el-form-item>                                              
              </el-form>
            </div>
            <div class="modal-footer alertFooter">
                <el-button type="primary" round @click="submit">保存</el-button>
                <el-button round @click="hideModal">取消</el-button> 
            </div>
        </div>
    </div>
</template>
<script>
  import {addclassRoom} from '../../../../../getserver/getAxios.js'
  import {checkMaxVal,checkText} from '../../../../../script/validate.js' 
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        }        
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            status:'1',
            form: {
              name: '',
              capacity: '',
              classRoom:'可用'
            },
            rules: {
              name: [
              { required: true, message: '请输入教室名称', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              ],
              capacity: [
              { required: true, message: '请输入容纳量', trigger: 'blur' },
              { validator:checkMaxVal, trigger: 'blur' }
              ],              
              classRoom: [
              { required: true, message: '请选择教室状态', trigger: 'change' }
              ],              
            },          
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    methods: {
        hideModal() {         
          this.$emit('hideModal')
        },
        submit() {
          var a = this.form.name;
          var b = this.form.capacity;
          var c = this.status;
          if(a == ''){
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return
          }
          else if(a.length < 2){
            this.$message.error('教室名称长度不正确');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          }; 
          if(this.checkMaxVal1(b) == '1'){
            this.$refs.capacityInput.$el.children[0].focus();
            this.$refs.capacityInput.$el.children[0].blur();
            return            
          }
          else if(this.checkMaxVal1(b) == '2'){
            this.$refs.capacityInput.$el.children[0].focus();
            this.$refs.capacityInput.$el.children[0].blur();
            return            
          }          
          this.addclassRooms (a,b,c)
        },

        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },
        cancelMove() {
            this.isCanMove = false
        },
        async addclassRooms (a,b,c) {
          let resDate = await addclassRoom(a,b,c,this.orgid)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });
            this.$emit('submit')
          }
          else{
            this.$message({
              type: 'info',
              message: resDate.data.error_msg
            });            
          }
          console.log(resDate.data)
        },
        currentSel(selVal) {
         this.status = selVal;
         console.log(selVal);
        },
        focusPrice(){
          if(this.form.capacity){
            var actualPrice = this.form.capacity;
            if(typeof(actualPrice) != 'number'){
              actualPrice = actualPrice.split(".")[0];
              this.form.capacity = Number(actualPrice) 
            }
            else{
              this.form.capacity = actualPrice
            }            
          }       
        },                
        change_actual_price(){
          var actualPrice = this.form.capacity;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 500){
              actualPrice=500
              this.$message.error('教室容量范围在0-500')
            }                         
          }            
          this.form.capacity = actualPrice
        },        
        checkMaxVal1(value) {
          var v = value
          if (isNaN(value) || typeof(v) != 'number' || v <= 0 || v > 500) {
            return 1;//'请输入[1,500]之间的整数'
          } else {
            const re = /^[0-9]*[1-9][0-9]*$/;
            const rsCheck = re.test(value);
            if (!rsCheck) {
              return 2;//'请输入正整数'
            } else {
              return 0;
            } 
          }
        },        
    }
  }
</script>
<style lang="scss" scoped>
