    box-sizing:border-box;
  }
  .el-button.is-round{
    border-radius:6px;
    //padding: vw1(12) vw1(23);
    background-color: #e7eaec;
    border: solid 1px #e7eaec;
    color: #999999;
    line-height:vw1(40)
  }
  .modal-container1 /deep/ .el-button--primary{
    color:#fff!important;
  }
  .el-button{
    font-size:vw1(16);
  }
  .fromBox /deep/ .el-form-item__label:before{
    color: #333 !important;
    display: inline-block;
    float: left;
    margin-right: 0px !important;
  }
  .fromBox /deep/ .el-form-item__label{
    height:vw1(40);
    line-height: vw1(40);
    text-align: justify;
    font-size: vw1(16);
    padding:0;
    color: #333333;
    vertical-align: top;
  }
  .fromBox /deep/ .el-form-item__label:after {
    content: " ";
    display: inline-block;
    width: 100%;
  }
  .fromBox /deep/ .el-input__inner{
    height:vw1(40);
    line-height:vw1(40)
  }
  .fromBox /deep/ .el-form-item{
    margin-top: vw1(30);
    font-size:vw1(16);
    margin-bottom:0
  }
  .fromBox /deep/ .el-form-item__content{
    line-height:vw1(40)
  }
  .fromBox /deep/ .el-select{
    width:100%
  }
}
