<template>
 <transition name='ags' >
    <div class='_addGrade' v-show='gradeThis.addGradeShow' v-dialogDrag>
        <transition name='Gra'>
            <section class='_addGrade_conenter el-dialog' v-show='MainShow'>
                <div class='_addGrade-canter'>
                    <header class='_addGrade-canter-header el-dialog__header'>
                        <span>新增班级</span>
                        <i class='whitedelete_icon icon-width' @click.stop='MainClick'></i>
                    </header>
                    <main class='_addGrade-canter-main'>
                        <section class='_addGrade-canter-main-cater' @scroll="scrollModalMain($event)">
                            <div class='_addGrade-canter-main-caterTop'>
                                <el-form ref="form" :model="form" >
                                    <el-form-item label="*班级名称：">
                                        <el-input placeholder='请输入班级名称' v-model='form.gradeName' maxlength='20'></el-input>
                                    </el-form-item>
                                    <label class='label_class' style='cursor: pointer;' @click.stop='rightShow=!rightShow;AddGradeShow=false'>
                                        <span class='label_title label-xin'>班级课程：</span>
                                        <div class='label_mian'>
                                            <span v-if='form.className==""'>选择课程</span>
                                            <em v-else >{{form.className.cur_name}}</em>
                                        </div>
                                    </label>
                                    <label class='label_class'>
                                        <span class='label_title'>*班级容量：</span>
                                        <div class='label_amin-input'>
                                            <input type="text" placeholder="请输入班级容量-最大值100" v-model='form.gradePeople'>
                                            <span>人</span>
                                        </div>
                                    </label>
                                    <label class='label_class'>
                                        <span class='label_title'>*班&nbsp;&nbsp;主&nbsp;&nbsp;任：</span>
                                        <el-select v-model='form.teacherCharge' slot="prepend" placeholder="请选择" class='label_amin-input-select'>
                                            <el-option label="请选择" value="0"></el-option>
                                            <el-option :label="item.t_name" :value="item.t_id" v-for='item in teacherChargeList' :key='item.t_id'></el-option>
                                        </el-select>
                                    </label>
                                    <label class='label_class' style='cursor: pointer;' @click.stop='AddGradeShow=!AddGradeShow;rightShow=false;'>
                                        <span class='label_title label-xin'>班级学生：</span>
                                        <div class='label_mian'>
                                            <span v-if='form.statusChange.length==0'>添加学生</span>
                                            <em  v-else v-for='item in form.statusChange' :key='item.stu_id'>{{item.truename}}、</em>
                                        </div>
                                    </label>
                                    <label class='label_class label-select'>
                                        <span class='label_title label-xin'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                                        <el-input type="textarea" placeholder="可填写备注说明" maxlength="500" show-word-limit v-model="form.note"></el-input>
                                    </label>
                                </el-form>
                            </div>
                        </section>
                        <section class='_addGrade-canter-main-footer' >
                            <el-button size='medium' @click.stop='saveClick'>保存</el-button>
                            <el-button size='medium'  @click.stop='MainClick'>取消</el-button>
                        </section>
                    </main>
                </div>
                <transition name='r'>
                    <div class='_addGrade-right' v-show='rightShow'>
                        <header class='_addGrade-right-header'>
                            <span>选择课程</span>
                            <i class='whitedelete_icon icon-width' @click.stop='rightShow=!rightShow'></i>
                        </header>
                        <main class='_addGrade-right-main'>
                            <section class='right-main-main'>
                                <el-collapse accordion>
                                    <el-collapse-item :title="item.sname" :name="item.sid" v-for='item in grade_list' :key='item.sid'>
                                        <div class='collapse-item-div' >
                                            <span class='not-data' v-if='item.currlist.length==0'>暂无内容</span>
                                            <ul class='collapse-item-ul' v-else>
                                                <li :class='{"active": items.f}' v-for ='items in item.currlist' :key='items.cur_id' @click.stop='GradeClick(item.sid, items.cur_id)'>{{items.cur_name}}</li>
                                            </ul>
                                        </div>
                                    </el-collapse-item>
                                </el-collapse>
                            </section>
                            <section class='right-main-footer'>
                                <el-button size='medium' @click.stop='courseComplete'>完成</el-button>
                            </section>
                        </main>
                    </div>
                </transition>
                <transition name='r'>
                    <div class='_addGrade-right' v-show='AddGradeShow'>
                        <header class='_addGrade-right-header'>
                            <span>添加学生</span>
                            <i class='whitedelete_icon icon-width' @click.stop='AddGradeShow=!AddGradeShow'></i>
                        </header>
                        <main class='_addGrade-right-main'>
                            <section class='right-main-main right-main-twomain'>
                                <el-input v-model="input" placeholder="请输入内容" suffix-icon="el-icon-search"></el-input>
                                <div class='checkboxClass'>
                                    <div class='checkboxClassleft'>
                                        <label class='item-label' v-for='item in statusChargeList' :key='item.stu_id' @change.stop='checkBoxClick(item.stu_id)'>
                                            <input type="checkbox" :checked='item.f'>
                                            <span>{{item.truename}}</span>
                                        </label>
                                    </div>
                                </div>
                            </section>
                            <section class='right-main-footer'>
                                <el-button size='medium' @click.stop='completeClick'>完成</el-button>
                            </section>
                        </main>
                    </div>
                </transition>
            </section>
        </transition>
    </div>
</transition>
</template>
<script>
import {getaddgradeList,getaddstudentList,addGrade,GradeList} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'addgrade',
  data () {
    return {
      input: '',
      rightShow: false,
      MainShow: false,
      AddGradeShow: false,
      teacherChargeList: [],// 班级 - 班主任列表
      statusChargeList: [],// 班级-学生列表
      grade_list: [],// 班级 - 课程列表
      form:{
        gradeName: '',// 班级名称
        gradePeople: '',// 班级人数
        teacherCharge: "0",// 选中-班主任
        className: "", // 选中的课程
        statusChange: [], // 选中的学生数组
        note: '' // 备注
      }
    }
  },
  inject:['gradeThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created () {
    this.getaddgradeList_fun(this.userLoginInfo.orgid)
    this.getaddstudentList_fun(this.userLoginInfo.orgid)
    this.GradeList_fun(this.userLoginInfo.orgid)
  },
  methods: {
    MainClick(){
        this.MainShow = false
        this.AddGradeShow = false
        this.rightShow = false
        setTimeout(()=>{
            this.gradeThis.addGradeShow = false
        }, 150)
    },
    checkBoxClick(id){
        const _this = this
        this.statusChargeList.forEach((data,index)=>{
            if (data.stu_id == id) {
                if (data.f) {
                    _this.statusChargeList[index].f = false
                } else {
                    _this.statusChargeList[index].f = true
                }
            }
        })
    },
    completeClick () {
        let _this = this
        this.form.statusChange = []
        this.statusChargeList.forEach((data)=>{
            if (data.f) {
              _this.form.statusChange.push(data);
            }
        })
    },
    saveClick () {
        this.addGrade_fun()
    },
    courseComplete(){
        const _this = this
        // 课程 - 完成功能
        this.form.className = []
        this.grade_list.forEach((data)=>{
            data.currlist.forEach((data_t)=>{
                if (data_t.f) {
                  _this.form.className = data_t
                }
            })
        })
    },
    GradeClick (fid,zid) {
        this.grade_list.forEach((data,i)=>{
            data.currlist.forEach((data_is,z)=>{
                if(data_is.cur_id == zid) {
                    if (data_is.f == false) {
                        this.grade_list[i].currlist[z].f = true
                    }
                } else {
                    this.grade_list[i].currlist[z].f = false
                }
            })
        })
    },
    async getaddgradeList_fun (id) {
        // 班主任 - list
        let resDate = await getaddgradeList(id)
        if(resDate.data.status) {
            this.teacherChargeList = resDate.data.data;
        }
    },
    async getaddstudentList_fun (id) {
        // 学生 - list
        let resDate = await getaddstudentList(id)
        if(resDate.data.status) {
            this.statusChargeList = resDate.data.data;
        }
    },
    async addGrade_fun () {
        // 添加课程
        if (this.form.gradeName =='' || this.form.gradePeople == '' || this.form.teacherCharge == 0 || this.form.gradeName.trim().length == 0){
            this.$message({
                message: '必填项必须填写完整,否则无法添加!',
                type: 'error',
                duration: 2000,
            });
            return 
        }
        let stabutArr = [];
        this.form.statusChange.forEach((data)=>{
            stabutArr.push(data.stu_id)
        })
        let resDate = await addGrade(this.form.className.cur_id, stabutArr, this.form.gradeName, this.form.gradePeople, this.form.teacherCharge, this.form.note, this.userLoginInfo.orgid)
        if (resDate.data.status) {
            this.$message({
                message: '添加成功',
                type: 'success',
                duration: 2000,
            });
            this.gradeThis.getGradeList(this.gradeThis.form.gradename,1,10);
            this.form = {
                gradeName: '',// 班级名称
                gradePeople: '',// 班级人数
                teacherCharge: "0",// 选中-班主任
                className: '', // 选中的课程
                statusChange: [], // 选中的学生数组
                note: '' // 备注
            }
            this.getaddstudentList_fun(this.userLoginInfo.orgid);
            this.GradeList_fun(this.userLoginInfo.orgid)
        }
    },
    async GradeList_fun (id) {
        // 课程列表
        let resDate = await GradeList(id)
        if (resDate.data.status) {
            resDate.data.data.forEach((data_one)=>{
                data_one.currlist.forEach((data_two)=>{
                    data_two['f'] = false
                })
            })
            this.grade_list = resDate.data.data
        }
    }
  },
  watch:{
    'gradeThis.addGradeShow'(val){
        this.MainShow = this.gradeThis.addGradeShow
    },
    'form.gradePeople'(val){
      // 班级容纳量-监听
      let repval = /^\d+$/g;
      if (!repval.test(val)){
        this.form.gradePeople = val.substring(0, val.length-1)
      } else {
        if(val > 100) {
          this.$message.error('班级容纳量不能超过100人');
          this.form.gradePeople = val.substring(0, val.length-1)
        }
      }   
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
.r-enter,
.r-leave-to,
.r-enter-to,
.r-leave{
    transition: all 0.5s ease;
}
.r-enter,
.r-leave-to{
  opacity: 0;
  transform: translateX(-100%);
}
.r-enter-to,
.r-leave{
    opacity: 1;
    transform: translateX(0%);
}
.Gra-enter,
.Gra-leave-to,
.Gra-enter-to,
.Gra-leave{
    transition: all 0.3s ease;
}
.Gra-enter,
.Gra-leave-to{
    transform: translateY(-45%);
    opacity: 0;
}
.Gra-enter-to,
.Gra-leave{
    transform: translateY(0%);
    opacity: 1;
}

.ags.enter,
.ags.leave-to,
.ags.enter-to,
.ags.leave{
    transition: all 1s ease;
}
.ags.enter,
.ags.leave-to{
    opacity: 0;
    display: none;
}
.ags.enter-to,
.ags.leave{
    display: block;
    opacity: 1;
}

