<template>
  <div class='currDate'>
        <el-dialog
            title="修改资料"
            v-dialogDrag
            :visible.sync="gradeThis.currDateShow"
            width="30%">
            <section>
              <label class='body-label-list'>
                 <span class='body-label-title'>*班级名称：</span>
                 <el-input type='text' v-model='form.gradeName' maxlength = '20'></el-input>
              </label>
              <label class='body-label-list'>
                 <span class='body-label-title'>*班级容量：</span>
                 <div class='body-label-inputbtn'>
                    <el-input type='text'  v-model='form.gradeCount'></el-input>
                    <span class='btn_zs'>人</span>
                 </div>
              </label>
              <label class='body-label-list'>
                 <span class='body-label-title is-xin'>班&nbsp; 主&nbsp;任：</span>
                 <el-select slot="prepend"  v-model='form.gradeTeacher' placeholder="请选择" class='label_amin-input-select'>
                    <el-option label="请选择" value="0" ></el-option>
                    <el-option :label="item.t_name" :value="item.t_id" v-for='item in teacherList' :key='item.t_id'></el-option>
                  </el-select>
              </label>
              <label class='body-label-list flex-end'>
                 <span class='body-label-title is-xin'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                 <el-input type="textarea" v-model='form.note' placeholder="可填写备注说明" maxlength="500" show-word-limit></el-input>
              </label>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size='small ' @click="saveClick">保 存</el-button>
                <el-button @click="gradeThis.currDateShow = false" size='small '>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {getaddgradeList, getCorrectinfo} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'corr_data',
  data () {
    return {
      teacherList: [],
      form: {
        gradeName: '',
        gradeCount: '',
        gradeTeacher: "0",
        note: ''
      }
    }
  },
  inject: ['gradeThis'],
  props: {
    individualGradeinfo: String
  },
  created () {
    this.getaddgradeList_fun()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods:{
    async getaddgradeList_fun () {
      let resDate = await getaddgradeList(this.userLoginInfo.orgid)
      if (resDate.data.status) {
        // 请求成功
        this.teacherList = resDate.data.data
      }
    },
    saveClick(){
      // 保存
      let _this = this
      if (this.form.gradeName == "" || this.form.gradeCount == "") {
          this.$message({
            message: '必填项不能为空,请检查!',
            type: 'error',
            duration: 2000,
          });
        return
      } else if (this.form.gradeName.trim().length == 0 || this.form.gradeName.trim().length > 20){
        this.$message({
            message: '课程名称不规范!不能是空格!',
            type: 'error',
            duration: 2000,
        });
        return
      }
      getCorrectinfo(this.individualGradeinfo.class_id, this.form.gradeName, this.form.gradeTeacher, this.form.gradeCount, this.form.note).then(res=>{
        if (res.data.status) {
          // 修改成功
          _this.$message({
              message: '修改成功',
              type: 'success',
              duration: 2000,
          });
          _this.gradeThis.getGradeList(_this.gradeThis.form.gradename,_this.gradeThis.page_num,_this.gradeThis.pageSize)
        }
      })
    }
  },
  watch: {
    individualGradeinfo(val){
      if (val!="" && typeof val == 'object') {
        this.form.gradeName = val.class_name;
        this.form.gradeCount = val.class_count;
        this.form.gradeTeacher = val.headmasterinfo!=null?val.headmasterinfo.t_id:"0";
        this.form.note = val.remarks;
      }
    },
    'form.gradeCount'(val){
      let repval = /^\d+$/g;
      if (!repval.test(val)){
        this.form.gradeCount = val.substring(0, val.length-1)
      } else {
        if(val > 500) {
          this.$message.error('班级容纳量不能超过500人');
          this.form.gradeCount = val.substring(0, val.length-1)
        }
      }
    }
  }
}
</script>
