<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索课程" v-model='that.formseach.searchValue'>
                    <i class='search_icon icon-width' @click='searchKec'></i>
                </label>
                <label class='label_elect selectone'>
                    <span>科目: </span>
                    <el-select v-model="that.formseach.kmActive" placeholder="请选择" >
                        <el-option label="全部" value="0"></el-option>
                        <el-option :label="item.sname" :value="item.sid" v-for='item in kmLisy' :key='item.sid'></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectTwo'>
                    <span>授课方式: </span>
                    <el-select v-model="that.formseach.value" placeholder="请选择">
                        <el-option label="全部" value="0"></el-option>
                        <el-option label="一对一" value="1"></el-option>
                        <el-option label="一对多" value="2"></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectone'>
                    <span>状态: </span>
                    <el-select v-model="that.formseach.states" placeholder="请选择">
                        <el-option label="全部" value="0"></el-option>
                        <el-option label="开课中" value="1"></el-option>
                        <el-option label="已结课" value="2"></el-option>
                    </el-select>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                <Lbutton class='hot_btn' @click.native='setUphot'>
                    设置热门
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="funClick(1)">
                    <i class='icon-width jia_icon'></i>
                    新增课程
                </Lbutton>
                <Lbutton class='hot_btn '  @click.native="exportClick(0, $event)">
                    <i class='icon-width import_icon'></i>
                    <!-- <input type="file" ref='files'> -->
                    导入Excel
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="exportClick(1, $event)">
                    <i class='icon-width download_icon'></i>
                    下载模板
                </Lbutton>
                <Lbutton class='hot_btn' @click.native="exportClick(2, $event)">
                    <i class='icon-width export_icon'></i>
                    导出Excel
                </Lbutton>
                 <Lbutton class='hot_btn' @click.native="refreshData">
                    <i class='icon-width refresh_icon'></i>
                    刷新数据
                </Lbutton>
            </div>
        </el-col>
      </el-row>
      <erp-upfile :dialogVisible='upfilemodel.dialogVisible'></erp-upfile>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import Imageport from '../../../../../config/ImagePz'
import erpUpfile from '../up_filemodel/upfile'
import {mapState} from 'vuex'
import {getkmList, getKclist, importExcel} from '../../../../../getserver/server'
/**
 * getkmList 獲取科目接口
 * getKclist 课程列表接口
 **/
export default {
    name: 'tabTops',
    data () {
      return {
        upfilemodel:{
          dialogVisible: false
        },
        kmLisy: [],// 科目列表
        // value: '',
        // states: '',
        // kmActive: '',
        // searchValue: '', // 搜索的课程
        addVisible: false // 控制新增课程模态窗
      }
    },
    inject: ['that'],
    provide () {
      return {
        tabTop: this
      }
    },
    created () {
      const _this = this
      getkmList().then((res)=>{
        if(res.status) {
          _this.kmLisy = res.data.data
        }
      })
    },
    computed: {
      ...mapState(['userLoginInfo'])
    },
    methods: {
      funClick (i) {
        switch (parseInt(i)) {
          case 1:
            this.that.repairDate = null
            this.that.addVisible = true
            break;
        }
      },
      refreshData () {
        this.that.getKclists()
        this.$message({
          message: "刷新成功",
          type: 'success',
          duration: 2000,
        });
      },
      searchKec () {
        // 搜索课程
        this.getcmList(this.that.formseach.searchValue, this.that.formseach.kmActive, this.that.formseach.value, this.that.formseach.states, this.that.pageSize)
      },
      exportClick (n, e) {
        // 导出excel
        switch (n) {
          case 0:
            this.upfilemodel.dialogVisible = !this.upfilemodel.dialogVisible
            // this.getcmList('','','','')
            break;
          case 1:
            window.location.href = Imageport + "./public/upload/file/moban.xls"
            break;
          case 2:
            window.location.href = Imageport + "index/Index/currm_export?x-token="+ this.userLoginInfo.token + "&x-uid=" + this.userLoginInfo.id +"&cur_name=" + this.that.formseach.searchValue + "&subject=" + this.that.formseach.kmActive + "&tmethods=" + this.that.formseach.value + "&status=" + this.that.formseach.states + "&orgid=" + this.userLoginInfo.orgid
            break;
        }
      },
      async importExclefun (files) {
        // 导入模板
        let from = new FormData()
        from.append('excel', files)
        from.append('uid', this.userLoginInfo.id)
        from.append('orgid', this.userLoginInfo.orgid)
        let resDate = await importExcel(from)
        if (resDate.data.status) {
          this.$message({
            message: resDate.data.sinfo,
            type: 'success',
            duration: 2000,
          });
          this.that.getKclists(1)
          return
        }
        this.$message.error(resDate.data.sinfo);
      },
      async getcmList (kc, kmid, fs, state, limt) {
        // kc 课程名称
        // kmid 科目id
        // fs 授课方式
        // state 状态
        // 筛选功能
        let resData = await getKclist(1, kc, kmid, fs, state, this.userLoginInfo.orgid, limt)
        if (resData.status) {
          this.$message({
            message: '筛选成功',
            type: 'success',
            duration: 2000,
          });
        }
        this.that.tableData = resData.data.data.data
        this.that.countNum = resData.data.data.total // 总页数
      },
      setUphot () {
        this.that.hotKcshow = !this.that.hotKcshow
      }
    },
    watch: {
      'that.formseach.kmActive':function(val1){
        this.searchKec()
      },
      'that.formseach.value': function(val1){
        this.searchKec()
      },
      'that.formseach.states': function(val1){
        this.searchKec()
      }
    },
    components: {
      Lbutton: Lbutton,
      erpUpfile: erpUpfile
    }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(160), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: 12px;
    padding-right: 12px;
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(80%, 100%);
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(35);
    .el-select{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    // height:vw(40);
    @include wh(vw(128), vw(40));
    white-space: nowrap;
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
}
@media screen and (max-width: 1380px) {
  ._tabTop{
    margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
  }
}
</style>
