<template>
    <div class='modifyInfo' >
        <el-dialog
            title="修改信息"
            :visible.sync="InstThis.modifuInfoShow"
            v-dialogDrag>
            <section class='body-section'>
                <div class='_section-left'>
                    <ul class='up_logo'>
                        <li>
                            <img src="../../../../../../static/header_back/default_bg1.png" alt="" v-if='form.logo==""'>
                            <img :src="ImagePz + form.logo" alt="" v-else>
                        </li>
                        <li>
                            <el-button size='small'>
                                上传LOGO
                            </el-button>
                            <input type="file" @change='filesEvent($event)'>
                        </li>
                    </ul>
                </div>
                <div class='_section-right'>
                    <el-form ref="form" :model="form" label-width="18%">
                        <el-form-item label="机构名称：">
                            <el-input v-model="form.or_name" disabled></el-input>
                        </el-form-item>
                        <div class='two-form'>
                            <el-row>
                                <el-col :span='12'>
                                    <el-form-item label="联系人：">
                                        <el-input v-model="form.contact_man" placeholder='请输入联系人' maxlength='12'></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span='12'>
                                    <el-form-item label="联系电话：">
                                        <el-input v-model="form.telephone" placeholder='请输入联系电话' maxlength='11'></el-input>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </div>
                        <el-form-item label="微信号：">
                            <el-input v-model="form.wechat" placeholder='请输入微信号' maxlength='20'></el-input>
                        </el-form-item>
                        <el-form-item label="机构简介：">
                            <el-input type="textarea" v-model="form.describe" maxlength='200' placeholder='请输入机构简介'></el-input>
                        </el-form-item>
                        <el-form-item label="机构地址：" class='textarea-form'>
                            <el-input type="textarea" v-model="form.address" placeholder='请输入机构地址' @blur='Inputblur($event)' @keyup.native='Inputblur($event)'></el-input>
                        </el-form-item>
                        <div class='MapGps'>
                            <inGps :vid='vid' :center='center' :window='window'/>
                        </div>
                    </el-form>
                </div>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="submitSave" size='small'>确 定</el-button>
                <el-button @click="InstThis.modifuInfoShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>

import ImagePz from '../../../../../config/ImagePz'
import inGps from '../insGps/insGps'
import {modifymechanismInfo,deleteImg,UpImage} from '../../../../../getserver/server'
export default {
  name: 'modifyInfo',
  data () {
    return {
      dialogVisible: true,
      vid: 'modifyinfo',
      center:[116.404, 39.915],
      window: {
        position: [116.404, 39.915],
        content: ""
      },
      form: {
        logo: "",
        or_name: "",
        contact_man: "",
        telephone: "",
        wechat: "",
        describe: "",
        address: ""
      },
      ImagePz
    }
  },
  created(){
  },
  inject: ['InstThis'],
  methods: {
    Inputblur (e) {
      const _this = this;
      let geocoder = ''; 
      let marker = '';
      var map = new AMap.Map("container", {
          resizeEnable: true
      });
      if(!geocoder){
          geocoder = new AMap.Geocoder({
            city: "", //默认：“全国”
          });
      }
      if (this.form.address.trim().length!=0){
        geocoder.getLocation(this.form.address, function(status, result) {
            if (status === 'complete'&&result.geocodes.length) {
                var lnglat = result.geocodes[0].location
                _this.center = [lnglat.lng, lnglat.lat]
                _this.window.position = [lnglat.lng, lnglat.lat]
                _this.window.content = _this.form.address;
                // _this.ruleForm.lng = lnglat.lng
                // _this.ruleForm.lat = lnglat.lat
            }else{
                if (e!=undefined && e.type == "blur") {
                    _this.$message.error('根据地址查询位置失败');
                }
            }
        });
      }
    },
    filesEvent (e) {
      const _this = this
      if (_this.form.logo=='') {
        // 第一次上传
        _this.upImg(e.target.files[0])
      } else {
        // 第n次上传
        deleteImg(_this.form.logo).then((res)=>{
          if (res.data.status) {
            // 删除成功
            _this.upImg(e.target.files[0])
          }
        })
      }
    },
    submitSave () {
        // 保存
        if (this.form.contact_man.trim().length == 0 ){
            this.$message({
              message: '联系人不能为空！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if(this.form.telephone.trim().length == 0) {
            this.$message({
              message: '联系电话不能为空！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if(!/^1[3589674]\d{9}/.test(this.form.telephone)){
            this.$message({
              message: '联系电话不规范！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if (this.form.wechat.trim().length == 0 ){
            this.$message({
              message: '微信号不能为空！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if (this.form.wechat.trim().length < 6 ){
            this.$message({
              message: '微信号不能小于6位！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if (!/^([a-zA-ZA-z]|\d|[a-zA-ZA-z]|_|-){5,20}$/.test(this.form.wechat)){
            this.$message({
              message: '微信号输入不规范！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if (this.form.describe.trim().length == 0) {
            this.$message({
              message: '机构简介不能为空！',
              type: 'error',
              duration: 2000,
            });
           return
        } else if (this.form.describe.trim().length > 200) {
            this.$message({
              message: '机构简介不能超出200个字！',
              type: 'error',
              duration: 2000,
            });
           return
        }else if (this.form.address.trim().length == 0) {
            this.$message({
              message: '机构地址不能为空！',
              type: 'error',
              duration: 2000,
            });
           return
        }else if (this.form.address.trim().length > 50) {
            this.$message({
              message: '机构地址不能大于50个字！',
              type: 'error',
              duration: 2000,
            });
           return
        }
        this.modifyInfo()
    },
    upImg (files) {
      // 上传方法
      let from = new FormData();
      from.append('img', files);
      const _this = this
      UpImage(from).then((res)=>{
        if (res.data.status) {
          _this.form.logo = res.data.sinfo
        }
      }).catch((error)=>{
        console.log(error)
      })
    },
    async modifyInfo () {
        let resDate = await modifymechanismInfo(this.form, this.center[0],this.center[1])
        if (resDate.data.status) {
            // 修改成功
            this.$message({
              message: '修改成功!',
              type: 'success',
              duration: 2000,
            });
            this.InstThis.taback.describe = this.form.describe
            this.InstThis.taback.wechat = this.form.wechat
            this.InstThis.taback.address = this.form.address
            this.InstThis.taback.logo = this.form.logo
            this.InstThis.taback.contact_man = this.form.contact_man
            this.InstThis.taback.telephone = this.form.telephone
        }
    }
  },
  components: {
    inGps: inGps
  },
  watch: {
    'InstThis.modifyInfo'(val){
        if (val!="") {
            this.form = {
                logo: val.logo,
                or_name:val.or_name,
                contact_man: val.contact_man,
                telephone: val.telephone,
                wechat: val.wechat,
                describe: val.describe,
                address: val.address,
                remarks: val.remarks
            }
            this.Inputblur()
        }
    },
    'InstThis.modifuInfoShow' (val) {
        if (val==false) {
            this.InstThis.modifyInfo = ""
            this.form = {
                logo: "",
                or_name:"",
                contact_man: "",
                telephone: "",
                wechat: "",
                describe: "",
                address: "",
                remarks: ""
            }
        }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
.modifyInfo /deep/ .el-dialog__wrapper .el-dialog{
    @include wh(vw(860),vw(660));
    border-radius: vw(8);
    display: flex;
    flex-direction: column;
    .el-dialog__header{
        @include wh(100%,vw(60));
        @include bg-defaul($bg-color);
        padding: vw(22) vw(30);
        box-sizing: border-box;
        border-top-left-radius: vw(8);
        border-top-right-radius: vw(8);
        .el-dialog__title{
            color:#fff;
            font-size: vw(16);
            line-height: 0;
        }
        .el-dialog__headerbtn{
            top: vw(20);
            .el-dialog__close{
                color:#fff;
            }
        }
    }
    .el-dialog__body{
        flex: 1;
        overflow: hidden;
        padding: 0;
        .body-section{
          @include wh(100%,100%);
          display: flex;
          ._section-left{
            @include wh(vw(220),100%);
            border-right: 1px solid #e7eaec;
            padding: vw(30) vw(30) 0 vw(30);
            box-sizing: border-box;
            .up_logo {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                li{
                    @include wh(100%,auto);
                    display: flex;
                    justify-content: center;
                    img{
                        @include wh(vw(160),vw(160));
                        margin-bottom: vw(20);
                    }
                    .el-button{
                        @include wh(100%, vw(40));
                        font-size: vw(16);
                        color: #47b6ff;
                        border-color: #47b6ff;
                    }
                }
                li:last-child{
                    position: relative;
                    input{
                        position: absolute;
                        @include wh(100%, 100%);
                        opacity: 0;
                        cursor: pointer;
                        z-index: 11;
                    }
                }
            }
          }
          ._section-right {
              flex: 1;
              padding: vw(30) vw(30) 0 vw(30);
              box-sizing: border-box;
              overflow: auto;
              .el-form {
                  .el-form-item {
                        margin-bottom: vw(30);
                      .el-form-item__label{
                          font-size: vw(16);
                          color: #333333;
                          height: vw(40);
                          text-align: justify;
                      }
                      .el-form-item__label::after{
                          content: "";
                          display: inline-block;
                          width: 100%;
                      }
                      .el-form-item__content{
                          .el-input {
                              .el-input__inner{
                                  height: vw(40);
                              }
                          }
                      }
                  }
                  .textarea-form{
                      .el-form-item__label{
                          line-height: vw(20);
                      }
                  }
                  .two-form{
                    .el-row{
                        .el-col {
                            .el-form-item {
                                .el-form-item__label{
                                    width: 36%!important;
                                }
                                .el-form-item__content{
                                    display: flex;
                                }
                            }
                        }
                        .el-col:first-child{
                            padding-right: vw(40);
                            box-sizing: border-box;
                            .el-form-item {
                                .el-form-item__label{
                                    width: 42%!important;
                                }
                            }
                        }
                    }
                  }
                  .MapGps{
                    @include wh(100%,vw(240));
                    margin-bottom: vw(30);
                  }
              }
          }
        }
    }
    .el-dialog__footer{
        @include wh(100%, vw(80));
        border-top: 1px solid #e7eaec;
        .el-button{
            font-size: vw(16);
            @include wh(vw(120),auto);
            background-color: #e7eaec;
            color: #999999;
            border-color: #e7eaec;
        }
        .el-button:first-child{
            @include bg-defaul($bg-color);
            color: #fff;
            margin-right: vw(20);
            border-radius: vw(6);
        }
    }
}
</style>