<template>
      <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
          <div class="modal-container modal-container1" id="addBox">
              <div class="modal-header" @mousedown="setStartingPoint">
                  {{ title }}
                  <i class="el-icon-close" round @click="hideModal"></i>
              </div>
              <div class="modal-main" @scroll="scrollModalMain($event)">
               <el-form ref="form" :model="form" :rules="rules" label-width="14%" class="fromBox">
                <div class="leftBox">
                  <div class="img1">
                    <div class="img1Box">
                      <img v-if="imageUrl1" :src="Imageport + imageUrl1">
                    </div>
                    <el-upload
                    class="avatar-uploader"
                    ref="upload"
                    action="/index/Basess/get_img_update"
                    :http-request="uploadImg1"
                    :before-upload="beforeAvatarUpload1"
                    :on-remove="handleRemove"
                    accept="image/jpeg,image/jpg,image/png">
                    <i class="el-icon-plus avatar-uploader-icon">上传图片</i>
                  </el-upload>
                </div>                
              </div>
              <div class="rightBox">
                <el-form-item prop="name" label="名 称">
                  <el-input v-model.trim="form.name" placeholder="请输入商品名称" maxlength="20" ref="nameInput"></el-input>
                </el-form-item>
                <el-form-item prop="classification" label="分 类" class="slkc">
                  <el-select v-model="form.classification" placeholder="选择分类" popper-class="treeSelect" @focus="showSelect()" ref="selectRef">
                    <el-option :value="valueModel" :label="labelModel" id="subject">
                      <el-tree :data="cateList" node-key="id" :check-strictly="true" check-on-click-node auto-expand-parent default-expand-all :default-checked-keys= "[cheackId]" :props="defaultProps" @node-click="handleNodeClick" @check-change="handleClick" ref="treeForm"></el-tree>
                    </el-option>        
                  </el-select>
                </el-form-item>                
                <el-form-item prop="price" label="售 价" class="short-item" style="margin-right: 8%">
                  <el-input v-model.trim="form.price" placeholder="请输入售价" maxlength="8" ref="priceInput" @keyup.native="change_num(1)"></el-input>
                </el-form-item>
                <el-form-item prop="units" label="单 位" class="short-item">
                  <el-input v-model.trim="form.units" placeholder="请输入单位" ref="unitsInput" maxlength="20"></el-input>
                </el-form-item> 
                <el-divider></el-divider>                                          
                </el-form-item>
                <el-form-item prop="deposit" label="押 金" class="short-item" style="margin-right: 8%">
                  <el-input v-model.trim="form.deposit" placeholder="请输入押金" maxlength="8" ref="depositInput" @keyup.native="change_num(2)"></el-input>
                </el-form-item>
                <el-form-item prop="daily_rent" label="日 租 金" class="short-item">
                  <el-input v-model.trim="form.daily_rent" placeholder="填数字" maxlength="8" ref="dailyRentInput" @keyup.native="change_num(3)"></el-input>
                  <span class="rightLabel">元/天</span>
                </el-form-item>
                <el-form-item prop="monthly_rent" label="月 租 金" class="short-item" style="margin-right: 8%">
                  <el-input v-model.trim="form.monthly_rent" placeholder="填数字" maxlength="8" ref="monthlyRentInput" @keyup.native="change_num(4)"></el-input>
                  <span class="rightLabel">元/月</span>
                </el-form-item>
                <el-form-item prop="annual_rent" label="年 租 金" class="short-item">
                  <el-input v-model.trim="form.annual_rent" placeholder="填数字" maxlength="8" ref="annualRentInput" @keyup.native="change_num(5)"></el-input>
                  <span class="rightLabel">元/年</span>
                </el-form-item>                                                 
                <el-divider></el-divider>
                <el-form-item prop="resume" label="备 注" >
                  <el-input
                  type="textarea"
                  :autosize="{ minRows: 3, maxRows: 10}"
                  placeholder="可填写备注说明"
                  maxlength="500"
                  v-model="form.resume">
                </el-input>
              </el-form-item>                                                                                               
            </div>                                             
          </el-form>             
      </div>
      <div class="modal-footer alertFooter">
        <el-button type="primary" round @click="submit">保存</el-button>
        <el-button round @click="hideModal">取消</el-button> 
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios';
  import {addGoods,cate_list} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import {validateIdNo,validatePhone,checkText,checkMaxVal10000000} from '../../../../../script/validate.js'
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
          type: String,
          default: false
        },
        that: {
          type: Object,
          default: false
        },                       
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            valueModel:'',
            labelModel:'', 
            cheackId:null,            
            form: {
              name: '',
              classification:'',
              classificationId:null,
              price:null,
              units:'',
              deposit:null,
              daily_rent:null,
              monthly_rent:null,
              annual_rent:null,
              resume:'',
            },
            rules: {
              name: [
              { required: true, message: '请输入商品名称', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              ],
              classification: [
              { required: true, message: '请选择分类', trigger: 'blur'}
              ],
              price: [
              { required: true, message: '请输入商品售价', trigger: 'blur'} ,
              ],
              units: [
              { required: true, message: '请输入商品单位', trigger: 'blur'}
              ],
              deposit: [
              { validator:checkMaxVal10000000, trigger: 'blur' },
              ],                                                  
            },
            defaultProps: {
              children: 'sub',
              label: 'cate_name',
              id: 'id'
            },            
            imageUrl1: '',
            cateList:[],
            Imageport          
        }
    },
    mounted() {
        this.node = document.querySelector('#addBox')
    },
    created(){
      this.cate_list ()
    },
    watch : {
      
    },
    filters: {
      
    },
    components:{
    },
    methods: {
      showSelect(){
        let _this = this
        if(this.cheackId){
          let node1 = document.querySelector('.treeSelect .is-current');
          setTimeout(function(){
            _this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop = node1.children[0].offsetTop-50          
          },200)
        }
      },      
      handleNodeClick(data) {
        this.form.classificationId = data.cate_id
        this.cheackId = data.cate_id
        this.form.classification = data.cate_name
        this.$refs.treeForm.setCheckedNodes([data]);
        this.$refs.selectRef.blur()
      }, 
      handleClick(data, checked, node){
       if(checked == true){
         this.form.classificationId = data.id
         this.cheackId = data.id;
         this.$refs.treeForm.setCheckedNodes([data]);
         this.$refs.selectRef.blur()
       }
     }, 
        hideModal() {
         this.$emit('hideModal');
        },        
        submit() {
          this.addGoods()
        },
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },
               
        async cate_list () {
          let resDate = await cate_list(this.orgid)
          if (resDate.data.status) {
            this.cateList = resDate.data.data
            console.log(resDate.data.data)
          }
        },
        async addGoods () {                      
          if(!this.form.name){
            this.$message.error('请输入商品名称');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;
          }
          if(!this.form.classificationId && this.form.classificationId != 0){
            this.$message.error('请选择商品分类');
            this.$refs.selectRef.$el.children[0].children[0].click();   
            return;
          };
          if(this.form.price){
            this.change_num(1)
          }         
          else{
            this.$message.error('请输入商品售价');
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();            
            return
          };
          if(!this.form.units){
            this.$message.error('请输入商品单位');
            this.$refs.unitsInput.$el.children[0].focus();
            this.$refs.unitsInput.$el.children[0].blur();    
            return;
          };                                                                        
          let goods_name = this.form.name
          let orgid1 = this.orgid
          let goods_amount = this.form.price
          let cate_id = this.form.classificationId
          let unit_name = this.form.units
          let rent_amount_day = this.form.daily_rent
          let rent_amount_mon = this.form.monthly_rent
          let rent_amount_year = this.form.annual_rent
          let margin_amount = this.form.deposit
          let remarks = this.form.resume
          let goods_img = this.imageUrl1  
          let resDate = await addGoods(goods_img,goods_name,goods_amount,cate_id,unit_name,rent_amount_day,rent_amount_mon,rent_amount_year,margin_amount,remarks,orgid1,this.that.userLoginInfo.id)
          if (resDate.data.status) {
            this.$message({
              type: 'success',
              message: resDate.data.sinfo
            });           
            this.$emit('submit')
          }
        },
        change_phone(){
          var actualPrice = this.form.phone;          
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];                                  
          }            
          this.form.phone = actualPrice
        },
        change_num1(){
          var actualPrice = this.form.idCard;         
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9X,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];                          
          }            
          this.form.idCard = actualPrice
        },
        change_num(val){
          let actualPrice;
          if(val == 1){
            actualPrice = this.form.price;
          }
          else if(val == 2){
            actualPrice = this.form.deposit;
          }
          else if(val == 3){
            actualPrice = this.form.daily_rent;
          }           
          else if(val == 4){
            actualPrice = this.form.monthly_rent;
          }           
          else if(val == 5){
            actualPrice = this.form.annual_rent;
          };                                     
          if(typeof(actualPrice) != 'number'){
            //actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/[^0-9X.,]*/g,"");  //清除“数字”和“.”以外的字符 
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");  
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
              actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 10000000){
              this.$message.error("最大值为10000000")
              actualPrice = 10000000
            }                                  
          }
          if(val == 1){
            this.form.price = actualPrice
          }
          else if(val == 2){
            this.form.deposit = actualPrice;
          }
          else if(val == 3){
            this.form.daily_rent = actualPrice;
          }           
          else if(val == 4){
            this.form.monthly_rent = actualPrice;
          }           
          else if(val == 5){
            this.form.annual_rent = actualPrice;
          }                      
        },                                              
        //上传图片1
        uploadImg1 (f) {
          console.log(f)
           let param = new FormData(); //创建form对象
           param.append('img',f.file);//通过append向form对象添加数据
           // param.append('key',response.data.key);//添加form表单中其他数据
           let config = {
            headers:{'Content-Type':'multipart/form-data'}            
           };  //添加请求头
           axios.post(f.action,param,config)//上传图片
           .then(response=>{
            console.log(response)
            this.imageUrl1 = response.data.sinfo;
                this.$refs.upload.clearFiles();
            //onSuccess(response.data)
           })
           .catch(({err}) => {
            //f.onError()
           })   
      },      
        beforeAvatarUpload1(file) {
          const isPNG = file.type === 'image/png';
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;

          if (!isPNG && !isJPG) {
            this.$message.error('上传图片只能是 png 或者jpg 格式!');
          }
          if (!isLt2M) {
            this.$message.error('上传图片大小不能超过 2MB!');
          }
          return isJPG && isLt2M;
        },        
      handleRemove(file, fileList) {
        console.log(file.raw)
           // 更新缓存文件
           //console.log('文件删除')
            let param = new FormData(); //创建form对象
             param.append('img',file.raw);//通过append向form对象添加数据
             // param.append('key',response.data.key);//添加form表单中其他数据
             let config = {
                headers:{'Content-Type':'multipart/form-data'}              
             };  //添加请求头
             axios.post(this.$refs.upload.action,param,config)//上传图片
             .then(response=>{
                console.log(response)
                this.imageUrl1 = response.data.sinfo;
                this.$refs.upload.clearFiles();
                
             })
             .catch(({err}) => {
                
             })             
       },        
    }
  }
</script>
