// 项目路由管理地
import Vue from 'vue'
import VueRouter from 'vue-router'

import login from '../page/login/login'
import index from '../page/index/index'
import certification from '../page/certification/certification'
import {getlocalStorage} from '../cookie/cookie'
Vue.use(VueRouter)
const router = new VueRouter({
  routes: [{ 
      path: '/',
      redirect: '/login' 
    },
    {
      path: '/login',
      component: login
    },
    {
      path: '/certification',
      component: certification
    },
    {
      path: '/index',
      component: index
    }]
})

router.beforeEach((to, from, next) => {
  // ...
  // console.log(to)
  if (to.path == '/index'){
    let userinfo = JSON.parse(getlocalStorage('userLoginInfo'+to.query.uid))
    if (userinfo==null) {
      next({path: '/login'});
      return
    } else if (userinfo.value.orgid == null) {
      next({path: '/certification',query:{uid: to.query.uid}});
      return
    }
  }
  next();
})
export default router
