<template>
    <section>
        <user-top></user-top>
        <div class='div_gulptable'>
            <el-table
                :data="tableData.data"
                stripe
                ref='table'
                border
                style="width: 100%"
                resizable=false>
                <el-table-column
                prop="uid"
                label="序号"
                align='center'
                show-overflow-tooltip
                min-width="7.7%">
                </el-table-column>
                <el-table-column
                label="登录名"
                align='center'
                show-overflow-tooltip
                min-width="8.8%">
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)'>
                        {{scope.row.nickname}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                label="性别"
                align='center'
                show-overflow-tooltip
                min-width='7.7%'>
                <template slot-scope="scope">
                    <span v-if='scope.row.sex == 1'>男</span>
                    <span v-else-if='scope.row.sex == 2'>女</span>
                </template>
                </el-table-column>
                <el-table-column
                label="手机"
                align='center'
                show-overflow-tooltip
                min-width='11.11%'>
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)'>
                        {{scope.row.cellphone}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                label="状态"
                align='center'
                show-overflow-tooltip
                min-width="7.7%">
                <template slot-scope="scope">
                    <span v-if='scope.row.incumbency == 1'>在职</span>
                    <span v-else-if='scope.row.incumbency == 2'>离职</span>
                </template>
                </el-table-column>
                <el-table-column
                label="所属门店"
                align='center'
                show-overflow-tooltip
                min-width='11.11%'>
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)' class='color'>
                        {{scope.row.orginfo.or_name}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                label="角色"
                align='center'
                show-overflow-tooltip
                min-width='11.11%'>
                <template slot-scope="scope">
                    <span @click='layerOpen(scope.row, $event)' class='color'>
                        {{scope.row.ridinfo}}
                    </span>
                </template>
                </el-table-column>
                <el-table-column
                align='center'
                label="更多操作"
                min-width='34.44%'>
                <template slot-scope="scope">
                    <div class='funBtn'>
                        <p v-if='scope.row.ridinfo == "超级管理员"'>
                            <el-button
                                type="text"
                                size="small">
                                重置密码
                            </el-button>
                            <el-button
                                type="text"
                                size="small">
                                设置考勤密码
                            </el-button>
                        </p>
                        <p v-else>
                            <el-button
                                type="text"
                                size="small"
                                @click.stop='currectClick(scope.row)'>
                                <i class='repair_icon icon-width'></i>
                                修改
                            </el-button>
                            <el-button
                                type="text"
                                size="small" @click.native="showDelete(scope.row.uid)">
                                <i class='delete_icon icon-width'></i>
                                删除
                            </el-button>
                            <el-button
                                type="text"
                                size="small"
                                @click.native="resetPasswordClick(scope.row)">
                                重置密码
                            </el-button>
                            <el-button
                                type="text"
                                size="small"
                                @click.native="show_position(scope.row, scope.row.incumbency)">
                                <span v-if='scope.row.incumbency == 1'>
                                    离职
                                </span>
                                <span v-if='scope.row.incumbency == 2'>
                                    复职
                                </span>
                            </el-button>
                            <!-- <el-button
                                type="text"
                                size="small"
                                @click.native="resetRole(scope.row)">
                                修改权限
                            </el-button> -->
                        </p>
                    </div>
                </template>
              </el-table-column>
            </el-table>
            <erp-paging :counts='countNum' :page_num='page_num' :pageSize='pageSize' @pagings='paging_father' v-if='countNum>1'></erp-paging>
            <toview></toview>
            <add-user></add-user>
            <add-role v-if='adddialogVisible'></add-role>
            <deleteModal :show="showDe" :title_props='title' :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
            <correct></correct>
            <resetpassword></resetpassword>
            <resetRole v-if='resetRoleVisible'></resetRole>
        </div>
    </section>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpPaging from '../../../paging/pagingShort';
import userTop from './user_top/userTop';
import toview from './to_view/to_view'
import addUser from './add_user/addUser'
import addRole from './addrole/addrole'
import correct from './correctUser/correct'
import resetpassword from './resetpassword/resetPassword'
import resetRole from './resetRole/resetRole'
import {userList, deleteUser, UserPosition} from '../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'user_mment',
  data () {
    return {
      toviewShow: false,
      addUserShow: false,
      addUserRoleShow: false,
      adddialogVisible: true,
      currectShow: false,
      resetShow: false,
      resetRoleShow: false,
      resetRoleVisible: false,
      resetRoleArr: '',
      resetuid: '',
      currectinfo: null,
      tabmodel: null,
      searchValue: '',
      countNum: 0,
      page_num: 0,
      pageSize: 8,
      tableData: [],
      showDe: false,
      title: '',
	  deleteId: null
    }
  },
  provide(){
    return {
      userThis: this
    }
  },
  created () {
    this.getuserList(this.searchValue,0,1,this.pageSize)
  },
  mounted(){
    this.tableHeight()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    tableHeight () {
        let tableRefs = this.$refs.table.$el; // table 父亲级节点
        let trheight = null
        const _this = this
        setTimeout(()=>{
            trheight = tableRefs.children[2].children[0].children[1].children[0].offsetHeight;// tr 高度
            tableRefs.style.height = trheight * ( _this.pageSize + 1 ) + "px"
        },500) 
    },
    layerOpen:function (a,b) {
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    paging_father (n) {
      this.getuserList(this.searchValue, 0, n, this.pageSize);
      this.page_num = n
    },
    hideDeleteModal(){
        this.showDe = false
        this.title = ''
    },
    show_position (info, code) {
        let infos = code==1?"离职":"复职"
        this.title='确定要对' + info.nickname + '进行' + infos + '操作吗?'
        this.deleteId = info.uid;
        this.showDe = true
    },
    showDelete(id){
        this.deleteId = id;
        this.showDe = true
    },
    submitDelete(id){
      this.showDe = false;
      this.delectclassRooms(id)
    },
    delectclassRooms(id){
        const _this = this
        
        if (this.title == "") {
            // 删除用户
            deleteUser(id).then((res)=>{
                if(res.data.status) {
                    _this.$message({
                        message: '删除成功',
                        type: 'success',
                        duration: 2000,
                    });
                    if (this.tableData.data.length == 1) {
                        if (_this.page_num == 1) {
                            _this.getuserList(this.searchValue, 0, 1, this.pageSize);
                        } else {
                            _this.getuserList(this.searchValue, 0, _this.page_num-1, this.pageSize);
                        }
                        return;
                    }
                   _this.getuserList(this.searchValue, 0, _this.page_num, this.pageSize);
                }
            })
        } else {
            // 复职 || 离职操作
            let code = null;
            this.tableData.data.forEach((data)=>{
                if(data.uid == id) {
                    code = data.incumbency == 1?2:1
                }
            })
            UserPosition (id, code).then((res)=>{
                if(res.data.status) {
                    _this.$message({
                        message: '操作成功',
                        type: 'success',
                        duration: 2000,
                    });
                    _this.tableData.data.forEach((data,index)=>{
                        if (data.uid == id) {
                            _this.tableData.data[index].incumbency = this.tableData.data[index].incumbency == 1?2:1
                        }
                    })
                }
            })
        }

    },
    currectClick(info){
      this.currectShow = !this.currectShow
      this.currectinfo = info
    },
    resetPasswordClick (uid) {
      this.resetShow = !this.resetShow
      this.resetuid = uid
    },
    resetRole (data) {
        this.resetRoleShow = !this.resetRoleShow
        this.resetRoleArr = data.aid
    },
    async getuserList (name, i, thispage, limit) {
        let resDate = await userList(this.userLoginInfo.orgid, name, thispage, this.pageSize);
        if (resDate.data.status) {
            if(i==1) {
                this.$message({
                    message: '筛选成功',
                    type: 'success',
                    duration: 2000,
                });
            }
            this.countNum = resDate.data.data.countarr // 总页数
            this.tableData = resDate.data.data
        }
    }
  },
  components: {
    erpPaging: erpPaging,
    userTop: userTop,
    toview: toview,
    addUser: addUser,
    addRole: addRole,
    correct: correct,
    resetpassword: resetpassword,
    resetRole: resetRole
  },
  watch: {
    addUserRoleShow(val){
        if (val==false) {
            this.tabmodel = null
            this.adddialogVisible = false
            this.$nextTick(()=>{
                this.adddialogVisible = true
            })
        }
    },
    resetRoleShow(val){
        if (val==false) {
            this.resetRoleVisible = false
            this.$nextTick(()=>{
                this.resetRoleVisible = true
            })
        }
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';

.funBtn{
    p{
        display: flex;
        justify-content: center;
        .el-button{
            @include wh(auto, 40px);
            margin-left: vw(44);
        }
        .el-button:first-child{
            margin-left: 0;
        }
    }
    
}
.icon-width{
    @include wh(20px,20px);
    display: inline-block;
}
.div_gulptable /deep/ .el-table {
    height: 540px;
    background-color: #ffffff;
    -webkit-box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.color{
  @include fontColor-defaul($fn-color)
}
</style>
