<template>
	    <div class="modal-bg" @mousemove="modalMove" @mouseup="cancelMove">
	        <div class="modal-container modal-container1">
	            <div class="modal-header" @mousedown="setStartingPoint">
	                {{ title }}
	                <i class="el-icon-close" round @click="hideModal"></i>
	            </div>
	            <div class="modal-main">
	              <el-tabs v-model="activeName" @tab-click="handleClick" class="tabBox" ref="tabList">
	              	<el-tab-pane label="基本信息" name="first">
									<el-form ref="form" :model="form" :rules="rules" class="fromBox" label-width="17%">
		                <div class="leftBox">
		                	<div class="img1">
		                		<div class="img1Box">
		                			<img v-if="imageUrl1" :src="Imageport + imageUrl1">
		                		</div>
		                		<el-upload
			                		class="avatar-uploader"
			                		ref="upload"
			                		action="/index/Basess/get_img_update"
			                		:http-request="uploadImg1"
			                		:before-upload="beforeAvatarUpload1"
			                		:on-remove="handleRemove"
                          accept="image/jpeg,image/jpg,image/png">
			                		<i class="el-icon-plus avatar-uploader-icon">上传图片</i>
			                	</el-upload>
		                	</div>               	
		                </div>
		                <div class="rightBox">
		                  <el-form-item prop="name" label="姓 名" >
		                    <el-input v-model.trim="form.name" placeholder="请输入姓名" maxlength="16" ref="nameInput"></el-input>
		                  </el-form-item>
		                  <el-form-item prop="sex" label="性 别" class="short-item" style="margin-right: 8%">
		                  	<el-select v-model="form.sex" placeholder="选择性别" ref="sexInput">
		                  		<el-option value="1" label="男"></el-option>
		                  		<el-option value="2" label="女"></el-option>
		                  	</el-select>
		                  </el-form-item>
		                  <el-form-item prop="seniority" label="资 历" class="short-item">
		                  	<el-select v-model="form.seniority" placeholder="选择资历" ref="seniorityInput">
		                  		<el-option v-for="item in seniority" :value="item.s_id" :label="item.s_name" :key="item.s_id"></el-option>
		                  	</el-select>
		                  </el-form-item>		                  		                  
		                  <el-form-item prop="setMeal" label="课 程" class="slkc">
		                    <el-select v-model="form.setMeal" placeholder="选择课程，可多选" ref="mySelect" popper-class="selectBox" @visible-change="changeValue($event,selectKey)">           
		                    </el-select>
		                  </el-form-item>
		                  <el-form-item prop="phone" label="手 机" class="short-item" style="margin-right: 8%">
		                  	<el-input v-model.trim="form.phone" placeholder="请输入手机号码" maxlength="11" ref="phoneInput" @keyup.native="change_phone()"></el-input>
		                  </el-form-item>
		                  <el-form-item prop="birthday" label="生 日" class="short-item birthdayBox">
		                  	<el-date-picker
		                  	  v-model="form.birthday"
		                  	  type="date"
		                  	  format
		                  	  prefix-icon="el-icon-caret-bottom"
		                  	  placeholder="选择生日"
		                  	  :picker-options="pickerOptions"
		                  	  popper-class="selectBirthday">
		                    </el-date-picker>		                  	
		                  	<!-- <el-select v-model="form.birthday" placeholder="选择生日">
		                  		
		                  	</el-select> -->
		                  </el-form-item>		                   
		                  <el-divider></el-divider>
		                  <el-form-item prop="comedata" label="入 职 日 期" class="birthdayBox short-item" style="margin-right: 8%">
		                    <el-date-picker
		                  	  v-model="form.comedata"
		                  	  type="date"
		                  	  format
		                  	  prefix-icon="el-icon-caret-bottom"
		                  	  placeholder="选择日期"
		                  	  :picker-options="pickerOptions"
		                  	  popper-class="selectBirthday"
		                  	  ref="comeDataInput">
		                    </el-date-picker>
		                  </el-form-item>
		                  <el-form-item prop="status" label="状 态" class="short-item">
		                  	<el-select v-model="form.status" placeholder="选择状态" ref="seniorityInput">
		                  		<el-option value="1" label="在职" key="1"></el-option>
		                  		<el-option value="2" label="离职" key="2"></el-option>
		                  	</el-select>
		                  </el-form-item>		                  
		                  <el-form-item prop="idCard" label="身 份 证 号" >
		                    <el-input v-model.trim="form.idCard" placeholder="请输入身份证号" maxlength="18" @keyup.native="change_num1()" ref="idCardInput"></el-input>
		                  </el-form-item>	                  	                  	                  	                                  
		                </div>                                             
		              </el-form>	              		
	              	</el-tab-pane>
	              	<el-tab-pane label="班级信息" name="second">
	              		<el-form class="fromBox">
	              			<div class="classData">
	              				<ul>
	              					<li v-if="classes.length == 0" v-for="(item,index) in classes" :value="item.id" :key="item.id"><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher(item.id)">更换教师</el-button></li>
	              					<!-- <li><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher">更换教师</el-button></li>
	              					<li><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher">更换教师</el-button></li>
	              					<li><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher">更换教师</el-button></li>
	              					<li><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher">更换教师</el-button></li>
	              					<li><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher">更换教师</el-button></li>
	              					<li><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher">更换教师</el-button></li> -->
	              					<li v-if="classes.length == 0">暂无班级</li>
	              					<li v-else v-for="(item,index) in classes" :value="item.id" :key="item.id"><p>高级艺术1班：满班率20.00%</p><el-button @click="showTeacher(item.id)">更换教师</el-button></li>
	              				</ul>
	              			</div>
	              		</el-form>	              		
	              	</el-tab-pane>
	              	<el-tab-pane label="所带学生" name="three">
	              		<el-form class="fromBox">
	              			<div class="studentList">
	              				<span v-if="students.length === 0" class="noStudent">暂无学生</span>
	              				<el-button v-else v-for="(item,index) in students" :value="item.stu_id" :key="item.stu_id">{{item.stu_name}}</el-button>
	              			</div>
		              	</el-form>	              		
		              </el-tab-pane>
		              <el-tab-pane label="简历介绍" name="four">
		              	<el-form class="fromBox">
		              		<el-input
			              		type="textarea"
			              		:autosize="{ minRows: 15, maxRows: 20}"
			              		placeholder="请输入教师简历介绍"
			              		maxlength="500"
			              		v-model="resume">
			              	</el-input>
			              </el-form>	              		
			            </el-tab-pane>		                            	
		              <el-tab-pane label="薪酬设置" name="five" ref="xinzi">
		              	<el-form class="fromBox gzBox" ref="form" :model="form.salary" :rules="rules">
		              		<el-form-item prop="basic_wages" label="基 本 工 资" class="wages" label-width="32%">
		              			<el-input v-model.trim="form.salary.basic_wages" placeholder="10000" maxlength="9" ref="gzInput" @keyup.native="change_num2()"></el-input>
		              		</el-form-item>
		              		<el-form-item prop="wages_type" label="基本工资是否包含课时费" class="hourMoney" label-width="45.8%">
		              			<el-select v-model="form.salary.wages_type" ref="hourMoneyInput">
		              				<el-option value="1" label="包含" key="1"></el-option>
		              				<el-option value="0" label="不包含" key="0"></el-option>
		              			</el-select>		              			
		              		</el-form-item>
		              		<el-form-item label="说 明" class="explain" label-width="10.6%">
		              			<p>包含，即取底薪与提成两者中最大值者为薪酬；不包含，底薪+提成作为薪酬。</p>
		              		</el-form-item>
		              		<el-divider></el-divider>
		              		<el-form-item prop="setUp" label="课 程 薪 酬 设 置" label-width="17.5%" class="setUp">
		              			<div style="width: 100%; margin-left: 5%;">
		              				<p>1、按出勤人数：按所上课程实际出勤学员数计算; </p>
		              				<p>2、按课时消费：按所有上课学员实际消费金额总和的百分比计算; </p>
		              				<p>3、按授课课时：根据老师上该课的课时数进行固定金额提成计算。</p>		              				
		              			</div>
		              		</el-form-item>
		              		<el-table
			              		:data="form.salary.courses"
			              		border
			              		empty-text="暂无数据"
			              		:show-header="false"
			              		cell-class-name="setUp_cell"
			              		class="setUpTable"
			              		ref="setUpTable"
			              		style="width:100%">
			              		<el-table-column
				              		prop="cur_name"
				              		label="钢琴"
				              		min-width="22.5%">
				              	</el-table-column>
				              	<el-table-column
					              	prop="cur_type"
					              	label="课程模式"
					              	min-width="17.5%">
					              	<template slot-scope="scope">
					              		<span v-if="scope.row.cur_type == 1">一对一</span>
					              		<span v-else>一对多</span>
					              	</template>
					              </el-table-column>				              	
				              	<el-table-column
					              	prop="p_id"
					              	label="课程模式"
					              	min-width="30%">
					              	<template slot-scope="scope">
					              		<el-select v-model="scope.row.p_id" placeholder="选择">
					              			<el-option v-for="(item,index) in salaryPayType" :value="item.p_id" :label="item.p_name" :key="item.p_id"></el-option>
						              	</el-select>
						              </template>
						            </el-table-column>
						            <el-table-column
					              	prop="p_num"
					              	label="课程模式"
					              	min-width="30%">
					              	<template slot-scope="scope">
						              	<el-input v-model.trim="scope.row.p_num" maxlength="9" class="setUp_td" @focus="focusPrice(scope.row.p_num,scope.$index)" @keyup.native="change_num(scope.row.p_num,scope.$index)"></el-input>
						              	<span v-if="scope.row.p_id == 3">元/课时</span>
						              	<span v-else-if="scope.row.p_id == 1">元/人</span>
						              	<span v-else="scope.row.p_id == 2">%</span>
					              	</template>		              	
					              </el-table-column>			              			              
										  </el-table>		              				              		              			              			              		
			              </el-form>	              		
			            </el-tab-pane>	              		              	
	              </el-tabs>
	            </div>
	            <div class="modal-footer alertFooter">
	                <el-button type="primary" round @click="submit">保存</el-button>
	                <el-button round @click="hideModal">取消</el-button> 
	            </div>
	            <right-modal v-if="show1" ref="rightModal" :show="show1" :title="title1" :orgid="orgid" :actives="actives" @hideRightModal="hideRightModal"></right-modal>
	            <teacher-list v-if="show2" :title="title2" :orgid="orgid" @hideRightModalTcl="hideRightModalTcl" @submitRightModalTcl="submitRightModalTcl"></teacher-list>
	        </div>
	    </div>
</template>
<script>
  import axios from 'axios';
  import {editTeacher,senioritieList,teacherData,teacher_courses,salary_pay_type} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import rightModalCom from './right-modal/right-modal-com.vue'
  import teacherList from './right-modal/teacherList.vue'
  import {validateIdNo,validatePhone,checkMaxVal100000000,checkText} from '../../../../../script/validate.js'
  let that
  export default {
    name: 'modal',
    props: {
        title: {
            type: String,
            default: ''
        },
        id: {
        	type: Number,
        	default: false
        },
        orgid: {
        	type: String,
        	default: false
        },
        manager: {
        	type: String,
        	default: false
        },               
    },
    beforeCreate: function () {
    	that = this;
    },    
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            activeName: 'first',
            status:'',
            form: {
              name: '',
              sex:'',
              seniority:'',              
              birthday:'',
              comedata:'',
              phone:'',
              setMeal:'',
              idCard:'',
              status:'',
              occupy:'',
              salary:{
              	wages_type:'1',
              	basic_wages:'',
              	s_id: 1,
              	courses:[]
              }
            },
            rules: {
              name: [
              { required: true, message: '请输入姓名', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              ],
              setMeal: [
              { required: true}
              ],
              phone: [
              { required: true, message: '请输入手机号码', trigger: 'blur'} ,
              { validator:validatePhone, trigger: 'blur'}
              ],
              seniority: [
              { required: true, message: '请选择资历', trigger: 'blur'}
              ],
              sex: [
              { required: true, message: '请选择性别', trigger: 'blur'}
              ],                            
              comedata: [
              { required: true, message: '请选择入职日期', trigger: 'blur'}
              ],                                         
              idCard: [
              { validator:validateIdNo, trigger: 'blur' }
              ],
              basic_wages:[
              { required: true, message: '请输入基本工资', trigger: 'blur' },
              { validator:checkMaxVal100000000, trigger: 'blur' }              		
              ],              
              wages_type: [
              { required: true,},
              ], 
              setUp: [
              { required: true,},
              ],                                                           
            },
            pickerOptions: {
            	disabledDate(time) {
            		let _now = Date.now(),
                    t = 365 * 24 * 60 * 60 * 1000,
                    //t = new Date(1950, 1, 1).getTime(),
                    sevenDays = _now - t;
                //console.log(new Date('1970/01/01 00:00:01').getTime())
                return time.getTime() > _now;
                //return time.getTime() > _now || time.getTime() < sevenDays;
            	},
            },
            imageUrl1: '',
            selectKey: 1,
            show1:false,
            show2:false,
            title1: '选择课程',
            title2: '更换教师',
            cur_list:[],
            resume:'',
            seniority:[],
            salaryPayType:[],
            teacherDetail:[],
            classes:[],
            students:[],
            setUpData:[
            // {
            // 	name:'钢琴',
            // 	pattern:'一对多',
            // 	selectP:'1',
            // 	occupy:''
            // },                    
            ],
            actives: [],
            Imageport          
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    created(){
    	this.teacherData()
    	this.senioritieList()
    	this.teacher_courses()
    	this.salary_pay_type()
    },
    watch : {
    	cur_list:function(res){
    		if(res.length != 0){
    			this.$refs.mySelect.$el.children[0].children[0].setAttribute('value', '已选择(可多选)')
    		}
    	},
    },
    filters: {
    	
    },
    components:{
    	rightModal:rightModalCom,
    	teacherList:teacherList
    },
    methods: {
	    	transform:function(res){
	    		// console.log(this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0])
	    		// console.log(window.innerWidth)
	    		if(window.innerWidth <= 1620){
	    			if(this.activeName == 'first'){
	    				this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '1.04167vw'
	    			}
	    			else{
	    				this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '0.3vw'
	    			}	    			
	    		}	    		
	    		else{
	    			if(this.activeName == 'first'){
	    				this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '1.04167vw'
	    			}
	    			else{
	    				this.$refs.tabList.$el.children[0].children[0].children[0].children[0].children[0].style.marginLeft = '0.6vw'
	    			}		    			
	    		}
	    	},    		
				handleClick(tab,index){
					this.transform(index)
					// console.log(tab,index)
				},
        hideModal() {
         this.$emit('hideModal');
        },

        hideRightModal() {        	
        	this.show1 = false;
        	this.selectKey = '1';      	
        },
        hideRightModalTcl(){
        	this.show2 = false;       	
        },
        showTeacher(){
        	this.show2 = true; 
        },       
                
        submit() {
          this.editTeacher()
        },
        submitRightModalTcl() {
        	this.show2 = false;
        },        
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },
        async salary_pay_type () {
        	let resDate = await salary_pay_type(this.orgid)
        	if (resDate.data.status) {
        		this.salaryPayType = resDate.data.data
        	}      	
        },  
        async senioritieList () {
        	let resDate = await senioritieList(this.orgid)
        	if (resDate.data.status) {
        		this.seniority = resDate.data.data
        	}
        	console.log(this.seniority)
        },
        async teacherData () {
        	let resDate = await teacherData(this.orgid,this.id)
        	if (resDate.data.status) {
        		this.teacherDetail = resDate.data.data;
        		if(this.teacherDetail.teacher.birthday){
        			this.form.birthday = new Date(this.teacherDetail.teacher.birthday*1000)
        		}
        		if(this.teacherDetail.teacher.entry_time){
        			this.form.comedata = new Date(this.teacherDetail.teacher.entry_time*1000)
        		}
        		this.form.phone = this.teacherDetail.teacher.cellphone;
        		this.form.name = this.teacherDetail.teacher.name;
        		this.form.sex = (this.teacherDetail.teacher.sex).toString();
        		this.form.seniority = this.teacherDetail.teacher.se_id;
        		this.form.status = (this.teacherDetail.teacher.status).toString();
        		this.form.tid = this.teacherDetail.teacher.tid;
        		this.imageUrl1 = this.teacherDetail.teacher.avator;
        		this.form.idCard = this.teacherDetail.teacher.id_card;
        		this.resume = this.teacherDetail.teacher.resume;
        		//this.cur_list = this.teacherDetail.curriculums;
        		this.classes = this.teacherDetail.classes;
        		this.teacherDetail.salary.courses.forEach(function(element,index){
        			let newObeject = {}
        			that.teacherDetail.salary.courses[index].p_id = that.teacherDetail.salary.courses[index].p_id
        			newObeject.p_id = that.teacherDetail.salary.courses[index].p_id
        			newObeject.cur_id = that.teacherDetail.salary.courses[index].cur_id
        			newObeject.p_num = that.teacherDetail.salary.courses[index].p_num
        			newObeject.cur_type = that.teacherDetail.salary.courses[index].cur_type
        			newObeject.cur_name = that.teacherDetail.salary.courses[index].cur_name
        			that.form.salary.courses.push(newObeject)
        		});
        		this.setUpData = this.teacherDetail.salary.courses;
        		this.form.salary.basic_wages = this.teacherDetail.salary.basic_wages
        		this.form.salary.s_id = this.teacherDetail.salary.s_id
        		this.form.salary.wages_type = this.teacherDetail.salary.wages_type.toString()
        		this.students = this.teacherDetail.students;
        		
        	}
        },        
        async editTeacher () { 	        	
        	// if(!this.imageUrl1){
        	// 	this.$message.error('请上传头像');
        	// 	return;
        	// };       	
        	if(!this.form.name){
        		this.$message.error('请输入姓名');
        		// console.log(this.$refs.nameInput.$el.children[0])
        		this.$refs.nameInput.$el.children[0].focus();
        		this.$refs.nameInput.$el.children[0].blur();
        		return;
        	}
					else if(this.form.name.length < 2){
            this.$message.error('教师姓名长度不正确');
            this.$refs.nameInput.$el.children[0].focus();
        		this.$refs.nameInput.$el.children[0].blur();
            return;            
        	};
        	if(!this.form.sex){
        		this.$message.error('请选择性别');
        		this.$refs.sexInput.$el.children[0].children[0].click();        		
        		return;
        	};
        	if(!this.form.seniority){
        		this.$message.error('请选择资历');
        		this.$refs.seniorityInput.$el.children[0].children[0].click();        		
        		return;
        	};        	
        	if(this.cur_list.length == 0){
        		this.$message.error('请选择课程');
        		this.$refs.mySelect.$el.children[0].children[0].click();        		
        		return;
        	};
        	let reg =/^[1][3,4,5,6,7,8,9][0-9]{9}$/;
        	if(!this.form.phone){
        		this.$message.error('请输入手机号码');
        		this.$refs.phoneInput.$el.children[0].focus();
        		this.$refs.phoneInput.$el.children[0].blur();
        		return;
        	}
        	else if((!reg.test(this.form.phone)) && this.form.phone != ''){
        		this.$message.error('请输入正确的电话号码')        		
        		return        		  		
        	};
        	if(!this.form.comedata){
        		this.$message.error('请选择入职日期');
        		this.$refs.comeDataInput.$el.children[0].focus();   		
        		return;
        	};
        	let reg1 = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        	if(this.form.idCard){
        		if ((!reg1.test(this.form.idCard))) {
        			this.$message.error('请输入正确的身份证号码');
        			this.$refs.idCardInput.$el.children[0].focus();
        			this.$refs.idCardInput.$el.children[0].blur(); 
        			return        		
        		};        		
        	};        	
        	if(!this.form.salary.basic_wages){
        		this.$message.error('请输入基本工资');
        		document.getElementById('tab-five').click()
        		setTimeout(function(){
        			that.$refs.gzInput.$el.children[0].focus();
        			that.$refs.gzInput.$el.children[0].blur(); 
        		},200)       		  		
        		return;
        	};
        	let key = 1;
        	this.form.salary.courses.forEach(function(element,index){
        		if(that.form.salary.courses[index].p_num == '' && key == 1){
        			key = 2;
        			that.$message.error('请输入课程金额');
        			document.getElementById('tab-five').click();
        			//console.log(that.$refs.setUpTable.$el.children[1].children[0].children[1].children[index].children[3].children[0].children[0].children[0])
        			setTimeout(function(){
        				that.$refs.setUpTable.$el.children[1].children[0].children[1].children[index].children[3].children[0].children[0].children[0].focus();
	        			//that.$refs.setUpTable.$el.children[1].children[0].children[1].children[index].children[3].children[0].children[0].children[0].blur();
        			},50);
        			return false;
        		}
        	});        	
        	if(key == 2){
        		return;
        	};        	        	       	         	      	   	         	        	        	       	
        	let name = this.form.name
        	let sex = this.form.sex
        	let se_id = this.form.seniority
        	let birthday = (new Date(this.form.birthday).getTime())/1000
        	let comeTime = (new Date(this.form.comedata).getTime())/1000
        	let phone = this.form.phone
        	let idCard = this.form.idCard
        	let resume = this.form.resume
        	let cur_list = this.cur_list.join(',')
        	let img = this.imageUrl1
        	let orgid = this.orgid
        	let salary = this.form.salary   	
        	let resDate = await editTeacher(img,name,sex,phone,birthday,comeTime,se_id,idCard,resume,orgid,this.id,salary)
        	if (resDate.data.status) {
        		this.$message({
        			type: 'success',
        			message: resDate.data.sinfo
        		});        		
        		this.$emit('submit')
        	}
        },
        async teacher_courses () {
        	let _this = this;
        	let resDate = await teacher_courses(this.orgid,this.id)
        	if (resDate.data.status) {
        		resDate.data.data.forEach(function(element,index){
        			for(let i=0;i<resDate.data.data[index].courses.length;i++){
        				if(resDate.data.data[index].courses[i].status == 1){
        					resDate.data.data[index].courses[i].active = true
        					_this.cur_list.push(resDate.data.data[index].courses[i].cur_id)
        					_this.actives.push(resDate.data.data[index].courses[i])
        				}
        			}
        		})
        		console.log(this.actives)
        	}
        }, 
        change_phone(){
        	var actualPrice = this.form.phone;        	
        	if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];                          
          }            
          this.form.phone = actualPrice
        },
        change_num1(){
        	var actualPrice = this.form.idCard;        	
        	if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9X,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];                          
          }            
          this.form.idCard = actualPrice
        },        
        change_num2(){
        	var actualPrice = this.form.salary.basic_wages;        	
        	if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0];
						if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
							actualPrice= parseFloat(actualPrice);  
						}
						if(actualPrice > 100000000){
							actualPrice=100000000
							this.$message.error('基本工资范围1-100000000')
						}						                                      
          }            
          this.form.salary.basic_wages = actualPrice
        },
        change_num(e,index){
        	var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
						actualPrice = actualPrice.replace(/[^\d.]/g,"");  //清除“数字”和“.”以外的字符
            actualPrice = actualPrice.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的 
            actualPrice = actualPrice.replace(".","$#$").replace(/\./g,"").replace("$#$",".");
            actualPrice = actualPrice.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数           	
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 100000000){
              actualPrice=100000000
              this.$message.error('薪酬范围不能超过100000000')
            }           
         }            
         this.form.salary.courses[index].p_num = actualPrice
        },
        focusPrice(e,index){
        	var actualPrice = e;
          this.form.salary.courses[index].p_num = e       
        },                                                       
        changeValue(event,b){
        	this.$refs.mySelect.handleClose();
        	if(event && this.selectKey == '1'){
        		this.selectKey = 2;
        		this.showRight()
        		this.show1 = true;
        	}
        },        
        showRight(){
        	
        },        
        //上传图片1
        uploadImg1 (f) {
        	console.log(f)
	         let param = new FormData(); //创建form对象
	         param.append('img',f.file);//通过append向form对象添加数据
	         // param.append('key',response.data.key);//添加form表单中其他数据
	         let config = {
	         	headers:{'Content-Type':'multipart/form-data'}	         	
	         };  //添加请求头
	         axios.post(f.action,param,config)//上传图片
	         .then(response=>{
	         	console.log(response)
	         	this.imageUrl1 = response.data.sinfo;
	         	//onSuccess(response.data)
	         })
	         .catch(({err}) => {
	         	//f.onError()
	         })   
	  	},	  	
        beforeAvatarUpload1(file) {
        	const isJPG = file.type === 'image/jpeg';
        	const isPNG = file.type === 'image/png';
        	const isLt2M = file.size / 1024 / 1024 < 2;

        	if (!isJPG && !isPNG) {
        		this.$message.error('上传图片只能是 png 或者jpg 格式!');
        	}
        	if (!isLt2M) {
        		this.$message.error('上传图片大小不能超过 2MB!');
        	}
        	return isJPG && isLt2M;
        },        
	    handleRemove(file, fileList) {
	     	console.log(file)
	         // 更新缓存文件
	         //console.log('文件删除')
						let param = new FormData(); //创建form对象
             param.append('img',file.raw);//通过append向form对象添加数据
             // param.append('key',response.data.key);//添加form表单中其他数据
             let config = {
                headers:{'Content-Type':'multipart/form-data'}              
             };  //添加请求头
             axios.post(this.$refs.upload.action,param,config)//上传图片
             .then(response=>{
                console.log(response)
                this.imageUrl1 = response.data.sinfo;
                this.$refs.upload.clearFiles();
                
             })
             .catch(({err}) => {
                
             }) 	         
	     },          
    }
  }
