<template>
    <section class='_curriculumDialog' v-dialogDrag >
        <!-- 新增课程弹窗 -->
        <div class='erp-dialog el-dialog'>
          <section class='erpDialogLeft ' >
            <header class='el-dialog__header'>
              <label>{{that.repairDate!=null?"修改课程":"新增课程"}}</label>
              <i class='whitedelete_icon' @click.stop='addSubjectsClick'></i>
            </header>
            <main>
              <section class='_main'>
                <div class='_main-left'>
                  <label>
                    <p>
                      <img src="../../../../../static/header_back/default_bg1.png" width="100%" height="100%"  alt="" v-if='form.listimg==""'>
                      <img :src="Imageport + form.listimg" alt="" v-else>
                    </p>
                    <i>上传课程列表图</i>
                    <input type="file" @change='one_file($event)'>
                  </label>
                  <label>
                    <p>
                      <img src="../../../../../static/header_back/default_bg2.png" width="100%" height="100%"  alt="" v-if='form.infoimg==""'>
                      <img :src="Imageport + form.infoimg" alt="" v-else>
                    </p>
                    <i>上传课程详情背景图</i>
                    <input type="file" @change='two_file($event)' >
                  </label>
                </div>
                <div class='_main-right' @scroll="scrollModalMain($event)">
                  <el-form ref="form" :model="form">
                    <el-form-item label="*课程名称：" >
                      <el-input v-model="form.cur_name" placeholder='请输入课程名称' maxlength='12'></el-input>
                    </el-form-item>
                    <label class='_curriculum_label'>
                      <span class='titles'>*科&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;目：</span>
                      <el-select v-model="form.subject_id" slot="prepend" placeholder="请选择" class='_main-rightSelect'>
                        <el-option v-for='item in getkuListData' :key='item.sid' :label="item.sname" :value="item.sid"></el-option>
                      </el-select>
                      <el-button slot="append" @click.stop='SubjectsClick'>参考</el-button>
                    </label>
                    <label class='_curriculum_labeltwo'>
                      <span class='titles'>*授课方式：</span>
                      <el-select v-model="form.tmethods" slot="prepend" placeholder="请选择" class='_main-rightSelect' :disabled='that.repairDate!=null? true : false'>
                        <el-option label="一对一" value="1"></el-option>
                        <el-option label="一对多" value="2"></el-option>
                      </el-select>
                    </label>
                    <label class='_curriculum_label _curriculum_time culum_time'>
                      <span class='titles'>*时&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;长：</span>
                      <el-input placeholder='填数字，可填范围为15-240' v-model='form.ctime'></el-input>
                      <el-button slot="append">分钟</el-button>
                    </label>
                    <i class='link'></i>
                    <label class='_curriculum_label'>
                      <span>*教师资历(收费标准)：</span>
                    </label>
                    <div class='_charge_label'>
                      <section class='label_left' v-for='init in teacherSeniorit' :key='init.s_id'>
                        <label class='_curriculum_label _curriculum_time'>
                          <span class='titles'>{{init.s_name}}：</span>
                          <el-input placeholder='填数字' v-for='(item,i) in form.Seniorit' :key='item.id' v-if='item.id == init.s_id'
                           v-model='item.sen' @keyup.native="keyUp(item.sen,i)" @change.native="change_fun(item.sen,i)"></el-input>
                          <el-button slot="append">元/课时  </el-button>
                        </label>
                      </section>
                    </div>
                    <label class='shelves_partlabel culum_time'>
                      <section class='label_left'>
                        <label class='label_checkbox'>
                          <span class='_checkbox_head titles'>课程上架：</span>
                          <label class='checkbox_conenter' @click.stop='checkboxClick(1)'>
                            <input type="checkbox" :checked='shelvesState'>
                            <span>上架，小程序可查看</span>
                          </label>
                        </label>
                      </section>
                      <section class='label_right'>
                        <label class='label_checkbox' @click.stop='checkboxClick(2)'>
                          <span class='_checkbox_head c2 titles'>通用课:</span>
                          <label class='checkbox_conenter'>
                            <input type="checkbox" :checked='generalState'>
                            <span>设置为通用课</span>
                          </label>
                        </label>
                      </section>
                    </label>
                      <i class='link'></i>
                      <label class='_curriculum_time textarea_tabel'>
                        <span class='textarea_span'>课程描述：</span>
                        <el-input
                            type="textarea"
                            placeholder="可填写课程描述"
                            maxlength="500"
                            show-word-limit
                            v-model='form.describe'
                          >
                          </el-input>
                      </label>
                    <label class='_curriculum_time textarea_tabel'>
                      <span class='textarea_span titles'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                      <el-input
                          type="textarea"
                          placeholder="可填写备注说明"
                          maxlength="500"
                          show-word-limit
                          v-model='form.remarks'>
                        </el-input>
                    </label>
                  </el-form>
                </div>
              </section>
              <section class='_mainBtn'>
                <el-button type="primary" size='small ' @click.stop='saveClick' :loading="SumbueShatus">保存</el-button>
                <el-button size='small ' @click.stop='addSubjectsClick'>取消</el-button>
              </section>
            </main>
            <transition name='erpSubjects'>
              <section class='erpDialogRight ' v-show='DialogRight'>
                <header>
                  <label>科目分类参考</label>
                  <i class='whitedelete_icon' @click.stop='SubjectsClick'></i>
                </header>
                <main>
                  <section class='Rightmain-contener'>
                    <ol class='fx_listol' v-for='item in getkuListData' :key='item.sid'>
                      <ul>
                        <li><span>{{item.sname}}：</span></li>
                      </ul>
                      <li class='fx_listLi'>
                        <em v-for='items in item.pids' :key='items.sid'>{{ items.sname }}、</em>
                      </li>
                    </ol>
                  </section>
                </main>
              </section>
            </transition>
          </section>
        </div>
    </section>
</template>
<script>
import Imageport from '../../../../config/ImagePz'
import {getkmList, UpImage, deleteImg, determineKc, getKclist, repairmineKc} from '../../../../getserver/server'
import {senioritieList} from '../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
  name: 'currculum',
  data () {
    return {
      currcoureShow: true,
      getkuListData: [],
      Imageport,
      teacherSeniorit: [],// 教师资历列表
      shelvesState: false,
      generalState: false,
      SumbueShatus: false,
      form: {
        cur_name: '',// 课程名称
        subject_id: '',// 科目id
        tmethods: '', // 授课方式 1 -> 一对一 2->一对多
        ctime: '',// 课程时长
        tqualific_one: '', // 教师资历费用 格式为 0/0
        tqualific_two: '',
        Seniorit: [],
        state: 2, // 1上架 2不上架
        conversion: 2, // 是否转化通用课1是2不是
        describe: '', // 课程描述
        remarks: '', // 备注
        listimg: '', // 列表图片
        infoimg: '' // 详情图片
      },
      DialogRight: false
    }
  },
  created () {
    this.getkmListfun()
    this.getsenioritieList()
  },
  inject: ['that'],
  props: {
    addVisible: Boolean
  },
  mounted () {
    if (this.that.repairDate != null) {
      const _this = this
      this.shelvesState = this.that.repairDate.state == 1 ? true : false
      this.generalState = this.that.repairDate.conversion == 1? true : false
      this.form = {
        cur_name: this.that.repairDate.cur_name,// 课程名称
        subject_id: this.that.repairDate.subject.sid,// 科目id
        tmethods: '' + this.that.repairDate.tmethods, // 授课方式 1 -> 一对一 2->一对多
        ctime: this.that.repairDate.ctime,// 课程时长
        Seniorit: [],
        // tqualific_one: this.that.repairDate.ordinary_tqualific, // 教师资历费用 格式为 0/0
        // tqualific_two: this.that.repairDate.senior_tqualific,
        state: this.that.repairDate.state, // 1上架 2不上架
        conversion: 2, // 是否转化通用课1是2不是
        describe: this.that.repairDate.describe, // 课程描述
        remarks: this.that.repairDate.remarks, // 备注
        listimg: this.that.repairDate.listimg, // 列表图片
        infoimg: this.that.repairDate.infoimg // 详情图片
      }
    }
  },
  computed:{
    ...mapState(['userLoginInfo']),
  },
  methods: {
    keyUp(value, i){
      let nums = ""
      if (!/\d+$/g.test(value)){
            // 不是数字 剔除掉非数字 并保留一个小数点
            nums = this.form.Seniorit[i].sen.replace(/[^0-9X.,]*/g,"");
            nums = nums.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            nums = nums.replace(".","$#$").replace(/\./g,"").replace("$#$","."); //只保留第一个. 清除多余的  
        } else {
            // 是数字的话 判断只能有一个小数点
            nums = this.form.Seniorit[i].sen.replace(/[^0-9X.,]*/g,"");
            nums= this.form.Seniorit[i].sen.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
        }
        if(nums > 100000){
          this.$message({
            message: '最高不能超过10万元',
            type: 'error',
            duration: 2000,
          });
          this.form.Seniorit[i].sen = '100000'
          return 
        } else {
          this.form.Seniorit[i].sen = nums
        }
    },
    change_fun(value,i){
      let nums = ""
      if (!/\d+$/g.test(value)){
            // 不是数字 剔除掉非数字 并保留一个小数点
            nums = this.form.Seniorit[i].sen.replace(/[^0-9X.,]*/g,"");
            nums = nums.replace(/\.{2,}/g,"."); //只保留第一个. 清除多余的  
            nums = nums.replace(".","$#$").replace(/\./g,"").replace("$#$","."); //只保留第一个. 清除多余的  
        } else {
            // 是数字的话 判断只能有一个小数点
            nums = this.form.Seniorit[i].sen.replace(/[^0-9X.,]*/g,"");
            nums= this.form.Seniorit[i].sen.replace(/^(\-)*(\d+)\.(\d\d).*$/,'$1$2.$3');//只能输入两个小数 
        }
        if(nums > 100000){
          this.$message({
            message: '最高不能超过10万元',
            type: 'error',
            duration: 2000,
          });
          this.form.Seniorit[i].sen = '100000'
          return 
        } else {
          this.form.Seniorit[i].sen = nums
        }
    },
    saveClick() {
      // 保存
      this.setkecC()
    },
    two_file (e) {
      // 上传课程详情图
      if (this.form.infoimg!='') {
        // 重复上传， 先删除后上传
        this.deleteImg(this.form.listimg, e, 1);
        return
      } 
      this.setUpImage(e, 1);
    },
    one_file (e) {
      // 上传列表图
      if (this.form.listimg!='') {
        // 重复上传， 先删除后上传
        this.deleteImg(this.form.listimg, e, 0);
        return
      } 
      this.setUpImage(e, 0);
    },
    async setUpImage (e, i) {
      // 上传 图片
      let files = e.target.files[0];
      let regulaImg = /(.png|.image|.img|.jpeg|.jpg)/;
      if (!regulaImg.test(files.name)) {
        this.$message.error('图片格式不正确');
        return
      }
      let form = new FormData();
      form.append('img', files)
      let setUpImgDate = await UpImage(form)
      if (setUpImgDate.data.status) {
        switch (i) {
          case 0:
            // 列表图上传
            this.form.listimg = setUpImgDate.data.sinfo
            break;
          case 1:
            // 列表详情图上传
            this.form.infoimg = setUpImgDate.data.sinfo
            break;
        }
        return
      }
    },
    async getkmListfun () {
      // 获取科目列表
      let getDatelist = await getkmList()
      if (getDatelist.data.status) {
        // 获取成功
        this.getkuListData = getDatelist.data.data
      }
    },
    async deleteImg (ImgUrl, files, i, k) {
      // 删除图片
      // k 为用户点击取消时传递的参数
      // if(k == 'close') {
      //   if (this.form.listimg!=''){
      //     deleteImg(this.form.listimg);
      //   }
      //   if (this.form.infoimg!=''){
      //     deleteImg(this.form.infoimg);
      //   }
      //   return
      // }
      let deleDate = await deleteImg(ImgUrl);
      if (deleDate.data.status){
        // 删除成功
        this.setUpImage(files, i)
      }
    },
    async setkecC(){
      // 保存课程
      this.SumbueShatus = true
      const _this = this
      if (this.form.cur_name.trim().length == 0) {
        this.$message({
          message: "课程名称不能为空",
          type: 'error',
          duration: 2000,
        });
        this.SumbueShatus = false
        return
      }
      let senSeniorit = [];
      let senSenioritid = []
      for (let i in this.form.Seniorit){
        senSeniorit.push(this.form.Seniorit[i].sen)
        senSenioritid.push(this.form.Seniorit[i].id)
      }
      if (this.that.repairDate!=null) {
        let curId = ''
        curId = this.that.repairDate.cur_id
        this.SumbueShatus = false
        let SetdetermineKc = await repairmineKc(this.form.cur_name,this.form.subject_id,this.form.tmethods, this.form.ctime, senSeniorit.join('/'), senSenioritid.join('/'), this.form.state, this.form.conversion,   this.form.describe, this.form.remarks, this.form.listimg, this.form.infoimg, curId, this.userLoginInfo.orgid)
        if (SetdetermineKc.data.status) {
          // 修改课程成功
          this.$message({
            message: SetdetermineKc.data.sinfo,
            type: 'success',
            duration: 2000,
          });
         }
         
        // this.getsenioritieList()
        getKclist(_this.that.page_num, _this.that.formseach.searchValue, _this.that.formseach.kmActive, _this.that.formseach.value, _this.that.formseach.states, this.userLoginInfo.orgid,8).then((res)=>{
          if (res.data.status) {
            _this.that.tableData = res.data.data.data
            _this.that.countNum = res.data.data.total
          }
        })
      } else {
        if (this.form.cur_name == '' || this.form.cur_name.trim().length == 0) {
          this.$message({
            message: '课程名称不能是空!',
            type: 'error',
            duration: 2000,
          });
          this.SumbueShatus = false
          return
        } else if (senSeniorit.length == 0) {
          this.$message.error('教师资历不能为空!');
          this.SumbueShatus = false
          return
        }
        setTimeout(()=>{
          _this.SumbueShatus = false
        },1000)
        let SetdetermineKc = await determineKc(this.form.cur_name,this.form.subject_id,this.form.tmethods, this.form.ctime, senSeniorit.join('/'), senSenioritid.join('/'), this.form.state, this.form.conversion, this.form.describe, this.form.remarks, this.form.listimg, this.form.infoimg,"",this.userLoginInfo.orgid)
        if (SetdetermineKc!=undefined) {
            // 添加课程成功
            this.$message({
              message: SetdetermineKc.data.sinfo,
              type: 'success',
              duration: 2000,
            });
            this.form.Seniorit.forEach((data)=> {
              data.sen = ""
            })
            this.form = {
              cur_name: '',// 课程名称
              subject_id: '',// 科目id
              tmethods: '', // 授课方式 1 -> 一对一 2->一对多
              ctime: '',// 课程时长
              Seniorit: this.form.Seniorit,
              state: 2, // 1上架 2不上架
              conversion: 2, // 是否转化通用课1是2不是
              describe: '', // 课程描述
              remarks: '', // 备注
              listimg: '', // 列表图片
              infoimg: '' // 详情图片
            }
            // this.getsenioritieList()
            getKclist(_this.that.page_num,'','','','','',8).then((res)=>{
              if (res.data.status) {
                _this.that.tableData = res.data.data.data
                _this.that.countNum = res.data.data.total
              }
            })
          } else {
            this.$message({
              message: SetdetermineKc.data.error_msg,
              type: 'error',
              duration: 2000,
            });
          }
        }
    },
    async getsenioritieList () {
      // 获取教师资历列表
      let resDate = await senioritieList(this.userLoginInfo.orgid)
      if (resDate.data.status) {
        // 获取成功
        this.teacherSeniorit = resDate.data.data
        const _this = this
        if (this.that.repairDate != null) { 
          let SenioritArr = _this.that.repairDate.tqualific.split('/');
          resDate.data.data.forEach((data,i)=>{
            SenioritArr.forEach((datas,index)=>{
              if (i == index) {
                _this.form.Seniorit.push({id: data.s_id, sen: datas});
              }
            })
          })
        } else {
          resDate.data.data.forEach((data,index)=>{
            _this.form.Seniorit.push({id: data.s_id, sen: "" });
          })
        }
      }
    },
    SubjectsClick () {
      // 科目参考click 打开或关闭
      this.DialogRight = !this.DialogRight
    },
    addSubjectsClick () {
      // 新增课程关闭
      this.$emit('close', false);
      // this.deleteImg('','','', 'close')
    },
    emitcurr (i) {
      console.log(i)
    },
    handleClose () {
      this.$emit('close', false)
    },
    checkboxClick (n) {
      switch (n) {
        case 1:
          this.shelvesState = !this.shelvesState
          this.shelvesState==true?this.form.state=1:this.form.state=2
          break;
        case 2:
          this.generalState = !this.generalState
          this.generalState==true?this.form.conversion=1:this.form.conversion=2
          break;
      }
    },
    handleSelect(item){
      // var restaurants = this.restaurants;
      // var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      //   // 调用 callback 返回建议列表的数据
      // cb(results);
    }
  },
  watch:{
    'form.ctime' (value) {
      if (!/^\d+$/g.test(value)){
        this.form.ctime = value.substring(0, value.length-1)
      }
    }
    // 'form.tqualific_one'(value){
    //   if (!/^[1-9]\d{0,5}$/g.test(value)){
    //     if(value > 100000){
    //       this.$message({
    //           message: '最高不能超过10w万',
    //           type: 'error',
    //           duration: 2000,
    //       });
    //       this.form.tqualific_one = value.substring(0, value.length-1)
    //       return
    //     }
    //     this.form.tqualific_one = value.substring(0, value.length-1)
    //   } else {
    //     if(value > 100000){
    //       this.$message({
    //           message: '最高不能超过10w万',
    //           type: 'error',
    //           duration: 2000,
    //       });
    //       this.form.tqualific_one = value.substring(0, value.length-1)
    //       return
    //     }
    //   }
    // },
    // 'form.tqualific_two'(value){
    //   if (!/^\d+$/g.test(value)){
    //     if(value > 100000){
    //       this.$message({
    //           message: '最高不能超过10w万',
    //           type: 'error',
    //           duration: 2000,
    //       });
    //       this.form.tqualific_two = value.substring(0, value.length-1)
    //       return
    //     }
    //     this.form.tqualific_two = value.substring(0, value.length-1)
    //   } else {
    //     if(value > 100000){
    //       this.$message({
    //           message: '最高不能超过10w万',
    //           type: 'error',
    //           duration: 2000,
    //       });
    //       this.form.tqualific_two = value.substring(0, value.length-1)
    //       return
    //     }
    //   }
    // },
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
@import '../../../../style/var';
@media screen and (max-width: 1380px){
  ._curriculumDialog .erp-dialog {
    @include wh(auto, vw1(660));
  } 
  .erpDialogLeft{
    @include wh(vw1(860), 100%);
    margin-right: vw1(4);
    header{
      @include wh(100%, vw1(60));
      font-size: vw1(16);
      padding: 0 vw1(30);
      i{
        @include wh(vw1(20), vw1(20));
      }
    }
    main{
      ._main-left{
        @include wh(vw1(219), 100%);
        p{
          @include wh(vw1(160),vw1(160));
          max-height: vw1(160);
          margin-top: vw1(30);
          margin-bottom: vw1(20);
        }
        i{
          display: inline-block;
          @include wh(vw1(160), vw1(40));
          line-height: vw1(40);
          font-size: vw1(16);
        }
        input[type='file']{
          @include wh(vw1(160), vw1(40));
          left: vw1(30);
        }
      }
      ._main-right{
        padding: vw1(30);
        /deep/ .el-form .el-form-item .el-form-item__content .el-input .el-input__inner{
          height: vw1(40)
        }
        ._main-rightSelect{
          margin-left: vw1(15);
          font-size: vw1(16);
        }
        ._curriculum_label,._curriculum_labeltwo{
          margin-bottom: vw1(30);
        }
        ._curriculum_labeltwo /deep/ .el-select .el-input .el-input__inner{
          @include wh(100%, vw1(40))
        }
        ._charge_label{
          .label_left{
            // margin-left: vw1(8);
            // flex: 1;
            width: 100%;
          }
          .label_right{
            margin-left: vw1(48);
          }
        }
        .shelves_partlabel {
          margin-left: vw1(8);
          font-size: vw1(16);
          margin-bottom: vw1(30);
          .label_right{
            margin-left: vw1(20);
          }
          .label_checkbox {
            .c2{
              letter-spacing: vw1(7.6);
            }
            ._checkbox_head{
              margin-right: vw1(16);
