<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索套餐" v-model="inputVal" @change="change">
                    <i class='search_icon icon-width'></i>
                </label>
                <label class='label_elect selectTwo'>
                    <span>套餐状态： </span>
                    <el-select v-model="value" placeholder="全部" @change="change($event)">
                    	<el-option value="" label="全部"></el-option>
                        <el-option value="1" label="启用中"></el-option>
                        <el-option value="2" label="已关闭"></el-option>
                    </el-select>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">
            	<Lbutton class='hot_btn' @click.native='refresh()'>
            		<i class='icon-width refresh_icon'></i>
            		刷新数据
            	</Lbutton>
            	<Lbutton class='hot_btn' @click.native="upload()">
            		<i class='icon-width export_icon'></i>
            		导出Excel
            	</Lbutton>                                        	          	
            	<Lbutton class='hot_btn' @click.native='showAlert()'>
            		<i class='icon-width jia_icon'></i>
            		新增套餐
            	</Lbutton> 
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="hackReset" :show="show" :title="title" :orgid="orgid" :manager="manager" @hideModal="hideModal" @submit="submit">
      	
      </dialogComponent>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/alertBox'
import Imageport from '../../../../../config/ImagePz.js'
import {mapState} from 'vuex'
export default {
    name: 'tabTops',
	props: {
        orgid: {
            type: String,
            default: false
        },
        manager: {
        	type: String,
        	default: false
        },        
    },    
    data () {
    	return {
        value: '',
        title: '新增套餐',
        show: false,
        inputVal:'',
        status:'',
        hackReset:true, 
        Imageport
      }
    },
    computed:{
    	...mapState(['userLoginInfo']),
    },     
    methods: {
      upload(){
        console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"index/Index/setmeal_export?orgid=" + this.userLoginInfo.orgid + "&uid=" + this.userLoginInfo.id + "&meal_name=" + this.inputVal + "&cur_state=" + this.status
      },
      showAlert(){
      	this.show = true
      },      
      hideModal() {
        // 取消弹窗回调
        this.hackReset = false
        this.$nextTick(() => {
        	this.hackReset = true
        });
        this.$emit('hideModal');
        this.show = false;
      },
      refresh(){
      	//刷新
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.$message({
         type: 'success',
         message: '刷新成功'
       });      		   	
      },      

      submit() {
        // 确认弹窗回调
        // this.$emit('getclassRoomLists', 1)
        this.hackReset = false
        this.$nextTick(() => {
        	this.hackReset = true
        });        
        this.show = false;
        this.$parent.last_page = 0
        this.$parent.paging_father(1)        
        //this.$emit('setMealList',1)
      },
      change() {
        this.$parent.name = this.inputVal
        this.$parent.status = this.value
        // console.log(this.$emit())
        this.$parent.last_page = 0
        this.$parent.paging_father(1)       
      },
      // selectFn(e) {
      //   console.log(e)
      //   this.status = e;
      //   this.$emit('selectsetMeal',e,1)
      // }    
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
    cursor: pointer
  }
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:block;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: vw(6);
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, vw(40));
  }
  .selectTwo{
    @include wh(vw(220), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: vw(8);
        height: 100%;
        .el-icon-arrow-up{
          display: flex;
          align-items: center;
        }
        /deep/ .el-input{
          height: 100%;
           .el-input__inner {
             height: 100%;
             border-radius:6px
           }
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding: 0 0.5vw;
    .icon-width{
        margin-right: vw(10);
    }
  }
  .selectTwo /deep/ .el-input__inner{
  	font-size:vw(16);
  	padding:0 vw(10)
  }  
}
@media screen and (max-width: 1440px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      width: 40%!important;
    }
    .selectone1{
    	width:35%;
    }
    .el-col-12:last-child{
      width: 60%!important;
      margin-top: 0px;
    }
  }
  .selectTwo /deep/ .el-input__inner{
  	font-size:vw1(16);
  	padding:0 vw1(10)
  }
  ._tabTop{
  	// margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
    .label_elect{
      font-size:16px
    }    
  }
  .selectone{
    @include wh(20.52%, vw1(40));
  }
  .selectTwo{
    @include wh(vw1(220), vw1(40));
  }       
}
</style>
