<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_search'>
                    <input type="text" placeholder="搜索教室" v-model="inputVal" @change="change">
                    <i class='search_icon icon-width'></i>
                </label>
                <label class='label_elect selectTwo selectone1'>
                    <span>教室状态： </span>
                    <el-select v-model="value" placeholder="全部" @change="change($event)">
                        <el-option value="" label="全部"></el-option>
                        <el-option value="1" label="可用"></el-option>
                        <el-option value="2" label="不可用"></el-option>
                    </el-select>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">
            	<Lbutton class='hot_btn' @click.native='refresh()'>
            		<i class='icon-width refresh_icon'></i>
            		刷新数据
            	</Lbutton>
            	<Lbutton class='hot_btn' @click.native="upload()">
            		<i class='icon-width export_icon'></i>
            		导出Excel
            	</Lbutton>  
            	<Lbutton class='hot_btn' @click.native="download()">
            		<i class='icon-width download_icon'></i>
            		下载模板
            	</Lbutton>
            	<Lbutton class='hot_btn' @click.native="importClassRoom()">
            		<i class='icon-width import_icon'></i>
            		导入Excel
            	</Lbutton>                                       	          	
            	<Lbutton class='hot_btn' @click.native='showAlert()'>
            		<i class='icon-width jia_icon'></i>
            		新增教室
            	</Lbutton> 
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="hackReset" :show="show" :title="title" @hideModal="hideModal" @submit="submit" :orgid="userLoginInfo.orgid">
      </dialogComponent>
      <importClassRoom v-if="hackReset1" :show="showLoad" @hideModal="hideLoadModal" @submit="submitLoad">
      </importClassRoom>      
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/alertBox'
import importClassRoom from '../alert-box/importClassRoom'
import Imageport from '../../../../../config/ImagePz.js'
import {mapState} from 'vuex'
export default {
    name: 'tabTops',
    data () {
    	return {
        addVisible: false,// 控制新增课程模态窗
        value: '',
        title: '新增教室',
        show: false,
        showLoad: false,
        inputVal:'',
        hackReset:true,
        hackReset1:true,
        Imageport    
      }
    },
    created(){
      
    },
    computed:{
      ...mapState(['userLoginInfo']),
    },    
    methods: {
      download(){
        // console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"public/upload/file/classroom.xlsx"
      },
      upload(){
        console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"index/excel/room_ept?orgid=" + this.userLoginInfo.orgid + "&uid=" + this.userLoginInfo.id
      },
      importClassRoom(){
        this.showLoad = true
      },           
      curriculumClose (k) {
        this.addVisible = k
      },
      showAlert(){
      	this.show = true
      },      
      hideModal() {
        this.hackReset = false
        this.$nextTick(() => {
          this.hackReset = true
        });        
        // 取消弹窗回调
        this.show = false
      },
      hideLoadModal() {
        this.hackReset1 = false
        this.$nextTick(() => {
          this.hackReset1 = true
        });        
        // 取消弹窗回调
        this.showLoad = false
      },      
      refresh(){
      	//刷新
      	this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.$message({
         type: 'success',
         message: '刷新成功'
       });      		   	
      },      

      submit() {
        // 确认弹窗回调
        this.hackReset = false
        this.$nextTick(() => {
          this.hackReset = true
        });        
        // this.$emit('getclassRoomLists', 1)
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.show = false
      },
      submitLoad() {
        // 确认弹窗回调
        this.hackReset1 = false
        this.$nextTick(() => {
          this.hackReset1 = true
        });     
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.showLoad = false
      },      
      change() {
        console.log(this.$parent.status)
      	this.$parent.name = this.inputVal
        this.$parent.status = this.value
        this.$parent.last_page = 0
        this.$parent.paging_father(1)        
      	//this.$parent.getclassRoomLists(1)
      },
      // selectFn(e) {
      //   console.log(e)

      //   this.$emit('selectclassRoom',e,1)
      // }    
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
      importClassRoom:importClassRoom
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:block;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: 6px;
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, vw(40));
  }
  .selectTwo{
    @include wh(vw(220), vw(40));
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: vw(8);
        height: 100%;
        /deep/ .el-input {
          height: 100%;
          .el-input__inner{
            height: 100%;
            border-radius:6px
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding:  0 0.5vw;
    .icon-width{
        margin-right: vw(10)
    }
  }
  .selectone1 /deep/ .el-input__inner{
    font-size:vw(16);
    padding:0 vw(10)
  }
}
@media screen and (max-width: 1440px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      width: 40%!important;
    }
    .selectone1{
    	//width:40%;
    }
    .el-col-12:last-child{
      width: 60%!important;
      margin-top: 0px;
    }
  }
.selectTwo /deep/ .el-input__inner{
    font-size:vw1(16);
    padding:0 vw1(10)
  }
  ._tabTop{
    // margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
    .label_elect{
      font-size:16px
    }
  }
  .selectone{
    @include wh(20.52%, vw1(40));
  }
  .selectTwo{
    @include wh(vw1(220), vw1(40));
  }   
}
</style>
