<template>
    <div class='_addStudent'>
        <el-dialog
            v-dialogDrag
            title="新增学生"
            :visible.sync="tablesThis.addStudentShow"
            width="">
            <section class='section_table_body'>
                <el-form ref="form"  >
                    <el-form-item label="*学生姓名：">
                        <el-input placeholder='请输入学生姓名' maxlength='12' v-model='form.studentName'></el-input>
                    </el-form-item>
                    <section class='_sectionl_two_label'>
                        <label class='_curriculum_labeltwo'>
                            <span class='label_text'>*性别：</span>
                            <el-select  v-model='form.sex' slot="prepend" placeholder="请选择" class='_main-rightSelect' >
                                <el-option label="男" :value="1" ></el-option>
                                <el-option label="女" :value="2" ></el-option>
                            </el-select>
                        </label>
                        <label class='_curriculum_labeltwo'>
                            <span class='label_text' style='margin-right:0'>出生年月：</span>
                            <el-date-picker
                                v-model="form.Times"
                                type="date"
                                placeholder="选择日期">
                            </el-date-picker>
                        </label>
                    </section>
                    <el-form-item label="* 手 机 号：" style='letter-spacing: 0.2px;'>
                        <el-input placeholder='请输入手机号' maxlength='11' v-model='form.phone'></el-input>
                    </el-form-item>
                    <el-form-item label=" 微 信 号：" class='phonefrom'>
                        <el-input placeholder='6-20位，字母、数字、下划线和减号，必须以字母开头' maxlength='20' v-model='form.wxphone'></el-input>
                    </el-form-item>
                    <label class='_curriculum_time textarea_tabel textarea_dizhi'>
                      <span class='textarea_span'>地&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;址：</span>
                      <el-input
                          type="textarea"
                          placeholder="请输入地址"
                          show-word-limit
                          v-model='form.gps'
                        >
                        </el-input>
                    </label>
                    <label class='_curriculum_time textarea_tabel'>
                      <span class='textarea_span'>备&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;注：</span>
                      <el-input
                          type="textarea"
                          placeholder="可填写备注说明"
                          show-word-limit
                          v-model='form.note'
                        >
                        </el-input>
                    </label>
                </el-form>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" size='mini' :loading="configShow" @click.stop='save'>保存</el-button>
                <el-button size='mini'  @click.stop='tablesThis.addStudentShow = false'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {studentAdd} from '../../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'addtudent',
  data () {
    return {
      configShow: false,
      form: {
        studentName: '',
        sex: '',
        Times: '',
        phone: '',
        wxphone: '',
        gps: '',
        note: ''
      }
    }
  },
  inject: ['tablesThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    save(){
      //  保存
      this.configShow = true
      let error_str = null
      let Repphone = /^1[384569]\d{9}$/
      let wxRepphone = /^([a-zA-ZA-z]|\d|[a-zA-ZA-z]|_|-){5,19}$/
      if (this.form.studentName == '') {
        error_str = '学生姓名不能为空！'
      } else if (this.form.sex == '') {
        error_str = '性别为必填项,请选填！'
      } else if (this.form.phone == '') {
        error_str = '手机号码不能为空！'
      } else if (!Repphone.test(this.form.phone)){
        error_str = '手机号码格式不正确，请检查！'
      } else if (!wxRepphone.test(this.form.wxphone)) {
        error_str = '微信号码格式不正确，请检查！'
      }
      if (error_str != null) {
          this.$message({
            message: error_str,
            type: 'warning',
            duration: 1500
        });
        return
      }
      this.form.Times =(new Date(this.form.Times).getTime()) / 1000
      this.setaddStudent()
    },
    async setaddStudent () {
      let resDate = await studentAdd(this.userLoginInfo.orgid, 1, this.form.studentName, this.form.wxphone, this.form.sex, this.form.phone, this.form.gps, this.form.note, this.form.Times)
      if (resDate.data.status) {
          // 添加成功
        this.$message({
            message: '添加成功',
            type: 'success'
        });
        this.form = {
            studentName: '',
            sex: '',
            Times: '',
            phone: '',
            wxphone: '',
            gps: '',
            note: ''
        }
      }
      this.configShow = false
    }
  },
  watch:{
    'tablesThis.addStudentShow'(){
        this.form = {
            studentName: '',
            sex: '',
            Times: '',
            phone: '',
            wxphone: '',
            gps: '',
            note: ''
        }
    }
  }
}
</script>
