<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './td-schedule-top/td-schedule-top'
import remarks from '../../../public/remarks.vue'
import tipBox from './alert-box/tipBox.vue'
import menuBox from './alert-box/menu.vue'
import editTdSchedule from '../td-schedule/alert-box/alertBox.vue'
import {cate_index,cate_del} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'classification',
  data () {
    return {
      x: 0,
      y: 0,
      node: null,
      isCanMove: false,      
      that: this,
      tableData: [
      {name:'凌老师',tb:[{student:'林乐曦',td:1,time:1568260121,endTime:1568262641,surplus:1},{student:'黄小乐',td:2,time:1568269215,endTime:1568273415,surplus:2,truancy:1}]},
      {name:'凌老师',tb:[{student:'林乐曦',td:3,time:1568505600,endTime:1568508600,surplus:5},{student:'周小小',td:4,time:1568529000,endTime:1568532600,surplus:1,schedule:1}]},
      {name:'凌老师',tb:[]},
      {name:'凌老师',tb:[{student:'周小小',td:5,time:1568514600,endTime:1568516400,surplus:1,adjustment:1}]},
      {name:'凌老师',tb:[]},
      {name:'凌老师',tb:[]},
      {name:'凌老师',tb:[{student:'林乐曦',td:6,time:1568541600,endTime:1568546400,surplus:1,deletedClass:1}]},
      {name:'凌老师',tb:[{student:'林乐曦',td:7,time:1568541600,endTime:1568546400,surplus:1,deletedClass:1}]},
      {name:'凌老师',tb:[{student:'林乐曦',td:8,time:1568541600,endTime:1568546400,surplus:1,deletedClass:1}]},
      {name:'凌老师',tb:[{student:'林乐曦',td:9,time:1568541600,endTime:1568546400,surplus:1,deletedClass:1}]},
      ],
		  value: '0',
		  title: '钢琴课',
		  show: false,
		  showDe:false,
		  deleteId:null,
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数
      cate_pid:'',//修改的分类父级id
      cate_pname:'',//修改的分类父级名称
      cate_name:'',//修改的分类名称
      id:'',//修改的分类id
      toDay: new Date(new Date(new Date().toLocaleDateString()).getTime() + 7 * 60 * 60 * 1000 ).getTime()/1000,
      nowDate:null,
      tipPosition:false,
      menuBoxPosition:false,
      tipX:0,
      tipY:0,
      menuX:0,
      menuY:0

    }
  },
  watch: {
    nowDate:function(){
      var t1 =  window.setTimeout(function(){
        that.nowDate = new Date()
        //console.log(Date.parse(that.nowDate),that.toDay)
        window.clearTimeout(t1);//去掉定时器 
      },360000)
    }    
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{    
  	serialNumber ( val ){
  		return (that.page_num-1)*8+(val + 1)
  	},
    time (val){
      let _now = new Date(val*1000)
      let hour = _now.getHours()
      let minutes = _now.getMinutes()
      return  hour + ":" + minutes   
    }
  }, 
  methods: {
    dragStart(event) {
      //console.log(event)
      this.hideTip ()
      event.dataTransfer.setData("Text", event.target.id);
    },    
    dragging(event) {
      //document.getElementById("demo").innerHTML = " p 元素正在拖动";
    },
    allowDrop(event) {
      event.preventDefault();
    },
    drop(event) {
      //console.log(event)
      this.hideMenu()
      event.preventDefault();
      var data = event.dataTransfer.getData("Text");
      event.target.appendChild(document.getElementById(data));
      document.getElementById(data).style.left = (event.clientX/document.body.clientWidth*100 - 3.125 - event.path[3].children[0].clientWidth/document.body.clientWidth*100)*document.body.clientWidth/100-parseFloat(document.getElementById(data).style.width)*event.path[2].clientWidth/100/2 + 'px'
      this.showAlert()
      //document.getElementById("demo").innerHTML = "p 元素已被拖动";
    },    
    headClass ({row, rowIndex}){
      if (rowIndex === 1) {
        return 'warning-row';
      }
      if (rowIndex === 0) {
        return 'warning-row1';
      }      
      return '';
    },
    paging_father (n) {
      this.cate_index(n)
    },
    showTip (e){
      if(this.isCanMove == false){
        this.tipX = e.x
        this.tipY = e.y
        this.tipPosition = true        
      }
      // console.log($(e))
    },
    hideTip (e){
      this.tipPosition = false
    },
    showMenu (e){
      console.log(e)
      if(this.isCanMove == false){
        this.menuX = $(e.path[0]).offset().left -10
        this.menuY = $(e.path[0]).offset().top+e.path[0].clientHeight+10
        this.menuBoxPosition = true        
      }
      // console.log($(e))
    },
    hideMenu (e){
      this.menuBoxPosition = false
    },             
    async cate_index (n) {
    	let t = (new Date()).getTime();
    	let resDate = await cate_index(n,this.userLoginInfo.orgid,t)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    		this.last_page = resDate.data.data.last_page
    		this.counts = resDate.data.data.total
    		this.page_num = n;   		            
    	}
    },            
    cate_del (id) {
		// 删除教室
		cate_del(id,this.userLoginInfo.orgid).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				if((this.tableData.length -1) == 0){
					this.cate_index (this.page_num-1)
				}
				else{
					this.cate_index (this.page_num)
				}
			}					
    })
		},        
    showAlert(a,b){
    	this.show = true
    },   
    hideModal() {
    // 取消弹窗回	        
	    this.show = false
    },
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.showDe = false;
    	this.cate_del(id)
    },
    submit() {
    // 确认弹窗回调   
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    },
    arraySpanMethod ({ row, column, rowIndex, columnIndex }) {
      // console.log(row, column, rowIndex, columnIndex)
      // 打印出的数据就是表格当前行的数据,当前列的数据,索引
      for(var i=0;i<this.tableData.length;i++){
        if (rowIndex === i) {
          // 合并第二行
          if (columnIndex === 1) {
            // 从第二列开始
            return [1, 17]  
          }
        }        
      }
    }   
  }, 
  created: function () {
  	//this.cate_index (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px") 
    this.nowDate = new Date()	
  },
  components: {
  	erpTabtop: erpTabtop,
  	remarks: remarks,
  	editTdSchedule: editTdSchedule,
    tipBox:tipBox,
    menuBox:menuBox
  }
}
</script>
