<template>
    <div class='_tabTop'>
      <el-row class='meadio'>
        <el-col :span="8">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_elect selectTwo' style="margin-left: 0">
                    <span>分类： </span>
                    <el-select v-model="cate_name" placeholder="选择分类" popper-class="treeSelect" @focus="showSelect()" ref="selectRef">
                      <el-option :value="valueModel" :label="labelModel">
                        <el-tree :data="cateList" node-key="id" :check-strictly="true" check-on-click-node auto-expand-parent default-expand-all :default-checked-keys= "[cheackId]" :props="defaultProps" @node-click="handleNodeClick" @check-change="handleClick" ref="treeForm"></el-tree>
                      </el-option>
                    </el-select>
                </label>
                <label class='label_search'>
                  <input type="text" placeholder="搜索商品" v-model="inputVal" @change="change">
                  <i class='search_icon icon-width'></i>
                </label>                             
            </div>          
        </el-col>
        <el-col :span="16">
            <div class="grid-content bg-purple-dark _tabTopconent rightBox">
            	<Lbutton class='hot_btn' @click.native='refresh()'>
            		<i class='icon-width refresh_icon'></i>
            		刷新数据
            	</Lbutton>
              <Lbutton class='hot_btn' @click.native="upload()">
                <i class='icon-width export_icon'></i>
                导出Excel
              </Lbutton>
              <Lbutton class='hot_btn' @click.native="upload()">
                <i class='icon-width export_icon'></i>
                下载模板
              </Lbutton>              
              <Lbutton class='hot_btn' @click.native="importGoods()">
                <i class='icon-width import_icon'></i>
                导入Excel
              </Lbutton>                                                  	          	
            	<Lbutton class='hot_btn' @click.native='showAlert()'>
            		<i class='icon-width jia_icon'></i>
            		新增商品
            	</Lbutton> 
            </div>
        </el-col>
      </el-row>
      <dialogComponent v-if="show" :show="show" :title="title" :that="that" @hideModal="hideModal" @submit="submit" :orgid="userLoginInfo.orgid">
      </dialogComponent>     
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import dialogComponent from '../alert-box/alertBox'
import {cate_list} from '../../../../../getserver/getAxios.js'
import Imageport from '../../../../../config/ImagePz.js'
import {mapState} from 'vuex'
export default {
    name: 'tabTops',
    data () {
    	return {
        addVisible: false,// 控制新增课程模态窗
        value: '',
        valueModel:'',
        labelModel:'',
        cheackId:null,
        cate_name:'',
        cateList:[],     
        title: '新增商品',
        inputVal:'',       
        show: false,
        showLoad: false,
        that:this.$parent,
        defaultProps: {
          children: 'sub',
          label: 'cate_name',
          id: 'id'
        },        
        Imageport    
      }
    },
    created(){
      this.cate_list ()
    },
    computed:{
      ...mapState(['userLoginInfo']),
    },    
    methods: {
      showSelect(){
        let _this = this
        if(this.cheackId){
          let node1 = document.querySelector('.treeSelect .is-checked');
          setTimeout(function(){
            _this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop = node1.children[0].offsetTop-50          
          },200)
        }
      },      
      handleNodeClick(data) {
        console.log(data)
        this.cheackId = data.id
        this.cate_name = data.cate_name
        this.$parent.cate_id = data.id
        this.$refs.treeForm.setCheckedNodes([data])
        this.$refs.selectRef.blur()
        this.$parent.last_page = 0
        this.$parent.paging_father(1)        
      }, 
      handleClick(data, checked, node){
        console.log(data)
       if(checked == true){
         this.cheackId = data.id;
         this.$parent.cate_id = data.id
         this.$refs.treeForm.setCheckedNodes([data]);
         this.$refs.selectRef.blur()
       }
      },
      async cate_list () {
        let resDate = await cate_list(this.userLoginInfo.orgid)
        if (resDate.data.status) {
          this.cateList = resDate.data.data
          // console.log(resDate.data.data)
        }
      },            
      download(){
        // console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"public/upload/file/classroom.xlsx"
      },
      upload(){
        console.log(this.userLoginInfo)
        window.location.href = this.Imageport+"index/excel/room_ept?orgid=" + this.userLoginInfo.orgid + "&uid=" + this.userLoginInfo.id
      },
      importGoods(){
        this.$parent.show5 = true
      },           
      curriculumClose (k) {
        this.addVisible = k
      },
      showAlert(){
      	this.show = true
      },      
      hideModal() {       
        // 取消弹窗回调
        this.show = false
      },
      hideLoadModal() {    
        // 取消弹窗回调
        this.$parent.show5 = false
      },      
      refresh(){
      	//刷新
      	// this.$emit('searchclassRoom', 1)
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.$message({
         type: 'success',
         message: '刷新成功'
       });      		   	
      },      
      change() {
        this.$parent.goods_name_search = this.inputVal
        this.$parent.cate_id = this.cheackId       
        this.$parent.last_page = 0
        this.$parent.paging_father(1)        
      },      
      submit() {
        // 确认弹窗回调       
        // this.$emit('getclassRoomLists', 1)
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.show = false
      },
      submitLoad() {
        // 确认弹窗回调    
        this.$parent.last_page = 0
        this.$parent.paging_father(1)
        this.$parent.submit5()
      },      
    },
    components: {
      Lbutton: Lbutton,
      dialogComponent:dialogComponent,
    },
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: vw(30) 0;
  .icon-width{
    display: inline-block;
    @include wh(vw(20), vw(20));
  }
  ._tabTopconent{
      display: flex;
  }
  .el-col .rightBox{
  	text-align:right;
  	display:block;
  }
  .el-col .rightBox .hot_btn{
  	float: right;
  	margin-left:vw(20);
  	margin-right:0
  }
  .label_search{
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid #e7eaec;
    padding-left: vw(12);
    padding-right: vw(12);
    box-sizing: border-box;
    border-radius: 6px;
    margin-left: vw(20);
    input {
        @include wh(100%, 100%);
        flex: 1;
    }
  }
  .selectone{
    @include wh(20.52%, vw(40));
  }
  .selectTwo{
    @include wh(vw(170), vw(40));
    width:vw(190)!important
  }
  .label_elect{
      display: inline-block;
      display: flex;
      align-items: center;
      font-size: 16px;
      color: #333333;
      margin-left: vw(20);
    .el-select{
        flex: 1;
        margin-left: vw(8);
        height: 100%;
        /deep/ .el-input {
          height: 100%;
          .el-input__inner{
            height: 100%;
            border-radius:6px
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
  }
  .hot_btn{
    @include wh(auto, vw(40));
    margin-right: vw(20);
    padding:  0 0.5vw;
    .icon-width{
        margin-right: vw(10)
    }
  }
  .selectone1 /deep/ .el-input__inner{
    font-size:vw(16);
    padding:0 vw(10)
  }
}
@media screen and (max-width: 1440px) {
  // 当文档小于1440时起效
  .meadio {
  	display: block;
  	-webkit-box-orient: vertical;
  	-webkit-box-direction: normal;
  	-ms-flex-direction: column;
  	flex-direction: column;
    .el-col-12{
      width: 40%!important;
    }
    .selectone1{
    	//width:40%;
    }
    .el-col-12:last-child{
      width: 60%!important;
      margin-top: 0px;
    }
  }
  .selectTwo /deep/ .el-input__inner{
    font-size:vw1(16);
    padding:0 vw1(10)
  }
  ._tabTop{
    // margin: vw1(30) 0;
    .icon-width{
      display: inline-block;
      @include wh(vw1(20), vw1(20));
    }
    .label_elect{
      font-size:16px
    }
    .label_search{
      @include wh(vw1(140), vw(40));
      margin-left: vw1(20);
    } 
    .selectTwo{
      @include wh(vw1(170), vw1(40));
      width:vw1(180)!important
    }        
  }
  .selectone{
    @include wh(20.52%, vw1(40));
  }  
}
.treeSelect .el-scrollbar .el-select-dropdown__item{
  height:auto;
  padding:0
}
.treeSelect .el-scrollbar .el-select-dropdown__item .el-tree /deep/ .el-tree-node.is-checked>.el-tree-node__content{
  @include bg-defaul($bg-color);
  @include fontColor-defaul($bgColor-color)
}
.treeSelect .el-scrollbar .el-select-dropdown__item.selected{
  font-weight: 500
}
</style>
