<template>
    <div class='_leaseTop'>
        <el-row class='meadio'>
        <el-col :span="12">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_elect selectone'>
                    <span>日期: </span>
                    <el-date-picker
                        type="date"
                        :clearable='false'
                        :editable='false'
                        v-model="leaseThis.form.start_time"
                        placeholder="选择日期">
                    </el-date-picker>
                </label>
                <label class='label_elect selectone' style='margin-left: 8px'>
                    <span>至</span>
                    <el-date-picker
                        type="date"
                        :editable='false'
                        :clearable='false'
                        v-model="leaseThis.form.end_time"
                        placeholder="选择日期"
                        :picker-options="pickerOptions">
                    </el-date-picker>
                </label>
                <label class='label_elect selectone'>
                    <span>状态: </span>
                    <el-select placeholder="请选择" v-model="leaseThis.form.status">
                      <el-option :value="item.id" :label='item.name' v-for='item in form.smantypeStatus' :key='item.id'></el-option>
                    </el-select>
                </label>
                <label class='label_search'>
                    <input type="text" placeholder="搜索" v-model="leaseThis.form.key">
                    <i class='search_icon icon-width' @click='searchKec'></i>
                </label>
            </div>
        </el-col>
        <el-col :span="12">
            <section class='tabConenter'>
                <span>总押金：{{leaseThis.tableData.total_margin}}元</span>
                <span>总预收租金：{{leaseThis.tableData.total_amount}}元</span>
                <span>已收租金：{{leaseThis.tableData.total_prepaid_rent}}元</span>
            </section>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import {cate_list} from '../../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
  name: 'seaticTop',
  data () {
    const that = this
    return {
        valueModel:'',
        labelModel:'',
        cheackId:null,
        data: [],
        pickerOptions: {
          disabledDate(time) {
            let times = new Date(that.leaseThis.form.start_time).getTime();
            let n = 24 * 60 * 60 * 1000
            let saveDate = times - n
            return time.getTime() <= saveDate
          }
        },
        form: {
          name: '',
          id:null,
          cate_name:'',
          smantypeStatus: [{id:1,name:"全部"},{id:2,name:"在租"},{id:3,name:'超期'},{id:4,name:'已归还'}]
        },
        defaultProps: {
          children: 'sub',
          label: 'cate_name'
        } 
    }
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created(){
    this.getcate_list()
  },
  inject: ['leaseThis'],
  methods: {
    async getcate_list () {
        let resDate = await cate_list(this.userLoginInfo.orgid)
        if (resDate.data.status){
            this.data = resDate.data.data
        }
    },
    searchKec(){
      //搜索 名称
      this.leaseThis.get_getrecords(1,this.leaseThis.pageSize);
      setTimeout(()=>{
        this.$message({
          message: '筛选成功',
          type: 'success',
          duration: 2000,
        });
      },1000)
    },
    handleNodeClick(data) {
      this.form.pid = data.cate_id
      this.cheackId = data.cate_id
      this.form.cate_name = data.cate_name
      this.$refs.treeForm.setCheckedNodes([data]);
      this.$refs.selectRef.blur()
    },
    handleClick(data, checked, node){
      if(checked == true){
        this.cheackId = data.id;
        this.$refs.treeForm.setCheckedNodes([data]);
        this.$refs.selectRef.blur()
      }
    },
    showSelect(){
      let _this = this
      if(this.cheackId){
        let node1 = document.querySelector('.treeSelect .is-current');
        setTimeout(function(){
          _this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop = node1.children[0].offsetTop-50
          // console.log(_this.$refs.treeForm.$parent.$parent)
          // console.log(_this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop)
          // console.log(node1.children[0].offsetTop)            
        },200)
      }
    },
    timeStamp (val) {
      let Stamp = (new Date(val)).getTime()
      return Stamp
    }
  },
  components: {
    Lbutton: Lbutton
  },
  watch:{
    'leaseThis.form.start_time'(val) {
      // 开始时间 监听
      if (val!=null || val!="") {
        let Stamp = this.timeStamp(val)/1000
        this.leaseThis.form.start_timeStamp = Stamp
        this.leaseThis.form.end_time = ""
        this.leaseThis.form.end_timeStamp = ""
      }
    },
    'leaseThis.form.end_time'(val) {
      // 结束时间 监听
      if (val!=null || val!="") {
        let Stamp = this.timeStamp(val)/1000
        this.leaseThis.form.end_timeStamp = Stamp
        if (this.leaseThis.form.start_timeStamp == "") {
          this.$message({
            message: '请先选择开始时间!',
            type: 'warning',
            duration: 2000,
          });
          this.leaseThis.form.end_timeStamp = ""
          this.leaseThis.form.end_time = ""
        } else if (this.leaseThis.form.start_time!="" && this.leaseThis.form.end_time!=""){
          // 请求接口
          this.leaseThis.get_getrecords(1,this.leaseThis.pageSize);
          this.$message({
            message: '筛选成功',
            type: 'success',
            duration: 2000,
          });
        }
      }
    },
    'leaseThis.form.status' (val) {
      this.leaseThis.get_getrecords(1,this.leaseThis.pageSize);
      setTimeout(()=>{
        this.$message({
          message: '筛选成功',
          type: 'success',
          duration: 2000,
        });
      },1000)
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._leaseTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .tabConenter{
    line-height: vw(40);
    padding-left: vw(50);
    box-sizing: border-box;
    font-size: vw(16);
    color: #333333;
    span{
      margin-left: vw(40);
    }
    span:last-child{
        margin-left: vw(40);
    }
  }
  .icon-width{
    display: inline-table;
    @include wh(vw(24), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: vw(12);
    padding-right: vw(16);
    box-sizing: border-box;
    border-radius: 6px;
    margin-left: vw(20);
    input {
        @include wh(80%, 100%);
        // flex: 1;
    }
  }
  .selectone{
    @include wh(vw(190), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
    display: inline-block;
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #333333;
    margin-left: vw(20);
    .el-select,.el-date-editor{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
    /deep/.el-date-editor{
        .el-input__inner{
            height: vw(40);
        }
        .el-input__prefix{
            display: none;
        }
    }

  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    @include wh(vw(128), vw(40));
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
} 
.treeSelect .el-scrollbar .el-select-dropdown__item{
  height:auto;
  padding:0
}
.treeSelect .el-scrollbar .el-select-dropdown__item .el-tree /deep/ .el-tree-node.is-checked>.el-tree-node__content{
  @include bg-defaul($bg-color);
  @include fontColor-defaul($bgColor-color)
}
.treeSelect .el-scrollbar .el-select-dropdown__item.selected{
  font-weight: 500
}
</style>