<template>
	    <div class="modal-bg" v-show="show" @mousemove="modalMove" @mouseup="cancelMove">
	        <div class="modal-container modal-container1">
	            <div class="modal-header" @mousedown="setStartingPoint">
	                {{ title }}
	                <i class="el-icon-close" round @click="hideModal"></i>
	            </div>
	            <div class="modal-main">
	              <el-form ref="form" :model="form" :rules="rules" label-width="17%" class="fromBox">
	                <div class="leftBox">
	                	<div class="img1">
	                		<div class="img1Box">
	                			<img v-if="imageUrl1" :src="Imageport + imageUrl1">
	                		</div>
	                		<el-upload
		                		class="avatar-uploader"
                        ref="upload1"
		                		action="/index/Basess/get_img_update"
		                		:http-request="uploadImg1"
		                		:before-upload="beforeAvatarUpload1"
		                		:on-remove="handleRemove1"
                        accept="image/jpeg,image/jpg,image/png">
		                		<i class="el-icon-plus avatar-uploader-icon">上传套餐列表图</i>
		                	</el-upload>
	                	</div>
	                	<div class="img2">
	                		<div class="img2Box">
	                			<img v-if="imageUrl2" :src="Imageport + imageUrl2">
	                		</div>
	                		<el-upload
		                		class="avatar-uploader"
                        ref="upload2"
		                		action="/index/Basess/get_img_update"
		                		:http-request="uploadImg2"
		                		:before-upload="beforeAvatarUpload2"
		                		:on-remove="handleRemove2"
                        accept="image/jpeg,image/jpg,image/png">
		                		<i class="el-icon-plus avatar-uploader-icon">上传套餐详情背景图</i>
		                	</el-upload>
		                </div>                	
	                </div>
	                <div class="rightBox">
	                  <el-form-item prop="name" label="套 餐 名 称" >
	                    <el-input v-model.trim="form.name" placeholder="请输入套餐名称" maxlength="30" ref="nameInput"></el-input>
	                  </el-form-item>
	                  <el-form-item prop="setMeal" label="套 餐 课 程" >
	                    <el-select v-model="form.setMeal" placeholder="选择课程" ref="mySelect" @visible-change="changeValue($event,selectKey)" popper-class="selectBox">           
	                    </el-select>
	                  </el-form-item> 
	                  <el-form-item class="tableKc">
	                  	<template>
	                  		<el-table
		                  		:data="tableData"
		                  		border
		                  		empty-text="未选择课程"
		                  		style="width:100%">
		                  	<el-table-column
		                  		prop="cur_name"
		                  		label="课程名称"
		                  		min-width="17.24%">
		                  	  </el-table-column>
		                  	  <el-table-column
			                  	prop="pattern"
			                  	label="课程模式"
			                  	min-width="17.24%">
			                  	<template slot-scope="scope">
			                  		<el-select v-model="scope.row.course_model" placeholder="选择">
			                  			<el-option
			                  			class="tableKcSelect"
			                  			v-for="item in optiopns"
			                  			:key="item.value"
			                  			:label="item.label"
			                  			:value="item.value">
			                  		</el-option>
			                  	  </el-select>
			                    </template>
			                  </el-table-column>
			                  <el-table-column
				                prop="cur_num"
				                label="课程节数"
				                min-width="17.24%">
				                <template slot-scope="scope">
				                	<el-input v-model="scope.row.cur_num" placeholder="填次数" maxlength="3" @focus="focusPrice2(scope.row.cur_num,scope.$index)" @keyup.native="change_actual_price2(scope.row.cur_num,scope.$index)" @change="change_cur_num(scope.row.cur_num,scope.$index)"></el-input>
				                </template>
				              </el-table-column>
				              <el-table-column
				                prop="validity"
				                label="有效期"
				                min-width="13.80%">
				                <template slot-scope="scope">
				                	<el-input v-model.trim="scope.row.validity" placeholder=" - " maxlength="3" class="validityInput" :disabled="true"></el-input>
				                	<span>天</span>
				                </template>		                
					          </el-table-column>				              
					          <el-table-column
					            prop="cur_value"
					            label="课程价值"
					            min-width="17.24%">
					            <template slot-scope="scope">
					            	<el-input v-model.trim="scope.row.cur_value" maxlength="8" @focus="focusPrice1(scope.row.cur_value,scope.$index)" @keyup.native="change_actual_price1(scope.row.cur_value,scope.$index)" @change="change_cur_value(scope.row.cur_value,scope.$index)"></el-input>
					            </template>					            
						      </el-table-column>				              
						      <el-table-column
                  prop="actual_price"
                  label="实际价值"
                  min-width="17.24%">
                  <template slot-scope="scope">
                   <el-input v-model="scope.row.actual_price" :disabled="true"></el-input>
                 </template>							        
               </el-table-column>				              
				          </el-table>
			      		</template>
	                  </el-form-item> 
	                  <el-form-item prop="value" label="套 餐 价 值" >
	                  	<el-input v-model="form.value" placeholder="显示套餐价值" :disabled="true"></el-input>
	                  </el-form-item>
	                  <el-form-item prop="price" label="套 餐 金 额" >
	                  	<el-input v-model.trim="form.price" placeholder="请输入套餐金额" @focus="focusPrice" @keyup.native="change_actual_price()" maxlength="7" ref="priceInput"></el-input>
	                  </el-form-item>
	                  <el-divider></el-divider>
	                  <el-form-item prop="cur_state" label="启 用 套 餐" class="check-item">
	                  	<el-checkbox v-model="form.cur_state">启用</el-checkbox>
	                  </el-form-item>
	                  <el-form-item prop="remarks" label="备 注" >
	                  	<el-input
	                  	type="textarea"
	                  	:autosize="{ minRows: 2, maxRows: 10}"
	                  	placeholder="可填写备注说明"
                      maxlength="500"
	                  	v-model="form.remarks">
	                  </el-input>
	                  </el-form-item>	                  	                  	                  	                                  
	                </div>                                             
	              </el-form>
	            </div>
	            <div class="modal-footer alertFooter">
	                <el-button type="primary" round @click="submit">保存</el-button>
	                <el-button round @click="hideModal">取消</el-button> 
	            </div>
	            <right-modal v-if="hackReset" ref="rightModal" :show="show1" :title="title1" :orgid="orgid" @hideRightModal="hideRightModal" @submitRight="submitRight" @changeTable="changeTable"></right-modal>
	        </div>
	    </div>
</template>
<script>
  import axios from 'axios';
  import {addmealcur,addmeals} from '../../../../../getserver/getAxios.js'
  import Imageport from '../../../../../config/ImagePz.js'
  import rightModal from './right-modal/right-modal.vue'
  import {checkMaxVal100,checkText} from '../../../../../script/validate.js'
  export default {
    name: 'modal',
    props: {
        show: {
            type: Boolean,
            default: false
        },
        title: {
            type: String,
            default: ''
        },
        orgid: {
        	type: String,
        	default: false
        },
        manager: {
        	type: String,
        	default: false
        },               
    },
    data() {
        return {
            x: 0,
            y: 0,
            node: null,
            isCanMove: false,
            status:'',
            tableData: [],
            form: {
              name: '',
              setMeal:'',
              value:'',
              price:'',
              cur_state:true,
              remarks:''
            },
            rules: {
              name: [
              { required: true, message: '请输入套餐名称', trigger: 'blur' },
              { validator:checkText, trigger: 'blur' },
              {}
              ],
              setMeal: [
              { required: true}
              ],
              value: [
              { required: true}
              ],                                         
              price: [
              { required: true, message: '请输入套餐金额', trigger: 'blur' },
              { validator:checkMaxVal100, trigger: 'blur'},
              ],                                      
            },
            imageUrl1: '',
            imageUrl2: '',
            selectKey: 1,
            show1:false,
            title1: '选择课程',
            optiopns:[{value: '2',label: '包时'}],
            hackReset:true,
            meals_cur:'',
            Imageport          
        }
    },
    mounted() {
        this.node = document.querySelector('.modal-container')
    },
    created(){
    },
    components:{
    	rightModal:rightModal
    },
    methods: {
        hideModal() {
        	// this.tableData = [];
        	// this.form = {
         //      name: '',
         //      cur_value:'',
         //      actual_price:''
         //    };
         this.$emit('hideModal');
        },

        hideRightModal() {
        	this.hackReset = false
        	this.$nextTick(() => {
        		this.hackReset = true
        	});        	
        	this.show1 = false;
        	this.selectKey = '1';      	
        },        
                
        submit() {
          this.addmealcur()
        },

        submitRight() {
        	this.hackReset = false
        	this.$nextTick(() => {
        		this.hackReset = true
        	});        	
        	this.show1 = false;
        	this.selectKey = '1';
        },
        
        setStartingPoint(e) {
            this.x = e.clientX - this.node.offsetLeft
            this.y = e.clientY - this.node.offsetTop
            this.isCanMove = true
        },       

        modalMove(e) {
            if (this.isCanMove) {
                this.node.style.left = e.clientX - this.x + 'px'
                this.node.style.top = e.clientY - this.y + 'px'
            } 
        },        
        cancelMove() {
            this.isCanMove = false
        },
               
        async addmealcur() {
          // if(!this.imageUrl1){
          // 	this.$message.error('请上传套餐列表图');
          // 	return;
          // };
          // if(!this.imageUrl2){
          // 	this.$message.error('请上传套餐详情背景图');
          // 	return;
          // };
          if(!this.form.name){
            this.$message.error('请输入套餐名称');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          }
          else if(this.form.name.length < 2){
            this.$message.error('套餐名称长度不正确');
            this.$refs.nameInput.$el.children[0].focus();
            this.$refs.nameInput.$el.children[0].blur();
            return;            
          };          
          if(this.tableData.length == 0){
            this.$message.error('请选择套餐课程');
            this.$refs.mySelect.$el.children[0].children[0].click();
            return;            
          };
          let b = this.form.price;
          if(this.checkMaxVal1(b) == '1'){
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();
            return            
          }
          else if(this.checkMaxVal1(b) == '2'){
            this.$refs.priceInput.$el.children[0].focus();
            this.$refs.priceInput.$el.children[0].blur();
            return            
          };          
          // if(!this.form.price){
          //   this.$message.error('请输入套餐金额');
          //   this.$refs.priceInput.$el.children[0].focus();
          //   this.$refs.priceInput.$el.children[0].blur();
          //   return;            
          // };                    
          console.log(this.tableData)
          let resDate = await addmealcur(this.tableData);
          let _this = this;
          if (resDate.data.status) {
          	resDate.data.data.forEach(function(element,index){
          		_this.meals_cur = _this.meals_cur + resDate.data.data[index] + '/';
          	});
          	this.addmeals() 
          }
        },
		async addmeals() {
		  let meal_name = this.form.name
		  let value = this.form.value.toString()
		  let price = this.form.price
		  let meals_cur = this.meals_cur.substring(0,this.meals_cur.length-1)
		  let list_img = this.imageUrl1
		  let bg_img = this.imageUrl2
		  let cur_state
		  if(this.form.cur_state){
		  	cur_state = 1
		  }
		  else{
		  	cur_state = 2
		  }
		  let remarks = this.form.remarks
          let resDate = await addmeals(meal_name,value,price,cur_state,remarks,meals_cur,list_img,bg_img,this.orgid,this.manager)
          if (resDate.data.status) {
          	this.$message({
          		type: 'success',
          		message: resDate.data.sinfo
          	});          	
            this.$emit('submit')
          } 
        },        
        changeValue(event,b){
        	this.$refs.mySelect.handleClose();
        	if(event && this.selectKey == '1'){
        		this.selectKey = 2;
        		this.showRight()
        		this.show1 = true;
        	}
        },
        changeTable(res){
        	this.hideRightModal()
        	this.tableData = res;
        	this.change_cur_value();
        	this.change_actual_price()        	
        	// console.log(this.tableData)
        },        
        showRight(){
        	this.$refs.rightModal.checkedDataArry(this.tableData);
        	this.$refs.rightModal.allCurrm(this.orgid);
        },
        change_cur_value(e,index){
          if(e == '' || e == '0'){
            this.tableData[index].cur_value = 100
            this.$message.error('课程价值不能为0或者空')
          }          
        	var curValues = 0;
        	var _this = this;
        	// console.log(this.tableData)
        	this.tableData.forEach(function(element,index){
        		if(_this.tableData[index].cur_value){
        			curValues = curValues + parseFloat(_this.tableData[index].cur_value);
        		}
        	})
        	this.form.value = curValues;
        	if(this.form.price){
        		this.tableData.forEach(function(element,index){
        			_this.tableData[index].actual_price = (_this.form.price*(_this.tableData[index].cur_value/_this.form.value)).toFixed(2);
        		})
        	}        	
        },
        change_cur_num(e,index){
          if(e == '' || e == '0'){
            this.tableData[index].cur_num = 10
            this.$message.error('课程节数不能为0或者空')
          }
        },
        focusPrice(){
          if(this.form.price){
            var actualPrice = this.form.price;
            if(typeof(actualPrice) != 'number'){
              actualPrice = actualPrice.split(".")[0];
              this.form.price = Number(actualPrice) 
            }
            else{
              this.form.price = actualPrice
            }            
          }       
        },                
        change_actual_price(){
        	var actualPrice = this.form.price;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".")< 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 1000000){
              actualPrice=1000000
              this.$message.error('套餐金额范围在1-1000000')
            }                         
          }            
          var _this = this;
          this.tableData.forEach(function(element,index){
            if(_this.tableData[index].cur_value){
              _this.tableData[index].actual_price = (actualPrice*(_this.tableData[index].cur_value/_this.form.value)).toFixed(2);
            }
          });
          this.form.price = actualPrice
        },
        focusPrice1(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.split(".")[0];
            e = Number(actualPrice) 
          }
          else{
            e = actualPrice
          } 
          this.tableData[index].cur_value = e      
        },        
        change_actual_price1(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”以外的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 1000000){
              actualPrice=1000000
              this.$message.error('课程价值范围在0-1000000')
            }           
         }            
         this.tableData[index].cur_value = actualPrice 
        },
        focusPrice2(e,index){
          var actualPrice = e;
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.split(".")[0];
            e = Number(actualPrice) 
          }
          else{
            e = actualPrice
          } 
          this.tableData[index].cur_num = e      
        },        
        change_actual_price2(e,index){
          console.log(index)
          var actualPrice = e;
          console.log(typeof(actualPrice))
          if(typeof(actualPrice) != 'number'){
            actualPrice = actualPrice.replace(/[^0-9,]*/g,"");  //清除“数字”的字符 
            actualPrice = actualPrice.split(".")[0]; 
            if(actualPrice.indexOf(".") < 0 && actualPrice !=""){//以上已经过滤，此处控制的是如果没有小数点，首位不能为类似于 01、02的金额  
             actualPrice= parseFloat(actualPrice);  
            }
            if(actualPrice > 100){
              actualPrice=100
              this.$message.error('课程节数范围在1-100')
            }           
         }            
         this.tableData[index].cur_num = actualPrice 
        },                 
        //上传图片1
        uploadImg1 (f) {
        	console.log(f)
	         let param = new FormData(); //创建form对象
	         param.append('img',f.file);//通过append向form对象添加数据
	         // param.append('key',response.data.key);//添加form表单中其他数据
	         let config = {
	         	headers:{'Content-Type':'multipart/form-data'}	         	
	         };  //添加请求头
	         axios.post(f.action,param,config)//上传图片
	         .then(response=>{
	         	console.log(response)
	         	this.imageUrl1 = response.data.sinfo;
	         	onSuccess(response.data)
	         })
	         .catch(({err}) => {
	         	f.onError()
	         })   
	  	},
		//上传图片2
        uploadImg2 (f) {
        	console.log(f)
	         let param = new FormData(); //创建form对象
	         param.append('img',f.file);//通过append向form对象添加数据
	         // param.append('key',response.data.key);//添加form表单中其他数据
	         let config = {
	         	headers:{'Content-Type':'multipart/form-data'}	         	
	         };  //添加请求头
	         axios.post(f.action,param,config)//上传图片
	         .then(response=>{
	         	console.log(response)
	         	this.imageUrl2 = response.data.sinfo;
	         	onSuccess(response.data)
	         })
	         .catch(({err}) => {
	         	f.onError()
	         })   
	  	},	  	
     //    handleImgChange (file, fileList) {// 这里可以打印file查看数据结构:on-change="handleImgChange()"
     //    	console.log(file)
     //      // if (file.data) {//判断是否上传成功
            
     //      // }
     //      // else{
     //      // 	this.imageUrl1 = file.sinfo;
     //      // }
    	// },
        beforeAvatarUpload1(file) {
        	const isJPG = file.type === 'image/jpeg';
        	const isPNG = file.type === 'image/png';
        	const isLt2M = file.size / 1024 / 1024 < 2;

        	if (!isJPG && !isPNG) {
        		this.$message.error('上传图片只能是 png 或者jpg 格式!');
        	}
        	if (!isLt2M) {
        		this.$message.error('上传图片大小不能超过 2MB!');
        	}
        	return isJPG && isLt2M;
        },
		    beforeAvatarUpload2(file) {
        	const isJPG = file.type === 'image/jpeg';
        	const isPNG = file.type === 'image/png';
        	const isLt2M = file.size / 1024 / 1024 < 2;

        	if (!isJPG && !isPNG) {
        		this.$message.error('上传图片只能是 png 或者jpg 格式!');
        	}
        	if (!isLt2M) {
        		this.$message.error('上传图片大小不能超过 2MB!');
        	}
        	return isJPG && isLt2M;
        },        
	      handleRemove1(file, fileList) {
           let param = new FormData(); //创建form对象
           param.append('img',file.raw);//通过append向form对象添加数据
           // param.append('key',response.data.key);//添加form表单中其他数据
           let config = {
            headers:{'Content-Type':'multipart/form-data'}            
           };  //添加请求头
           axios.post(this.$refs.upload1.action,param,config)//上传图片
           .then(response=>{
            console.log(response)
            this.imageUrl1 = response.data.sinfo;
           })
           .catch(({err}) => {
           })	     	   
	      },
		   handleRemove2(file, fileList) {
	     	console.log(file)
	         // 更新缓存文件
	         //console.log('文件删除')
           let param = new FormData(); //创建form对象
           param.append('img',file.raw);//通过append向form对象添加数据
           // param.append('key',response.data.key);//添加form表单中其他数据
           let config = {
            headers:{'Content-Type':'multipart/form-data'}            
           };  //添加请求头
           axios.post(this.$refs.upload2.action,param,config)//上传图片
           .then(response=>{
            console.log(response)
            this.imageUrl2 = response.data.sinfo;
           })
           .catch(({err}) => {
           })          
	     },
       checkMaxVal1(value) {
        var v = value
        if (isNaN(value) || typeof(v) != 'number' || v <= 0 || v > 1000000) {
            return 1;//'请输入[1,500]之间的整数'
          } else {
            const re = /^[0-9]*[1-9][0-9]*$/;
            const rsCheck = re.test(value);
            if (!rsCheck) {
              return 2;//'请输入正整数'
            } else {
              return 0;
            } 
          }
        },       	          
    }
  }
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@media screen and(max-width: 1381px){
    .modal-container {
	  	width:vw1(860);
	    height:vw1(660);
	}
	.modal-container2{
		width:vw1(366);
		height:vw1(660);
	}
	.modal-header {
		border-radius: vw1(10) vw1(10) 0 0;
	}
	.modal-header {
		height: vw1(60);
		padding:0 vw1(30);
		font-size:vw1(16);
	}
	.el-icon-close{
		top:vw1(24);
		right: vw1(30);		
	}
	.modal-footer {
		height: vw1(80);
	}
	.modal-footer button {
		margin-right:vw1(30);
