<template>
    <div class='table-gulp'>
    	<erp-tabtop @cate_index="cate_index" :that="that" @orgid="32"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="index"
	                show-overflow-tooltip
	                align='center'
	                min-width='22.22%'
	                label="序号">
	                <template slot-scope="scope"><span>{{scope.$index | serialNumber}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="level_text"
	                show-overflow-tooltip
	                align='center'
	                min-width='22.22%'
	                label="分类">
	            </el-table-column>
	            <el-table-column
	                prop="cate_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='22.22%'
	                label="名称">
	                <template slot-scope="scope">
	                	<span @click="layerOpen($event)">{{scope.row.cate_name}}</span>
	                </template>	                
	            </el-table-column> 
	            <el-table-column
	                label="操作"
	                min-width='33.333%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
	                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon'>
	                            <i class='repair_icon icon-width'></i>
	                            修改
	                        </el-button>
	                        <el-button type="text" @click="showDelete(scope.row.cate_id)" size="small"  class='erp-button-icon'>
	                            <i class='delete_icon icon-width '></i>
	                            删除
	                        </el-button>
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
        <editClassification v-if="show" :that="that" @hideModal="hideModal" @submit="submit"></editClassification>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import erpTabtop from './classification-top/classification-top'
import erpPaging from '../../../paging/pagingShort'
import editClassification from '../classification/alert-box/editClassification.vue'
import {cate_index,cate_del} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'classification',
  data () {
    return {
      that: this,
      tableData: [],
		  value: '',
		  title: '修改分类',
		  show: false,
		  showDe:false,
		  deleteId:null,
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数
      cate_pid:'',//修改的分类父级id
      cate_pname:'',//修改的分类父级名称
      cate_name:'',//修改的分类名称
      id:'',//修改的分类id

    }
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{
  	serialNumber ( val ){
  		return (that.page_num-1)*8+(val + 1)
  	}
  }, 
  methods: {
    paging_father (n) {
      this.cate_index(n)
    },    
    async cate_index (n) {
    	let t = (new Date()).getTime();
    	let resDate = await cate_index(n,this.userLoginInfo.orgid,t)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    		this.last_page = resDate.data.data.last_page
    		this.counts = resDate.data.data.total
    		this.page_num = n;   		            
    	}
    },            
    cate_del (id) {
		// 删除教室
		cate_del(id,this.userLoginInfo.orgid).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				if((this.tableData.length -1) == 0){
					this.cate_index (this.page_num-1)
				}
				else{
					this.cate_index (this.page_num)
				}
			}					
    })
		},        
    showAlert(a,b){
    	this.cate_pid = a.cate_pid
    	this.id = a.cate_id
      this.cate_name = a.cate_name
      this.cate_pname = a.cate_pname
    	this.show = true
    },   
    hideModal() {
    // 取消弹窗回	        
	    this.show = false
    },
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.showDe = false;
    	this.cate_del(id)
    },
    submit() {
    // 确认弹窗回调   
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    }    
  }, 
  created: function () {
  	this.cate_index (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")  	
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editClassification: editClassification,
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
</style>
