<template>
    <div class='_class_info' @click.self='closeclick' v-dialogDrag>
        <transition name='info'>
            <div class='_class_infoConenter el-dialog' v-show='tablesThis.shoppclassInfoShow'>
                <header class='_class_infoHeader el-dialog__header'>
                    <p>
                        <span>林月息</span>
                        <span>手机: 158000006582</span>
                    </p>
                    <i class='whitedelete_icon icon_width' @click.stop='closeclick'></i>
                </header>
                <main class='_class_infobody'>
                    <section class='_class_infobody_Top'>
                        <div class='_top_left'>
                            <p class='_top_leftTimes'>
                                <span>日期：</span>
                                <el-date-picker
                                    v-model="value1"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                                <span> 至 </span>
                                <el-date-picker
                                    v-model="value1"
                                    type="date"
                                    placeholder="选择日期">
                                </el-date-picker>
                            </p>
                            <p class='_top_leftSelect'>
                                <label class='label_elect selectTwo'>
                                    <span>选择：</span>
                                    <el-select v-model="value" placeholder="请选择">
                                        <el-option label="全部" value="0"></el-option>
                                        <el-option label="一对一" value="1"></el-option>
                                        <el-option label="一对多" value="2"></el-option>
                                    </el-select>
                                </label>
                                <label class='label_elect selectTwo'>
                                    <span>课程：</span>
                                    <el-select v-model="value" placeholder="请选择">
                                        <el-option label="全部" value="0"></el-option>
                                        <el-option label="一对一" value="1"></el-option>
                                        <el-option label="一对多" value="2"></el-option>
                                    </el-select>
                                </label>
                            </p>
                        </div>
                        <div class='_top_right'>
                            <Lbutton class='hot_btn' @click.native="exportClick(2, $event)">
                                <i class='icon-width export_icon'></i>
                                导出Excel
                            </Lbutton>
                        </div>
                    </section>
                    <section class='_class_infobody_button div_gulptable'>
                        <el-table
                            :data="tableData"
                            height="320"
                            border
                            stripe
                            resizable=false
                            style="width: 100%">
                            <el-table-column
                                prop="date"
                                show-overflow-tooltip
                                align='center'
                                min-width='12.23%'
                                label="日期">
                            </el-table-column>
                            <el-table-column
                                show-overflow-tooltip
                                prop="date"
                                align='center'
                                min-width='7.7%'
                                label="时间">
                            </el-table-column>
                            <el-table-column
                                show-overflow-tooltip
                                align='center'
                                min-width='10%'
                                label="课程名称">
                                <template slot-scope="scope">
                                    <span class='hot' @click='layerOpen(scope.row,$event)'>
                                        {{scope.row.date}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                show-overflow-tooltip
                                align='center'
                                min-width='7.7%'
                                label="收费">
                                <template slot-scope="scope">
                                    <span class='_color'>
                                        {{scope.row.date}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                show-overflow-tooltip
                                prop="date"
                                align='center'
                                min-width='7.7%'
                                label="教师">
                            </el-table-column>
                            <el-table-column
                                prop="date"
                                show-overflow-tooltip
                                align='center'
                                min-width='9.23%'
                                label="教室">
                            </el-table-column>
                            <el-table-column
                                prop="date"
                                show-overflow-tooltip
                                align='center'
                                min-width='7.7%'
                                label="状态">
                            </el-table-column>
                            <el-table-column
                                show-overflow-tooltip
                                align='center'
                                min-width='7.7%'
                                label="备注">
                                <template slot-scope="scope">
                                    <span class='_color'>
                                        {{scope.row.date}}
                                    </span>
                                </template>
                            </el-table-column>
                            <el-table-column
                                label="更多操作"
                                min-width='29.23%'
                                show-overflow-tooltip>
                                <template class='erp-call'>
                                    <div class="grid-content bg-purple">
                                        <label>
                                            <el-button  type="text" size="small" class='erp-button-icon'>
                                                <!-- <i class='blue_three_icon icon-width'></i> -->
                                                课表
                                            </el-button>
                                            <el-button type="text"  size="small"  class='erp-button-icon'>
                                                <!-- <i class='book_icon icon-width '></i> -->
                                                排课
                                            </el-button>
                                        </label>
                                        <label>
                                            <el-button type="text"  size="small"  class='erp-button-icon'>
                                                <!-- <i class='shop_icon icon-width '></i> -->
                                                购课
                                            </el-button>
                                            <el-button type="text"  size="small"  class='erp-button-icon'>
                                                <!-- <i class='shop_class_icon icon-width '></i> -->
                                                购物
                                            </el-button>
                                        </label>
                                        <label>
                                            <el-button type="text"  size="small"  class='erp-button-icon'>
                                                <!-- <i class='classroom_icon icon-width '></i> -->
                                                充值
                                            </el-button>
                                        </label>
                                    </div>
                                </template>
                            </el-table-column>
                        </el-table>
                    </section>
                </main>
                <footer class='_class_infofooter'>
                    <erpPaging></erpPaging>
                </footer>
            </div>
        </transition>
    </div>
</template>
<script>
import $ from '../../../../../script/jquery-vendor.js'
import erpPaging from '../../../../paging/paging'
import Lbutton from '../../../../button/button'
export default {
  name: 'shopClassinfo',
  data () {
    return {
      tableData: [{
            date: '2016-05-02',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1518 弄'
        }, {
            date: '2016-05-04',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1517 弄'
        }, {
            date: '2016-05-01',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1519 弄'
        }, {
            date: '2016-05-03',
            name: '王小虎',
            address: '上海市普陀区金沙江路 1516 弄'
        }]
    }
  },
  inject: ['tablesThis'],
  methods: {
    layerOpen:function (a,b) {
        // console.log($(b.path[1]).width())
        // console.log(b.currentTarget.getBoundingClientRect())
        // console.log(b)
        var pt = $(b.path[1]).width();
        var t = b.currentTarget.getBoundingClientRect();
        if (pt < t.width) {
            var that = b.currentTarget;
            var text = b.path[0].innerText;
            var l = t.x-10+'px';
            var t = t.y-10+'px';
            layer.open({
                type: 1,
                fixed: false,
                icon: 2, 
                offset: [t,l],   //相对定位
                shade: false,
                title: false, //不显示标题
                area: [pt, 'auto'],
                content: text, //捕获的元素，注意：最好该指定的元素要存放在body最外层，否则可能被其它的相对元素所影响
                success: function(layero,index){
                    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //如果该父级原来没有设置相对定位，那么在追加该弹层之前需要设置
                }
            });
        }      	
    },
    closeclick () {
        this.tablesThis.shoppclassInfoShow = !this.tablesThis.shoppclassInfoShow
    }
  },
  components: {
    erpPaging: erpPaging,
    Lbutton: Lbutton
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
._color{
    @include fontColor-defaul($fn-color);
}
.info-enter,
.info-leave-to,
.info-enter-to,
.info-leave{
    transition: all 0.4s ease-out;
}
.info-enter,
.info-leave-to{
  transform: translateY(-20%);
  opacity: 0;
}
.info-enter-to,
.info-leave{
  transform: translateY(0%);
  opacity: 1;
}
@media screen and (max-width: 1380px){
._class_info{
    @include wh(100%,100%);
