    left: 0;
    background: rgba(0,0,0,0.2);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;

    ._class_infoConenter{
        @include wh(vw1(1360),vw1(660));
        background: #ffffff;
        border-radius: 8px;
        display: flex;
        flex-direction: column;
        ._class_infoHeader {
          @include wh(100%, vw1(60));
          border-top-left-radius: 8px;
          border-top-right-radius: 8px;
          display: flex;
          align-items: center;
          padding: 0 vw1(30);
          box-sizing: border-box;
          color: #ffffff;
          font-size: vw1(16);
          @include bg-defaul($bg-color);
          justify-content: space-between;
          p{
            flex: 1;
            span:nth-child(2) {
              margin-left: vw1(60);
            }
          }
          .icon_width{
              @include wh(20px, 20px);
              cursor: pointer;
          }
        }
        ._class_infobody{
            flex: 1;
            display: flex;
            flex-direction: column;
            padding: 0 vw1(30);
            box-sizing: border-box;
            overflow: hidden;
            ._class_infobody_Top{
                display: flex;
                justify-content: space-between;
                padding: vw1(30) 0;
                box-sizing: border-box;
                ._top_left {
                    display: flex;
                    ._top_leftTimes {
                        margin-right: vw1(20);
                        /deep/ .el-date-editor{
                            .el-input__prefix{
                                display: none;
                            }
                        }
                        /deep/ .el-input {
                             @include wh(vw(160), vw1(40));
                            .el-input__inner{
                                @include wh(100%, vw1(40));
                                @include borderColor-defaul($border-color);
                            }
                            .el-input__prefix .el-input__icon {
                                line-height: vw1(40)
                            }
                        }
                    }
                    ._top_leftSelect{
                        .label_elect{
                            margin-right: vw1(20);
                            /deep/ .el-select .el-input{
                                @include wh(vw1(120),vw1(40));
                                .el-input__inner{
                                    @include wh(100%,vw1(40));
                                    @include borderColor-defaul($border-color);
                                }
                            }
                        } 
                    }
                }
                ._top_right .hot_btn {
                    font-size: vw1(16);
                    padding: 0 vw1(16);
                    height: vw1(40);
                    line-height: vw1(40);
                    box-sizing: border-box;
                    @include borderColor-defaul($border-color);
                    .icon-width{
                        @include wh(vw1(20),vw1(20));
                    }
                }
            }
            ._class_infobody_button{
                flex: 1;
                .grid-content{
                    text-align: center;
                    display: flex;
                    padding: 0 35px;
                    box-sizing: border-box;
                    label{
                    width: 50%;
                    display: flex;
                    /deep/ .el-button span{
                        @include fontColor-defaul($fn-color)
                    }
                    .icon-width{
                            @include wh(20px, 20px);
                            display: inline-block;
                            margin-right: vw1(6);
                            font-size: 16px;
                            color: #666666;
                        }
                    }
                    button:nth-child(2n){
                        margin-right: vw1(30);
                        margin-left: vw1(30);
                    }
                }
            }
        }
        ._class_infofooter{
            @include wh(100%,vw1(80));
            display: flex;
            align-items: center;
            justify-content: flex-end;
            padding: 0 vw1(45);
            box-sizing: border-box;
        }
    }
}
}
