<template>
    <div class='adduser'>
        <el-dialog
        title="新增账号"
        v-dialogDrag
        :visible.sync="userThis.addUserShow"
        width="33.33%"
        >
        <section class='list_fw' @scroll="scrollModalMain($event)">
            <label class='user_name fw_label'>
                <span class='text_span'>*用户名：</span>
                <input type="text" placeholder="请输入用户名" v-model='form.userName' maxlength="18">
            </label>
            <label class='fw_label'>
                <span class='text_span'>*联系方式：</span>
                <input type="text" placeholder="请输入联系方式-手机号" v-model='form.contact' maxlength="11">
            </label>
            <label class='user_password fw_label'>
                <span class='text_span'>*密 码：</span>
                <input type="password" placeholder="请输入密码"  v-model='form.password' maxlength="18">
            </label>
            <label class='fw_label'>
                <span class='text_span'>*确认密码：</span>
                <input type="password" placeholder="请重新确认密码" v-model='form.repeatpassword' maxlength="18">
            </label>
            <label class='_curriculum_labeltwo fw_label'>
                <span>*性 别：</span>
                <el-select slot="prepend" v-model='form.sex' placeholder="请选择" class='_main-rightSelect'>
                    <el-option label="男" value="1"></el-option>
                    <el-option label="女" value="2"></el-option>
                </el-select>
            </label>
            <div class='fw_label checkLabel'>
                <span class='text_span'>*角色授权：</span>
                <section class='section_contener'>
                    <p v-for='item in roleform.auth' :key='item.role_id'>
                        <label class='checkbox_label' @change.stop='RoleChecked(item.role_id)' >
                            <input type="checkbox" :checked='item.f==1?false:true'>
                            <span>{{item.role_name}}</span>
                        </label>
                    </p>
                </section>
            </div>
            <label class='fw_label checkLabel'>
                <span class='text_span'>*所属门店：</span>
                <section class='section_contener'>
                    <p>
                        <label class='checkbox_label' @change.stop='intuClick(item.or_id)' v-for='item in this.roleform.orglist' :key='item.or_id'>
                            <input type="checkbox" :checked='item.f==1?false:true'>
                            <span>{{item.or_name}}</span>
                        </label>
                    </p>
                </section>
            </label>
        </section>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" size='mini' @click="save" :loading="saveShow">保存</el-button>
            <el-button @click="userThis.addUserShow = false" size='mini'>取消</el-button>
        </span>
        </el-dialog>
    </div>
</template>
<script>
import {userRole, Adduser} from '../../../../../getserver/server'
/**
 * userRole - 添加账号 角色授权信息以及所属门店信息
 * 
 * */
import {mapState} from 'vuex'
export default {
  name: 'adduser',
  data () {
    return {
      dialogVisible: true,
      roleform: {
          orglist:{
            or_name: ''
          }
      },
      saveShow: false,
      form: {
        userName: '',
        contact: '',
        password: '',
        repeatpassword: '',
        sex: ''
      }
    }
  },
  inject: ['userThis'],
  created(){
    this.getuserRole()
  },
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    intuClick (ids){
        this.roleform.orglist.forEach((data)=>{
            if (ids == data.or_id) {
                data.f== "2"?data.f="1":data.f="2"
            } else {
                data.f = "1"
            }
        })
        console.log(data.f)
    },
    RoleChecked (id) {
      this.roleform.auth.forEach((data)=>{
        if (data.role_id == id){
            if(data.f == 1) {
                data.f = 2
            } else {
                data.f = 1
            }
        }
      })
    },
    save(){
      // 保存
      const _this = this
      let rolsAccArr = []
      this.saveShow = true
      setTimeout(()=>{
        _this.saveShow = false
      }, 1500)
      if (this.form.userName.length < 6) {
        this.$message.error('用户名不能少于6个字符!');
        return
      } else if (!/^[a-zA-zA-Z]/.test(this.form.userName)){
        this.$message.error('用户名必须以字母开头');
        return
      } else if(this.form.contact.trim().length == 0){
        this.$message.error('请输入联系方式!');
        return
      } else if(!/^1(3|8|4|9|5|6|7)\d{9}/.test(this.form.contact)){
        this.$message.error('手机号码格式不正确!请检查');
        return
      } else if(this.form.password < 6 || this.form.repeatpassword < 6 || !/\w/.test(this.form.password) ||!/\w/.test(this.form.password)){
        this.$message.error('密码只能以为数字、字母、或下划线组成，且必须为6到18位');
        return
      } else if(this.form.password!=this.form.repeatpassword){
        this.$message.error('两次密码必须相同!请检查');
        return
      } else if(!/\w/.test(this.form.password)){
        this.$message.error('密码只能以为数字、字母、或下划线组成，且必须为6到18位');
        return
      } else if (this.form.sex == ''){
          this.$message.error('请选择您的性别');
          return
      }
      this.roleform.auth.forEach((data)=>{
        if(data.f == 2) {
          rolsAccArr.push(data.role_id)
        }
      })

      this.getAddaccount(rolsAccArr)
    },
   async getuserRole () {
       // 获取 角色授权信息 以及 机构信息
       let resDate = await userRole(this.userLoginInfo.orgid)
       if (resDate.data.status) {
        this.roleform = resDate.data.data
       }
    },
    getAddaccount (rolsAccArr) {
        // 添加账号
        const _this = this;
        let orginzation = null
        this.roleform.orglist.forEach((data)=>{
            if(data.f==2) {
                orginzation = data.or_id
            }
        })
        if (orginzation == null) {
            _this.$message({
                message: '请选择所属机构',
                type: 'error'
            });
            return
        }
        Adduser(this.form.userName, this.form.contact, this.form.password, this.form.repeatpassword, this.form.sex, rolsAccArr, orginzation).then((res)=>{
            if (res.data.status){
                _this.$message({
                    message: res.data.sinfo,
                    type: 'success'
                });
                _this.form={
                    userName: '',
                    contact: '',
                    password: '',
                    repeatpassword: '',
                    sex: ''
                }
                this.roleform.orglist.forEach((data)=>{
                    data.f = '1'
                })
                _this.roleform.auth.forEach((data)=>{
                    data.f = '1'
                })
                _this.userThis.getuserList('',0,this.userThis.page_num,10)
            }
        })
    }
  },
  watch: {
    'form.contact'(val){
        if (!/^\d+$/.test(val)){
            this.form.contact = val.substring(0, val.length-1)
        }
    },
    'userThis.addUserShow'(){
        this.getuserRole()
    }
  }
}
</script>
