.adduser /deep/ .el-dialog__wrapper .el-dialog{
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    .el-dialog__header{
        padding-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        @include bg-defaul($bg-color);
        border-top-right-radius: 8px;
        border-top-left-radius: 8px;
        span{
          color: #ffffff;
        }
        .el-dialog__headerbtn{
            .el-dialog__close{
                color: #ffffff;
            }
        }
    }
    .el-dialog__body{
        flex: 1;
        overflow: hidden;
        box-sizing: border-box;
        .list_fw {
          height: 100%;
          overflow: auto;
          .user_name span{
          }
          .user_password span{
          }
          ._curriculum_labeltwo span:nth-child(1){
              @include wh(vw(100), auto);
              color: #333333;
          }
          .fw_label {
            display: flex;
            align-items: center;
            .section_contener{
                flex:1;
