                    user-select: none; /* Non-prefixed version, currently not supported by any browser */
                    .checkbox_label{
                        display: flex;
                        align-items: center;
                        color: #999999;
                        input[type=checkbox]{
                            background: #fff;
                            -webkit-appearance:none;
                            border: 2px solid #999999;
