<template>
    <div class='_return'>
        <!-- 归还操作 -->
        <el-dialog
            title="归还商品"
            :visible.sync="leaseThis.returnShow"
            v-dialogDrag
            :before-close="handleClose">
            <section class='body__section' @scroll="scrollModalMain($event)">
                <div class='body__top'>
                    <ul class='info_list'>
                        <li>
                            <span>租客姓名</span>：1
                        </li>
                        <li>
                            <span>商品名称</span>：1
                        </li>
                        <li>
                            <span>租赁数量</span>：1
                        </li>
                        <li>
                            <span>计费方式</span>：1
                        </li>
                        <li>
                            <span>租赁押金</span>：1
                        </li>
                        <li>
                            <span>预付租金</span>：1
                        </li>
                        <li>
                            <span>开始时间</span>：1
                        </li>
                        <li>
                            <span>结束时间</span>：1
                        </li>
                        <li>
                           <span>租赁备注</span>：1
                        </li>
                    </ul>
                </div>
                <div class='body__bottom'>
                    <el-form ref="form" :model="form" label-width="20%" :label-position="labelPosition">
                        <el-form-item label="归还时间：" class='xin'>
                            <el-date-picker
                                v-model="value1"
                                type="date"
                                :editable='false'
                                placeholder="选择日期">
                            </el-date-picker>
                        </el-form-item>
                        <el-row>
                            <el-col :span='12'>
                                <el-form-item label="实付租金：">
                                        <el-input
                                            type="text"
                                            placeholder="请输入内容"
                                            v-model="textarea">
                                        </el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span='12'>
                                <el-form-item label="实退金额：">
                                    <el-input
                                        type="text"
                                        disabled
                                        placeholder="请输入内容"
                                        v-model="textarea">
                                    </el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-form-item label="付款方式：">
                            <el-select v-model="value" placeholder="请选择">
                                <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <el-form-item label="备 注：">
                            <el-input
                                type="textarea"
                                :rows="3"
                                placeholder="请输入内容"
                                v-model="textarea">
                            </el-input>
                        </el-form-item>
                    </el-form>
                </div>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="leaseThis.returnShow= false" size='small'>确 定</el-button>
                <el-button @click="leaseThis.returnShow= false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {
  name: 'return',
  data () {
    return {
      form: {},
      labelPosition: "left"
    }
  },
  inject: ['leaseThis']
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';  
@media screen and (max-width: 1380px){
._return /deep/ .el-dialog__wrapper .el-dialog{
    @include wh(vw1(640),vw1(660));
    display: flex;
    flex-direction: column;
    border-radius: vw1(8);
    .el-dialog__header{
        @include wh(100%,vw1(60));
        @include bg-defaul($bg-color);
        box-sizing: border-box;
        padding: vw1(22) vw1(30);
        border-top-left-radius: vw1(8);
        border-top-right-radius: vw1(8);
        .el-dialog__title{
            font-size:vw1(16);
            color: #ffffff;
            line-height: 0;
        }
        .el-dialog__headerbtn{
            top: vw1(20);
            .el-dialog__close  {
                color: #fff;
            }
        }
    }
    .el-dialog__body {
        flex: 1;
        overflow: hidden;
        padding: 0;
        box-sizing: border-box;
        .body__section{
            padding: vw1(30);
            box-sizing: border-box;
            height: 100%;
            overflow: auto;
            display: flex;
            flex-direction: column;
            .body__top{
                @include wh(100%,auto);
                .info_list{
                    display: flex;
                    flex-wrap: wrap;
                    li{
                        width: 50%;
                        margin-bottom: vw1(25);
                        font-size: vw1(16);
                        color:#666666;;
                        display: flex;
                        align-items: center;
                        span{
                            display: inline-block;
                            width: 28%;
                            height: vw1(20);
                            color:#333333;
                            text-align: justify;
                        }
                        span::after{
                            content: "";
                            display: inline-block;
                            width: 100%;
                        }
                    }
                }
            }
            .body__bottom{
                flex: 1;
                .el-form{
                    .el-row .el-col{
                        .el-form-item{
                            .el-form-item__label{
                                width: 40%!important;
                            }
                        }
                    }
                    .el-row .el-col:last-child{
                        padding-left: vw1(25)
                    }
                    .el-form-item{
                        margin-bottom: vw1(30);
                        .el-form-item__label{
                            font-size: vw1(16);
                            color: #333333;
                            height: vw1(40);
                            text-align: justify;
                        }
                        .el-form-item__label::after{
                            content: " ";
                            display: inline-block;
                            width: 100%;
                        }
                        .el-form-item__content{
                             display: flex;
                             align-items: center;
                             .el-date-editor{
                                 width: 100%;
                                 .el-input__prefix{
                                     display: none;
                                 }
                             }
                            .el-input{
                                .el-input__inner{
                                    height: vw1(40);
                                }
                            }
                        }
                    }
                    .xin{
                        .el-form-item__label::before{
                            content: "*";
                            display: inline-block;
                        }
                    }
                }
            }
        }
    }
    .el-dialog__footer{
        @include wh(100%,vw1(80));
        .el-button{
            @include wh(vw1(120),auto);
            font-size: vw1(16);
            background: #e7eaec;
            border-color: #e7eaec;
            color: #999999;
            border-radius: vw1(6);
        }
        .el-button:first-child{
            margin-right: vw1(30);
            @include bg-defaul($bg-color);
            color: #fff;
        }
    }
} 
}
