<template>
    <div class='_reset'>
        <el-dialog
            title="重置密码"
            v-dialogDrag
            :visible.sync="userThis.resetShow">
            <span>
                <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="17%" class="demo-ruleForm">
                    <el-form-item label="密码" prop="pass" :rules="rules.pass">
                        <el-input type="password" v-model="ruleForm.pass" autocomplete="off" placeholder='请输入密码' maxlength='18'></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="cpass" :rules="rules.cpass">
                        <el-input type="password" v-model="ruleForm.cpass" autocomplete="off" placeholder='请再次输入密码' maxlength='18'></el-input>
                    </el-form-item>
                </el-form>
            </span>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="saveClick" size='small'>确 定</el-button>
                <el-button @click="userThis.resetShow = false" size='small'>取 消</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
import {isPassword} from '../../../../../script/validate'
import {mapState} from 'vuex'
import {ResetPassword} from '../../../../../getserver/server'
export default {
  name: 'resetPssword',
  data () {
    var validatePass = (rule, value, callback) => {
        if (value !== this.ruleForm.pass) {
            callback(new Error('两次输入密码不一致!'));
        } else {
            callback();
        }
    };
    return {
      ruleForm: {
        pass: '',
        cpass: ''
      },
      rules: {
        pass: [
          {required: true, validator: isPassword, trigger: 'blur' }
        ],
        cpass: [
          {required: true, validator: validatePass, trigger: 'blur' }
        ]
      }
    }
  },
  inject: ['userThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  methods: {
    saveClick () {
      if (this.ruleForm.pass.length < 6 || this.ruleForm.cpass.length < 6 ) {
          this.$message({
            message: '密码不能少于6位,请检查',
            type: 'error'
          });
          return
      } else if (this.ruleForm.pass != this.ruleForm.cpass) {
          this.$message({
            message: '两次密码必须相同,请检查',
            type: 'error'
          });
          return
      } else if (!/^[_a-zA-Z0-9]+$/.test(this.ruleForm.pass) || !/^[_a-zA-Z0-9]+$/.test(this.ruleForm.cpass)) {
          this.$message({
            message: '密码格式不正确!请检查',
            type: 'error'
          });
          return
      }
      this.getResPassword()
    },
    async getResPassword() {
        let resDate = await ResetPassword(this.userThis.resetuid.uid, this.userLoginInfo.orgid, this.ruleForm.pass, this.ruleForm.cpass, this.userThis.resetuid.cellphone);
        if (resDate.data.status) {
            this.$message({
                message: '修改成功',
                type: 'success'
            });
            this.ruleForm= {
                pass: '',
                cpass: ''
            }
         }
      }
   }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
@media screen and (max-width: 1380px){
    ._reset{
        /deep/ .el-dialog{
            @include wh(vw1(640),vw1(360));
            border-radius: vw1(8);
            margin-top: 30vh!important;
            display: flex;
            flex-direction: column;
            .el-dialog__header{
                @include wh(100%,vw1(60));
                box-sizing: border-box;
                padding: vw1(22) vw1(30);
                @include bg-defaul($bg-color);
                border-top-left-radius: vw1(8);
                border-top-right-radius: vw1(8);
                .el-dialog__title{
                    font-size: vw1(16);
                    color: #ffffff;
                    line-height: vw1(25);
                }
                .el-dialog__headerbtn{
                    top: vw1(16);
                    i{
                        color: #ffffff;
                    }
                }
            }
            .el-dialog__body{
                flex: 1;
                overflow: hidden;
                .el-form{
                    .el-form-item{
                        height: vw1(60);
                        .el-form-item__label{
                            text-align: justify;
                            font-size: vw1(16);
                            height: 100%;
                            padding: 0;
                            color: #333333;
                        }
                        .el-form-item__label::before{
                            color: #000;
                            float: left;
                        }
                        .el-form-item__label::after{
                            content: " ";
                            display: inline-block;
                            width: 100%;
                        }
                        .el-form-item__content{
                            display: flex;
                            .el-input{
                                .el-input__inner{
                                    height: vw1(40);
                                }
                            }
                        }
                        .el-form-item__content::before{
                            content: "：";
                            display: inline-block;
                        }
                    }
                }
            }
            .el-dialog__footer{
                @include wh(100%,vw1(80));
                .dialog-footer {
                    button{
                        @include wh(vw1(120),auto);
                        font-size: vw1(16);
                        background-color: #e7eaec; 
                    }
                    button:first-child{
                        @include bg-defaul($bg-color);
                    }
                    button:last-child{
                        color: #999999;
                        margin-left: vw1(30)
                    }
                }
            }
        }
    }
}
