<template>
    <div class='table-gulp'>
    	<erp-tabtop @cate_index="sale_index"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="goods_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="商品名称">
	                <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.goods_name}} </span></template>
              </el-table-column>
              <el-table-column
                 prop="cate_name"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="所属分类">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.cate_name}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sale_num"
                 show-overflow-tooltip
                 align='center'
                 min-width='5.555%'
                 label="数量">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sale_num}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="single_price"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="零售价(元)">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.single_price}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sum_payable"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="应付款(元)">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sum_payable}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="pay_amount"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="实付款(元)">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.pay_amount}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sale_time"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="销售时间">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sale_time | date}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sman_name"
                 show-overflow-tooltip
                 align='center'
                 min-width='5.555%'
                 label="销售员">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sman_name}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sale_code"
                 show-overflow-tooltip
                 align='center'
                 min-width='11.111%'
                 label="销售单号">
                 <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sale_code}} </span></template>
              </el-table-column>              
	            <el-table-column
	            prop=""
	            show-overflow-tooltip
	            align='center'
	            min-width='5.555%'
	            label="操作员">
              <template slot-scope="scope"><span @click="layerOpen($event)">{{userLoginInfo.config.contacts}} </span></template>
	            </el-table-column>	            
	            <el-table-column
	                prop="sale_obj_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='5.555%'
	                label="销售对象">
                  <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.sale_obj_name}} </span></template>
	            </el-table-column>
              <el-table-column
                prop="pay_type"
                show-overflow-tooltip
                align='center'
                min-width='5.555%'
                label="付款方式">
                <template slot-scope="scope"><span @click="layerOpen($event)">{{scope.row.pay_type}} </span></template>
              </el-table-column>              
	            <el-table-column
	                prop="remark"
	                show-overflow-tooltip
	                align='center'
	                min-width='5.555%'
	                label="备注">
	                <template slot-scope="scope">
	                	<span @click="layerOpen($event)">{{scope.row.remark}}</span>
	                </template>	                
	            </el-table-column> 
	            <el-table-column
	                label="操作"
	                min-width='15.555%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
                        <el-button  type="text" @click="showAlert(scope.row, scope.$index)" size="small"  class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          修改
                        </el-button>                                                                       	                    	
	                    	<el-button type="text" @click="showDelete(scope.row.sale_id)" size="small"  class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		删除
	                    	</el-button>                    	
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
        <edit-sales-records v-if="show" :that="that" :goods_details="goods_details" @hideModal="hideModal" @submit="submit"></edit-sales-records>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
// import layerOpen from '../../../../script/tableLayer.js'
import erpTabtop from './sales_records-top/sales_records-top.vue'
import erpPaging from '../../../paging/pagingShort'
import edit_sales_records from '../salesRecords/alert-box/edit_sales_records.vue'
import {sale_index,sale_del} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'salesman',
  data () {
    return {
      that: this,
      tableData: [],
		  value: '',
		  title: '修改销售信息',
		  show: false,
		  showDe:false,
		  deleteId:null,
      goods_name_search:'',//搜索名字
      counts:null,//总条数 
      page_num:1,//当前页数 
      last_page:1,//总页数 
      pageSize:8,//每页显示条数
      delectType:'0',//0是删除，2是顺延,3是取消消课,4是还原,5是离职,6是复职
      goods_details:{},

    }
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{
    date: function (value) {   
     var d = new Date(value * 1000);    //根据时间戳生成的时间对象
     var date = that.appendzero(d.getFullYear()) + "-" + 
     that.appendzero(d.getMonth() + 1) + "-" +
     that.appendzero(d.getDate());           
     return date
    },    
  	serialNumber ( val ){
  		return (that.page_num-1)*8+(val + 1)
  	}
  }, 
  methods: {
    appendzero:function(obj)
    {
      if(obj<10) return "0" +""+ obj;
      else return obj;
    },    
    paging_father (n) {
      this.sale_index(n)
    },    
    // layerOpen:function (a,b) {//引进点击省略号查看全部的方法
    // 	 layerOpen.layerOpen(a,b)	
    // },
    async sale_index (n) {
    	let t = (new Date()).getTime();
    	let resDate = await sale_index(n,this.userLoginInfo.orgid,t,this.goods_name_search)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    		this.last_page = resDate.data.data.last_page
    		this.counts = resDate.data.data.total
    		this.page_num = n;   		            
    	}
    },            
    sale_del (id) {
		// 删除商品
		sale_del(id).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				if((this.tableData.length -1) == 0){
					this.sale_index (this.page_num-1)
				}
				else{
					this.sale_index (this.page_num)
				}
			}					
    })
		}, 		        
    showAlert(a,b){
      this.goods_details = a
      console.log(this.goods_details)
    	this.show = true
    },                      
    hideModal() {
    // 取消弹窗回	        
	    this.show = false
    },                    
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	this.sale_del(id)       
    	this.showDe = false    	
    },
    submit() {
    // 确认弹窗回调   
	    this.last_page = 0
      this.paging_father(this.page_num)
	    this.show = false
    },                        
  }, 
  created: function () {
  	this.sale_index (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")  	
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editSalesRecords: edit_sales_records,
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
.noIcon{
	margin:0 vw(8)!important;
}
.noIcon .icon-width {
	width:1px
}
.el-table /deep/ th>.cell{
  padding:0;
  font-size:vw(16)
}
@media screen and(max-width: 1381px){
  .el-table /deep/ th>.cell{
    font-size:vw1(16)
  }
  .noIcon{
    margin:0 vw1(8)!important;
  }
}
</style>
